import React, { Component, Fragment } from 'react';
import { withStyles } from '@material-ui/core';
import { getFriendlyLabel } from '../../util';
import TreeItem from '@material-ui/lab/TreeItem';
import DragIndicatorIcon from '@material-ui/icons/DragIndicator';

type ComponentDrawerItemProps = {
  node: any;
  nodeId: any;
  classes: any;
  triggerHelpDrawer: (helpContent: any) => void;
};

const styles = (theme: any) => ({
  componentItem: {
    display: 'inline-block' as 'inline-block',
    padding: theme.spacing(1),
    textAlign: 'center' as 'center',
    backgroundColor: '#FFFFFF',
    borderRadius: 5,
    margin: 5,
    fontSize: 12,
    width: '45%',
    maxWidth: '45%'
  },
  icon: {
    marginBottom: 4,
    fontSize: 20
  }
});

class ComponentDrawerItem extends Component<ComponentDrawerItemProps> {
  render() {
    //console.log("this.props.node.documentation: ");
    //console.log(this.props.node.documentation);
    return (
      <TreeItem
        nodeId={this.props.nodeId}
        label={
          <Fragment>
            <DragIndicatorIcon className="drag-icon" />
            <span>
              {
                this.props.node.facet_info.custom_facet_type && this.props.node.facet_info.custom_facet_type === 'API' ? (
                  this.props.node.facet_info.custom_facet_type
                ):(
                  getFriendlyLabel(this.props.node.facet_info.facet_type)
                )
              }
            </span>
            {this.props.node.documentation &&
              this.props.node.documentation !== '' && (
                <span className="drawer-help form-help-icon" onClick={() => { this.props.triggerHelpDrawer(this.props.node.documentation)}}>?</span>
              )}
          </Fragment>
        }
        className="grabbable"
        draggable={true}
        onDragStart={event => {
          event.dataTransfer.setData(
            'node-data',
            JSON.stringify(this.props.node)
          );
        }}
      ></TreeItem>
    );
  }
}

export default withStyles(styles)(ComponentDrawerItem);
