import React, { Fragment } from 'react';
import * as EmailValidator from 'email-validator';
import IkiDialog from '../IkiDialog';
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';
//import FolderOpenIcon from '@material-ui/icons/FolderOpen';
//import PersonIcon from '@material-ui/icons/Person';

export function DuplicateProjectDialog(props: any) {
  const {
    handleDialogClose,
    open,
    projectID,
    projectName,
    onSubmit,
    duplicateSubmitLoading
  } = props;

  const [newName, setNewName] = React.useState(projectName);
  //const [nameErrorMsg, setNameErrorMsg] = React.useState('');
  const [nameErrorState, setNameErrorState] = React.useState(false);
  const [targetUserEmail, setTargetUserMail] = React.useState('');
  const [emailErrorMsg, setEmailErrorMsg] = React.useState('');

  const handleTargetUserEmailChange = (e: any) => {
    setTargetUserMail(e.target.value);
  };

  const handleNameChange = (e: any) => {
    setNewName(e.target.value);
  };

  const handleDuplicateDialogSubmit = (e: any) => {
    var projectNameValid: boolean = false;
    var projectEmailValid: boolean = false;
    if (newName !== '') {
      projectNameValid = true;
      setNameErrorState(false);
    } else {
      setNameErrorState(true);
    }
    if (targetUserEmail !== '') {
      if (EmailValidator.validate(targetUserEmail) === true) {
        setEmailErrorMsg('');
        projectEmailValid = true;
      } else {
        setEmailErrorMsg('Invalid email'); 
        projectEmailValid = false;
      }
    }else{
      projectEmailValid = true;
    }
    if(projectNameValid && projectEmailValid){
      onSubmit(projectID, newName, targetUserEmail);
    }
  };

  const handleOnEnter = () => {
    var copyName = projectName;
    var copyIndex = copyName.indexOf('copy');
    if (copyIndex > -1) {
      var copyVersion = parseInt(copyName.substring(copyIndex + 4));
      if (isNaN(copyVersion)) {
        copyVersion = 1;
        copyName += ' ' + copyVersion;
      } else {
        copyVersion++;
        copyName = copyName.substring(0, copyIndex + 4) + ' ' + copyVersion;
      }
    } else {
      copyName = projectName + ' copy';
    }
    setNewName(copyName);
  };

  const handleOnClose = () => {
    handleDialogClose();
    setNameErrorState(false);
  };

  return (
    <IkiDialog
            _open={open}
            _onEnter={handleOnEnter}
            _onClose={handleOnClose}
            _type='clone'
            _icon={false}
            _title='Clone project'
            _subtitle=''
            _size='xs'
            _align='left'
            _content={
              <>
                <div className="section">
                  <div className="txt-dialog-faded1">{projectID}</div>
                  <div className="txt-dialog-impact1">{projectName}</div>
                </div>
                <div className="section">
                  <IkiInput 
                    _label="Project name"
                    _placeholder="Enter project name"
                    _type='text'
                    _autoComplete="new-password"
                    _value={newName}
                    _onChange={handleNameChange}
                    _characterCounter={40}
                    _autoFocus={true}
                    _fullWidth={true}
                    _error={nameErrorState}
                  />
                  <IkiInput 
                    _label="Project owner (optional), current user by default"
                    _placeholder="Enter email of user that will be project owner"
                    _type='text'
                    _autoComplete="new-password"
                    _value={targetUserEmail}
                    _onChange={handleTargetUserEmailChange}
                    _characterCounter={40}
                    _fullWidth={true}
                    _autoFocus={false}
                    _error={emailErrorMsg}
                  />
                </div>
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={handleOnClose}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Clone"
                  _type='button'
                  _fullWidth={false}
                  _loading={duplicateSubmitLoading}
                  _disabled={duplicateSubmitLoading}
                  _onClick={handleDuplicateDialogSubmit}
                />
              </Fragment>
            }
        />
  );
}
