import React, { useState, Fragment } from 'react';
import {
  FormHelperText
} from '@material-ui/core';
import FileInput from '../file-input';
import ErrorIcon from '@material-ui/icons/Error';
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';
import IkiDialog from '../../components/IkiDialog';

export function ImportFlowDialog(props: any) {
  const {
    handleDialogClose,
    handleSubmit,
    handleCancel,
    open,
    isImportingProgress,
    importFlowNameExists
  } = props;
  const [flowName, setFlowName] = useState('');
  const [flowNameError, setFlowNameError] = useState('');
  const [file, setFile] = useState();
  const [definition, setDefinition] = useState('');
  const [fileError, setFileError] = useState('');

  const handleNameChange = (e: any) => {
    setFlowName(e.target.value);
  };

  const handleSubmitImportFlow = () => {
    //console.log('flowName:' + flowName);
    //console.log('importFlowNameExists:' + importFlowNameExists);
    var countErrors: number = 0;

    if (definition === '' || definition === null) {
      countErrors++;
      setFileError('Required')
    }else {
      setFileError('')
    }

    if (flowName === '') {
      countErrors++;
      setFlowNameError('Required')
    } else {
      if(importFlowNameExists) {
        countErrors++;
        setFlowNameError('Flow with same name already exists. Please choose different name');
      } else {
        setFlowNameError('')
      }
    }

    if(countErrors === 0) {
      handleSubmit(flowName, definition);
    }
  }

  const handleOnEnter = () => {
    setFlowName('');
    setFlowNameError('');
    setFile(null);
    setFileError('');
    setDefinition('');
  }

  const handleOnClose = () => {
    setFile(null);
    setDefinition('');
    setFileError('');
    setFlowName('');
    setFlowNameError('');
    handleDialogClose();
  };

  return (
    <IkiDialog
            _open={open}
            _onClose={handleOnClose}
            _onExit={handleOnClose}
            _onEnter={handleOnEnter}
            _type='regular'
            _icon={false}
            _title='Import flow'
            _subtitle=''
            _size='xs'
            _content={
              <Fragment>
                <IkiInput 
                  _label="Flow name"
                  _placeholder="Enter flow name"
                  _type='text'
                  _value={flowName}
                  _onChange={handleNameChange}
                  _characterCounter={40}
                  _autoFocus={true}
                  _fullWidth={true}
                  _error={flowNameError}
                />
                <FileInput
                  onFileAdded={(file: File) => {
                    var reader = new FileReader();
                    reader.readAsText(file, 'UTF-8');
                    reader.onload = function(evt: any) {
                      try {
                        var pipelineDefinition = JSON.parse(evt.target.result);
                        if (
                          !pipelineDefinition.facets ||
                          pipelineDefinition.facets.length === 0
                        ) {
                          setFileError('Invalid pipeline');
                        } else {
                          setDefinition(pipelineDefinition);
                          setFile(file);
                          setFileError('');
                        }
                      } catch (e) {
                        setFileError('Invalid pipeline');
                      }
                    };
                    reader.onerror = function(evt: any) {
                      setFileError('Unable to read file');
                    };
                  }}
                  // disabled={uploadStatus === 'pending'}
                  accept=".iki"
                  fileData={file}
                ></FileInput>
                {fileError !== '' && (
                  <FormHelperText className="input-error" error>
                    <ErrorIcon />
                    <span>{fileError}</span>
                  </FormHelperText>
                )}
              </Fragment>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={() => handleCancel()}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Create"
                  _type='button'
                  _fullWidth={false}
                  _loading={isImportingProgress}
                  _disabled={isImportingProgress}
                  _isAlternate={true}
                  _onClick={handleSubmitImportFlow}
                />
              </Fragment>
            }
    />
  );
}
