import React, { Component, Fragment } from 'react';
import {
  Button,
  Grid,
  Box,
  TablePagination,
  Card,
  CardContent
} from '@material-ui/core';
import FileSaver from 'file-saver';
import SearchIcon from '@material-ui/icons/Search';
import ViewListIcon from '@material-ui/icons/ViewList';
import ViewComfyIcon from '@material-ui/icons/ViewComfy';
import FindInPageIcon from '@material-ui/icons/FindInPage';
import StorageIcon from '@material-ui/icons/Storage';
import {
  downloadDataset
} from '../../../services/app-data.service';
import {
  AddDatasetDialog,
  DeleteDatasetDialog,
  EditDatasetDialog
} from '../../../components/dialogs';
import SnackbarMsg from '../../../components/SnackbarMsg';
import TablePaginationActions from '@material-ui/core/TablePagination/TablePaginationActions';
import IkiButton from '../../../components/IkiFormElements/IkiButton';
import IkiButtonGroup from '../../../components/IkiFormElements/IkiButtonGroup';
import IkiInput from '../../../components/IkiFormElements/IkiInput';
import IkiGrid from '../../../components/IkiLayout/IkiGrid';
import IkiTable from '../../../components/IkiLayout/IkiTable';

type DatasetsProps = {
  datasets: any;
  projects: any;
  projectId: string;
  subpageClick: (subpageLink: string) => void;
  createDataset: (projectId: string, datasetName: string, file: File) => void;
  editDataset: (projectId: string, datasetId: string, datasetName: string, file: File) => void;
  deleteDataset: (datasetId: string) => void;
  uploadProgress: any;
  uploadStatus: any;
};

type DatasetsState = {
  sort: string;
  d_sort: string,
  deleteDialogState: boolean;
  deletePipeline: any;
  deletePipelineName: string;
  deletePipelineID: string;
  selectedDatasetData?: any;
  selectedDashData?: any;
  newFlowDialogState: boolean;
  newFlowName: string;
  newFlowNameError: boolean;
  newFlowNameErrorMsg: string;
  projectId: string;
  projectNameEditMode: Boolean;
  title: string;
  projectDeleteDialogState: boolean;
  projectShareDialogState: boolean;
  projectShareDialogErrorState: boolean;
  projectShareDialogErrorMsg: string;
  snackbarShareProSuccessState: boolean;
  editProjectDialogState: boolean;
  addDatasetDialogState: boolean;
  addDashboardtDialogState: boolean;
  addDatasetMinimize: boolean;
  editDatasetDialogState: boolean;
  editDashboardDialogState: boolean;
  deleteDatasetDialogState: boolean;
  uploadDatasetDialogState: boolean;
  duplicateDialogProjectState: boolean;
  importFlowDialogState: boolean;
  importFlowNameExists: boolean;
  tabValue: number;
  datasetEditMode: boolean;
  dashEditMode: boolean;
  snackbarOpen: boolean;
  snackbarMessage: any;
  snackbarType: string;
  sharingLoading: boolean;
  renamingLoading: boolean;
  deletingLoading: boolean;
  newlyAddedDatasetNameExists: boolean;
  newlyAddedDashNameExists: boolean;
  addFlowLoading: boolean;
  rowsPerPage: number;
  page: number;
  searchTerm: string;
  flowsFilter: string;
  renderAsList: boolean;
  tableSortAsc: boolean;
  tableSortProperty: string;
  toggleFilterDisplay: boolean;
  clickedBox: string;
  d_rowsPerPage: number;
  d_page: number;
  d_searchTerm: string;
  d_flowsFilter: string;
  d_renderAsList: boolean;
  d_tableSortAsc: boolean;
  d_tableSortProperty: string;
  d_toggleFilterDisplay: boolean;
  d_clickedBox: string;
  cloneFlowDialogState: boolean;
  cloneFlowId: string;
  cloneFlowName: string;
  cloneFlowNameError: boolean;
  cloneFlowNameErrorMsg: string;
  cloneProcess: boolean;
  sessionCreated: boolean;
  sessionID: string;
  showContent: boolean;
  loadingTxt: string;
};

class DatasetsComponent extends Component<DatasetsProps, DatasetsState> {
  state: DatasetsState = {
    sort: 'all',
    d_sort: 'all',
    deletePipeline: null,
    deleteDialogState: false,
    deletePipelineName: '',
    deletePipelineID: '',
    newFlowDialogState: false,
    newFlowName: '',
    newFlowNameError: false,
    newFlowNameErrorMsg: '',
    projectId: '',
    projectNameEditMode: false,
    title: '',
    projectDeleteDialogState: false,
    projectShareDialogState: false,
    projectShareDialogErrorState: false,
    projectShareDialogErrorMsg: '',
    snackbarShareProSuccessState: false,
    editProjectDialogState: false,
    addDatasetDialogState: false,
    addDashboardtDialogState: false,
    deleteDatasetDialogState: false,
    uploadDatasetDialogState: false,
    editDatasetDialogState: false,
    editDashboardDialogState: false,
    duplicateDialogProjectState: false,
    tabValue: 0,
    datasetEditMode: false,
    addDatasetMinimize: false,
    dashEditMode: false,
    snackbarOpen: false,
    snackbarMessage: null,
    snackbarType: '',
    sharingLoading: false,
    renamingLoading: false,
    deletingLoading: false,
    newlyAddedDatasetNameExists: false,
    newlyAddedDashNameExists: false,
    addFlowLoading: false,
    rowsPerPage: 16,
    page: 0,
    searchTerm: '',
    flowsFilter: 'all',
    renderAsList: false,
    tableSortAsc: true,
    tableSortProperty: '',
    toggleFilterDisplay: false,
    clickedBox: '',
    d_rowsPerPage: 16,
    d_page: 0,
    d_searchTerm: '',
    d_flowsFilter: 'all',
    d_renderAsList: false,
    d_tableSortAsc: true,
    d_tableSortProperty: '',
    d_toggleFilterDisplay: false,
    d_clickedBox: '',
    importFlowDialogState: false,
    importFlowNameExists: false,
    cloneFlowDialogState: false,
    cloneFlowId: '',
    cloneFlowName: '',
    cloneFlowNameError: false,
    cloneFlowNameErrorMsg: '',
    cloneProcess: false,
    sessionCreated: false,
    sessionID: '',
    showContent: false,
    loadingTxt: ''
  };
  componentDidMount() {
    //console.log("this.props: ", this.props);
  }
  componentWillUnmount() {

  }

  onRequestSort = (event: any, property: string) => {
    this.setState({
      tableSortAsc: !this.state.tableSortAsc,
      tableSortProperty: property
    });
  };

  handleChangeSearchTerm = (e: any) => {
    this.setState({
      searchTerm: e.target.value
    });
  };

  handleRenderAsList = () => {
    this.setState({ renderAsList: true });
  };

  handleRenderAsGrid = () => {
    this.setState({ renderAsList: false });
  };

  handleChangePage = (event: unknown, newPage: number) => {
    this.setPage(newPage);
  };

  handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.setRowsPerPage(parseInt(event.target.value, 10));
    this.setPage(0);
  };

  setPage = (page: number) => {
    this.setState({ page: page });
  };

  setRowsPerPage = (rows: number) => {
    this.setState({ rowsPerPage: rows });
  };

  handleToggleFilter = () => {
    if (this.state.toggleFilterDisplay === true) {
      this.setState({ toggleFilterDisplay: false });
    } else {
      this.setState({ toggleFilterDisplay: true });
    }
  }

  sortDatasets = (datasets: any) => {
    //console.log("SORT PROJECTS: ");
    //console.log(datasets);
    if (this.state.tableSortProperty !== '') {
      return this.sortOn(datasets, this.state.tableSortProperty);
    } else {
      return datasets;
    }
  };

  sortOn = (arr: any, property: string) => {
    //console.log("ARR: ");
    //console.log(arr);
    var sortedArray = arr.sort((a: any, b: any) => {
      if (b[property] < a[property]) {
        return this.state.tableSortAsc ? 1 : -1;
      }
      if (b[property] > a[property]) {
        return this.state.tableSortAsc ? -1 : 1;
      }
      return 0;
    });
    return sortedArray;
  };

  filterDatasets = (datasets: any) => {
    //console.log("import p: ");
    //console.log(datasets);
    if (this.state.searchTerm !== '') {
      datasets = datasets.filter(
        (p: any) =>
          p.name
            .toLowerCase()
            .indexOf(this.state.searchTerm.toLowerCase()) > -1
      );
    }
    return datasets;
  };

  onNewDatasetItemClick() {
    this.setState({
      addDatasetDialogState: true,
      datasetEditMode: false
    });
  }

  viewTypeSwitchButtons = () => {
    const buttons = [
      <Button
        key={1}
        color={this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsList()}
      >
        <ViewListIcon />
      </Button>,
      <Button
        key={2}
        color={!this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsGrid()}
      >
        <ViewComfyIcon />
      </Button>
    ]
    return buttons
  }

  getDatasetsFilters = (datasets: any) => {
    if(datasets) {
      return (
        <div className="filters-and-pagination">
          <div className="table-filters">
                <IkiInput 
                  _label=""
                  _placeholder="Search..."
                  _type='text'
                  _autoComplete="new-password"
                  _value={this.state.searchTerm}
                  _size="small"
                  _margin="none"
                  _filterType={true}
                  _onChange={(e: any) => this.handleChangeSearchTerm(e)}
                  _startIcon={
                    {
                      _icon: (
                        <SearchIcon fontSize="small" />
                      )
                    }
                  }
                />
                <IkiButtonGroup
                  _variant="contained"
                  _size="small"
                  _color="secondary"
                  _buttons={
                    this.viewTypeSwitchButtons()
                  }
                />
              </div>
              <div className="table-pagination">
                <TablePagination
                  component="div"
                  className="flows-pagination"
                  labelRowsPerPage="Datasets per page"
                  rowsPerPageOptions={[8, 16, 32]}
                  colSpan={3}
                  count={datasets.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  SelectProps={{
                    inputProps: {
                      'aria-label': 'Datasets per page'
                    }
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
                />
              </div>
          </div>
      );
    }
  };

  getDatasets(datasets: any) {
    var datasetsArr: any = [], pageDatasets, datasetsWithUploading: any = [];
    let datasetUploading: any;
    if(this.props.datasets) {
      datasetsArr = this.props.datasets;
      datasetsArr = this.sortDatasets(this.filterDatasets(datasetsArr));
      pageDatasets = datasetsArr.slice(
        this.state.page * this.state.rowsPerPage,
        this.state.page * this.state.rowsPerPage + this.state.rowsPerPage
      );
      datasetsArr.forEach((datasetData: any, index: number) => {
        datasetUploading = this.props.uploadProgress.filter((uploadProgressData: any)=>{
          return uploadProgressData.status !== 'complete' && datasetData.dataset_id === uploadProgressData.datasetId}
        )
        if(Object.keys(datasetUploading).length > 0){
          //datasetsWithUploading.push(datasetData.dataset_id);
          datasetsWithUploading[datasetData.dataset_id] = datasetData.dataset_id;
        }
      });
    }
    //console.log("datasetsWithUploading: ", datasetsWithUploading, ", datasetUploading: ", datasetUploading);
    if (datasetsArr && datasetsArr.length === 0) {
      return (
        <Fragment>
          {this.getDatasetsFilters(datasetsArr)}
          <Grid item xs={12}>
            <Box display="flex" alignItems="center" className="no-results">
              <FindInPageIcon className="no-results-icon" />
              <span className="no-results-txt">No datasets found</span>
            </Box>
          </Grid>
        </Fragment>
      );
    }
    return(
      <Fragment>
        {this.getDatasetsFilters(datasetsArr)}
        {this.state.renderAsList ? (
          <Grid item xs={12} className="i-pb-0">
            <Card className="cardbox has-table tableHolder">
              <CardContent className="content table-inside">
                <IkiTable
                      _type="Dataset"
                      _link='/dataset/'
                      _header={
                        [
                          {
                            key: 'name',
                            name: 'Dataset'
                          },
                          {
                            key: 'dataset_id',
                            name: 'ID'
                          }
                        ]
                      }
                      _data={pageDatasets}
                      _functions={
                        [
                          {
                            edit: (item: any) => this.handleDatasetEdit(item)
                          },
                          {
                            delete: (item: any) => this.handleDatasetDelete(item)
                          },
                          {
                            download: (item: any) => this.handleDatasetDownload(item)
                          },
                          {
                            browse: (itemId: string) => this.props.subpageClick(`/dataset/${itemId}`)
                          }
                        ]
                      }
                  />
              </CardContent>
            </Card>
          </Grid>
        ):(
          <IkiGrid
            _type='Dataset'
            _link='/dataset/'
            _icon={<StorageIcon />}
            _data={pageDatasets}
            _uploadInfo={datasetUploading}
            _functions={
              [
                {
                  edit: (item: any) => this.handleDatasetEdit(item)
                },
                {
                  delete: (item: any) => this.handleDatasetDelete(item)
                },
                {
                  download: (item: any) => this.handleDatasetDownload(item)
                },
                {
                  browse: (itemId: string) => this.props.subpageClick(`/dataset/${itemId}`)
                }
              ]
            }
          />
        )}
        {datasetsArr.length > this.state.rowsPerPage && (
          <TablePagination
                  component="div"
                  className="pagination-sm"
                  labelRowsPerPage="Datasets per page"
                  rowsPerPageOptions={[8, 16, 32]}
                  colSpan={3}
                  count={datasetsArr.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  SelectProps={{
                    inputProps: {
                      'aria-label': 'Datasets per page'
                    }
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
          />
        )}
      </Fragment>
    );
  }

  render() {
    var datasets: any = [];
    if(this.props.datasets){
      datasets = this.props.datasets;
    }
    var projectData = this.props.projects.filter(
      (p: any) => p.project_id === this.props.projectId
    )[0];
    return (
      <Fragment>
        <div className="under-tab-buttons">
                    <IkiButton
                      _variant='contained'
                      _color='primary'
                      _label="Upload New Dataset"
                      _type='button'
                      _fullWidth={false}
                      _loading={false}
                      _startIcon={
                        <i className="zmdi zmdi-cloud-upload zmdi-hc-fw"></i>
                      }
                      _onClick={() => this.onNewDatasetItemClick()}
                    />
        </div>
        <div className="cardbox-holder">
          {this.getDatasets(datasets)}
        </div>
            <AddDatasetDialog
                projectId={this.props.projectId}
                projectName={projectData ? projectData.name : ''}
                isEdit={this.state.datasetEditMode}
                open={
                this.state.addDatasetDialogState 
                }
                name={
                this.state.selectedDatasetData
                    ? this.state.selectedDatasetData._name
                    : ''
                }
                datasetID={
                    this.state.selectedDatasetData
                    ? this.state.selectedDatasetData._id
                    : ''
                }
                uploadStatus={this.props.uploadStatus}
                handleDialogClose={() => this.handleAddDatasetDialogClose()}
                handleSubmit={(name: string, file: File) =>
                this.handleAddDatasetDialogSubmit(name, file)
                }
                handleSubmitOverwrite={(name: string, file: File) =>
                this.handleAddDatasetDialogSubmitOverwrite(name, file)
                }
                handleCancel={() => this.handleAddDatasetDialogClose()}
                newDatasetNameExists={this.state.newlyAddedDatasetNameExists}
                dismissAlert={() => this.handleDismissDatasetNameExists()}
            ></AddDatasetDialog>
            <DeleteDatasetDialog
                datasetId={
                    this.state.selectedDatasetData
                    ? this.state.selectedDatasetData._id
                    : ''
                }
                datasetName={
                    this.state.selectedDatasetData
                    ? this.state.selectedDatasetData._name
                    : ''
                }
                open={this.state.deleteDatasetDialogState}
                handleDialogClose={() => this.handleDeleteDatasetDialogClose()}
                handleSubmit={() => this.handleDeleteDatsetDialogSubmit()}
                handleCancel={() => this.handleDeleteDatasetDialogClose()}
            ></DeleteDatasetDialog>
            <EditDatasetDialog
                datasetId={
                    this.state.selectedDatasetData
                    ? this.state.selectedDatasetData._id
                    : ''
                }
                datasetName={
                    this.state.selectedDatasetData
                    ? this.state.selectedDatasetData._name
                    : ''
                }
                open={this.state.editDatasetDialogState}
                handleDialogClose={() => this.handleEditDatasetDialogClose()}
                handleSubmit={() => this.handleEditDatasetDialogSubmit()}
                handleCancel={() => this.handleEditDatasetDialogClose()}
            ></EditDatasetDialog>
            <SnackbarMsg
              snackbarOpen={this.state.snackbarOpen}
              snackbarType={this.state.snackbarType}
              snackbarMsg={this.state.snackbarMessage}
              onSnackbarClose={this.handleSnackbarClose}
            />
      </Fragment>
    );
  }

  handleEditDatasetDialogSubmit() {
    this.handleEditDatasetDialogClose();
  }

  handleUploadDatasetDialogClose() {
    this.setState({ uploadDatasetDialogState: false });
  }

  handleUploadDatasetDialogSubmit() {
    this.handleUploadDatasetDialogClose();
  }

  handleDeleteDatsetDialogSubmit() {
    if (this.state.selectedDatasetData) {
      this.props.deleteDataset(this.state.selectedDatasetData._id);
      this.setState({
        snackbarMessage: (
          <Fragment>
            <div className="msg">You have succesfully deleted dataset: </div>
            <div className="val">{this.state.selectedDatasetData._name}</div>
          </Fragment>
        ),
        snackbarType: 'success',
        snackbarOpen: true
      });
    }
    this.handleDeleteDatasetDialogClose();
  }

  handleDeleteDatasetDialogClose() {
    this.setState({ deleteDatasetDialogState: false });
  }

  handleDismissDatasetNameExists() {
    this.setState({
      newlyAddedDatasetNameExists: false
    });
  }

  handleAddDatasetDialogSubmitOverwrite(name: string, file: File) {
    // CALL TO API TO OVERWRITE DATASET
    //console.log('OVERWRITE dataset: ' + name);
    //this.props.createDataset(this.props.projectId, name, file);
    var existingDatasetId = '';
    const datasets = this.props.datasets.filter(
      (datasetData: any) =>
        datasetData.project_id === this.props.projectId
    );
    datasets.forEach((datasetData: any) => {
        //console.log("NAME: " + name + ", datasetData.name: " + datasetData.name + ", id: " + datasetData.dataset_id);
        if (
          datasetData.name === name &&
          datasetData.project_id === this.props.projectId
        ) {
          existingDatasetId = datasetData.dataset_id;
        }
    });
    if (existingDatasetId !== '') {
      this.props.editDataset(
        this.props.projectId,
        existingDatasetId,
        name,
        file
      );
      this.setState({
        newlyAddedDatasetNameExists: false,
        /*snackbarMessage: (
          <Fragment>
            <div className="msg">You have succesfully updated dataset: </div>
            <div className="val">{name}</div>
          </Fragment>
        ),
        snackbarType: 'success',
        snackbarOpen: true*/
      });
      this.handleAddDatasetDialogClose();
    }
  }

  handleAddDatasetDialogSubmit(name: string, file: File) {
    var checkDatasetNameOverlap = false;
    const datasets = this.props.datasets.filter(
      (datasetData: any) =>
        datasetData.project_id === this.props.projectId
    );
    datasets.forEach((datasetData: any) => {
        //console.log("NAME: " + name + ", datasetData.name: " + datasetData.name + ", id: " + datasetData.dataset_id);
        if (
          datasetData.name === name &&
          datasetData.project_id === this.props.projectId
        ) {
          checkDatasetNameOverlap = true;
        }
    });
    //console.log("checkDatasetNameOverlap: " + checkDatasetNameOverlap);
    if (checkDatasetNameOverlap !== false) {
      this.setState({ newlyAddedDatasetNameExists: true });
    } else {
      if (!this.state.datasetEditMode) {
        this.props.createDataset(this.props.projectId, name, file);
        //this.props.createDataset(this.props.projectId, name, file, "", false);
        this.handleAddDatasetDialogClose();
      } else {
        this.props.editDataset(
          this.props.projectId,
          this.state.selectedDatasetData._id,
          name,
          file
        );
        this.handleAddDatasetDialogClose();
      }
      this.handleAddDatasetDialogClose();
    }
  }

  handleAddDatasetDialogClose() {
    this.setState({ addDatasetDialogState: false });
  }

  handleEditDatasetDialogClose() {
    this.setState({ addDatasetDialogState: false });
  }
  handleDatasetEdit(data: any) {
    console.log("handleDatasetEdit: ", data);
    this.setState({
      addDatasetDialogState: true,
      selectedDatasetData: data,
      datasetEditMode: true
    });
  }
  handleDatasetDelete(data: any) {
    //console.log("handleDatasetDelete: ", data);
    this.setState({
      deleteDatasetDialogState: true,
      selectedDatasetData: data
    });
  }
  handleDatasetUpload(data: any) {
    this.setState({
      uploadDatasetDialogState: true,
      selectedDatasetData: data
    });
  }
  handleDatasetDownload(data: any) {
    //console.log("handleDatasetDownload: ", data);
    downloadDataset(data.name, data.project_id, data.dataset_id).then(
      (res: any) => {
        //console.log("download res: ", res);
        /*var blb = new Blob([res.file], { 
          type: res.fileType
        });*/
        FileSaver.saveAs(res.file.blobFile, res.file.fileName);
        //window.location = res.fileUrl;
        /*var link = document.createElement('a');
        link.download = 'IkigaiLabs_' + res.file.fileName;
        link.setAttribute("download", 'IkigaiLabs_' + res.file.fileName);
        //link.href = res.fileUrl;
        link.href = URL.createObjectURL(res.file.blobFile);
        link.click();*/
      }
    );
  }

  handleSnackbarClose = () => {
    this.setState({ snackbarOpen: false });
  };
  
}

export default DatasetsComponent;
