import React, { Component } from 'react';
import SchedulesComponent from '../../../routes/schedules/schedules-component';
import classNames from 'classnames/bind';
import styles from './ContentOverlay.module.scss';
let cx = classNames.bind(styles);

export default class IkiContentOverlay extends Component {
  prepareContent = () => {
    const _type = this.props._type ? this.props._type : '';
    const _menuItem = this.props._menuItem ? this.props._menuItem : '';
    let _navMinimzed = this.props._navMinimzed ? this.props._navMinimzed : false;
    //console.log("IkiContentOverlay props...", this.props);
    var contentAreaClass = cx({
      contentArea: true,
      'minimized': _navMinimzed
    });
    return (
      <>
        <div className={contentAreaClass}>
          {_menuItem === 'schedule' ? (
            <SchedulesComponent />
          ):(
            <div>Something went wrong. Please reload page</div>
          )}
        </div>
      </>
    );
  }
  render() {
    return this.prepareContent();
  }
}
