import { actionIds } from '../common';

export type FacetState = any;

let defaultState = {
  componentList: [],
  facetDocLoaded: false,
  facetDocError: false,
  facetDocErrorMessage: ''
};

export const facetReducer = (state: FacetState = defaultState, action: any) => {
  switch (action.type) {
    case actionIds.GET_FACET_DOC_COMPLETE:
      return handleGetFacetDocComplete(state, action.payload);
    case actionIds.GET_FACET_DOC_FAIL:
      return handleGetFacetDocFail(state, action.payload);
    default:
      return state;
  }
};

const handleGetFacetDocComplete = (state: any, data: any) => {
  return {
    ...state,
    componentList: data,
    facetDocLoaded: true,
    facetDocError: false,
    facetDocErrorMessage: ''
  };
};

const handleGetFacetDocFail = (state: any, reason: string) => {
  return {
    ...state,
    facetDocErrorMessage: reason,
    facetDocError: true
  };
};
