interface Props {
    data: any;
    match: any;
    classes: any;
    history: any;
    metabaseLogin: boolean;
    metabaseLoginId: string;
    uploadProgress: any;
    enterSession: (projectId: string) => void;
    fireReturnToProject: (sourcePage: string) => void;
    cleanSession: () => void;
    logout: () => void;
}

interface State {
    isLoaded: boolean;
    metabaseSessionActive: boolean;
    projectId: string;
    projectName: string;
    datasetId: string;
    datasetName: string;
    datasetHeader: any[];
    iFrameURL: string;
    iFrameContentLoaded: boolean;
    iframeKey: number;
    metabaseContent: any;
    tabValue: number;
    dataPrepOptions: any[];
    isDataprepDialogOpen: boolean;
    uniformizationPreferences: {};
    selectedDataprepName: string;
    snackbarOpen: boolean;
    snackbarMessage: any;
    snackbarType: string;
    hideAutoLayoutLoading: boolean;
    loadingDone: boolean;
    loadingTxt: string;
    statusState: string;
    statusTxt: string;
    statusDatetime: string;
    saveDialogOpen: boolean;
    isSaveInProgress: boolean;
    newDatasetNameExists: boolean;
    goBackAnchor: any;
    updateOrSave: string;
}

