import React, { Component, Fragment } from 'react';
//import { Drawer, withStyles } from '@material-ui/core';
import ComponentDrawerItem from './component-drawer-item';
import { getFriendlyLabel } from '../../util';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
//import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
//import { Link } from 'react-router-dom';
//import { IkigaiLogo } from '../../components/IkigaiLogo';
import HelpDrawer from './help-drawer';
import './component-drawer.scss';
//import { ChatNavModule } from '../../components/chat';

type ComponentDrawerProps = {
  components: [];
  facetDocLoaded: boolean;
  backLink: string;
  uploadProgress:any;
};

type ComponentDrawerStates = {
  toggleHelpDrawer: boolean;
  helpDrawerContent: any;
};

class ComponentDrawer extends Component<ComponentDrawerProps, ComponentDrawerStates> {
  state = {
    toggleHelpDrawer: false,
    helpDrawerContent: ''
  }
  render() {
      return (
          <Fragment>
                <div className="tree-view-holder-title">
                    <span>Facets</span>
                </div>
                <div className="tree-view-holder">
                  <TreeView
                    defaultCollapseIcon={<ExpandMoreIcon />}
                    defaultExpandIcon={<ChevronRightIcon />}
                    className="tree-view-component"
                  >
                    {this.getComponentList()}
                  </TreeView>
                </div>
                <div className={this.state.toggleHelpDrawer ? 'help-drawer-wrap is-open' : 'help-drawer-wrap'}>
                  {this.state.toggleHelpDrawer && 
                    <HelpDrawer
                      helpText={this.state.helpDrawerContent}
                      handleCloseHelpDrawer={() => this.closeHelpDrawer()}
                    />
                  }
                </div>
          </Fragment>
      );
  }

  handleHelpDrawer(helpContent: any) {
    //console.log("handleHelpDrawer");
    this.setState({ 
      toggleHelpDrawer: true,
      helpDrawerContent: helpContent
    })
  }

  closeHelpDrawer() {
    this.setState({ 
      toggleHelpDrawer: false,
      helpDrawerContent: ''
    })
  }

  getComponentList() {
    if (this.props.components) {
      //console.log("this.props.components: ");
      //console.log(this.props.components);

      var componentsByGroup: any = [];
      componentsByGroup = this.props.components;

      //console.log("componentsByGroup: ");
      //console.log(componentsByGroup);

      return Object.keys(componentsByGroup).sort().map((key: any) => {
        if (key === 'MID') {
          return Object.keys(componentsByGroup[key]).sort().map((key1: any) => (
            <TreeItem
              nodeId={key + '-' + key1}
              key={key + '-' + key1}
              label={getFriendlyLabel(key1)}
            >
              {Object.keys(componentsByGroup[key][key1]).sort().map((component: any) => {
                //console.log("component: ");
                //console.log(component);
                return (<ComponentDrawerItem
                  nodeId={key + '-' + key1 + '-' + component}
                  key={key + '-' + key1 + '-' + component}
                  node={componentsByGroup[key][key1][component]}
                  triggerHelpDrawer={(helpContent: any) => { this.handleHelpDrawer(helpContent) }}
                ></ComponentDrawerItem>)
              })}
            </TreeItem>
          ));
        } else {
          return (
            <TreeItem nodeId={key} key={key} label={getFriendlyLabel(key)}>
              {Object.keys(componentsByGroup[key]).sort().map((key1: any) => (
                <TreeItem
                  nodeId={key + '-' + key1}
                  key={key + '-' + key1}
                  label={getFriendlyLabel(key1)}
                >
                  {Object.keys(componentsByGroup[key][key1]).sort().map((component: any) => (
                    <ComponentDrawerItem
                      nodeId={key + '-' + key1 + '-' + component}
                      key={key + '-' + key1 + '-' + component}
                      node={componentsByGroup[key][key1][component]}
                      triggerHelpDrawer={(helpContent: any) => { this.handleHelpDrawer(helpContent) }}
                    ></ComponentDrawerItem>
                  ))}
                </TreeItem>
              ))}
            </TreeItem>
          );
        }
      });
    }
  }
}

export default ComponentDrawer;
