import { State } from './../../reducers';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import {
  createProjectActionStart,
  deleteProjectActionStart,
  duplicateProjectActionStart,
  handleCleanSession,
  handleProjectReturn
} from '../../actions';
import ProjectsComponent from './projects-component';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  createNewProject: (name: string) => {
    dispatch(createProjectActionStart(name));
  },
  deleteProject: (id: string) => {
    dispatch(deleteProjectActionStart(id));
  },
  duplicateProject: (projectId: string, name: string, targetUser: string) => {
    dispatch(duplicateProjectActionStart(projectId, name, targetUser));
  },
  cleanSession: () => {
    dispatch(handleCleanSession())
  },
  fireReturnToProject: (sourcePage: string) => {
    dispatch(handleProjectReturn(sourcePage))
  },
});
const mapStateToProps = (state: State) => ({
  projects: state.flowReducerState.projects,
  projectsLoaded: state.flowReducerState.projectsLoaded,
  projectsLoadedError: state.flowReducerState.projectsLoadedError,
  email: state.userReducerState.email,
  metabaseLoginId: state.flowReducerState.metabaseLoginId,
  activeProjectId: state.flowReducerState.activeProjectId,
});

export const ProjectsContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(ProjectsComponent);
