import React, { Component, Fragment } from 'react';
import { 
    Typography 
} from '@material-ui/core';
import { IkigaiLogo } from '../../../components/IkigaiLogo';
import VizCentralContent from './viz-central-content';
import { getFriendlyLabel } from '../../../util';
import classNames from 'classnames/bind';
import styles from './Preview.module.scss';
let cx = classNames.bind(styles);

type VisualizeComponentProps = {
  open: boolean;
  minimized: boolean;
  inFocus: boolean;
  url: string;
  isMetabase: boolean;
  status: string;
  loading: boolean;
  projectName: string;
  pipeline?: any;
  vizElement: any;
  onClose: (vizElement: any) => void;
  onMinimize: (vizElement: any) => void;
};

type VisualizeComponentState = {
  url: string;
  iFrameContentLoaded: boolean;
};

class VisualizeComponent extends Component<VisualizeComponentProps, VisualizeComponentState> {
  componentDidMount() {
      //console.log("VisualizeComponent - vizElement...");
      //console.log(this.props.vizElement);
      //console.log("VisualizeComponent - pipeline...");
      //console.log(this.props.pipeline);
  }

  onClose = () => {
    this.props.onClose(this.props.vizElement);
  }

  onMinimize = () => {
    this.props.onMinimize(this.props.vizElement);
  }

  getLeftSidebar() {
    return(
      <div className="interactive-wrap-drawer">
        <IkigaiLogo
          _size='small'
          _color='white'
        />
        <div className="back-btn-holder">
          <div 
            className="back-to-dashboard drawer-back-link viz-drawer-btn"
            onClick={this.onClose}
          >
            <i className="zmdi zmdi-close zmdi-hc-fw" />
            <div>Close Preview</div>
          </div>
          <div 
            className="back-to-dashboard drawer-back-link viz-drawer-btn"
            onClick={this.onMinimize}
          >
            <i className="zmdi zmdi-window-minimize zmdi-hc-fw" />
            <div>Minimize</div>
          </div>
        </div>
      </div>
    );
  }

  getRightSidebar() {
    return(
      <div className="interactive-wrap-rightbar">
                <div className="info-drawer-root">
                  <div className="info-drawer-section">
                    <div className="info-drawer-title">
                      <span className="title">Flow</span>
                    </div>
                    <div className="info-drawer-title-group">
                      <div className="info-drawer-title-title">Flow name:</div>
                      <div className="info-drawer-title-subtitle clip-txt">
                        <span>{this.props.pipeline.name}</span>
                      </div>
                    </div>
                    <div className="info-drawer-title-group">
                      <div className="info-drawer-title-title">Project name:</div>
                      <div className="info-drawer-title-subtitle clip-txt">
                        <span>{this.props.projectName}</span>
                      </div>
                    </div>
                  </div>
                  <div className="info-drawer-section">
                    <div className="info-drawer-title">
                      <span className="title">{this.props.vizElement.vizType}</span>
                    </div>

                    {this.props.vizElement && this.props.vizElement.vizFacetInfo && this.props.vizElement.vizType !== "Link" && 
                      <React.Fragment>
                        <div className="info-drawer-title-group">
                          <div className="info-drawer-title-title">Chain Group:</div>
                          <div className="info-drawer-title-subtitle clip-txt">
                            {this.props.vizElement.vizFacetInfo["chain_group"] &&
                              <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo["chain_group"])}</span>
                            }
                          </div>
                        </div>
                        <div className="info-drawer-title-group">
                          <div className="info-drawer-title-title">Facet Group:</div>
                          <div className="info-drawer-title-subtitle clip-txt">
                            {this.props.vizElement.vizFacetInfo["facet_group"] &&
                              <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo["facet_group"])}</span>
                            }
                          </div>
                        </div>
                        <div className="info-drawer-title-group">
                          <div className="info-drawer-title-title">Facet Type:</div>
                          <div className="info-drawer-title-subtitle clip-txt">
                            {this.props.vizElement.vizFacetInfo["facet_type"] &&
                              <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo["facet_type"])}</span>
                            }
                          </div>
                        </div>
                      </React.Fragment>
                    }
                    {this.props.vizElement && this.props.vizElement.vizFacetInfo && this.props.vizElement.vizType === "Link" && 
                      <React.Fragment>
                        <div className="info-drawer-title-group">
                          <div className="info-drawer-title-title">Source Facet:</div>
                          <div className="info-drawer-title-subtitle clip-txt">
                            {this.props.vizElement.vizFacetInfo.sourcePort &&
                              <React.Fragment>
                                <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo.sourcePort.parent.chain_group)}</span>
                                <span> / </span>
                                <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo.sourcePort.parent.facet_group)}</span>
                                <span> / </span>
                                <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo.sourcePort.parent.facet_type)}</span>
                              </React.Fragment>
                            }
                          </div>
                        </div>
                        <div className="info-drawer-title-group">
                          <div className="info-drawer-title-title">Target Facet:</div>
                          <div className="info-drawer-title-subtitle clip-txt">
                            {this.props.vizElement.vizFacetInfo.targetPort &&
                              <React.Fragment>
                                <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo.targetPort.parent.chain_group)}</span>
                                <span> / </span>
                                <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo.targetPort.parent.facet_group)}</span>
                                <span> / </span>
                                <span>{getFriendlyLabel(this.props.vizElement.vizFacetInfo.targetPort.parent.facet_type)}</span>
                              </React.Fragment>
                            }
                          </div>
                        </div>
                      </React.Fragment>
                    }
                  </div>
                </div>
      </div>
    );
  }

  getContent() {
    var interactivePreviewClass = cx({
      interactiveWrap: true,
      'show': this.props.open
    });
    return(
      <div className={interactivePreviewClass}>
          {this.getLeftSidebar()}
          <div className={"interactive-central-holder " + 
              (this.props.vizElement["vizType"]).toLowerCase() + 
              (this.props.vizElement["isDashView"] ? ' dash-central-panel' : '')}
          >
            <VizCentralContent
              url={this.props.vizElement["iFrameURL"]}
              loading={this.props.loading}
            >
            </VizCentralContent>
          </div>
          {this.getRightSidebar()}
      </div>
    );
  }

  render() {
    return (
      <Fragment>
          {this.getContent()}
      </Fragment>
    );
  }
}

export default VisualizeComponent;