import * as React from 'react';
import {
  DiagramEngine,
  PortWidget,
  PortModel
} from '@projectstorm/react-diagrams';
import { CustomNodeModel } from './customnodemodel';
import ErrorIcon from '@material-ui/icons/Error';
import { Fab, Card, Tooltip } from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import DetailsIcon from '@material-ui/icons/Details';
import { getFriendlyLabel } from '../../../util';
//import './node.scss';
import classNames from 'classnames/bind';
import styles from './Node.module.scss';
let cx = classNames.bind(styles);

export interface CustomNodeWidgetProps {
  node: CustomNodeModel;
  engine: DiagramEngine;
}

export interface CustomNodeWidgetState {
  valid: boolean;
  mouseOver: boolean;
  updatedNodeName: string;
}

class CustomNodeWidget extends React.Component<
  CustomNodeWidgetProps,
  CustomNodeWidgetState
> {
  listenerRef: any;
  constructor(props: CustomNodeWidgetProps) {
    super(props);
    this.state = {
      valid: props.node.valid,
      mouseOver: false,
      updatedNodeName: ''
    };
  }

  componentDidMount() {
    //console.log("CustomNodeWidget this.props.node: ");
    //console.log(this.props.node);
    this.listenerRef = this.props.node.registerListener({
      validatedNode: this.handleNodeValidate.bind(this),
      updatedFacetName: this.handleNodeNameUpdate.bind(this)
    });
  }

  componentWillUnmount() {
    this.props.node.deregisterListener(this.listenerRef);
  }

  handleNodeValidate(e: any) {
    this.setState({ valid: this.props.node.valid });
  }

  handleNodeNameUpdate(e: any) {
    //console.log("this.props.node.name: " + this.props.node.name);
    this.setState({ updatedNodeName: this.props.node.name });
  }

  handlePreviewClick(e: any) {
    this.props.node.fireEvent({ node: this.props.node }, 'visualize');
  }

  handleEditClick(e: any) {
    this.props.node.fireEvent({ node: this.props.node }, 'editNode');
  }

  handleDeleteClick(e: any) {
    this.props.node.fireEvent({ node: this.props.node }, 'deleteNode');
  }
  handleMouseOver() {
    if (this.props.node.facet_type === 'PYTHON_SCRIPT') {
      this.setState({
        mouseOver: true
      });
    }
  }

  handleMouseOut() {
    if (this.props.node.facet_type === 'PYTHON_SCRIPT') {
      this.setState({
        mouseOver: false
      });
    }
  }

  getErrorMessage() {
    return (
      <div>
        <div className={styles.errorMessageTitle}>
          Please fix the following errors!
        </div>
        <ul className={styles.errorList}>
          {this.props.node.errorMessages.map((msg: string, index: number) => {
            return (
              <li className={styles.errorMessage} key={`error-${index}`}>
                {msg}
              </li>
            );
          })}
        </ul>
      </div>
    );
  }

  getErrorMessages() {
    if (!this.state.valid) {
      return (
        <div>
          <Tooltip
            arrow={true}
            interactive
            title={this.getErrorMessage()}
            leaveDelay={200}
            placement="right"
            className="tooltip-holder"
          >
            <ErrorIcon className={styles.nodeErrorIcon} />
          </Tooltip>
        </div>
      );
    }
  }

  render() {
    var className = cx({
      customNode: true,
      'error': !this.state.valid
    });

    var cardClassName = cx({
      'jr-card': true,
      'jr-card-widget': true,
      'customNodeCard': true,
      'error': !this.state.valid,
      'hovered': this.state.mouseOver
    });

    //console.log("this.props: ");
    //console.log(this.props);
    return (
      <div className={className}>
        {this.getErrorMessages()}
        <div className={styles.buttonGroup}>
          {(this.props.node.facet_type === 'PYTHON_SCRIPT' || this.props.node.custom_facet_type === 'API') &&
            <Tooltip 
              arrow={true}
              title="Preview" 
              placement="top" 
              className="default-tooltip"
              classes={{
                tooltip: "default-tooltip",
                popper: "default-popper"
              }}
            >
              <Fab
                color="primary"
                aria-label="visualize"
                size="small"
                onClick={this.handlePreviewClick.bind(this)}
                //style={{ backgroundColor: "#FFF", border: "1px solid #3F51B5" }}
              >
                <DetailsIcon />
              </Fab>
            </Tooltip>
          }
          <Tooltip 
              arrow={true}
              title="Edit" 
              placement="top" 
              className="default-tooltip"
              classes={{
                tooltip: "default-tooltip",
                popper: "default-popper"
              }}
          >
            <Fab
              color="primary"
              aria-label="edit"
              size="small"
              onClick={this.handleEditClick.bind(this)}
            >
              <EditIcon />
            </Fab>
          </Tooltip>
          <Tooltip 
              arrow={true}
              title="Delete" 
              placement="top" 
              className="default-tooltip"
              classes={{
                tooltip: "default-tooltip",
                popper: "default-popper"
              }}
          >
            <Fab
              color="primary"
              aria-label="delete"
              size="small"
              onClick={this.handleDeleteClick.bind(this)}
            >
              <DeleteIcon />
            </Fab>
          </Tooltip>
        </div>
        <Card
          onMouseEnter={() => {
            this.handleMouseOver();
          }}
          onMouseLeave={() => {
            this.handleMouseOut();
          }}
          className={cardClassName}
        >
          {this.props.node.name !== '' && (
            <div className={styles.facetNameHolder}>
              <div className={styles.facetName}>{this.props.node.name}</div>
            </div>
          )}
          {this.props.node.chain_group !== 'INPUT' && (
            <PortWidget
              className="in"
              engine={this.props.engine}
              port={this.props.node.getPort('in') as PortModel}
            >
              <div className={styles.circlePort} />
            </PortWidget>
          )}
          {this.props.node.chain_group !== 'OUTPUT' && (
            <PortWidget
              className="out"
              engine={this.props.engine}
              port={this.props.node.getPort('out') as PortModel}
            >
              <div className={styles.circlePort} />
            </PortWidget>
          )}
          <div className={styles.customNodeName}>
            <div className={styles.customNodeHeader}>
              <div className={styles.customNodeChainGroup}>
                {getFriendlyLabel(this.props.node.chain_group)}
              </div>
              <div>
                {this.props.node.custom_facet_type === 'API' ? (
                  'Connector'
                ):(
                  getFriendlyLabel(this.props.node.facet_group)
                )}
              </div>
            </div>
            <div className={styles.customNodeTitle}>
              {this.props.node.custom_facet_type === 'API' ? (
                this.props.node.custom_facet_type.toUpperCase()
               ):(
                 getFriendlyLabel(this.props.node.facet_type)
              )}
            </div>
          </div>
        </Card>
      </div>
    );
  }
}

export default CustomNodeWidget;
