import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableRow,
    TableSortLabel
  } from '@material-ui/core';
  import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
//import classNames from 'classnames/bind';
import styles from './Table.module.scss';
import IkiPopupMenu from '../IkiPopupMenu';

//let cx = classNames.bind(styles);

/**
   * Grid
   * @param _type Type - string
   */
export default class IkiTable extends Component {
  constructor(props) {
    super(props);
    this.state = {
        tableSortAsc: false,
        tableSortProperty: ''
    }
  }

  onRequestSort = (event, property) => {
    //console.log("property: ", property, ", tableSortAsc: ", this.state.tableSortAsc);
    this.setState({
        tableSortAsc: !this.state.tableSortAsc,
        tableSortProperty: property
    });
  };

  sortTable = (tableContent) => {
    if (this.state.tableSortProperty !== '') {
      return this.sortOn(tableContent, this.state.tableSortProperty);
    } else {
      return tableContent;
    }
  };

  sortOn = (arr, property) => {
    //console.log(this.state.tableSortAsc);
    var sortedArray = arr.sort((a, b) => {
      if (b[property] < a[property]) {
        return this.state.tableSortAsc ? 1 : -1;
      }
      if (b[property] > a[property]) {
        return this.state.tableSortAsc ? -1 : 1;
      }
      return 0;
    });
    return sortedArray;
  };

  prepareTable = () => {
    const { 
      _header,
      _data
    } = this.props;
    const _functions = this.props._functions ? this.props._functions : '';
    const _type = this.props._type ? this.props._type : '';
    const _link = this.props._link ? this.props._link : '';
    const _headerDescr = this.props._headerDescr ? this.props._headerDescr : '';
    const _stickyHeader = this.props._stickyHeader ? this.props._stickyHeader : false;
    var sortedTable = this.sortTable(_data);
    var popupMenuIdKey, popupMenuNameKey;
    if(_type === 'Project') {
      popupMenuIdKey = 'project_id';
      popupMenuNameKey = 'name';
    } else if(_type === 'Flow') {
      popupMenuIdKey = 'pipeline_id';
      popupMenuNameKey = 'name';
    } else if(_type === 'Dataset') {
      popupMenuIdKey = 'dataset_id';
      popupMenuNameKey = 'name';
    } else if(_type === 'Dashboard') {
      popupMenuIdKey = 'dashboard_id';
      popupMenuNameKey = 'name';
    } else if(_type) {
      popupMenuIdKey = '';
      popupMenuNameKey = '';
    }

    //console.log("IkiTable: ", this.props);

    if(_type === 'Peek') {
      return(
        <>
          <div className={styles.tableHolder}>
            <Table
                aria-label="simple table"
                component="div"
                className="table"
                stickyHeader={_stickyHeader}
            >
                <TableHead component="div" className="table-head">
                    <TableRow
                            component="div"
                        >
                        {_header.map((headerColumn, num) => (
                            <TableCell 
                                key={num}
                                align="left" 
                                component="div"
                                sortDirection={
                                    this.state.tableSortAsc ? 'asc' : 'desc'
                                }
                            >
                                {headerColumn !== '' && (
                                    <TableSortLabel
                                        active={this.state.tableSortProperty === num}
                                        IconComponent={ArrowDropDownIcon}
                                        direction={
                                            this.state.tableSortAsc ? 'asc' : 'desc'
                                        }
                                        onClick={(event) =>
                                            this.onRequestSort(event, num)
                                        }
                                    >
                                        {_header[num]}
                                    </TableSortLabel>
                                )}
                            </TableCell>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody component="div" className="table-body">
                    <TableRow
                      key="peek-tbl-head-descr"
                      component="div"
                    >
                      {_headerDescr.map((headerDescrColumn, headerDescrColumnIndex) => (
                        <TableCell align="left" className="first-row-col" component="div" key={'peek-tbl-head-descr-cell-' + headerDescrColumnIndex}>
                          <span>{headerDescrColumn}</span>
                        </TableCell>
                      ))}
                    </TableRow>
                    {sortedTable.map((bodyRow, bodyRowIndex) => (
                        <TableRow
                            key={'tbl-body-row-' + bodyRowIndex}
                            component="div"
                        >
                            {_header.map((headerColumn, headerindex) => (
                              <TableCell align="left" component="div" key={'peek-tbl-c-row-' + bodyRowIndex + '-cell-' + headerindex}>
                                {typeof bodyRow[headerindex] === 'boolean' ? (
                                  <span>{(bodyRow[headerindex]).toString()}</span>
                                ):(
                                  <span>{bodyRow[headerindex]}</span>
                                )}
                              </TableCell>
                            ))}
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
          </div>
        </>
      );
    } else {
      return(
        <>
          <div className={styles.tableHolder}>
            <Table
                aria-label="simple table"
                component="div"
                className="table"
            >
                <TableHead component="div" className="table-head">
                    <TableRow
                            component="div"
                        >
                        {_header.map((headerColumn, num) => (
                            <TableCell 
                                key={num}
                                align="left" 
                                component="div"
                                sortDirection={
                                    this.state.tableSortAsc ? 'asc' : 'desc'
                                }
                            >
                                {headerColumn.key !== '' && (
                                    <TableSortLabel
                                        active={this.state.tableSortProperty === headerColumn.key}
                                        IconComponent={ArrowDropDownIcon}
                                        direction={
                                            this.state.tableSortAsc ? 'asc' : 'desc'
                                        }
                                        onClick={(event) =>
                                            this.onRequestSort(event, headerColumn.key)
                                        }
                                    >
                                        {headerColumn.name}
                                    </TableSortLabel>
                                )}
                            </TableCell>
                        ))}
                        {_functions !== '' && (
                          <TableCell 
                            align="left" 
                            component="div"
                          ></TableCell>
                        )}
                    </TableRow>
                </TableHead>
                <TableBody component="div" className="table-body">
                    {sortedTable.map((bodyRow, bodyRowIndex) => (
                        <TableRow
                            key={'tbl-body-row-' + bodyRowIndex}
                            component="div"
                        >
                            {_header.map((headerColumn, headerindex) => (
                              <TableCell align="left" component="div" key={'tbl-c-row-' + bodyRowIndex + '-cell-' + headerindex}>
                                {_link !== '' && headerindex === 0 ? (
                                  <Link to={`${_link + '' + bodyRow[popupMenuIdKey]}`}>{bodyRow[headerColumn.key]}</Link>
                                ):(
                                  <span>{bodyRow[headerColumn.key]}</span>
                                )}
                              </TableCell>
                            ))}
                            {_functions !== '' && (
                              <TableCell align="right" component="div" key={'tbl-c-row-func-' + bodyRowIndex}>
                                <IkiPopupMenu
                                  _type={_type}
                                  _data={
                                    { 
                                      _id: bodyRow[popupMenuIdKey],
                                      _name: bodyRow[popupMenuNameKey],
                                      ...bodyRow
                                    }
                                  }
                                  _functions={_functions}
                                />
                              </TableCell>
                            )}
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
          </div>
        </>
      );
    }
  }

  render() {
    return this.prepareTable()
  }
}