import { NodeModel } from '@projectstorm/react-diagrams';

import {
  BaseModelOptions,
  DeserializeEvent
} from '@projectstorm/react-canvas-core';
import { CustomPortModel } from './customportmodel';

export interface CustomNodeModelOptions extends BaseModelOptions {
  color: string;
  name: string;
  facet_id: string;
  facet_type: string;
  facet_group: string;
  chain_group: string;
  custom_facet_type: string;
  custom_facet_group: string;
  custom_chain_group: string;
}

export class CustomNodeModel extends NodeModel {
  color: string;
  name: string;
  facet_id: string;
  facet_type: string;
  facet_group: string;
  chain_group: string;
  custom_facet_type: string;
  custom_facet_group: string;
  custom_chain_group: string;
  linksRef: any[] = [];
  valid: boolean;
  errorMessages: string[];

  constructor(
    options: CustomNodeModelOptions = {
      color: 'red',
      name: 'Model',
      facet_id: '0',
      facet_type: 'facet type',
      facet_group: 'facet group',
      chain_group: 'chain group',
      custom_facet_type: 'custom facet type',
      custom_facet_group: 'custom facet group',
      custom_chain_group: 'custom facet group',
    }
  ) {
    super({
      ...options,
      type: 'custom-node'
    });
    //console.log("customnodemodel: ");
    //console.log(options);
    this.color = options.color;
    this.name = options.name;
    this.valid = true;
    this.errorMessages = [];
    this.facet_type = options.facet_type;
    this.facet_group = options.facet_group;
    this.facet_id = options.facet_id;
    this.chain_group = options.chain_group;
    this.custom_facet_type = options.custom_facet_type;
    this.custom_facet_group = options.custom_facet_group;
    this.custom_chain_group = options.custom_chain_group;

    if (this.chain_group !== 'INPUT') {
      this.addPort(
        new CustomPortModel({
          in: true,
          name: 'in'
        })
      );
    }
    if (this.chain_group !== 'OUTPUT') {
      this.addPort(
        new CustomPortModel({
          in: false,
          name: 'out'
        })
      );
    }
  }

  serialize() {
    return {
      ...super.serialize(),
      color: this.color
    };
  }

  deserialize(event: DeserializeEvent): void {
    super.deserialize(event);
  }
}
