import { State } from '../../../reducers';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import LoginComponent from './login-component';
import { authenticateRequestStartAction } from './../../../actions';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  authenticate: (email: string, password: string) =>
    dispatch(authenticateRequestStartAction(email, password))
});
const mapStateToProps = (state: State) => ({
  loginSubmitErrorMsg: state.userReducerState.loginResponse,
  isLoggedIn: state.userReducerState.isLoggedIn
});

export const LoginContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(LoginComponent);
