  checkIsPipelineRunning = (pipelineId: any) => {
    this.isPipelineRunningPromise = makeCancelable(
      isPipelineRunning(pipelineId)
    );
    this.isPipelineRunningPromise.promise
      .then((result: any) => {
        let pipelineRunning: boolean = result.data === 'true';
        if (!pipelineRunning) {
          //this.setState({ pipelineRunning: pipelineRunning });
          this.currentRetryForPipelineLog = 0;
          this.getPipelineLogs(this.state.pipeline.pipeline_id, true);
          setTimeout(() => {
            /**
             * Get the datasets and dashboards again after the pipeline run completes. Since the pipeline run may have
             * created new datasets or dashboards.
             */
            this.props.getDatasets(this.state.projectId);
            this.props.getDashboards(this.state.projectId);
          }, 2000);
          /* this.pipelineLogIntervalId = setTimeout(() => {
            this.getPipelineLogs(this.state.pipeline.pipeline_id);
          }, 2000); */
        } else {
          this.pipelineCheckIntervalId = setTimeout(() => {
            this.checkIsPipelineRunning(pipelineId);
          }, 2000);
        }
      })
      .catch((err: any) => {
        this.setState({ pipelineRunning: false });
      });
  };

  /**
   * Get pipeline logs
   * @param pipelineId
   * @param justRan If this is being called after running the pipeline or when the page is loaded.
   */
  getPipelineLogs(pipelineId: any, justRan?: boolean) {
    this.setState({
      loadingLogs: true
    });
    this.pipelineLogPromise = makeCancelable(getPipelineLog(pipelineId));
    this.pipelineLogPromise.promise
      .then((result: any) => {
        var pipelineLog = result.data.pipeline_log;
        if (
          (pipelineLog.length !== 0 &&
            Object.keys(this.state.pipelineLogs).length ===
            Object.keys(pipelineLog).length &&
            this.currentRetryForPipelineLog < this.maxRetriesForPipelineLog) ||
          (justRan &&
            pipelineLog.length === 0 &&
            this.currentRetryForPipelineLog < this.maxRetriesForPipelineLog)
        ) {
          this.currentRetryForPipelineLog++;
          this.pipelineLogIntervalId = setTimeout(() => {
            this.getPipelineLogs(pipelineId, justRan);
          }, 2000);
        } else {
          this.currentRetryForPipelineLog = 0;
          this.setState({
            pipelineRunning: false,
            loadingLogs: false,
            pipelineLogs: pipelineLog
          });
        }
        //console.log("2 - old arr length: " + Object.keys(this.state.pipelineLogs).length + ", new arr length: " + Object.keys(pipelineLog).length);
      })
      .catch
      //err => console.log(err)
      ();
  }

