  handleAutoSaveFacet = (pipeline_id: string, project_id: string, selectedNodeId: string, selectedFacetParentId: string) => {
    //console.log("autosave loop saveFacetIntervalId: " + this.saveFacetIntervalId);
    this.saveFacetIntervalId = setTimeout(() => {
      saveFacet(pipeline_id, project_id, selectedNodeId, selectedFacetParentId).then((saveFacetRes: any) => {
        if (this.saveFacetIntervalId > 0) {
          this.handleAutoSaveFacet(pipeline_id, project_id, selectedNodeId, selectedFacetParentId);
        }
      }).catch((err: any) => {
        saveFacet(pipeline_id, project_id, selectedNodeId, selectedFacetParentId).then((saveFacetRes: any) => {
          if (this.saveFacetIntervalId > 0) {
            this.handleAutoSaveFacet(pipeline_id, project_id, selectedNodeId, selectedFacetParentId);
          }
        });
      });
    }, 20000);
  }

