  _peekArrow(node: CustomLinkModel) {
    //console.log("_peekArrow: ");
    //console.log(node);

    this.peekLinkObject = node;
    let peekLinkObjectNode: {} | undefined;

    this.setState({
      mode: 'peek',
      peekInitiated: true,
      peekLoading: true
    });

    var pipelineId: string = "";
    var projectId: string = "";
    // eslint-disable-next-line
    var sourceFacet: string = "";
    var sourceFacetId: string = "";
    // eslint-disable-next-line
    var targetFacet: string = "";
    var targetFacetId: string = "";
    var linkFacetsInfo: any = {};
    if (this.state.pipeline && node !== undefined) {
      peekLinkObjectNode = node;
      //console.log("_browseLink: ");
      //console.log(this.state.pipeline);
      pipelineId = this.state.pipeline.pipeline_id;
      projectId = this.state.pipeline.project_id;
      targetFacetId = peekLinkObjectNode["targetPort"].parent.facet_id;
      sourceFacetId = peekLinkObjectNode["sourcePort"].parent.facet_id;
      this.state.pipeline.definition.facets.map((facet: any) => {
        //console.log("_browseLink facet: ");
        //console.log(facet);
        if (sourceFacetId === facet.facet_id) {
          linkFacetsInfo["source"] = facet;
        }
        if (targetFacetId === facet.facet_id) {
          linkFacetsInfo["target"] = facet;
        }
        return ''
      });
      this.setState({ vizFacetInfo: linkFacetsInfo });
    }

    //console.log("pipelineId: " + pipelineId + ", projectId: " + projectId + "sourceFacetId: " + sourceFacetId + ", targetFacetId: " + targetFacetId);
    if (pipelineId !== "" && projectId !== "" && sourceFacetId !== "" && targetFacetId !== "") {

      peekArrow(pipelineId, projectId, sourceFacetId, targetFacetId).then((peekArrowRes: any) => {
        //console.log("peekArrowRes res: ");
        //console.log(peekArrowRes);
        this.peekLinkObject.handleButtonsStateDisabled();
        this.setState({
          peekContent: peekArrowRes,
          peekLoading: false
        });
        //this.getPipelineLogs(pipelineId);
        //this.checkIsPipelineRunning(pipelineId);
      }).catch((peekArrowErr: any) => {
        //console.log("ERR peekArrowErr res: ");
        //console.log(peekArrowErr);
        if (peekArrowErr === 501) {
          enterSession(projectId).then((enterSessionRes: any) => {
            peekArrow(pipelineId, projectId, sourceFacetId, targetFacetId).then((peekArrowRes: any) => {
              //console.log("peekArrowRes res: ");
              //console.log(peekArrowRes);
              this.peekLinkObject.handleButtonsStateDisabled();
              this.setState({
                peekContent: peekArrowRes,
                peekLoading: false
              });
              //this.getPipelineLogs(pipelineId);
            }).catch((peekArrowErr: any) => {
              this.peekLinkObject.handleButtonsStateDisabled();
              this.setState({
                snackbarMessage: 'Something went wrong. Please try again',
                snackbarOpen: true,
                snackbarVariant: 'error',
                mode: 'edit',
                peekContent: {},
                peekInitiated: false,
                peekLoading: false
              });
              //this.checkIsPipelineRunning(pipelineId);
              //this.getPipelineLogs(pipelineId);
            });
          }).catch((enterSessionErr: any) => {
            //console.log("ERR enterSession res: ");
            //console.log(errenterSession);
            this.peekLinkObject.handleButtonsStateDisabled();
            this.setState({
              snackbarMessage: 'Something went wrong. Please try again',
              snackbarOpen: true,
              snackbarVariant: 'error',
              mode: 'edit',
              peekContent: {},
              peekInitiated: false,
              peekLoading: false
            });
            //this.checkIsPipelineRunning(pipelineId);
            //this.getPipelineLogs(pipelineId);
          });
        } else {
          peekArrow(pipelineId, projectId, sourceFacetId, targetFacetId).then((peekArrowRes: any) => {
            //console.log("peekArrowRes res: ");
            //console.log(peekArrowRes);
            this.peekLinkObject.handleButtonsStateDisabled();
            this.setState({
              peekContent: peekArrowRes,
              peekLoading: false
            });
            //this.getPipelineLogs(pipelineId);
          }).catch((peekArrowErr: any) => {
            this.peekLinkObject.handleButtonsStateDisabled();
            this.setState({
              snackbarMessage: 'Something went wrong. Please try again',
              snackbarOpen: true,
              snackbarVariant: 'error',
              mode: 'edit',
              peekContent: {},
              peekInitiated: false,
              peekLoading: false
            });
            //this.checkIsPipelineRunning(pipelineId);
            //this.getPipelineLogs(pipelineId);
          });
        }
      });
    } else {
      this.setState({
        snackbarMessage: 'Something went wrong. Please try again',
        snackbarOpen: true,
        snackbarVariant: 'error',
        mode: 'edit',
        peekContent: {},
        peekInitiated: false,
        peekLoading: false
      });
    }
  }

