  handleCreateNewDatasetDialogSubmit(name: string, file: File) {
    var checkDatasetNameOverlap = false;
    // eslint-disable-next-line
    {
      this.props.data.datasets.map((datasetData: any) => {
        //console.log("NAME: " + name + ", datasetData.name: " + datasetData.name);
        if (
          datasetData.name === name &&
          datasetData.project_id === this.state.projectId
        ) {
          //console.log("HITHITHIT!!! - NAME: " + name + ", name: " + datasetData.name);
          //this.setState({ newlyAddedDatasetNameExists: true });
          checkDatasetNameOverlap = true;
        }
        return ''
      });
    }
    //console.log("checkDatasetNameOverlap: " + checkDatasetNameOverlap);
    if (checkDatasetNameOverlap !== false) {
      this.setState({ newlyAddedDatasetNameExists: true });
    } else {
      this.props.createDataset(this.state.projectId, name, file);
      this.setState({
        newlyAddedDatasetName: name,
        newlyAddedDatasetFileName: file.name,
        newlyAddedDatasetNameExists: false
      });
      this.handleCreateNewDatasetDialogClose();
    }
  }

  handleCreateNewDatasetDialogClose() {
    this.setState({
      createNewDatasetDialogOpened: false
      //newlyAddedDatasetName: ''
    });
  }

  handleDismissDatasetNameExists() {
    this.setState({
      newlyAddedDatasetNameExists: false
    });
  }

  handleCreateNewDatasetDialogSubmitOverwrite(name: string, file: File) {
    // CALL TO API TO OVERWRITE DATASET
    //console.log('OVERWRITE dataset: ' + name);
    //this.props.createDataset(this.state.projectId, name, file);
    var existingDatasetId = '';
    var existingDatasetName = '';
    // eslint-disable-next-line
    {
      this.props.data.datasets.map((datasetData: any) => {
        //console.log("NAME: " + name + ", datasetData.name: " + datasetData.name);
        if (
          datasetData.name === name &&
          datasetData.project_id === this.state.projectId
        ) {
          //console.log("HITHITHIT!!! - NAME: " + name + ", name: " + datasetData.name);
          //this.setState({ newlyAddedDatasetNameExists: true });
          existingDatasetId = datasetData.dataset_id;
          existingDatasetName = datasetData.name;
        }
        return ''
      });
    }
    if (existingDatasetId !== '') {
      this.props.editDataset(
        this.state.projectId,
        existingDatasetId,
        existingDatasetName,
        file
      );
      this.setState({
        newlyAddedDatasetName: existingDatasetName,
        newlyAddedDatasetFileName: file.name,
        newlyAddedDatasetNameExists: false
      });
      this.handleCreateNewDatasetDialogClose();
    }
  }

  fetchSuggestions() {
    if (this.state.pipeline.definition.arrows.length > 0) {
      getSuggesstions({
        project_id: this.state.projectId,
        pipeline_id: this.state.pipeline.pipeline_id,
        source_facet_id: this.state.pipeline.definition.arrows[0].source,
        destination_facet_id: this.state.pipeline.definition.arrows[0].destination
      }).then((a: any) => {
        var p: any = []
        a.column_names.forEach((v: string) => {
          p.push({ name: v })
        })
        this.setState({ columnNames: p })
      }).catch((err: any) => {
        if (err === 501) {
          enterSession(this.state.projectId).then((data: any) => {
            getSuggesstions({
              project_id: this.state.projectId,
              pipeline_id: this.state.pipeline.pipeline_id,
              source_facet_id: this.state.pipeline.definition.arrows[0].source,
              destination_facet_id: this.state.pipeline.definition.arrows[0].destination
            }).then((a: any) => {
              var p: any = []
              a.column_names.forEach((v: string) => {
                p.push({ name: v })
              })
              this.setState({ columnNames: p })
            });
          }).catch((err: any) => {
            this.setState({
              snackbarMessage: 'Something went wrong. Please try again',
              snackbarOpen: true,
              snackbarVariant: 'error',
              mode: 'edit'
            });
          });
        } else {
          getSuggesstions({
            project_id: this.state.projectId,
            pipeline_id: this.state.pipeline.pipeline_id,
            source_facet_id: this.state.pipeline.definition.arrows[0].source,
            destination_facet_id: this.state.pipeline.definition.arrows[0].destination
          }).then((a: any) => {
            var p: any = []
            a.column_names.forEach((v: string) => {
              p.push({ name: v })
            })
            this.setState({ columnNames: p })
          });
        }
      });
    }
  }

