import moment from 'moment';

/**
 * Returns random string.
 */

export const getRandomString = function(): string {
  return (
    Math.random()
      .toString(36)
      .substring(2, 10) +
    Math.random()
      .toString(36)
      .substring(2, 10)
  );
};

/**
 * Returns current date
 */

export const getCurrentDate = function(): string {
  return moment().format('DD/MM/YYYY, H:mm');
};

/**
 * Removes '_' between string and makes first letter of every word capital
 * @param label
 */

export const getFriendlyLabel = (label: string): string => {
  return label
    .split('_')
    .map(
      (labelWord: string) =>
        `${labelWord[0].toUpperCase()}${labelWord.slice(1).toLowerCase()}`
    )
    .join(' ');
};

export const cleanCodeString = (stringToClean: any) => {
  var attrLowercase = stringToClean.toLowerCase();
  var cleanedFrom_symbols = attrLowercase.replace(/_/g, " ");
  var uppercasedAttr = cleanedFrom_symbols.replace(/\b\w/g, (l:any) => l.toUpperCase());
  return uppercasedAttr;
};

export const getCodeString = (stringToFormat: any) => {
  var strUpperCase = stringToFormat.toUpperCase();
  var formatedString = strUpperCase.replace(/ /g, '_');
  return formatedString;
};

/**
 * Capitalizes the first letter of the word
 * @param label string
 */

export const capitalizeLabel = (label: string): string => {
  return `${label[0].toUpperCase()}${label.slice(1).toLowerCase()}`;
};

/**
 * Quick way to deep clone an object
 * @param obj Object
 */

export const cloneObject = (obj: any) => {
  if (obj) {
    return JSON.parse(JSON.stringify(obj));
  }
};
