import React, { Fragment } from 'react';
import {
  Box,
  Stepper,
  Step,
  StepLabel,
  StepContent,
  RadioGroup,
  Radio,
  FormControlLabel,
  FormControl,
  Checkbox
} from '@material-ui/core';
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider, DatePicker, TimePicker } from 'material-ui-pickers';
import AccountTreeOutlinedIcon from '@material-ui/icons/AccountTreeOutlined';
import PowerIcon from '@material-ui/icons/Power';
import IkiDialog from '../IkiDialog';
import IkiButton from '../IkiFormElements/IkiButton';
import IkiInput from '../IkiFormElements/IkiInput';
import IkiAutocomplete from '../IkiFormElements/IkiAutocomplete';
import moment from 'moment';

export function NewScheduleDialog(props: any) {
  const {
    handleDialogClose,
    open,
    newScheduleDialogErrorMsg,
    onSubmit,
    newScheduleSubmitLoading,
    appData
  } = props;

  const getSteps = () => {
    //console.log('appData...', appData);
    return [
      'What would you like to Schedule?', 
      'Which subject would you like to run', 
      'Set time frame of execution',
      'Enter Schedule Name and Description'
    ];
  }

  const setSubjectItems = () => {
    var subjectsItems: any = [];
    Object.keys(appData).forEach((projectKey: any) => {
      var projectPipelines: any = [];
      if(appData[projectKey].pipelines) {
        projectPipelines = appData[projectKey].pipelines;
        var projectName: string = appData[projectKey].project.name;
        Object.keys(projectPipelines).forEach((pipelineKey: any) => {
          var pipelineId: string = projectPipelines[pipelineKey].pipeline_id;
          var pipelineName: string = projectPipelines[pipelineKey].name;
          var scheduleItem: any = {
            projectName: projectName,
            pipelineId: pipelineId,
            pipelineName: pipelineName
          }
          subjectsItems.push(scheduleItem);
        });
        //console.log('project...', appData[projectKey]);
      }
    });
    //console.log('subjectsItems...', subjectsItems);
    return subjectsItems;
  }

  const [activeStep, setActiveStep] = React.useState(0);
  const [scheduleName, setScheduleName] = React.useState('');
  const [scheduleNameErrorMsg, setScheduleNameErrorMsg] = React.useState('');
  const [scheduleDescription, setScheduleDescription] = React.useState('');
  const [scheduleDescrErrorMsg, setScheduleDescrErrorMsg] = React.useState('');
  const [subjectsArr, setSubjectsArr] = React.useState([]);
  const [subjectItem, setSubjectItem] = React.useState('');
  const [subjectItemError, setSubjectItemError] = React.useState('');
  const [scheduleTimeType, setScheduleTimeType] = React.useState('once');
  const [scheduleTime, setScheduleTime] = React.useState({
    type: 'once',
    enabled: true,
    startDate: moment(),
    startTime: moment(),
    endDate: moment(),
    recur: false,
    days: 0,
    weeks: 0,
    weekDays: [],
    cron: ''
  });
  const [recurDaily, setRecurDaily] = React.useState(1);
  const [recurDailyErrorMsg, setRecurDailyErrorMsg] = React.useState('');
  const [recurWeekly, setRecurWeekly] = React.useState(1);
  const [recurWeeklyErrorMsg, setRecurWeeklyErrorMsg] = React.useState('');
  const steps = getSteps();
  const subjectItems = setSubjectItems();
  //console.log('subjectItems...', subjectItems);
  const weekDaysObject = [
    {
      day: 'Monday',
      value: false,
      shortForm: 'mon'
    },
    {
      day: 'Tuesday',
      value: false,
      shortForm: 'tue'
    },
    {
      day: 'Wednesday',
      value: false,
      shortForm: 'wed'
    },
    {
      day: 'Thursday',
      value: false,
      shortForm: 'thu'
    },
    {
      day: 'Friday',
      value: false,
      shortForm: 'fri'
    },
    {
      day: 'Saturday',
      value: false,
      shortForm: 'sat'
    },
    {
      day: 'Sunday',
      value: false,
      shortForm: 'sun'
    }
  ];
  const [weekDays, setWeekDays] = React.useState(weekDaysObject);
  const [selectedDaysError, setSelectedDaysError] = React.useState('');

  const getStepContent = (step: number) => {
    switch (step) {
      case 0:
        return (
          <div className="subject-cardbox-wrap">
            <div className="subject-cardbox active">
              <div className="subject-cardbox-icon">
                <AccountTreeOutlinedIcon />
              </div>
              <div className="subject-cardbox-title">Flow</div>
            </div>
            <div className="subject-cardbox disabled">
              <div className="subject-cardbox-icon">
                <PowerIcon />
              </div>
              <div className="subject-cardbox-title">Connector</div>
            </div>
          </div>
        );
      case 1:
        return (
          <IkiAutocomplete 
            _label='Subject'
            _placeholder='Select Subject...'
            _value={subjectItem}
            _isMultiple={false}
            _selectOptions={subjectItems}
            _fullWidth={true}
            _groupByValue='projectName'
            _keyValue='pipelineId'
            _nameValue='pipelineName'
            _onChange={(event: any, option: any) => handleColumnNameSelect(option)}
            _error={subjectItemError}
          />
        );
      case 2:
        return (
          <>
            <FormControl 
              className="radio-btn-holder with-label horizontal" 
              component="fieldset" 
              margin="normal"
            >
              <RadioGroup 
                aria-label="schedule-time" 
                name="schedule-time"
                //defaultValue='once'
                value={scheduleTimeType} 
                onChange={handleScheduleTimeType}
              >
                <FormControlLabel 
                  value='once'
                  key='once'
                  control={
                    <Radio 
                      color="primary"
                      size="small"
                      //onClick={(e: any) => setScheduleTimeType(e.target.value)}
                      //checked={scheduleTimeType === '' || scheduleTimeType === 'once'}
                    />
                  } 
                  label='Once' 
                />
                <FormControlLabel 
                  value='daily'
                  key='daily'
                  control={
                    <Radio 
                      color="primary"
                      size="small"
                      //onClick={(e: any) => setScheduleTimeType(e.target.value)}
                    />
                  } 
                  label='Daily' 
                />
                <FormControlLabel 
                  value='weekly'
                  key='weekly'
                  control={
                    <Radio 
                      color="primary"
                      size="small"
                      //onClick={(e: any) => setScheduleTimeType(e.target.value)}
                    />
                  } 
                  label='Weekly' 
                />
              </RadioGroup>
            </FormControl>
            <div className="time-controls-holder">
              {scheduleTimeType === 'once' && (
                <div className="time-controls once">
                  <MuiPickersUtilsProvider utils={MomentUtils}>
                      <FormControl>
                        <DatePicker
                          value={scheduleTime.startDate}
                          onChange={(date: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: date,
                              startTime: scheduleTime.startTime,
                              endDate: date,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="Date"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                      <FormControl>
                        <TimePicker
                          value={scheduleTime.startTime}
                          onChange={(time: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: scheduleTime.startDate,
                              startTime: time,
                              endDate: scheduleTime.endDate,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="Time"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                    </MuiPickersUtilsProvider>
                  </div>
              )}
              {scheduleTimeType === 'daily' && (
                  <MuiPickersUtilsProvider utils={MomentUtils}>
                    <div className="time-controls once">
                      <FormControl>
                        <DatePicker
                          value={scheduleTime.startDate}
                          onChange={(date: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: date,
                              startTime: scheduleTime.startTime,
                              endDate: scheduleTime.endDate,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="Start Date"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                      <FormControl>
                        <DatePicker
                          value={scheduleTime.endDate}
                          onChange={(date: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: scheduleTime.startDate,
                              startTime: scheduleTime.startTime,
                              endDate: date,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="End Date"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                    </div>
                  <div className="time-controls once">
                      <FormControl className="time-control">
                        <TimePicker
                          value={scheduleTime.startTime}
                          onChange={(time: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: scheduleTime.startDate,
                              startTime: time,
                              endDate: scheduleTime.endDate,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="Time"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                      <IkiInput 
                        _label="Recur every X days"
                        _placeholder=""
                        _type='number'
                        _autoComplete="new-password"
                        _value={recurDaily > 0 ? recurDaily : 1}
                        _onChange={handleRecurDailyChange}
                        _autoFocus={false}
                        _fullWidth={true}
                        _error={recurDailyErrorMsg}
                      />
                    </div>
                  </MuiPickersUtilsProvider>
              )}
              {scheduleTimeType === 'weekly' && (
                  <MuiPickersUtilsProvider utils={MomentUtils}>
                    <div className="time-controls once">
                      <FormControl>
                        <DatePicker
                          value={scheduleTime.startDate}
                          onChange={(date: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: date,
                              startTime: scheduleTime.startTime,
                              endDate: scheduleTime.endDate,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="Start Date"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                      <FormControl>
                        <DatePicker
                          value={scheduleTime.endDate}
                          onChange={(date: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: scheduleTime.startDate,
                              startTime: scheduleTime.startTime,
                              endDate: date,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="End Date"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                    </div>
                  <div className="time-controls once">
                      <FormControl className="time-control">
                        <TimePicker
                          value={scheduleTime.startTime}
                          onChange={(time: any) => {
                            setScheduleTime({
                              type: scheduleTime.type,
                              enabled: scheduleTime.enabled,
                              startDate: scheduleTime.startDate,
                              startTime: time,
                              endDate: scheduleTime.endDate,
                              recur: scheduleTime.recur,
                              days: scheduleTime.days,
                              weeks: scheduleTime.weeks,
                              weekDays: scheduleTime.weekDays,
                              cron: scheduleTime.cron
                            })
                          }}
                          disabled={false}
                          variant="outlined"
                          label="Time"
                          InputLabelProps={{
                            shrink: true
                          }}
                        />
                      </FormControl>
                      <IkiInput 
                        _label="Recur every X weeks"
                        _placeholder=""
                        _type='number'
                        _autoComplete="new-password"
                        _value={recurWeekly > 0 ? recurWeekly : 1}
                        _onChange={handleRecurWeeklyChange}
                        _autoFocus={false}
                        _fullWidth={true}
                        _error={recurWeeklyErrorMsg}
                      />
                    </div>
                    <div>
                      <FormControl fullWidth>
                        {weekDays.map((day: any) => {
                          return (
                            <FormControlLabel
                              className="checkbox-label"
                              control={
                                <Checkbox
                                  //disabled={!this.state.settings.week.enabled}
                                  color="primary"
                                  //checked={day}
                                  onChange={handleChangeDaySelect}
                                  value={day.shortForm}
                                />
                              }
                              label={day.day}
                            />
                          );
                        })}
                      </FormControl>
                    </div>
                  </MuiPickersUtilsProvider>
              )}
            </div>
          </>
        );
      case 3:
        return (
          <>
            <IkiInput 
              _label="Name"
              _placeholder="Enter Schedule Name..."
              _type='text'
              _autoComplete="new-password"
              _value={scheduleName}
              _onChange={handleScheduleNameChange}
              _characterCounter={40}
              _autoFocus={true}
              _fullWidth={true}
              _error={scheduleNameErrorMsg}
            />
            {/* <IkiInput 
              _label="Description"
              _placeholder="Enter Schedule description..."
              _type='text'
              _autoComplete="new-password"
              _value={scheduleDescription}
              _onChange={handleScheduleDescriptionChange}
              _characterCounter={200}
              _fullWidth={true}
              _multiline={true}
              _rows={4}
              _error={scheduleDescrErrorMsg}
            /> */}
          </>
        )
      default:
        return 'Unknown step';
    }
  }

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const handleScheduleNameChange = (e: any) => {
    setScheduleName(e.target.value);
  };

  const handleRecurDailyChange = (e: any) => {
    var days = e.target.value;
    setRecurDaily(e.target.value);
    setScheduleTime({
      type: scheduleTime.type,
      enabled: scheduleTime.enabled,
      startDate: scheduleTime.startDate,
      startTime: scheduleTime.startTime,
      endDate: scheduleTime.endDate,
      recur: scheduleTime.recur,
      days: days,
      weeks: scheduleTime.weeks,
      weekDays: scheduleTime.weekDays,
      cron: scheduleTime.cron
    })
  };

  const handleRecurWeeklyChange = (e: any) => {
    var weeks = e.target.value;
    setRecurWeekly(e.target.value);
    setScheduleTime({
      type: scheduleTime.type,
      enabled: scheduleTime.enabled,
      startDate: scheduleTime.startDate,
      startTime: scheduleTime.startTime,
      endDate: scheduleTime.endDate,
      recur: scheduleTime.recur,
      days: scheduleTime.days,
      weeks: weeks,
      weekDays: scheduleTime.weekDays,
      cron: scheduleTime.cron
    })
  };

  const handleScheduleDescriptionChange = (e: any) => {
    setScheduleDescription(e.target.value);
  };

  const handleColumnNameSelect = (selectedVal: any) =>{
    if(selectedVal !== null && selectedVal !== undefined && selectedVal !== ""){
      setSubjectItem(selectedVal);  
    }else{
      setSubjectItem(''); 
    }
  }

  const handleScheduleTimeType = (event: React.ChangeEvent<HTMLInputElement>) => {
    var scheduleType = (event.target as HTMLInputElement).value;
    setScheduleTimeType(scheduleType);
    setScheduleTime({
      type: scheduleType,
      enabled: true,
      startDate: moment(),
      startTime: moment(),
      endDate: moment(),
      recur: false,
      days: 0,
      weeks: 0,
      weekDays: [],
      cron: ''
    })
  };

  const handleChangeDaySelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    var checkedDay = (event.target as HTMLInputElement).value;
    //console.log('checkedDay: ' + checkedDay);
    //setWeekDays()
    weekDays.forEach((dayObject: any) => {
      if(dayObject.shortForm === checkedDay) {
        if(!dayObject.value) {
          dayObject.value = true;
        } else {
          dayObject.value = false;
        }
      }
    });

    let selectedDaysArray: any = []; 
    selectedDaysArray = weekDays.filter((day: any) => day.value).map((day: any) => day.shortForm);

    //console.log('selectedDaysArray...', selectedDaysArray);

    setScheduleTime({
          type: scheduleTime.type,
          enabled: scheduleTime.enabled,
          startDate: scheduleTime.startDate,
          startTime: scheduleTime.startTime,
          endDate: scheduleTime.endDate,
          recur: scheduleTime.recur,
          days: scheduleTime.days,
          weeks: scheduleTime.weeks,
          weekDays: selectedDaysArray,
          cron: scheduleTime.cron
    });

    //console.log('scheduleTime...', scheduleTime);

    //console.log('dayObject after handleChangeDaySelect...', weekDays);
  }

  const handleScheduleDialogSubmit = (e: any) => {
    let validationCounter = 0;

    if (scheduleName === '') {
      setScheduleNameErrorMsg('Required');
      validationCounter++;
    }

    if (subjectItem === '') {
      setSubjectItemError('Required');
      validationCounter++;
    }

    if (validationCounter === 0) {
      onSubmit(subjectItem, scheduleTime, scheduleName);
    }
  };

  const handleOnCloseScheduleDialog = () => {
    setScheduleName('');
    setScheduleNameErrorMsg('');
    setSubjectItem('');
    setSubjectItemError('');
    handleDialogClose();
  }

  const handleOnEnterScheduleDialog = () => {

  }

  return (
      <IkiDialog
            _open={open}
            _onEnter={() => handleOnEnterScheduleDialog()}
            _onClose={() => handleOnCloseScheduleDialog()}
            _onExit={() => handleOnCloseScheduleDialog()}
            _type='share'
            _icon={false}
            _title='New Schedule'
            _subtitle={'Step ' + (activeStep + 1 ) + ' / 4'}
            _size='xs'
            _align='left'
            _content={
              <>
                <Stepper activeStep={activeStep} orientation="vertical" className="newSchedule">
                  {steps.map((label, index) => (
                    <Step key={label}>
                      <StepLabel>{label}</StepLabel>
                      <StepContent>
                        <div>
                          {getStepContent(index)}
                        </div>
                        <Box mt={1}>
                            <IkiButton
                              _disabled={activeStep === 0}
                              _variant='text'
                              _color='default'
                              _label="Back"
                              _type='button'
                              _fullWidth={false}
                              _loading={false}
                              _onClick={() => handleBack()}
                            />
                            {(index+1) !== steps.length && (
                              <IkiButton
                                _variant='text'
                                _color='primary'
                                _label="Next"
                                _type='button'
                                _fullWidth={false}
                                _loading={false}
                                _onClick={() => handleNext()}
                              />
                            )}
                        </Box>
                      </StepContent>
                    </Step>
                  ))}
                </Stepper>
                {newScheduleDialogErrorMsg !== '' && (
                  <div className="section">
                    <div className="submit-error has-txt-centered">
                      {newScheduleDialogErrorMsg}
                    </div>
                  </div>
                )}
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={() => handleOnCloseScheduleDialog()}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Submit"
                  _type='button'
                  _fullWidth={false}
                  _loading={newScheduleSubmitLoading}
                  _disabled={newScheduleSubmitLoading}
                  _onClick={handleScheduleDialogSubmit}
                />
              </Fragment>
            }
        />
  );
}
