import React, { Component, Fragment } from 'react';
import classNames from 'classnames/bind';
import styles from './Logo.module.scss';
let cx = classNames.bind(styles);

export class IkigaiLogo extends Component {
  render() {
    const _size = this.props._size ? this.props._size : 'normal';
    const _color = this.props._color ? this.props._color : 'primary';
    const _navMinimzed = this.props._navMinimzed ? this.props._navMinimzed : false;
    //var loginLogo = 'loginLogo';
    /*var loginLogoClass = cx({
      loginLogo: true,
      'normal': _size === 'normal',
      'small': _size === 'small',
      'primary': _color === 'primary'
    });*/
    /*let loginLogoClass = cx(
      loginLogo,
      _size,
      _color,
    );*/
    let loginLogoClass = cx({
      loginLogo: true,
      'minimized': _navMinimzed
    },
      _size,
      _color
    );
    return (
      <div className={loginLogoClass}>
        {_navMinimzed ? (
          <span>Iki</span>
        ):(
          <Fragment>
            <span>Ikigai</span>
            <span>Labs</span>
          </Fragment>
        )}
      </div>
    );
  }
}
