import React, { Component, Fragment } from 'react';
//import { Link } from 'react-router-dom';
import {
  CssBaseline,
  Link as MaterialUiLink,
  Grid,
  Box,
  Typography,
  Container,
  Divider,
} from '@material-ui/core';
//import ErrorIcon from '@material-ui/icons/Error';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import * as EmailValidator from 'email-validator';
//import './login.css';
import styles from './Login.module.scss';
import { IkigaiLogo } from '../../../components/IkigaiLogo';
import IkiInput from '../../../components/IkiFormElements/IkiInput';
import IkiButton from '../../../components/IkiFormElements/IkiButton';

interface Props {
  data: any;
  classes: any;
  history: any;
  authenticate: (email: string, password: string) => void;
  loginSubmitErrorMsg: string;
  isLoggedIn: boolean;
}

interface State {
  emailValidation: boolean;
  passwordValidation: boolean;
  emailValue: any;
  passwordValue: any;
  emailErrorMsg: any;
  passErrorMsg: any;
  showPassword: boolean;
}

class SignIn extends Component<Props, State> {
  loadingInProgress: boolean = false;
  state = {
    emailValidation: false,
    passwordValidation: false,
    emailValue: '',
    passwordValue: '',
    emailErrorMsg: '',
    passErrorMsg: '',
    showPassword: true,
  };

  handleEmailChange = (e: any) => {
    //console.log("get email val: " + e.target.value)
    this.setState({ emailValue: e.target.value });
  };

  handlePassChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    //console.log("get pass val: " + e.target.value)
    this.setState({ passwordValue: e.target.value });
  };

  handleClickShowPassword = (e: any) => {
    if (this.state.showPassword === true) {
      this.setState({ showPassword: false });
    } else {
      this.setState({ showPassword: true });
    }
  };

  handleSignInFormSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
    this.loadingInProgress = true;
    e.preventDefault();

    let emailIsValid = false;
    let passIsValid = false;

    setTimeout(() => {
      if (this.state.emailValue !== '') {
        //console.log("email with val: " + this.state.emailValue)
        if (EmailValidator.validate(this.state.emailValue) === true) {
          this.setState({
            emailValidation: false,
            emailErrorMsg: ''
          });
          emailIsValid = true;
        } else {
          this.setState({
            emailValidation: true,
            emailErrorMsg: 'Invalid email'
          });
          this.loadingInProgress = false;
        }
      } else {
        //console.log("email without val: " + this.state.emailValue)
        this.setState({
          emailValidation: true,
          emailErrorMsg: 'Email is required'
        });
        this.loadingInProgress = false;
      }

      if (this.state.passwordValue !== '') {
        //console.log("email with val: " + this.state.emailValue)
        this.setState({
          passwordValidation: false,
          passErrorMsg: ''
        });
        passIsValid = true;
      } else {
        //console.log("email without val: " + this.state.emailValue)
        this.setState({
          passwordValidation: true,
          passErrorMsg: 'Password is required'
        });
        this.loadingInProgress = false;
      }

      if (emailIsValid === true && passIsValid === true) {
        this.props.authenticate(
          this.state.emailValue,
          this.state.passwordValue
        );
        if(this.props.isLoggedIn === false){
          this.loadingInProgress = false;
        }
      }else{
        this.loadingInProgress = false;
      }
    }, 1000);
  };

  render() {
    if(this.state.emailErrorMsg !== '' || this.state.passErrorMsg !== ''){
      this.loadingInProgress = false;
    }
    const {
      emailValue,
      passwordValue,
      //emailValidation,
      //passwordValidation,
      emailErrorMsg,
      passErrorMsg,
      showPassword
    } = this.state;
    const { loginSubmitErrorMsg, isLoggedIn } = this.props;
    if (isLoggedIn) {
      this.props.history.push('/dashboard');
    }
    return (
      <Fragment>
        {this.loadingInProgress === true && <div className={styles.mask}></div>}
        <Container className={styles.mainContainer} component="main" maxWidth="xs">
          <CssBaseline />
          <div className={styles.box}>
            <div className={styles.logoHolder}>
              <IkigaiLogo />
            </div>
            <Box mb={1}>
              <Typography component="h1" variant="h5" className={styles.mainTitle}>
                Sign in
              </Typography>
              <Divider className={styles.subtitleDivider} variant="middle" />
            </Box>
            <Typography className={styles.subtitle} component="div" variant="caption">
              Welcome back! <br /> Sign in to access IkigaiLabs dashboard
            </Typography>
            <form
              className={styles.loginForm}
              autoComplete="off"
              noValidate
              onSubmit={(event: any) => this.handleSignInFormSubmit(event)}
            >
                <IkiInput 
                  _label="Email"
                  _placeholder="Enter Your Email address"
                  _type="search"
                  _autoComplete="new-username"
                  _value={emailValue}
                  _onChange={(e: any) => this.handleEmailChange(e)}
                  _error={emailErrorMsg}
                  _required={true}
                  _fullWidth={true}
                  _autoFocus={true}
                />
                <IkiInput 
                  _label="Password"
                  _placeholder="Enter Your Password"
                  _type={showPassword ? 'password' : 'text'}
                  _autoComplete="new-password"
                  _value={passwordValue}
                  _onChange={(e: any) => this.handlePassChange(e)}
                  _error={passErrorMsg}
                  _required={true}
                  _fullWidth={true}
                  _endIcon={
                    {
                      _withButton: true,
                      _icon: (
                        <Fragment>
                          {showPassword ? (
                            <VisibilityOff fontSize="small" />
                          ) : (
                            <Visibility fontSize="small" />
                          )}
                        </Fragment>
                      ),
                      _position: "end",
                      _edge: "end",
                      _size: "small",
                      _onClick: (e: any) =>this.handleClickShowPassword(e)
                    }
                }
              />
              {/* <Link to="/forgot-password">Forgot password?</Link> */}
              {loginSubmitErrorMsg !== '' && (
                <Box mt={4} mb={4}>
                  <div className={styles.submitError}>
                    {loginSubmitErrorMsg}
                  </div>
                </Box>
              )}
              <Box mt={4}>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <IkiButton
                      _variant='contained'
                      _color='primary'
                      _label="Sign In"
                      _type='submit'
                      _fullWidth={true}
                      _loading={this.loadingInProgress}
                    />
                  </Grid>
                  {/* <Grid item xs={12}>
                    <Link to="/SignUp">Don't have an account? Sign Up</Link>
                  </Grid> */}
                </Grid>
              </Box>
            </form>
          </div>
          <Box className={styles.loginCopyright}>
            <Typography variant="body2" color="textSecondary" align="center">
              {'Copyright  '}
              <MaterialUiLink color="inherit" href="http://ikigailabs.io/">
                IkigaiLabs.io
              </MaterialUiLink>{' '}
              {new Date().getFullYear()}
              {'.'}
            </Typography>
          </Box>
        </Container>
      </Fragment>
    );
  }
}

export default SignIn;
