    componentWillUnmount() {
      //console.log("componentWillUnmount subpageC: " + this.subpageClicked);
      //console.log("componentWillUnmount - this.props...", this.props);
  
      let isUnderProject: boolean = true;
      let pathName:string = '';
      let pathName2: string = '';
      if(this.props.history.location.pathname && this.props.match.path) {
        pathName = this.props.history.location.pathname;
        pathName2 = this.props.match.path;
        var pathName_array = pathName.split("/");
        var page_id = pathName_array[pathName_array.length - 1];
        var page_type = pathName_array[2];
        //console.log('page_type: ' + page_type + ", pathName: " + pathName + ", pathName2: " + pathName2);
        if(page_type === 'projects') {
          //pathName !== '/dashboard/projects' && pathName2 !== '/dashboard/schedules'
          if(pathName2 !== '/dashboard/schedules') {
            isUnderProject = false;
          }
        }
      }

      if(!isUnderProject) {
        exitSessionManually(this.state.projectId).then((exitSessRes: any) => {
          this.props.cleanSession();
        }).catch((exitSessErr: any) => {
          this.props.cleanSession();
        });
      }
    }

