import React, { Component, Fragment } from 'react';
import {
  Container,
  Grid,
  Tabs,
  Tab,
} from '@material-ui/core';
import IkiLoader from '../../components/IkiLoader';

import './ProjectPage.module.scss';
import MoreMenu from './more-menu';
import {
  shareProject,
  enterSession as enterSessionManully,
  exitSession as exitSessionManually
} from '../../services/app-data.service';
import {
  DeleteProjectDialog,
  ShareProjectDialog,
  EditProjectDialog,
  DuplicateProjectDialog,
} from '../../components/dialogs';
import SnackbarMsg from '../../components/SnackbarMsg';
import FlowsComponent from './flows/flows-component';
import DatasetsComponent from './datasets/datasets-components';
import DashboardsComponent from './dashboards/dashboards-component';
import IkiPageHeader from '../../components/IkiLayout/IkiPageHeader'

type FlowsProps = {
  data: any;
  match: any;
  classes: any;
  history: any;
  componentList: any;
  facetDocLoaded: boolean;
  addFlow: (flowObj: any) => void;
  deleteFlow: (id: string) => void;
  restoreFlow: (id: string) => void;
  renameFlow: (id: string, name: string) => void;
  getFlows: (projectId: string) => void;
  logout: () => void;
  renameProject: (projectId: string, newName: string) => void;
  deleteProject: (projectId: string) => void;
  duplicateProject: (projectId: string, name: string, targetUser: string) => void;
  getDatasets: (projectId: string) => void;
  getDashboards: (projectId: string) => void;
  createDataset: (projectId: string, datasetName: string, file: File) => void;
  createDashboard: (projectId: string, datasetId: any, name: string, file: File) => void;
  deleteDataset: (datasetId: string) => void;
  deleteDashboard: (dashboardId: string) => void;
  editDataset: (
    projectId: string,
    datasetId: string,
    datasetName: string,
    file: File
  ) => void;
  editDashboard: (
    projectId: string,
    datasetId: any,
    name: string,
    dashboardId: string,
    file: File
  ) => void;
  enterSession: (projectId: string) => void;
  email: string;
  metabaseLoginId: string;
  activePageType: string;
  fireReturnToProject: (sourcePage: string) => void;
  cleanSession: () => void;
  fillSessions: (proSess: string, proId: string) => void;
  uploadProgress: any;
};
type FlowsState = {
  sort: string;
  deleteDialogState: boolean;
  deletePipeline: any;
  deletePipelineName: string;
  deletePipelineID: string;
  selectedDatasetData?: any;
  selectedDashData?: any;
  newFlowDialogState: boolean;
  newFlowName: string;
  newFlowNameError: boolean;
  newFlowNameErrorMsg: string;
  projectId: string;
  projectNameEditMode: Boolean;
  title: string;
  projectDeleteDialogState: boolean;
  projectShareDialogState: boolean;
  projectShareDialogErrorState: boolean;
  projectShareDialogErrorMsg: string;
  snackbarShareProSuccessState: boolean;
  editProjectDialogState: boolean;
  addDatasetDialogState: boolean;
  addDashboardtDialogState: boolean;
  addDatasetMinimize: boolean;
  editDatasetDialogState: boolean;
  editDashboardDialogState: boolean;
  deleteDatasetDialogState: boolean;
  uploadDatasetDialogState: boolean;
  duplicateDialogProjectState: boolean;
  importFlowDialogState: boolean;
  importFlowNameExists: boolean;
  tabValue: number;
  datasetEditMode: boolean;
  dashEditMode: boolean;
  snackbarOpen: boolean;
  snackbarMessage: any;
  snackbarType: string;
  sharingLoading: boolean;
  renamingLoading: boolean;
  deletingLoading: boolean;
  newlyAddedDatasetNameExists: boolean;
  newlyAddedDashNameExists: boolean;
  addFlowLoading: boolean;
  rowsPerPage: number;
  page: number;
  searchTerm: string;
  flowsFilter: string;
  renderAsList: boolean;
  tableSortAsc: boolean;
  tableSortProperty: string;
  toggleFilterDisplay: boolean;
  clickedBox: string;
  cloneFlowDialogState: boolean;
  cloneFlowId: string;
  cloneFlowName: string;
  cloneFlowNameError: boolean;
  cloneFlowNameErrorMsg: string;
  cloneProcess: boolean;
  sessionCreated: boolean;
  sessionID: string;
  showContent: boolean;
  loadingTxt: string;
};

class ProjectComponent extends Component<FlowsProps, FlowsState> {
  subpageClicked: boolean = false;
  state: FlowsState = {
    sort: 'all',
    deletePipeline: null,
    deleteDialogState: false,
    deletePipelineName: '',
    deletePipelineID: '',
    newFlowDialogState: false,
    newFlowName: '',
    newFlowNameError: false,
    newFlowNameErrorMsg: '',
    projectId: '',
    projectNameEditMode: false,
    title: '',
    projectDeleteDialogState: false,
    projectShareDialogState: false,
    projectShareDialogErrorState: false,
    projectShareDialogErrorMsg: '',
    snackbarShareProSuccessState: false,
    editProjectDialogState: false,
    addDatasetDialogState: false,
    addDashboardtDialogState: false,
    deleteDatasetDialogState: false,
    uploadDatasetDialogState: false,
    editDatasetDialogState: false,
    editDashboardDialogState: false,
    duplicateDialogProjectState: false,
    tabValue: 0,
    datasetEditMode: false,
    addDatasetMinimize: false,
    dashEditMode: false,
    snackbarOpen: false,
    snackbarMessage: null,
    snackbarType: '',
    sharingLoading: false,
    renamingLoading: false,
    deletingLoading: false,
    newlyAddedDatasetNameExists: false,
    newlyAddedDashNameExists: false,
    addFlowLoading: false,
    rowsPerPage: 16,
    page: 0,
    searchTerm: '',
    flowsFilter: 'all',
    renderAsList: false,
    tableSortAsc: true,
    tableSortProperty: '',
    toggleFilterDisplay: false,
    clickedBox: '',
    importFlowDialogState: false,
    importFlowNameExists: false,
    cloneFlowDialogState: false,
    cloneFlowId: '',
    cloneFlowName: '',
    cloneFlowNameError: false,
    cloneFlowNameErrorMsg: '',
    cloneProcess: false,
    sessionCreated: false,
    sessionID: '',
    showContent: false,
    loadingTxt: ''
  };
  componentDidMount() {
    /*console.log('Project props...', this.props);
    if(this.props.data.activeProjectId !== '') {
      //console.log('this.props.data.activeProjectId... ', this.props.data.activeProjectId);
      let pathName = '';
      if(this.props.history.location.pathname) {
        pathName = this.props.history.location.pathname;
        var pathName_array = pathName.split("/");
        var page_id = pathName_array[pathName_array.length - 1];
        var page_type = pathName_array[2];
        if(page_type === 'project') {
          this.props.fillSessions('', page_id);
        }
      }
    }*/
    //console.log("props metabaseLoginId - project page: " + this.props.metabaseLoginId);
    //console.log("this.props: ", this.props);
    this.setState({ loadingTxt: 'Creating Project Session...' });
    var urlParamProjectId = this.props.match.params['id'];
    var typeOfPageAccess = "";
    if (this.props.history.action) {
      typeOfPageAccess = this.props.history.action;
    }
    //this.props.fireReturnToProject("project");
    if (!urlParamProjectId || urlParamProjectId === '') {
      this.props.history.push('/dashboard');
    } else {
      if (this.props.activePageType === "schedule" || this.props.activePageType === "pipeline" || this.props.activePageType === "dataset" || this.props.activePageType === "dash") {
        if (this.props.activePageType === "pipeline") {
          this.setState({
            projectId: urlParamProjectId,
            tabValue: 0
          });
        } else if (this.props.activePageType === "dataset") {
          //console.log("this.props.activePageType IN dataset");
          this.setState({
            projectId: urlParamProjectId,
            tabValue: 1
          });
        } else if (this.props.activePageType === "dash") {
          this.setState({
            projectId: urlParamProjectId,
            tabValue: 2
          });
        } else {
          this.setState({
            projectId: urlParamProjectId,
            tabValue: 0
          });
        }
        this.props.getFlows(urlParamProjectId);
        this.props.getDatasets(urlParamProjectId);
        this.props.getDashboards(urlParamProjectId);
        this.setState({
          sessionCreated: true,
          loadingTxt: 'Preparing Project View...'
        });
        setTimeout(() => {
          this.setState({ showContent: true });
        }, 2000);
      } else {
        this.setState({
          projectId: urlParamProjectId,
          tabValue: 0
        });
        //console.log("props metabaseLoginId - project page: " + this.props.metabaseLoginId);
        //console.log("typeOfPageAccess: " + typeOfPageAccess);
        if (typeOfPageAccess !== "POP") {
          enterSessionManully(urlParamProjectId).then((sessRes: any) => {
            //console.log("project, after enter sess: " + sessRes);
            this.props.getFlows(urlParamProjectId);
            this.props.getDatasets(urlParamProjectId);
            this.props.getDashboards(urlParamProjectId);
            this.setState({
              sessionCreated: true,
              loadingTxt: 'Preparing Project View...'
            });
            this.props.fillSessions(sessRes, urlParamProjectId);
            setTimeout(() => {
              this.setState({ showContent: true });
            }, 2000);
          }).catch((enterSessRes: any) => {
            //this.showStatus("error", "Something went wrong. Redirecting to Projects...");
            this.setState({
              snackbarMessage: (
                <Fragment>
                  <div className="msg">Something went wrong. Redirecting to Projects...</div>
                </Fragment>
              ),
              snackbarType: "error",
              snackbarOpen: true
            });
            setTimeout(() => {
              this.props.history.push(`/dashboard/projects`);
            }, 3000)
          });
          //this.props.enterSession(urlParamProjectId);
        } else {
          this.props.getFlows(urlParamProjectId);
          this.props.getDatasets(urlParamProjectId);
          this.props.getDashboards(urlParamProjectId);
          this.setState({
            sessionCreated: true,
            loadingTxt: 'Preparing Project View...'
          });
          setTimeout(() => {
            this.setState({ showContent: true });
          }, 2000);
        }
      }
      //this.props.getFlows(urlParamProjectId);
      //this.props.getDatasets(urlParamProjectId);
      //this.props.getDashboards(urlParamProjectId);
    }
  }
  componentWillUnmount() {
    //console.log("componentWillUnmount subpageC: " + this.subpageClicked);
    //console.log("componentWillUnmount - this.props...", this.props);

    let isUnderProject: boolean = false;
    if(this.props.history.location.pathname) {
      var pathName = this.props.history.location.pathname;
      var pathName_array = pathName.split("/");
      var page_id = pathName_array[pathName_array.length - 1];
      var page_type = pathName_array[2];
      //console.log('page_type: ' + page_type);
      if(page_type === 'project-schedules') {
        isUnderProject = true;
      }
    }

    var typeOfPageAccess = "";
    if (this.props.history.action) {
      typeOfPageAccess = this.props.history.action;
    }
    //console.log('isUnderProject: ' + isUnderProject);
    if (typeOfPageAccess !== "POP" && !isUnderProject) {
      if (!this.subpageClicked) {
        this.props.fireReturnToProject("");
        this.setState({ sessionCreated: false });
        exitSessionManually(this.state.projectId).then((exitSessRes: any) => {
          this.props.cleanSession();
        }).catch((exitSessErr: any) => {
          this.props.cleanSession();
        });
      } else {
        if (this.state.tabValue === 0) {
          this.props.fireReturnToProject("pipeline");
        } else if (this.state.tabValue === 1) {
          this.props.fireReturnToProject("dataset");
        } else if (this.state.tabValue === 2) {
          this.props.fireReturnToProject("dash");
        } else {
          this.props.fireReturnToProject("pipeline");
        }
      }
    }
  }

  showStatus(variant: any, message: string) {
    this.setState({
      snackbarMessage: (
        <Fragment>
          <div className="msg">
            {message}
          </div>
        </Fragment>
      ),
      snackbarType: variant,
      snackbarOpen: true
    });
  }

  handleDialogkOpen = (flow: any) => {
    this.setState({
      deleteDialogState: true,
      deletePipeline: flow,
      deletePipelineName: flow.name,
      deletePipelineID: flow.pipeline_id
    });
    //console.log("deleteDialogState: " + this.state.deleteDialogState)
  };
  handleOnClone = (pipelineId: string, pipelineName: string) => {
    this.setState({
      cloneFlowDialogState: true,
      cloneFlowId: pipelineId,
      cloneFlowName: pipelineName
    });
    //console.log("deleteDialogState: " + this.state.deleteDialogState)
  };
  handleDialogClose = () => {
    this.setState({
      deleteDialogState: false,
      deletePipeline: null,
      deletePipelineName: '',
      deletePipelineID: '',
      projectShareDialogState: false,
      projectShareDialogErrorState: false,
      projectShareDialogErrorMsg: ''
    });
    //console.log("deleteDialogState: " + this.state.deleteDialogState)
  };

  toggleProjectNameEditMode(name: string) {
    if (this.state.projectNameEditMode) {
      this.setState({
        projectNameEditMode: false
      });
      this.props.renameProject(this.state.projectId, this.state.title);
    } else {
      this.setState({
        projectNameEditMode: true,
        title: name
      });
    }
  }

  logoutHandler() {
    this.props.logout();
  }

  onChangeTitle(e: any) {
    this.setState({
      title: e.target.value
    });
  }

  onChangeTabs(event: React.ChangeEvent<{}>, newValue: number) {
    this.setState({ tabValue: newValue });
  }

  handleToggleFilter = () => {
    if (this.state.toggleFilterDisplay === true) {
      this.setState({ toggleFilterDisplay: false });
    } else {
      this.setState({ toggleFilterDisplay: true });
    }
  }

  onEditClick(pipelineId: string) {
    var subpageLink = `/flow/${pipelineId}`;
    this.handleSubpageClick(subpageLink);
    //this.props.history.push(`/flow/${pipelineId}`);
  }

  handleDismissDatasetNameExists() {
    this.setState({
      newlyAddedDatasetNameExists: false
    });
  }
  handleDismissDashNameExists() {
    this.setState({
      newlyAddedDashNameExists: false
    });
  }

  handleSubpageClick(subpageLink: string) {
    this.subpageClicked = true;
    //this.props.history.push(`/flow/${this.props.data.pipeline_id}`);
    this.props.history.push(subpageLink);
  }

  render() {
    //console.log("metabase session id: " + this.props.metabaseLoginId);
    //console.log(this.props.metabaseLoginId);
    //console.log("activePageType: " + this.props.activePageType);
    //console.log("props metabaseLoginId - project page: " + this.props.metabaseLoginId);
    //console.log("this.props.uploadProgress: ", this.props.uploadProgress);
    // eslint-disable-next-line
    var projectId: any,
      datasetId: any,
      dashboardId: any,
      dashName: any,
      datasetName: any,
      flows: any,
      projectData: any,
      datasets: any,
      dashboards: any
    if (this.state.sessionCreated && this.props.data.flowsLoaded && this.props.data.datasetsLoaded && this.props.data.dashboardsLoaded) {
      projectId = this.state.projectId;
      flows = this.props.data.flows[projectId];
      //console.log("data FLOWS in render: ");
      //console.log(this.props.data);
      datasetId = this.state.selectedDatasetData
        ? this.state.selectedDatasetData.dataset_id
        : '';

      dashboardId = this.state.selectedDashData
        ? this.state.selectedDashData.dashboard_id
        : '';

      dashName = this.state.selectedDashData
        ? this.state.selectedDashData.name
        : '';

      datasetName = this.state.selectedDatasetData
        ? this.state.selectedDatasetData.name
        : '';

      projectData = this.props.data.projects.filter(
        (p: any) => p.project_id === projectId
      )[0];

      datasets = this.props.data.datasets.filter(
        (datasetData: any) => datasetData.project_id === projectId
      );

      dashboards = this.props.data.dashboards.filter(
        (dashboardData: any) => dashboardData.project_id === projectId
      );

      if (
        (this.props.data.projects.length > 0 &&
          !projectData &&
          projectId !== '') ||
        (this.props.data.projectsLoaded && this.props.data.projects.length === 0)
      ) {
        this.props.history.push('/dashboard');
      }
      if (this.props.data.flowsLoaded) {

      }
      if (this.props.data.datasetsLoaded) {

      }
      if (this.props.data.dashboardsLoaded) {

      }
    }
    //console.log(this.props,this.state)
    return (
      <Fragment>
        <Container className="main-container" component="main" maxWidth={false}>
          <Grid container>
            <Grid item xs={12}>
              <IkiLoader
                loadingTxt={this.state.loadingTxt}
                fadeOut={this.state.showContent}
                placement="next-to-nav"
                history={this.props.history}
                backLink={`/dashboard`}
              />
              <IkiPageHeader
                  _breadcrumbLink='/dashboard'
                  _breadcrumbText='Home / Projects'
                  _breadcrumbBackIcon={true}
                  _breadcrumbTitle={projectData ? projectData.name : ''}
                  _actionButtons={
                    <MoreMenu
                      onEdit={() => {
                        this.setState({ editProjectDialogState: true });
                      }}
                      onDelete={() => {
                        this.setState({ projectDeleteDialogState: true });
                      }}
                      onShare={() => {
                        this.setState({ projectShareDialogState: true });
                      }}
                      onDuplicate={()=>{
                        this.setState({duplicateDialogProjectState:true});
                      }}
                    />
                  }
                />
            </Grid>
          </Grid>
          <Grid container className="tab-container flow-tab-container">
            <Grid item xs={12}>
              <Tabs
                value={this.state.tabValue}
                classes={{
                  indicator: 'flow-tab-indicator'
                }}
                onChange={this.onChangeTabs.bind(this)}
              >
                <Tab label="Flows"></Tab>
                <Tab label="Datasets"></Tab>
                <Tab label="Dashboards"></Tab>
              </Tabs>
            </Grid>
          </Grid>
          <div hidden={this.state.tabValue !== 0}>
              <FlowsComponent
                flows={flows}
                subpageClick={(link: string) => this.handleSubpageClick(link)}
                addFlow={(flowObj: any) => this.props.addFlow(flowObj)}
                deleteFlow={(flowObj: any) => this.props.deleteFlow(flowObj)}
                creatingFlow={this.props.data.creatingFlow}
                projects={this.props.data.projects}
                projectId={this.state.projectId}
              />
          </div>
          <div hidden={this.state.tabValue !== 1}>
              <DatasetsComponent
                  datasets={datasets}
                  subpageClick={(link: string) => this.handleSubpageClick(link)}
                  createDataset={(projectId: string, datasetName: string, file: File) => this.props.createDataset(projectId, datasetName, file)}
                  editDataset={(projectId: string, datasetId: string, datasetName: string, file: File) => this.props.editDataset(projectId, datasetId, datasetName, file)}
                  deleteDataset={(datasetId: string) => this.props.deleteDataset(datasetId)}
                  uploadProgress={this.props.uploadProgress}
                  uploadStatus={this.props.data.uploadStatus}
                  projects={this.props.data.projects}
                  projectId={this.state.projectId}
                />
          </div>
          <div hidden={this.state.tabValue !== 2}>
              <DashboardsComponent
                dashboards={dashboards}
                datasets={datasets}
                subpageClick={(link: string) => this.handleSubpageClick(link)}
                createDashboard={(projectId: string, datasetId: any, name: string, file: File) => this.props.createDashboard(projectId, datasetId, name, file)}
                editDashboard={(projectId: string, datasetId: any, name: string, dashboardId: string, file: File) => this.props.editDashboard(projectId, datasetId, name, dashboardId, file)}
                deleteDashboard={(dashboardId: string) => this.props.deleteDashboard(dashboardId)}
                uploadProgress={this.props.uploadProgress}
                dashUploadStatus={this.props.data.dashUploadStatus}
                projects={this.props.data.projects}
                projectId={this.state.projectId}
              />
          </div>
        </Container>

        <DeleteProjectDialog
          projectId={this.state.projectId}
          projectName={projectData ? projectData.name : ''}
          handleDialogClose={() => this.handleDeleteDialogCancel()}
          handleDeleteDialogSubmit={() => this.handleDeleteDialogSubmit()}
          handleDeleteDialogCancel={() => this.handleDeleteDialogCancel()}
          open={this.state.projectDeleteDialogState}
          deleteSubmitLoading={this.state.deletingLoading}
        />
        <ShareProjectDialog
          handleDialogClose={() => this.handleShareDialogCancel()}
          handleShareDialogCancel={() => this.handleShareDialogCancel()}
          open={this.state.projectShareDialogState}
          projectId={this.state.projectId}
          projectName={projectData ? projectData.name : ''}
          projectShareDialogErrorState={this.state.projectShareDialogErrorState}
          projectShareDialogErrorMsg={this.state.projectShareDialogErrorMsg}
          onSubmit={(email: string) => this.handleShareDialogSubmit(email)}
          shareSubmitLoading={this.state.sharingLoading}
        />
        <EditProjectDialog
          handleDialogClose={() => this.handleEditProjectDialogClose()}
          handleEditCancel={() => this.handleEditProjectDialogClose()}
          onSubmit={(name: string) => this.handleEditProjectDialogSubmit(name)}
          open={this.state.editProjectDialogState}
          projectName={projectData ? projectData.name : ''}
          projectID={this.state.projectId}
          renameSubmitLoading={this.state.renamingLoading}
        />
        <DuplicateProjectDialog
          projectName={projectData ? projectData.name : ''}
          projectID={this.state.projectId}
          open={this.state.duplicateDialogProjectState}
          onSubmit={(projectId: string, newProjectName: string, targetUser: string) => this.handleDuplicateProjectDialogSubmit(projectId, newProjectName, targetUser)}
          handleDialogClose={() => {
            this.handleDuplicateProjectDialogClose();
          }}
        />
        <SnackbarMsg
          snackbarOpen={this.state.snackbarOpen}
          snackbarType={this.state.snackbarType}
          snackbarMsg={this.state.snackbarMessage}
          onSnackbarClose={this.handleSnackbarClose}
        />
      </Fragment>
    );
  }

  handleDuplicateProjectDialogSubmit = (projectId: string, newProjectName: string, targetUser: string) => {
    var matchProjects_Arr = this.props.data.projects.filter(
      (p: any) => p.name === newProjectName
    );
    if (Object.keys(matchProjects_Arr).length > 0) {
      //console.log("COPY name");
      setTimeout(() => {
        this.setState({
          duplicateDialogProjectState: false,
          snackbarMessage: (
            <Fragment>
              <div className="msg">Project with same name already exists. Please choose different name</div>
            </Fragment>
          ),
          snackbarType: 'error',
          snackbarOpen: true
        });
      }, 1500);
    } else {
      this.props.duplicateProject(this.state.projectId, newProjectName, targetUser);
      setTimeout(() => {
        this.setState({
          duplicateDialogProjectState: false,
          snackbarMessage: (
            <Fragment>
              <div className="msg">
                You have succesfully cloned project
              </div>
            </Fragment>
          ),
          snackbarType: 'success',
          snackbarOpen: true,
        });
      }, 1500);
    }
    this.handleDuplicateProjectDialogClose();
  }
  handleDuplicateProjectDialogClose() {
    this.setState({ duplicateDialogProjectState: false });
  }
  handleEditProjectDialogClose() {
    this.setState({
      editProjectDialogState: false
    });
  }
  handleEditProjectDialogSubmit(name: string) {
    this.setState({ renamingLoading: true });
    this.props.renameProject(this.state.projectId, name);
    setTimeout(() => {
      this.setState({
        editProjectDialogState: false,
        snackbarMessage: (
          <Fragment>
            <div className="msg">You have succesfully renamed project</div>
            <div className="val">{name}</div>
          </Fragment>
        ),
        snackbarType: 'success',
        snackbarOpen: true,
        renamingLoading: false
      });
    }, 1500);
  }
  handleShareDialogCancel() {
    this.setState({
      projectShareDialogState: false,
      projectShareDialogErrorState: false,
      projectShareDialogErrorMsg: ''
    });
  }
  handleShareDialogSubmit(email: string) {
    this.setState({ sharingLoading: true });
    var projectData = this.props.data.projects.filter(
      (p: any) => p.project_id === this.state.projectId
    )[0];
    try {
      shareProject(this.state.projectId, email)
        .then((res: any) => {
          //console.log('try res.status: ' + res.status);
          if (res.status === 200) {
            //console.log('sharing success');
            setTimeout(() => {
              this.setState({
                projectShareDialogState: false,
                projectShareDialogErrorState: false,
                projectShareDialogErrorMsg: '',
                snackbarMessage: (
                  <Fragment>
                    {/* <span>Project shared successfully</span> */}
                    <div className="msg">
                      You have succesfully shared project{' '}
                      <span className="val">{projectData ? projectData.name : ''}</span>
                    </div>
                    <div>
                      with user <span className="val">{email}</span>
                    </div>
                  </Fragment>
                ),
                snackbarType: 'success',
                snackbarOpen: true,
                sharingLoading: false
              });
            }, 1500);
          } else {
            this.setState({
              projectShareDialogErrorState: true,
              projectShareDialogErrorMsg:
                'Sharing failed! Check user email and try again.',
              sharingLoading: false
            });
          }
        })
        .catch(err => {
          this.setState({
            projectShareDialogErrorState: true,
            projectShareDialogErrorMsg:
              'Sharing failed! Check user email and try again.',
            sharingLoading: false
          });
        });
    } catch (e) {
      this.setState({
        projectShareDialogState: false,
        projectShareDialogErrorState: false,
        projectShareDialogErrorMsg: '',
        snackbarMessage: (
          <Fragment>
            <div className="msg">
              Sharing failed. Something went wrong. Please try again
            </div>
          </Fragment>
        ),
        snackbarType: 'error',
        snackbarOpen: true,
        sharingLoading: false
      });
    }
  }
  handleCloseShareSuccesSnackbar = () => {
    this.setState({ snackbarShareProSuccessState: false });
  };
  handleDeleteDialogSubmit() {
    this.setState({ deletingLoading: true });
    var projectData = this.props.data.projects.filter(
      (p: any) => p.project_id === this.state.projectId
    )[0];
    this.setState({
      snackbarMessage: (
        <Fragment>
          <div className="msg">Deleting project ...</div>
          <div className="val">{projectData ? projectData.name : ''}</div>
        </Fragment>
      ),
      snackbarType: 'processing',
      snackbarOpen: true
    });
    setTimeout(() => {
      this.props.deleteProject(this.state.projectId);
      this.setState({
        projectDeleteDialogState: false,
        deletingLoading: false
      });
    }, 2000);
  }
  handleDeleteDialogCancel() {
    this.setState({
      projectDeleteDialogState: false
    });
  }
  onDelete(flowObj: any) {
    this.props.deleteFlow(flowObj);
    this.setState({
      deleteDialogState: false,
      deletePipeline: '',
      snackbarMessage: (
        <Fragment>
          <div className="msg">You have succesfully deleted pipeline</div>
          <div className="val">{this.state.deletePipelineName}</div>
        </Fragment>
      ),
      snackbarType: 'success',
      snackbarOpen: true
    });
  }

  handleSnackbarClose = () => {
    this.setState({ snackbarOpen: false });
  };
}

export default ProjectComponent;
