import React, { Component } from 'react';
import { 
    Drawer
} from '@material-ui/core';
import classNames from 'classnames/bind';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { IkigaiLogo } from '../../IkigaiLogo';
import DrawerMenu from './side-menu';
import styles from './Navigation.module.scss';
let cx = classNames.bind(styles);

export default class IkigaiNavigation extends Component {
  prepareNavigation = () => {
    const _email = this.props._email ? this.props._email : '';
    const _logoutHandler = this.props._logoutHandler ? this.props._logoutHandler : () => {};
    const _type = this.props._type ? this.props._type : '';
    const _page = this.props._page ? this.props._page : '';
    const _leftSidebar = this.props._leftSidebar ? this.props._leftSidebar : '';
    const _rightSidebar = this.props._rightSidebar ? this.props._rightSidebar : '';
    const _filterSidebar = this.props._filterSidebar ? this.props._filterSidebar : '';
    const _handleGoBack = this.props._handleGoBack ? this.props._handleGoBack : '';
    const _uploadProgress = this.props._uploadProgress ? this.props._uploadProgress : '';
    let _getMenuItemActive = this.props._getMenuItemActive ? this.props._getMenuItemActive : '';
    const _toggleNavigation = this.props._toggleNavigation ? this.props._toggleNavigation : '';
    let _navMinimzed = this.props._navMinimzed ? this.props._navMinimzed : false;
    let _menuItemActive = this.props._menuItemActive ? this.props._menuItemActive : '';
    let _appData = this.props._appData ? this.props._appData : null;
    //let _isProjectPage = this.props._isProjectPage ? this.props._isProjectPage : false;
    var drawerHolderClass = cx({
      drawerHolder: true,
      'minimized': _navMinimzed
    });

    var goBackTxt = 'Go back ';
    if(_page === 'Dataset') {
      goBackTxt += 'to Datasets'
    } else if(_page === 'Dashboard') {
      goBackTxt += 'to Dashboards'
    } else if(_page === 'Flow') {
      goBackTxt += 'to Flows'
    }

    var activePageType = '';
    if(_appData !== null) {
      activePageType = _appData.activePageType;
    }

    //console.log('IkigaiNavigation...', this.props);

    if(_type === 'sub-page') {
      if(_filterSidebar !== '') {
        return (
          <>
            <Drawer
              variant="permanent"
              anchor="left"
              className={drawerHolderClass}
              classes={{
                paper: 'drawer minimized'
              }}
            >
              <IkigaiLogo
                _size='big'
                _color='white'
                _navMinimzed={true}
              />
              <div
                className={styles.goBack}
                onClick={_handleGoBack}
                style={{cursor: 'pointer'}}
              >
                <ArrowRightAltIcon className={styles.goBackIcon} />
                {/* <span>{goBackTxt}</span> */}
              </div>
              <DrawerMenu 
                _email={_email}
                _uploadProgress={_uploadProgress} 
                _logoutHandler={_logoutHandler}
                _navMinimzed={true}
                _getMenuItemActive={_getMenuItemActive}
                _menuItemActive={_menuItemActive}
                _activePageType={activePageType}
                _appData={_appData}
                _type={_type}
                _page={_page}
              />
              {/* <IkigaiLogo
                _size='small'
                _color='white'
              />
              <div
                className={styles.goBack}
                onClick={_handleGoBack}
                style={{cursor: 'pointer'}}
              >
                <ArrowRightAltIcon className={styles.goBackIcon} />
                <span>{goBackTxt}</span>
              </div>
              {_leftSidebar}
              <div className="pureBg"></div> */}
            </Drawer>
            <div className={styles.filterSidebar}>
              {/* <div
                  className={styles.goBack}
                  onClick={_handleGoBack}
                  style={{cursor: 'pointer'}}
                >
                  <ArrowRightAltIcon className={styles.goBackIcon} />
                  <span>{goBackTxt}</span>
              </div> */}
              {_filterSidebar}
              <div className={styles.pureBg}></div>
            </div>
          </>
        );
      } else if(_rightSidebar !== '') {
        return (
          <>
            <Drawer
              variant="permanent"
              anchor="right"
              className={styles.infoDrawerHolder}
              classes={{
                paper: 'info-drawer-paper'
              }}
            >
              {_rightSidebar}
            </Drawer>
          </>
        );
      } else {
        return (
          <>
            <Drawer
              variant="permanent"
              anchor="left"
              className={drawerHolderClass}
              classes={{
                paper: 'drawer minimized'
              }}
            >
              <IkigaiLogo
                _size='big'
                _color='white'
                _navMinimzed={true}
              />
              <div
                className={styles.goBack}
                onClick={_handleGoBack}
                style={{cursor: 'pointer'}}
              >
                <ArrowRightAltIcon className={styles.goBackIcon} />
                {/* <span>{goBackTxt}</span> */}
              </div>
              <DrawerMenu 
                _email={_email}
                _uploadProgress={_uploadProgress} 
                _logoutHandler={_logoutHandler}
                _navMinimzed={true}
                _getMenuItemActive={_getMenuItemActive}
                _menuItemActive={_menuItemActive}
                _activePageType={activePageType}
                _appData={_appData}
                _type={_type}
                _page={_page}
              />
              {/* <IkigaiLogo
                _size='small'
                _color='white'
              />
              <div
                className={styles.goBack}
                onClick={_handleGoBack}
                style={{cursor: 'pointer'}}
              >
                <ArrowRightAltIcon className={styles.goBackIcon} />
                <span>{goBackTxt}</span>
              </div> */}
            </Drawer>
          </>
        );
      }
    } else if(_type === 'preview') {
      if(_leftSidebar !== '') { 
        return (
          <>
            <Drawer
              variant="permanent"
              anchor="left"
              className={drawerHolderClass}
              classes={{
                paper: 'drawer'
              }}
            >
              <IkigaiLogo
                _size='small'
                _color='white'
              />
              {_leftSidebar}

            </Drawer>
          </>
        );
      }  
    } else { 
      return (
        <>
          <Drawer
            variant="permanent"
            anchor="left"
            className={drawerHolderClass}
            classes={{
              paper: 'drawer'
            }}
          >
            <IkigaiLogo
              _size='big'
              _color='white'
              _navMinimzed={_navMinimzed}
            />
            <DrawerMenu 
              _email={_email}
              _uploadProgress={_uploadProgress} 
              _logoutHandler={_logoutHandler}
              _navMinimzed={_navMinimzed}
              _getMenuItemActive={_getMenuItemActive}
              _menuItemActive={_menuItemActive}
              _activePageType={activePageType}
              _appData={_appData}
              _type={_type}
              _page={_page}
            />
            <div 
              className={styles.toggleNavButton}
              onClick={_toggleNavigation}
            >
              {_navMinimzed ? (
                <ArrowForwardIosIcon />
              ):(
                <ArrowBackIosIcon />
              )}
            </div>
          </Drawer>
        </>
      );
    }
  }
  render() {
    return this.prepareNavigation()
  }
}
