import React, { Fragment } from 'react';
//import PersonIcon from '@material-ui/icons/Person';
import * as EmailValidator from 'email-validator';
import IkiDialog from '../IkiDialog';
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';

export function ShareProjectDialog(props: any) {
  const {
    handleDialogClose,
    open,
    projectId,
    projectName,
    projectShareDialogErrorState,
    projectShareDialogErrorMsg,
    onSubmit,
    shareSubmitLoading
  } = props;

  const [targetUserEmail, setTargetUserMail] = React.useState('');
  const [emailErrorMsg, setEmailErrorMsg] = React.useState('');

  const handleTargetUserEmailChange = (e: any) => {
    setTargetUserMail(e.target.value);
  };

  const handleShareDialogSubmit = (e: any) => {
    let emailIsValid = false;

    if (targetUserEmail !== '') {
      if (EmailValidator.validate(targetUserEmail) === true) {
        setEmailErrorMsg('');

        emailIsValid = true;
      } else {
        setEmailErrorMsg('Invalid email');

        emailIsValid = false;
      }
    } else {
      setEmailErrorMsg('Email is required');
      emailIsValid = false;
    }
    if (emailIsValid) {
      onSubmit(targetUserEmail);
    }
  };

  const handleOnCloseShareDialog = () => {
    setTargetUserMail('');
    setEmailErrorMsg('');
    handleDialogClose();
  }

  return (
      <IkiDialog
            _open={open}
            _onClose={handleOnCloseShareDialog}
            _type='share'
            _icon={false}
            _title='Share project'
            _subtitle=''
            _size='xs'
            _align='left'
            _content={
              <>
                <div className="section">
                  <div className="txt-dialog-faded1">{projectId}</div>
                  <div className="txt-dialog-impact1">{projectName}</div>
                </div>
                <div className="section">
                  <IkiInput 
                    _label="User email"
                    _placeholder="Enter email of user to share project with"
                    _type='text'
                    _autoComplete="new-password"
                    _value={targetUserEmail}
                    _onChange={handleTargetUserEmailChange}
                    _characterCounter={40}
                    _autoFocus={true}
                    _fullWidth={true}
                    _error={emailErrorMsg}
                  />
                </div>
                {projectShareDialogErrorState === true && (
                  <div className="section">
                    <div className="submit-error has-txt-centered">
                      {projectShareDialogErrorMsg}
                    </div>
                  </div>
                )}
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={handleOnCloseShareDialog}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Share"
                  _type='button'
                  _fullWidth={false}
                  _loading={shareSubmitLoading}
                  _disabled={shareSubmitLoading}
                  _onClick={handleShareDialogSubmit}
                />
              </Fragment>
            }
        />
  );
}
