import React, { Component } from 'react';

import moment from 'moment';

import { DatePicker, TimePicker } from 'material-ui-pickers';

import { parseCronString } from '../../../util/cron';

import {
  withStyles,
  TextField,
  Radio,
  FormControl,
  FormControlLabel,
  Checkbox,
  Grid
} from '@material-ui/core';

type ScheduleSettingsProps = {
  classes: any;
  settings: any;
  validated: boolean;
  onChange: (name: string, settings: any) => void;
};
type ScheduleSettingsState = {};

const styles = (theme: any) => ({
  textField: {
    width: '100%'
  },
  formBox: {
    padding: theme.spacing(2),
    border: '1px solid rgba(0, 0, 0, 0.42)',
    marginTop: 20
  },
  formBoxTitle: {
    color: '#000',
    marginTop: -32,
    marginLeft: 5,
    backgroundColor: '#FFF',
    display: 'inline-block',
    /* width: 100%; */
    position: 'absolute' as 'absolute',
    padding: 5
  },
  sheduleDialog: {},
  scheduleDialogContent: {
    padding: 0
  },
  scheduleTable: {},
  scheduleTableRow: {
    width: '100%',
    borderBottom: '1px solid #ccc',
    display: 'inline-flex',
    alignItems: 'center',
    justifyContent: 'flex-start'
  },
  scheduleTableRowCenter: {
    borderBottom: '0'
  },
  scheduleTableCell: {
    width: '25%',
    float: 'left' as 'left',
    display: 'flex',
    alignItems: 'center'
  },
  scheduleTableCell1: {
    width: '15%',
    float: 'left' as 'left',
    display: 'flex',
    alignItems: 'center'
  },
  scheduleTableCellDays: {
    width: '100%'
  },
  scheduleTableleft: {
    width: 'auto',
    float: 'left' as 'left',
    marginRight: 10
  },
  scheduleTableright: {
    width: '60%',
    float: 'right' as 'right'
  },
  scheduleNumberTextField: {
    width: 30
  }
});

class ScheduleSettings extends Component<
  ScheduleSettingsProps,
  ScheduleSettingsState
> {
  state = {
    name: '',
    settings: {
      oneTime: {
        enabled: true,
        startDate: moment(),
        startTime: moment()
      },
      day: {
        enabled: false,
        recurEvery: 1,
        atTime: moment(),
        startDate: moment(),
        endDate: moment()
      },
      week: {
        enabled: false,
        recurEvery: 1,
        atTime: moment(),
        startDate: moment(),
        endDate: moment(),
        days: [
          {
            day: 'Monday',
            value: false,
            shortForm: 'mon'
          },
          {
            day: 'Tuesday',
            value: false,
            shortForm: 'tue'
          },
          {
            day: 'Wednesday',
            value: false,
            shortForm: 'wed'
          },
          {
            day: 'Thursday',
            value: false,
            shortForm: 'thu'
          },
          {
            day: 'Friday',
            value: false,
            shortForm: 'fri'
          },
          {
            day: 'Saturday',
            value: false,
            shortForm: 'sat'
          },
          {
            day: 'Sunday',
            value: false,
            shortForm: 'sun'
          }
        ]
      }
    }
  };
  componentDidMount() {
    var cron = this.props.settings.cron;

    //var startTime_temp = this.props.settings.start_time;
    var startTime = moment(
      this.props.settings.start_time,
      'MMMM Do YYYY, h:mm:ss a'
    );
    //console.log("startTime_temp: " + startTime_temp);
    //var stillUtc = new Date(startTime_temp * 1000)
    //console.log("stillUtc: " + stillUtc);
    //var startTime= moment.utc(stillUtc).local().format();
    //console.log("startTime: " + startTime);
    var endTime = this.props.settings.end_time
      ? moment(this.props.settings.end_time, 'MMMM Do YYYY, h:mm:ss a')
      : moment();
    if (!startTime.isValid()) {
      startTime = moment();
      //var startTime_temp = (new Date(startTime.toString()).getTime() / 1000)
      //startTime = moment(startTime_temp, "MMMM Do YYYY, h:mm:ss a");
      //console.log("startTime: " + startTime + ", startTime_temp: " + startTime_temp);
    }
    if (!endTime.isValid()) {
      endTime = moment();
    }
    var time;
    if (cron !== '') {
      var cronObject = parseCronString(cron);
      if (cronObject.dayOfMonth.recur) {
        if (cronObject.dayOfMonth.value % 7 !== 0) {
          time = this.state.settings.day.atTime.clone();
          /*time.set({
            hour: cronObject.hours.value,
            minute: cronObject.minutes.value
          });*/
          time.set({
            hour: new Date(this.props.settings.start_time * 1000).getHours(),
            minute: new Date(this.props.settings.start_time * 1000).getMinutes()
          });
          this.setState(
            {
              ...this.state,
              name: this.props.settings.name,
              settings: {
                ...this.state.settings,
                oneTime: {
                  ...this.state.settings.oneTime,
                  enabled: false
                },
                week: {
                  ...this.state.settings.week,
                  enabled: false
                },
                day: {
                  enabled: true,
                  recurEvery: cronObject.dayOfMonth.value,
                  atTime: time,
                  startDate: startTime,
                  endDate: endTime
                }
              }
            },
            this.callback
          );
        } else {
          time = this.state.settings.week.atTime.clone();
          time.set({
            hour: new Date(this.props.settings.start_time * 1000).getHours(),
            minute: new Date(this.props.settings.start_time * 1000).getMinutes()
          });
          /*time.set({
            hour: cronObject.hours.value,
            minute: cronObject.minutes.value
          });*/
          var days = this.state.settings.week.days.map(day => {
            day.value = cronObject.dayOfWeek.indexOf(day.shortForm) > -1;
            return day;
          });
          this.setState(
            {
              ...this.state,
              name: this.props.settings.name,
              settings: {
                ...this.state.settings,
                oneTime: {
                  ...this.state.settings.oneTime,
                  enabled: false
                },
                day: {
                  ...this.state.settings.day,
                  enabled: false
                },
                week: {
                  enabled: true,
                  recurEvery: cronObject.dayOfMonth.value / 7,
                  atTime: time,
                  days: days,
                  startDate: startTime,
                  endDate: endTime
                }
              }
            },
            this.callback
          );
        }
      } else {
        time = this.state.settings.oneTime.startTime.clone();
        //var utcToUserStartTime = new Date(this.props.settings.start_time * 1000).getHours() + ":" + new Date(this.props.settings.start_time * 1000).getMinutes();
        time.set({
          hour: new Date(this.props.settings.start_time * 1000).getHours(),
          minute: new Date(this.props.settings.start_time * 1000).getMinutes()
        });
        /*time.set({
          hour: cronObject.hours.value,
          minute: cronObject.minutes.value
        });*/
        //console.log(time, startTime);
        this.setState(
          {
            ...this.state,
            name: this.props.settings.name,
            settings: {
              ...this.state.settings,
              oneTime: {
                startTime: time,
                startDate: startTime,
                enabled: true
              },
              day: {
                ...this.state.settings.day,
                enabled: false
              },
              week: {
                ...this.state.settings.week,
                enabled: false
              }
            }
          },
          this.callback
        );
      }
    } else {
      this.callback();
    }
  }
  handleChangeForm = (name: any) => (event: any) => {
    switch (name) {
      case 'name':
        this.setState(
          {
            name: event.target.value
          },
          this.callback
        );
        break;
      case 'oneTime':
      case 'day':
      case 'week':
        this.setState(
          {
            settings: {
              oneTime: {
                ...this.state.settings.oneTime,
                enabled: name === 'oneTime'
              },
              day: {
                ...this.state.settings.day,
                enabled: name === 'day'
              },
              week: {
                ...this.state.settings.week,
                enabled: name === 'week'
              }
            }
          },
          this.callback
        );
        break;
      case 'oneTimeDate':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              oneTime: {
                ...this.state.settings.oneTime,
                startDate: event
              }
            }
          },
          this.callback
        );
        break;
      case 'oneTimeStartTime':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              oneTime: {
                ...this.state.settings.oneTime,
                startTime: event
              }
            }
          },
          this.callback
        );
        break;
      case 'dayRecurEvery':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              day: {
                ...this.state.settings.day,
                recurEvery: event.target.value
              }
            }
          },
          this.callback
        );
        break;
      case 'dayAtTime':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              day: {
                ...this.state.settings.day,
                atTime: event
              }
            }
          },
          this.callback
        );
        break;
      case 'dayStartDate':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              day: {
                ...this.state.settings.day,
                startDate: event
              }
            }
          },
          this.callback
        );
        break;
      case 'dayEndDate':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              day: {
                ...this.state.settings.day,
                endDate: event
              }
            }
          },
          this.callback
        );
        break;
      case 'weekRecurEvery':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              week: {
                ...this.state.settings.week,
                recurEvery: event.target.value
              }
            }
          },
          this.callback
        );
        break;
      case 'weekStartDate':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              week: {
                ...this.state.settings.week,
                startDate: event
              }
            }
          },
          this.callback
        );
        break;
      case 'weekEndDate':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              week: {
                ...this.state.settings.week,
                endDate: event
              }
            }
          },
          this.callback
        );
        break;
      case 'weekAtTime':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              week: {
                ...this.state.settings.week,
                atTime: event
              }
            }
          },
          this.callback
        );
        break;
      case 'weekday':
        this.setState(
          {
            settings: {
              ...this.state.settings,
              week: {
                ...this.state.settings.week,
                days: this.state.settings.week.days.map((day: any) => {
                  if (day.day === event.target.value) {
                    day.value = !day.value;
                  }
                  return day;
                })
              }
            }
          },
          this.callback
        );
        break;
    }
  };
  callback() {
    this.props.onChange(this.state.name, this.state.settings);
  }
  render() {
    /*console.log('====================================');
    console.log(this.state.settings.oneTime.startDate);
    console.log(this.state.settings.oneTime.startTime);
    console.log('====================================');*/
    return (
      <div className="tab-content">
        <TextField
          error={this.props.validated && this.state.name === ''}
          id="standard-name"
          label="Name"
          placeholder="Enter schedule flow"
          className={this.props.classes.textField}
          value={this.state.name}
          onChange={this.handleChangeForm('name')}
          margin="normal"
          variant="outlined"
          InputLabelProps={{
            shrink: true
          }}
        />
        <Grid
          container
          className="cardboxes-container settings-container"
          alignItems="stretch"
          spacing={4}
        >
          {/* <span className="settings-box-title">SETTINGS</span>
          <FormLabel>Schedule on every:</FormLabel> */}
              <Grid
                item
                xs={4}
                className="grid-view settings-box"
              >
                <div className={this.state.settings.oneTime.enabled ? "settings-box-card selected" : "settings-box-card"}>
                  <div className="settings-box-card-header">
                    <FormControlLabel
                      value="bottom"
                      className="header-title"
                      control={
                        <Radio
                          color="primary"
                          checked={this.state.settings.oneTime.enabled}
                          onChange={this.handleChangeForm('oneTime')}
                          value="a"
                          aria-label="A"
                        />
                      }
                      label="One time"
                      labelPlacement="bottom"
                    />
                    <div className="header-descr">
                      Execute flow only once
                    </div>
                  </div>
                  <hr className="settings-box-card-divider" />
                  <FormControl fullWidth>
                    <DatePicker
                      fullWidth
                      value={this.state.settings.oneTime.startDate}
                      onChange={this.handleChangeForm('oneTimeDate')}
                      disabled={!this.state.settings.oneTime.enabled}
                      variant="outlined"
                      label="Start date"
                      InputLabelProps={{
                        shrink: true
                      }}
                    ></DatePicker>
                  </FormControl>
                  <FormControl fullWidth>
                    <TimePicker
                      fullWidth
                      value={this.state.settings.oneTime.startTime}
                      onChange={this.handleChangeForm('oneTimeStartTime')}
                      disabled={!this.state.settings.oneTime.enabled}
                      variant="outlined"
                      label="Start time"
                      InputLabelProps={{
                        shrink: true
                      }}
                    />
                  </FormControl>
                </div>
              </Grid>
              <Grid
                item
                xs={4}
                className="grid-view settings-box"
              >
                <div className={this.state.settings.day.enabled ? "settings-box-card selected" : "settings-box-card"}>
                  <div className="settings-box-card-header">
                    <FormControlLabel
                      value="bottom"
                      className="header-title"
                      control={
                        <Radio
                          color="primary"
                          checked={this.state.settings.day.enabled}
                          onChange={this.handleChangeForm('day')}
                          value="b"
                          aria-label="B"
                        />
                      }
                      label="Daily"
                      labelPlacement="bottom"
                    />
                    <div className="header-descr">
                      Execute flow on a daily basis
                    </div>
                  </div>
                  <hr className="settings-box-card-divider" />
                  <FormControl fullWidth>
                    <TextField
                      id="recurEvery"
                      value={this.state.settings.day.recurEvery}
                      onChange={this.handleChangeForm('dayRecurEvery')}
                      type="number"
                      disabled={!this.state.settings.day.enabled}
                      margin="normal"
                      variant="outlined"
                      label="Recur every x days"
                      InputLabelProps={{
                        shrink: true
                      }}
                      fullWidth
                    />
                  </FormControl>
                  <FormControl fullWidth>
                    <TimePicker
                      fullWidth
                      value={this.state.settings.day.atTime}
                      onChange={this.handleChangeForm('dayAtTime')}
                      disabled={!this.state.settings.day.enabled}
                      variant="outlined"
                      label="at Time"
                      InputLabelProps={{
                        shrink: true
                      }}
                    />
                  </FormControl>
                  <FormControl fullWidth>
                    <DatePicker
                      fullWidth
                      value={this.state.settings.day.startDate}
                      onChange={this.handleChangeForm('dayStartDate')}
                      disabled={!this.state.settings.day.enabled}
                      variant="outlined"
                      label="Start Date"
                      InputLabelProps={{
                        shrink: true
                      }}
                    ></DatePicker>
                  </FormControl>
                  <FormControl fullWidth>
                    <DatePicker
                      fullWidth
                      value={this.state.settings.day.endDate}
                      onChange={this.handleChangeForm('dayEndDate')}
                      disabled={!this.state.settings.day.enabled}
                      variant="outlined"
                      label="End Date"
                      InputLabelProps={{
                        shrink: true
                      }}
                    ></DatePicker>
                  </FormControl>
                </div>
              </Grid>
              <Grid
                item
                xs={4}
                className="grid-view settings-box"
              >
                <div className={this.state.settings.week.enabled ? "settings-box-card selected" : "settings-box-card"}>
                  <div className="settings-box-card-header">
                    <FormControlLabel
                      value="bottom"
                      className="header-title"
                      control={
                        <Radio
                          color="primary"
                          checked={this.state.settings.week.enabled}
                          onChange={this.handleChangeForm('week')}
                          value="c"
                          aria-label="C"
                        />
                      }
                      label="Weekly"
                      labelPlacement="bottom"
                    />
                    <div className="header-descr">
                      Execute flow on a weekly basis
                    </div>
                  </div>
                  <hr className="settings-box-card-divider" />
                  <FormControl fullWidth>
                    <TextField
                      id="recurEvery"
                      value={this.state.settings.week.recurEvery}
                      onChange={this.handleChangeForm('weekRecurEvery')}
                      disabled={!this.state.settings.week.enabled}
                      type="number"
                      margin="normal"
                      variant="outlined"
                      label="Recur every x weeks on"
                      InputLabelProps={{
                        shrink: true
                      }}
                    />

                  </FormControl>
                  <FormControl fullWidth>
                    <TimePicker
                      fullWidth
                      disabled={!this.state.settings.week.enabled}
                      value={this.state.settings.week.atTime}
                      onChange={this.handleChangeForm('weekAtTime')}
                      variant="outlined"
                      label="at time"
                      InputLabelProps={{
                        shrink: true
                      }}
                    />
                  </FormControl>
                  <FormControl fullWidth>
                    <DatePicker
                      fullWidth
                      value={this.state.settings.week.startDate}
                      onChange={this.handleChangeForm('weekStartDate')}
                      disabled={!this.state.settings.week.enabled}
                      variant="outlined"
                      label="Start Date"
                      InputLabelProps={{
                        shrink: true
                      }}
                    ></DatePicker>
                  </FormControl>
                  <FormControl fullWidth>
                    <DatePicker
                      fullWidth
                      value={this.state.settings.week.endDate}
                      onChange={this.handleChangeForm('weekEndDate')}
                      disabled={!this.state.settings.week.enabled}
                      variant="outlined"
                      label="End Date"
                      InputLabelProps={{
                        shrink: true
                      }}
                    ></DatePicker>
                  </FormControl>
                  <FormControl fullWidth>
                    {/*console.log(this.state.settings.week)*/}
                    {this.state.settings.week.days.map((day: any) => {
                      return (
                        <FormControlLabel
                          className="checkbox-label"
                          control={
                            <Checkbox
                              disabled={!this.state.settings.week.enabled}
                              color="primary"
                              checked={day.value}
                              onChange={this.handleChangeForm('weekday')}
                              value={day.day}
                            />
                          }
                          label={day.day}
                        />
                      );
                    })}
                  </FormControl>
                </div>
              </Grid>
        </Grid>
      </div>
    );
  }
}

export default withStyles(styles)(ScheduleSettings);
