import React, { Component, Fragment } from 'react';
import {
  Grid,
  Container,
  Card,
  CardContent,
  Box
} from '@material-ui/core';
import SnackbarMsg from '../../components/SnackbarMsg';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import { confirmChangePassword } from '../../services';
import IkiPageHeader from '../../components/IkiLayout/IkiPageHeader'
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';
import { getCookie, removeCookie } from '../../util/cookie';
import {
  exitSession as exitSessionManually
} from './../../services/app-data.service';
var passwordValidator = require('password-validator');

type ProfileState = {
  passwordValue: string;
  passwordValue2: string;
  passwordValue3: string;
  showPassword: boolean,
  showPassword2: boolean,
  showPassword3: boolean,
  passErrorMsg: string;
  passErrorMsg2: string;
  passErrorMsg3: string;
  verificationCodeSubmitErrorMsg: string;
  verificationCodeSubmitErrorState: boolean;
  resetpassSubmitErrorMsg: string;
  resetpassSubmitErrorState: boolean;
  resetpassSubmitSuccessState: boolean;
  resetpassSubmitSuccessMsg: string;
  snackbarOpen: boolean;
  snackbarMessage: any;
  snackbarType: string;
};

type ProfileProps = {
  email: string;
  metabaseLoginId: string;
  activeProjectId: string;
  cleanSession: () => void;
  fireReturnToProject: (sourcePage: string) => void;
};

// Define rules and minimum requirements for password
var passwordRules = new passwordValidator();
passwordRules
.is().min(8)            // Minimum length 8
.is().max(100)          // Maximum length 100
.has().uppercase()      // Must have uppercase letters
.has().lowercase()      // Must have lowercase letters
.has().digits()         // Must have digits
.has().not().spaces();  // Should not have spaces

class ProfileComponent extends Component<ProfileProps, ProfileState> {

  state = {
    passwordValue: '',
    passwordValue2: '',
    passwordValue3: '',
    showPassword: true,
    showPassword2: true,
    showPassword3: true,
    passErrorMsg: '',
    passErrorMsg2: '',
    passErrorMsg3: '',
    verificationCodeSubmitErrorMsg: '',
    verificationCodeSubmitErrorState: false,
    resetpassSubmitErrorMsg: '',
    resetpassSubmitErrorState: false,
    resetpassSubmitSuccessState: false,
    resetpassSubmitSuccessMsg: '',
    snackbarOpen: false,
    snackbarMessage: null,
    snackbarType: ''
  }

  componentDidMount() {
    //var activeProjectId = '';
    //var ikiProject_cookie = '';
    //ikiProject_cookie = getCookie('ikiProject');
    //console.log('ikiProject_cookie: ', ikiProject_cookie);
    //this.props.fireReturnToProject("profile");
    /*if(this.props.activeProjectId !== '' && this.props.metabaseLoginId !== '') {
      activeProjectId = this.props.activeProjectId;
    } */
    /*else if (ikiProject_cookie !== '') {
      activeProjectId = ikiProject_cookie;
    }*/

    /*if(activeProjectId !== '') {
      exitSessionManually(activeProjectId).then((exitSessRes: any) => {
        this.props.cleanSession();
      }).catch((exitSessErr: any) => {
        this.props.cleanSession();
      });
    }*/
  }

  handleClickShowPassword = () => {
    if(this.state.showPassword === true){
      this.setState({ showPassword: false })
    }else{
      this.setState({ showPassword: true })
    }
  }

   handleClickShowPassword2 = () => {
    if(this.state.showPassword2 === true){
      this.setState({ showPassword2: false })
    }else{
      this.setState({ showPassword2: true })
    }
  }

  handleClickShowPassword3 = () => {
    if(this.state.showPassword3 === true){
      this.setState({ showPassword3: false })
    }else{
      this.setState({ showPassword3: true })
    }
  }

  handlePassChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ passwordValue: e.target.value })
  }

  handlePassChange2 = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ passwordValue2: e.target.value })
  }

  handlePassChange3 = (e: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ passwordValue3: e.target.value })
  }

  handleSnackbarClose = () => {
    this.setState({ snackbarOpen: false })
  }

  handleChangePassFormSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
    e.preventDefault()

    let passIsValid = false
    let passIsValid2 = false
    let passIsValid3 = false

    if(this.state.passwordValue !== ''){
      if(passwordRules.validate(this.state.passwordValue) === true){
        this.setState({ 
          passErrorMsg: ''
        })
        passIsValid = true
      }else{
        this.setState({ 
          passErrorMsg: 'Password must contain uppercase and lowercase letter, number and be 8 - 16 characters long'
        })
        passIsValid = false
      }
    }else{
      this.setState({ 
        passErrorMsg: 'Password is required'
      })
      passIsValid = false
    }

    if(this.state.passwordValue !== ''){
      if(passwordRules.validate(this.state.passwordValue2) === true){
        this.setState({ 
          passErrorMsg2: ''
        })
        passIsValid2 = true
      }else{
        this.setState({ 
          passErrorMsg2: 'Password must contain uppercase and lowercase letter, number and be 8 - 16 characters long'
        })
        passIsValid2 = false
      }
    }else{
      this.setState({ 
        passErrorMsg2: 'New password is required'
      })
      passIsValid2 = false
    }

    if(this.state.passwordValue3 !== ''){
      if(this.state.passwordValue3 === this.state.passwordValue2){
        this.setState({ 
          passErrorMsg3: ''
        })
        passIsValid3 = true
      }else{
        this.setState({ 
          passErrorMsg3: 'Passwords are not equal'
        })
        passIsValid3 = false
      }
    }else{
      this.setState({ 
        passErrorMsg3: 'Confirm new password is required'
      })
      passIsValid3 = false
    }

    if(passIsValid === true && passIsValid2 === true && passIsValid3 === true){
      // COMPLETE REGISTRATION / CONFIRM-SIGN-UP API CALL GOES HERE AND IF EVERYTHING OK, GOES TO DASHBOARD
      //console.log("SUBMIT - email: " + this.props.email + ", pass1: " + this.state.passwordValue + ", pass2: " + this.state.passwordValue3)
      confirmChangePassword(this.props.email, this.state.passwordValue, this.state.passwordValue3).then(res => {
        if(res.status === 200){
          this.setState({ 
            verificationCodeSubmitErrorMsg: '',
            verificationCodeSubmitErrorState: false,
            resetpassSubmitSuccessState: true,
            resetpassSubmitSuccessMsg: 'Congratulations. You have successfully created new password.',
            snackbarMessage: <Fragment>
              <div className="msg">Congratulations. You have successfully created new password.</div>
            </Fragment>,
            snackbarType: 'success',
            snackbarOpen: true,
            passwordValue: '',
            passwordValue2: '',
            passwordValue3:''
          });
        }else{
          this.setState({
            verificationCodeSubmitErrorMsg: '',
            verificationCodeSubmitErrorState: false,
            snackbarMessage: <Fragment>
              <div className="msg">Error: Unable to change password.</div>
            </Fragment>,
            snackbarType: 'error',
            snackbarOpen: true,
          });
        }
        /*else if(res.status === 401){
          //this.setState({ verificationCodeSubmitErrorMsg: 'Error 401: Something went wrong!' })
          this.setState({ verificationCodeSubmitErrorMsg: 'Error 401: Unable to change password.' })
          this.setState({ verificationCodeSubmitErrorState: true })
        }else if(res.status === 404){
          this.setState({ verificationCodeSubmitErrorMsg: 'Error 404: Something went wrong.' })
          this.setState({ verificationCodeSubmitErrorState: true })
        }else{
          this.setState({ verificationCodeSubmitErrorMsg: 'Error: Unable to change password.' })
          this.setState({ verificationCodeSubmitErrorState: true })
        }*/
      }).catch(err => {
        this.setState({
          verificationCodeSubmitErrorMsg: '',
          verificationCodeSubmitErrorState: false,
          snackbarMessage: <Fragment>
            <div className="msg">Error: Unable to change password.</div>
          </Fragment>,
          snackbarType: 'error',
          snackbarOpen: true,
        });
        /*if(err.response.status === 404) {
          this.setState({ verificationCodeSubmitErrorMsg: 'Error 404: Unable to change password.' })
          this.setState({ verificationCodeSubmitErrorState: true })
        } else if(err.response.status === 401) {
          //this.setState({ verificationCodeSubmitErrorMsg: 'Error 401: Something went wrong!' })
          this.setState({ verificationCodeSubmitErrorMsg: 'Error 401: Something went wrong.' })
          this.setState({ verificationCodeSubmitErrorState: true })
        }else{
          this.setState({ verificationCodeSubmitErrorMsg: 'Error: Unable to change password. (catch error)' })
          this.setState({ verificationCodeSubmitErrorState: true })
        }*/
      })
    }
  }

  render() {
    const {
      passwordValue,
      passwordValue2,
      passwordValue3,
      showPassword,
      showPassword2,
      showPassword3,
      passErrorMsg,
      passErrorMsg2,
      passErrorMsg3,
      verificationCodeSubmitErrorMsg,
      verificationCodeSubmitErrorState,
      resetpassSubmitSuccessState,
    } = this.state;
    return(
    <Fragment>
        <Container className="main-container profile" component="main" maxWidth={false}>
          <Grid container>
            <Grid item xs={12}>
              <IkiPageHeader
                  _breadcrumbLink='/dashboard'
                  _breadcrumbText='Dashboard'
                  _breadcrumbTitle='Profile'
              />
            </Grid>
          </Grid>
          <div className="cardbox-holder">
            <Grid container spacing={4}>
              <Grid item xs={12} md={4} lg={4}>
                <Card className="cardbox page-content">
                  <CardContent>
                    <div className="header">
                      <span className="title">User info</span>
                    </div>
                    <div className="content">
                      <Grid container alignItems="center">
                        <Grid item xs={12} lg={4}>
                          <AccountCircleIcon className="avatar" fontSize="large" color="primary" />
                        </Grid>
                        <Grid item xs={12} lg={8} className="has-txt-left">
                          <Box mb={1} className="user-name default-color">
                            {this.props.email.split('@')[0]}
                          </Box>
                          <Box className="user-email default-color">
                            {this.props.email}
                          </Box>
                        </Grid>
                      </Grid>
                    </div>
                  </CardContent>
                </Card>
              </Grid>
              <Grid item xs={12} md={8} lg={8}>
                <Card className="cardbox page-content">
                  <CardContent>
                    <div className="header">
                      <span className="title">Change password</span>
                    </div>
                    <div className="content">
                      <form 
                        autoComplete="off" 
                        noValidate
                        onSubmit={this.handleChangePassFormSubmit}
                      >
                        <IkiInput 
                          _label="Password"
                          _placeholder="Enter current Password"
                          _type={showPassword ? 'password' : 'text'}
                          _autoComplete="new-password"
                          _value={passwordValue}
                          _onChange={(e: any) => this.handlePassChange(e)}
                          _error={passErrorMsg}
                          _required={true}
                          _fullWidth={true}
                          _endIcon={
                            {
                              _withButton: true,
                              _icon: (
                                <Fragment>
                                  {showPassword ? (
                                    <VisibilityOff fontSize="small" />
                                  ) : (
                                    <Visibility fontSize="small" />
                                  )}
                                </Fragment>
                              ),
                              _position: "end",
                              _edge: "end",
                              _size: "small",
                              _onClick: this.handleClickShowPassword
                            }
                          }
                        />
                          <Grid container alignItems="center" alignContent="center" justify="center" spacing={2}>
                            <Grid item xs={12} md={6} lg={6}>
                              <IkiInput 
                                _label="New Password"
                                _placeholder="Enter new Password"
                                _type={showPassword2 ? 'password' : 'text'}
                                _autoComplete="new-password"
                                _value={passwordValue2}
                                _onChange={(e: any) => this.handlePassChange2(e)}
                                _error={passErrorMsg2}
                                _required={true}
                                _fullWidth={true}
                                _endIcon={
                                  {
                                    _withButton: true,
                                    _icon: (
                                      <Fragment>
                                        {showPassword2 ? (
                                          <VisibilityOff fontSize="small" />
                                        ) : (
                                          <Visibility fontSize="small" />
                                        )}
                                      </Fragment>
                                    ),
                                    _position: "end",
                                    _edge: "end",
                                    _size: "small",
                                    _onClick: this.handleClickShowPassword2
                                  }
                                }
                              />
                            </Grid>
                            <Grid item xs={12} md={6} lg={6}>
                              <IkiInput 
                                  _label="Confirm Password"
                                  _placeholder="Enter new Password again"
                                  _type={showPassword3 ? 'password' : 'text'}
                                  _autoComplete="new-password"
                                  _value={passwordValue3}
                                  _onChange={(e: any) => this.handlePassChange3(e)}
                                  _error={passErrorMsg3}
                                  _required={true}
                                  _fullWidth={true}
                                  _endIcon={
                                    {
                                      _withButton: true,
                                      _icon: (
                                        <Fragment>
                                          {showPassword3 ? (
                                            <VisibilityOff fontSize="small" />
                                          ) : (
                                            <Visibility fontSize="small" />
                                          )}
                                        </Fragment>
                                      ),
                                      _position: "end",
                                      _edge: "end",
                                      _size: "small",
                                      _onClick: this.handleClickShowPassword3
                                    }
                                  }
                                />
                            </Grid>
                          </Grid>
                          <div className="has-txt-right footer">
                          {verificationCodeSubmitErrorState === true &&
                            <Box mt={4} mb={4}>
                              <div className="submit-error">{verificationCodeSubmitErrorMsg}</div>
                            </Box>
                          }
                          <Box mt={2} className="txt-right">
                            <IkiButton
                              _variant='contained'
                              _color='primary'
                              _label="Update"
                              _type='submit'
                              _fullWidth={false}
                              _loading={false}
                              _disabled={resetpassSubmitSuccessState}
                            />
                          </Box>
                          </div>
                        </form>
                    </div>
                  </CardContent>
                </Card>
              </Grid>
            </Grid>
          </div>
        </Container>
        <SnackbarMsg
          snackbarOpen={this.state.snackbarOpen}
          snackbarType={this.state.snackbarType}
          snackbarMsg={this.state.snackbarMessage}
          onSnackbarClose={this.handleSnackbarClose}
        />
      </Fragment>
    )
  }
}

export default ProfileComponent;
