import React, { Component } from 'react';
//import classNames from 'classnames/bind';
//import styles from './Layout.module.scss';
import IkigaiNavigation from './IkiNavigation';
import IkigaiContent from './IkiContent';
import IkiContentOverlay from './IkiContentOverlay';

//let cx = classNames.bind(styles);

export default class IkigaiLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      navMinimized: false,
      menuItem: ''
    }
  }
  prepareLayout = () => {
    let _type = this.props._type ? this.props._type : '';
    let _page = this.props._page ? this.props._page : '';
    let _appData = this.props._appData ? this.props._appData : null;
    let _historyData = this.props._historyData ? this.props._historyData : null;
    //let _matchData = this.props._matchData ? this.props._matchData : null;
    const _email = this.props._email ? this.props._email : '';
    const _uploadProgress = this.props._uploadProgress ? this.props._uploadProgress : '';
    const _logoutHandler = this.props._logoutHandler ? this.props._logoutHandler : '';
    const _leftSidebar = this.props._leftSidebar ? this.props._leftSidebar : '';
    const _filterSidebar = this.props._filterSidebar ? this.props._filterSidebar : '';
    const _rightSidebar = this.props._rightSidebar ? this.props._rightSidebar : '';
    const _content = this.props._content ? this.props._content : '';
    //const _drawer = this.props._drawer ? this.props._drawer : '';
    const _handleGoBack = this.props._handleGoBack ? this.props._handleGoBack : '';
    /*if(_type === 'sub-page') {
      this.setState({ navMinimized: true });
    }*/

    var activePageType = '';
    if(_appData !== null) {
      activePageType = _appData.activePageType;
    }

    /*let isSubpage = false;
    if(_type === 'sub-page' || activePageType === 'schedule') {
      isSubpage = true;
    }*/

    /*let isProjectPage = false;
    if(_historyData.location.pathname) {
      let pathName = _historyData.location.pathname;
      var pathName_array = pathName.split("/");
      if(pathName_array[2]) {
        console.log('is project page... ', pathName_array[2]);
        if(pathName_array[2] === 'project') {
          isProjectPage = true;
        }
      }
    }*/

    //let isNavMinimized = isSubpage ? true : this.state.navMinimized;
    let isNavMinimized = this.state.navMinimized;
    
    //console.log("IkigaiLayout...", this.props);
    //console.log("_type...", _type, "_page...", _page);
    //console.log('activePageType...', activePageType);

    return (
      <>
        <IkigaiNavigation
          _type={_type}
          _page={_page}
          _email={_email}
          _logoutHandler={_logoutHandler}
          _handleGoBack={_handleGoBack}
          _leftSidebar={_leftSidebar}
          _filterSidebar={_filterSidebar}
          _uploadProgress={_uploadProgress}
          _toggleNavigation={() => this.toggleNavigation()} 
          _navMinimzed={isNavMinimized}
          _getMenuItemActive={(type) => this._getMenuItemActive(type)}
          _menuItemActive={this.state.menuItem}
          _appData={_appData}
          //_isProjectPage={isProjectPage}
        />
        <IkigaiContent
          _type={_type}
          _content={_content}
          _navMinimzed={isNavMinimized}
        />
        {/*this.state.menuItem === 'schedule' && (
          <IkiContentOverlay
            _type={_type}
            _menuItem={this.state.menuItem}
            _navMinimzed={this.state.navMinimized}
          />
        )*/}
        {_rightSidebar !== '' && (
          <IkigaiNavigation
            _type={_type}
            _page={_page}
            _email={_email}
            _logoutHandler={_logoutHandler}
            _rightSidebar={_rightSidebar}
          />
        )}
      </>
    );
  }
  toggleNavigation = () => {
    if(this.state.navMinimized) {
      this.setState({ navMinimized: false })
    } else {
      this.setState({ navMinimized: true })
    }
  }
  _getMenuItemActive(type) {
    //console.log("_getMenuItemActive...", type);
    if(this.state.menuItem !== type){
      this.setState({ menuItem: type });
      if(type === 'schedule') {
        this.setState({ navMinimized: true });
      }
    }
    //this.setState({ menuItem: type })
  }
  render() {
    //console.log("IkigaiLayout...", this.props);
    return this.prepareLayout()
  }
}
