import createEngine, {
  DiagramEngine,
  DiagramModel,
  PortModel,
  DagreEngine,
  DefaultDiagramState
} from '@projectstorm/react-diagrams';
import { CustomNodeFactory } from '../routes/flow-diagram/node/customnodefactory';
import { CustomNodeModel } from '../routes/flow-diagram/node/customnodemodel';
import {
  CustomLinkFactory,
  CustomLinkModel
} from '../routes/flow-diagram/link';
import { InputType } from '@projectstorm/react-canvas-core';
import { CustomPortModel } from '../routes/flow-diagram/node/customportmodel';
import { Point } from '@projectstorm/geometry';

var _engine: DiagramEngine;
var _model: DiagramModel;
var _dagreEngine: DagreEngine;

interface FacetType {
  [key: string]: CustomNodeModel;
}

var _facets: FacetType = {};
var currentNodeId = 0;

var autoLayoutLoopsMax = 10;
var autoLayoutLoopCounter = 0;

export const initDiagramEngine = () => {
  _engine = createEngine();
  const state = _engine.getStateMachine().getCurrentState();
  if (state instanceof DefaultDiagramState) {
    state.dragNewLink.config.allowLooseLinks = false;
  }
  _engine.getNodeFactories().registerFactory(new CustomNodeFactory());
  _engine.getLinkFactories().registerFactory(new CustomLinkFactory());
  _engine
    .getActionEventBus()
    .deregisterAction(
      _engine.getActionEventBus().getActionsForType(InputType.KEY_DOWN)[0]
    );
  _model = new DiagramModel();

  _engine.setModel(_model);
  _engine.setMaxNumberPointsPerLink(0);
  _dagreEngine = new DagreEngine({
    graph: {
      rankdir: 'LR',
      ranker: 'longest-path',
      ranksep: 200
    },
    includeLinks: true
  });
  //console.log("_dagreEngine: ");
  //console.log(_dagreEngine);
  return _engine;
};

export const addNodesAndArrows = (facets: any, arrows: any) => {
  for (var i = 0; i < facets.length; i++) {
    let facetObject: any = facets[i];
    var fId = parseInt(facetObject.facet_id);
    if (fId > currentNodeId) {
      currentNodeId = fId;
    }
    currentNodeId++;
    let node = _createNode(
      facetObject.name,
      facetObject.facet_id,
      facetObject.facet_type,
      facetObject.facet_group,
      facetObject.chain_group,
      facetObject.custom_facet_type,
      facetObject.custom_facet_group,
      facetObject.custom_chain_group
    );
    _model.addNode(node);

    _facets['facet_' + facetObject.facet_id] = node;
  }
  if (arrows) {
    addArrows(arrows);
  }
  if (facets.length > 0) {
    setTimeout(function() {
      autoLayout();
      //centerCanvas();
      //setTimeout(centerCanvas, 3000);
    }, 1000);
    // setTimeout(function() {
    //   //_engine.zoomToFit();
    //   //_engine.repaintCanvas();
    // }, 10000);
  }
};

export const addArrows = (arrows: any[]) => {
  for (var i = 0; i < arrows.length; i++) {
    const arrow: any = arrows[i];
    addArrow(arrow.source, arrow.destination);
  }
};

export const addArrow = (source: any, destination: any) => {
  const link: CustomLinkModel = new CustomLinkModel();
  const node1 = _facets['facet_' + source];
  const node2 = _facets['facet_' + destination];
  link.setSourcePort(node1.getPort('out') as PortModel);
  link.setTargetPort(node2.getPort('in') as PortModel);
  var firstPoint: Point = link.getFirstPoint().getPosition();
  if (firstPoint.x === 0 || firstPoint.y === 0) {
    var sourcePosition: Point = link
      .getSourcePort()
      .getPosition()
      .clone();
    sourcePosition.translate(7, 7);
    link.getFirstPoint().setPosition(sourcePosition);
  }
  var lastPoint: Point = link.getLastPoint().getPosition();
  if (lastPoint.x === 0 || lastPoint.y === 0) {
    var targetPosition: Point = link
      .getTargetPort()
      .getPosition()
      .clone();
    targetPosition.translate(7, 7);
    link.getLastPoint().setPosition(targetPosition);
  }
  link.isConnectedToANode = true;
  link.added = true;
  _model.addLink(link);
};

export const centerCanvas = () => {
  let engine = getDiagramEngine();
  let model = engine.getModel();
  let canvas = engine.getCanvas();
  if (!canvas) return;
  let zoomFactor = model.getZoomLevel() / 100;

  var width = 0,
    height = 0,
    x = 99999,
    y = 99999;
  var nodes = Array.from(canvas.getElementsByClassName('node'));
  nodes.map((node: Element) => {
    var el: HTMLElement = node as HTMLElement;
    var w = el.offsetWidth;
    var h = el.offsetHeight;
    var l = el.offsetLeft;
    var t = el.offsetTop;
    width = Math.max(width, Math.abs(l) + w);
    height = Math.max(height, Math.abs(t) + h);
    x = Math.min(x, l);
    y = Math.min(y, t);
    return node
  });

  width -= x;
  height -= y;

  if (nodes.length > 0 && width === 0 && height === 0) {
    requestAnimationFrame(centerCanvas);
    return;
  }

  model.setOffset(
    (canvas.clientWidth - width * zoomFactor) * 0.5,
    (canvas.clientHeight - height * zoomFactor) * 0.5
  );
  engine.repaintCanvas();
};

export const zoomIn = () => {
  let engine = getDiagramEngine();
  let model = engine.getModel();
  model.setZoomLevel(model.getZoomLevel() * 1.1);
  centerCanvas();
};

export const zoomOut = () => {
  let engine = getDiagramEngine();
  let model = engine.getModel();
  model.setZoomLevel(model.getZoomLevel() * 0.9);
  centerCanvas();
};

export const zoomToFit = () => {
  let engine = getDiagramEngine();
  let model = engine.getModel();
  let canvas = engine.getCanvas();
  if (!canvas) return;
  var width = 0,
    height = 0,
    x = 9999,
    y = 9999;
  var nodes = Array.from(canvas.getElementsByClassName('node'));
  nodes.map((node: Element) => {
    var el: HTMLElement = node as HTMLElement;
    var w = el.offsetWidth;
    var h = el.offsetHeight;
    var l = el.offsetLeft;
    var t = el.offsetTop;
    width = Math.max(width, Math.abs(l) + w);
    height = Math.max(height, Math.abs(t) + h);
    x = Math.min(x, l);
    y = Math.min(y, t);
    return node
  });

  width -= x;
  height -= y;

  const xFactor = canvas.clientWidth / width;
  const yFactor = canvas.clientHeight / height;
  var zoomFactor = xFactor < yFactor ? xFactor : yFactor;
  zoomFactor = Math.min(zoomFactor, 1);
  model.setZoomLevel(zoomFactor * 100);
  model.setOffset(
    (canvas.clientWidth - width * zoomFactor) * 0.5,
    (canvas.clientHeight - height * zoomFactor) * 0.5
  );

  engine.repaintCanvas();
};

export const getNode = (id: any) => {
  return _facets[`facet_${id}`];
};

export const addNode = (
  nodeData: any,
  position: any,
  existingNode?: CustomNodeModel
) => {
  var facetId: string;
  let node: CustomNodeModel;
  if (existingNode) {
    facetId = existingNode.facet_id;
    node = existingNode;
    _model.addNode(node);
    if (existingNode.linksRef.length > 0) {
      existingNode.linksRef.map((link: any) => {
        if ((link as CustomLinkModel).isConnectedToANode) {
          const sourcePort: CustomPortModel = link.getSourcePort();
          const targetPort: CustomPortModel = link.getTargetPort();
          link.clearPort(sourcePort);
          link.clearPort(targetPort);
          link.setSourcePort(sourcePort);
          link.setTargetPort(targetPort);
          _model.addLink(link as CustomLinkModel);
        }
        return link
      });
      existingNode.linksRef = [];
    }
  } else {
    facetId = currentNodeId.toString();
    node = _createNode(
      nodeData.facet_info.name,
      facetId,
      nodeData.facet_info.facet_type,
      nodeData.facet_info.facet_group,
      nodeData.facet_info.chain_group,
      nodeData.facet_info.custom_facet_type,
      nodeData.facet_info.custom_facet_group,
      nodeData.facet_info.custom_chain_group
    );
    _model.addNode(node);
    if (position) {
      node.setPosition(position);
    }
    currentNodeId++;
  }

  _facets['facet_' + facetId] = node;
  return {
    facet_group: node.facet_group,
    facet_type: node.facet_type,
    chain_group: node.chain_group,
    custom_facet_group: node.custom_facet_group,
    custom_facet_type: node.custom_facet_type,
    custom_chain_group: node.custom_chain_group,
    arguments: {},
    facet_id: facetId
  };
};

export const getDiagramEngine = (): DiagramEngine => {
  return _engine;
};

export const getDiagramModel = (): DiagramModel => {
  return _model;
};

export const deleteNode = (node: CustomNodeModel) => {
  var links: any[] = [];
  //_model.removeNode(node);
  const ports: any = node.getPorts();
  const inPort: CustomPortModel = ports.in;
  const outPort: CustomPortModel = ports.out;
  if (inPort) {
    const inportLinks: any = inPort.getLinks();
    Object.keys(inportLinks).map((key: string) => links.push(inportLinks[key]));
  }
  if (outPort) {
    const outPortLinks: any = outPort.getLinks();
    Object.keys(outPortLinks).map((key: string) =>
      links.push(outPortLinks[key])
    );
  }
  node.linksRef = links;
  // _model.removeNode(node);
  node.remove();
  console.log(node);
  _engine.repaintCanvas();
  //autoLayout();
};

export const deleteLink = (link: CustomLinkModel) => {
  _model.removeLink(link);
  console.log('should delete link', link);
  //link.remove();
  _engine.repaintCanvas();
};

