const _createNode = (
  _name: string,
  _id: string,
  facet_type: string,
  facet_group: string,
  chain_group: string,
  custom_facet_type: string,
  custom_facet_group: string,
  custom_chain_group: string
) => {
  const node = new CustomNodeModel({
    color: '#FFFFFF',
    name: _name,
    facet_id: _id,
    facet_group: facet_group,
    facet_type: facet_type,
    chain_group: chain_group,
    custom_facet_group: custom_facet_group,
    custom_facet_type: custom_facet_type,
    custom_chain_group: custom_chain_group,
  });
  return node;
};