//Parses a cron string. The cron string is used to schedule the flow run.
export const parseCronString = cronString => {
  var elements = cronString.split(' ');
  var minutes = getCronElement(elements[0]);
  var hours = getCronElement(elements[1]);
  var dayOfMonth = getCronElement(elements[2]);
  var month = getCronElement(elements[3]);
  var dayOfWeek = elements[4];
  if (dayOfWeek.indexOf(',') > -1) {
    dayOfWeek = dayOfWeek.split(',');
  }
  return {
    minutes,
    hours,
    dayOfMonth,
    month,
    dayOfWeek
  };
};

const getCronElement = elementString => {
  return {
    recur: elementString.indexOf('*/') > -1,
    value:
      elementString !== '*'
        // eslint-disable-next-line
        ? parseInt(elementString.replace(/[\/!@#$%^&*]/g, ''))
        : elementString
  };
};

/**
 * converts the time objects to cron string.
 * @param minutes: Object {value:string, recur:boolean}
 * @param hours: Object {value:string, recur:boolean}
 * @param dayOfMonth Object {value:string, recur:boolean}
 * @param month Object {value:string, recur:boolean}
 * @param dayOfWeek Object {value:string, recur:boolean}
 *
 * @returns string
 */

export const convertToCronString = (
  minutes,
  hours,
  dayOfMonth,
  month,
  dayOfWeek
) => {
  var cronArray = [];
  cronArray[0] = minutes
    ? minutes.recur
      ? `*/${minutes.value}`
      : minutes.value
    : '*';
  cronArray[1] = hours ? (hours.recur ? `*/${hours.value}` : hours.value) : '*';
  cronArray[2] = dayOfMonth
    ? dayOfMonth.recur
      ? `*/${dayOfMonth.value}`
      : dayOfWeek.value
    : '*';
  cronArray[3] = month ? (month.recur ? `*/${month.value}` : month.value) : '*';
  cronArray[4] =
    dayOfWeek && dayOfWeek.length > 0
      ? Array.isArray(dayOfWeek)
        ? dayOfWeek.join(',')
        : dayOfWeek
      : '*';
  return cronArray.join(' ');
};
