import Cookies from 'universal-cookie';

const cookies = new Cookies();

/* const cookieOptions = {
  path: '/',
  secure: true,
  httpOnly: true,
  sameSite: 'strict'
}; */

export const setCookie = (key, value) => {
  cookies.set(key, value);
};

export const getCookie = key => {
  return cookies.get(key);
};

export const removeCookie = key => {
  cookies.remove(key);
};
