import React, { Fragment, useState } from 'react';
import { Tooltip, Tabs, Tab } from '@material-ui/core';
import ReactMarkdown from 'react-markdown/with-html';

export default function HelpPopover(props: any) {
  const { helpText } = props;
  const [tabIndex, setTabIndex] = useState(0);
  const [helperPopoverState, setHelperPopoverState] = useState(false);

  const handleTabChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setTabIndex(newValue);
  };

  const handleOnClose = (event: any) => {
    //setHelperPopoverState(false);
    //console.log(event.target.className);
    let tabClicked: string = 'false';
    if(event.target.className !== ""){
      var targetElClassVal: string = event.target.className + "";
      if(targetElClassVal === 'MuiTab-wrapper' || targetElClassVal === 'MuiTab-root'){
        tabClicked = 'true';
      }else{
        var targetElClass = targetElClassVal.split(" ");
        targetElClass.map((classItem: any) => {
          //console.log("el class: " + classItem);
          if(classItem === 'MuiTab-wrapper' || classItem === 'MuiTab-root'){
            tabClicked = 'true';
            //console.log("don't close popup");
          }
          return ''
        });
      }
    }
    //console.log("tabClicked: " + tabClicked);
    if(tabClicked !== 'true'){
      //console.log("close popup");
      setHelperPopoverState(false);
    }else{
      setHelperPopoverState(true);
    }
  };

  const handleOnOpen = (event: any) => {
    //alert(event.target);
    //setHelperPopoverState(false);
    setHelperPopoverState(true);
  };

  const getHelpText = (text: string) => {
    // text =
    //   '# Exported\n' +
    //   '> Exported facet outputs your processed data to your chosen ' +
    //   'output dataset.\n' +
    //   '## How To Set Arguments\n' +
    //   '### Facet Arguments\n' +
    //   '> 1. **file_type** : Exported facet supports csv format.\n' +
    //   '> 2. **file_name** : Choose a name for your output file.\n' +
    //   '> 3. **dataset_name** : Choose a name for your output ' +
    //   'dataset.\n' +
    //   '> (!) **overwrite** window pops up when you type in a ' +
    //   'dataset name that already exists in your project, choose ' +
    //   'yes to overwrite existing dataset.\n' +
    //   '\n' +
    //   'Example:\n' +
    //   'http://d18f1dd5shmawi.cloudfront.net/images/image10.png';
    //var
    //return <ReactMarkdown source={text} escapeHtml={false}></ReactMarkdown>;
    var helpTexts_clearBrackets2 = text.split("[").join("");
    var helpTexts_clearBrackets3 = helpTexts_clearBrackets2.split("[").join("");
    var helpTexts = helpTexts_clearBrackets3.split('Example:\n');

    if (text.length === 1) {
      return (
        <Fragment>
          <ReactMarkdown source={helpTexts[0]} escapeHtml={false}></ReactMarkdown>
        </Fragment>
      );
    } else{
      return (
        <div className="help-dialog-popup">
          <Tabs
            value={tabIndex}
            onChange={handleTabChange}
            indicatorColor="primary"
            className="help-tabs"
          >
            <Tab label="Info" id="tab-0"></Tab>
            <Tab label="Example" id="tab-1"></Tab>
          </Tabs>
          <div hidden={tabIndex !== 0} className="info-block">
            <ReactMarkdown
              source={helpTexts[0]}
              escapeHtml={false}
            ></ReactMarkdown>
          </div>
          <div hidden={tabIndex !== 1} className="info-example-block">
            <img src={helpTexts[1].trim()} alt="example"></img>
          </div>
        </div>
      );
    }
  };

  return (
    <Tooltip
      open={helperPopoverState}
      arrow={true}
      interactive
      title={getHelpText(helpText)}
      //title={helpText}
      leaveDelay={500}
      placement="right-end"
      className="tooltip-holder"
      onClose={handleOnClose}
      onOpen={handleOnOpen}
    >
      <span className="form-help-icon">?</span>
    </Tooltip>
  );
}