import React, { Component } from 'react';
import { Alert } from '@material-ui/lab';
import { Box } from '@material-ui/core';
import styles from './Alert.module.scss';

export default class AlertMsg extends Component {
    
    render() {
        const alertType = this.props.alertType;
        const alertContent = this.props.alertContent;
        const alertButtons = this.props.alertButtons;

        return (
            <Alert severity={alertType} className={styles.alert}>
                <div className={styles.title}>{alertType}</div>
                <Box className={styles.descr}>
                    {alertContent}
                </Box>
                <Box className={styles.buttons}>
                    {alertButtons}
                </Box>
            </Alert>
        );
    }
}