import React from 'react';
import {
  Box,
  FormControlLabel,
  Checkbox
} from '@material-ui/core';
import IkiButton from '../../IkiFormElements/IkiButton';
//import IkiDivider from '../../IkiLayout/IkiDivider';
//import IkiButtonGroup from '../../IkiFormElements/IkiButtonGroup';
import IkiInput from '../../IkiFormElements/IkiInput';
import IkiDialog from '../../IkiDialog';
//import ErrorIcon from '@material-ui/icons/Error';
import Viewer from 'react-viewer';
import * as EmailValidator from 'email-validator';

export default function SupportFeedbackDialog(props: any) {
  const {
    handleFeedbackDialogClose,
    handleFeedbackDialogCancel,
    openFeedbackDialog,
    feedbackImgData,
    onFeedbackDialogSubmit,
    feedbackSubmitState,
    handleDialogSubmitState
  } = props;

  const [shareTargetType, setShareTargetType] = React.useState('support');
  const [supportTargetType, selectedSupportTargetType] = React.useState('is-selected');
  const [userTargetType, selectedUserTargetType] = React.useState('');
  const [targetUserEmailState, setTargetUserMailState] = React.useState(true);
  const [targetUserEmail, setTargetUserMail] = React.useState('');
  const [targetUserEmailError, setTargetUserMailError] = React.useState(false);
  const [targetUserEmailErrorMsg, setTargetUserMailErrorMsg] = React.useState('');
  const [screenshootPreview, setScreenshootPreview] = React.useState(false);
  const [feedbackImageCheckbox, setFeedbackImageCheckbox] = React.useState(true);
  const [feedbackMessage, setFeedbackMessage] = React.useState('');
  const [feedbackMessageError, setFeedbackMessageError] = React.useState(false);

  const handleFeedbackDialogShareButton = (e: any) => {
    var emailIsValid = false;
    var msgIsValid = false;
    if(shareTargetType === 'user'){
      if(targetUserEmail !== ''){
        if(EmailValidator.validate(targetUserEmail) === true){
          emailIsValid = true;
          setTargetUserMailError(false);
          setTargetUserMailErrorMsg('');
        }else{
          emailIsValid = false;
          setTargetUserMailError(true);
          setTargetUserMailErrorMsg('Invalid email');
        }
      }else{
        emailIsValid = false;
        setTargetUserMailError(true);
        setTargetUserMailErrorMsg('Email is required');
      }
    }else if(shareTargetType === 'support'){
      emailIsValid = true;
      setTargetUserMailError(false);
      setTargetUserMailErrorMsg('');
    }

    if(feedbackMessage !== ''){
      msgIsValid = true;
      setFeedbackMessageError(false);
    }else{
      msgIsValid = false;
      setFeedbackMessageError(true);
    }

    //console.log("emailIsValid: " + emailIsValid + ", msgIsValid: " + msgIsValid + ", feedbackMessage: " + feedbackMessage)

    if(emailIsValid === true && msgIsValid === true){
      handleDialogSubmitState(true);
      onFeedbackDialogSubmit(targetUserEmail, feedbackMessage, feedbackImageCheckbox, shareTargetType);
    }
  };

  const handleTargetUserEmailChange = (e: any) => {
    setTargetUserMail(e.target.value);
  };

  const handleFeedbackMessage = (e: any) => {
    setFeedbackMessage(e.target.value);
  };

  const handleTargetUserTypeChange = (e: any, targetUserType: any) => {
    setShareTargetType(targetUserType);
    if(targetUserType === 'support'){
      selectedSupportTargetType('is-selected');
      selectedUserTargetType('');
      setTargetUserMailState(true);
      setTargetUserMailError(false);
      setTargetUserMailErrorMsg('');
      setTargetUserMail('');
    }else if(targetUserType === 'user'){
      selectedSupportTargetType('');
      selectedUserTargetType('is-selected');
      setTargetUserMailState(false);
    }
    /*var feedbackImgData_full = feedbackImgData.match(/base64,(.+)$/);
    var base64String = feedbackImgData_full[1];
    console.log("mailtoData: " + base64String);*/
  };

  const handleFeedbackImageCheckbox = (e: any) => {
    setFeedbackImageCheckbox(e.target.checked);
  };

  const handleEnterDialog = () => {
    setShareTargetType('support');
    setTargetUserMailState(true);
    setTargetUserMail('');
    setTargetUserMailError(false);
    setTargetUserMailErrorMsg('');
    setFeedbackMessage('');
    setFeedbackMessageError(false);
  };

  const handleExitDialog = () => {
    setShareTargetType('support');
    setTargetUserMailState(true);
    setTargetUserMail('');
    setTargetUserMailError(false);
    setTargetUserMailErrorMsg('');
    setFeedbackMessage('');
    setFeedbackMessageError(false);
    handleFeedbackDialogClose();
  };

  return (
      <IkiDialog
        _open={openFeedbackDialog}
        _onEnter={handleEnterDialog}
        _onClose={handleExitDialog}
        _onExit={handleExitDialog}
        _type='regular'
        _icon={false}
        _title='Send screenshot to ...'
        _size='xs'
        _content={
          <>
                <div className={"custom-radio-holder " + supportTargetType} onClick={(e) => handleTargetUserTypeChange(e, 'support')}>
                  <div className="fake-radio-div">
                    {shareTargetType === 'support' ? (
                      <div className="fake-radio is-selected" onClick={(e) => handleTargetUserTypeChange(e, 'support')}></div>
                    ) : (
                      <div className="fake-radio" onClick={(e) => handleTargetUserTypeChange(e, 'support')}></div>
                    )}
                  </div>
                  <div className="radio-txt-holder">
                    <div className="radio-title">Support</div>
                    {/* <div className="radio-descr">
                      <div>Send feedback to our support team.</div>
                      <div>We will respond to your email within 24 hours.</div>
                    </div> */}
                  </div>
                </div>
                <div className={"custom-radio-holder " + userTargetType} onClick={(e) => handleTargetUserTypeChange(e, 'user')}>
                  <div className="fake-radio-div">
                    {shareTargetType === 'user' ? (
                      <div className="fake-radio is-selected" onClick={(e) => handleTargetUserTypeChange(e, 'user')}></div>
                    ) : (
                      <div className="fake-radio" onClick={(e) => handleTargetUserTypeChange(e, 'user')}></div>
                    )}
                  </div>
                  <div className="radio-txt-holder">
                    <div className="radio-title">User</div>
                    <div className="radio-descr">
                      {!targetUserEmailState && (
                        <Box mt={1}>
                          <IkiInput 
                            _disabled={targetUserEmailState}
                            _label="User email"
                            _placeholder="Enter email of user ..."
                            _type='search'
                            _autoComplete="new-password"
                            _value={targetUserEmail}
                            _onChange={(e: any) => handleTargetUserEmailChange(e)}
                            _characterCounter={50}
                            _autoFocus={false}
                            _fullWidth={true}
                            _error={targetUserEmailErrorMsg}
                          />
                        </Box>
                      )}
                    </div>
                  </div>
                </div>
                <Box mt={4} mb={2}>
                  <Box mt={4}>
                    <IkiInput 
                      _label="Message"
                      _placeholder="Enter message..."
                      _type='search'
                      _autoComplete="new-password"
                      _value={feedbackMessage}
                      _onChange={(e: any) => handleFeedbackMessage(e)}
                      _characterCounter={200}
                      _autoFocus={false}
                      _fullWidth={true}
                      _error={feedbackMessageError ? 'Message is required' : ''}
                      _multiline={true}
                      _rows={4}
                    />
                  </Box>
                </Box>
                {feedbackImageCheckbox === true && 
                  <Box className="feedback-img-holder">
                    <img className="feedback-img" src={feedbackImgData} alt="feedback dialog screenshot" />
                    <IkiButton
                      _variant='contained'
                      _color='default'
                      _label="Preview Screenshot"
                      _type='button'
                      _fullWidth={false}
                      _loading={false}
                      _onClick={() => { setScreenshootPreview(true); }}
                    />
                    <Viewer
                      visible={screenshootPreview}
                      onClose={() => { setScreenshootPreview(false); } }
                      images={[{src: feedbackImgData, alt: 'feedback dialog screenshot'}]}
                      className="img-viewer"
                      noFooter={true}
                      drag={false}
                    />
                  </Box>
                }
                <Box mt={1}>
                  <FormControlLabel
                    className="feedback-img-cbx"
                    control={
                      <Checkbox
                        checked={feedbackImageCheckbox}
                        onChange={handleFeedbackImageCheckbox}
                        value=""
                        color="primary"
                      />
                    }
                    label="Send email with screenshot"
                  />
                </Box>
          </>
        }
        _actions={
          <>
              <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={handleFeedbackDialogCancel}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Send"
                  _type='button'
                  _fullWidth={false}
                  _loading={feedbackSubmitState}
                  _disabled={feedbackSubmitState}
                  _onClick={handleFeedbackDialogShareButton}
                />
          </>
        }
      />
  );
}
