import React, { Component } from 'react';
import './ccounter.css';

export default class CharacterCounter extends Component {
  render() {
    const error = this.props.error;
    const currentLength = this.props.currentLength;
    const maxLength = this.props.maxLength;
    return (
      <div className={"cCounter-holder " + error}>
          <span className="left">{currentLength}</span>
          <span className="middle"> / </span>
          <span className="right">{maxLength}</span>
      </div>
    );
  }
}
