import React, { Component } from 'react';
//import classNames from 'classnames/bind';
import styles from './Header.module.scss';
import IkiBreadcrumb from '../IkiBreadcrumb'

//let cx = classNames.bind(styles);

/**
   * Page Header
   * @param _breadcrumbLink Breadcrumb back/parent Link - string
   * @param _breadcrumbText Breadcrumb text content - string
   * @param _breadcrumbTitle Breadcrumb page title - string
   * @param _breadcrumbBackIcon Breadcrumb Back Icon - boolean
   * @param _actionButtons Buttons - component
   */
export default class IkiPageHeader extends Component {
  render() {
    const _breadcrumbLink = this.props._breadcrumbLink ? this.props._breadcrumbLink : '';
    const _breadcrumbText = this.props._breadcrumbText ? this.props._breadcrumbText : '';
    const _breadcrumbTitle = this.props._breadcrumbTitle ? this.props._breadcrumbTitle : '';
    const _breadcrumbBackIcon = this.props._breadcrumbBackIcon ? this.props._breadcrumbBackIcon : '';
    const _actionButtons = this.props._actionButtons ? this.props._actionButtons : '';
    //console.log("IkiPageHeader props: ", this.props);
    return(
      <>
        <div className={styles.header}>
            {_breadcrumbLink !== '' && (
                <IkiBreadcrumb
                    _parentLink={_breadcrumbLink}
                    _parentText={_breadcrumbText}
                    _pageTitle={_breadcrumbTitle}
                    _backIcon={_breadcrumbBackIcon}
                />
            )}
            {_actionButtons !== '' && (
                <div className={styles.buttonsHolder}>
                    {_actionButtons}
                </div>
            )}
        </div>
      </>
    );
  }
}