import { actionIds } from '../common';
import { setCookie } from '../util/cookie';
import decode from 'jwt-decode';
import { _axios } from '../common';

export type UserState = any;

let defaultState = {
  email: '',
  idToken: '',
  refreshToken: '',
  accessToken: '',
  checkForLoginCookieComplete: false,
  loginResponse: '',
  isLoggedIn: false
};

export const userReducer = (state: UserState = defaultState, action: any) => {
  switch (action.type) {
    case actionIds.AUTHENTICATE_COMPLETE:
      return authenticateCompleteHandler(state, action.payload);
    case actionIds.CHECK_FOR_LOGIN_COOKIE_COMPLETE:
      return checkForLoginCookieCompleteHandler(state, action.payload);
    case actionIds.CHECK_FOR_LOGIN_COOKIE_FAIL:
      return checkForLoginCookieFailHandler(state, action.payload);
    case actionIds.RESET_USER_STATE:
      return handleResetUserState(state, action.payload);
  }
  return state;
};

const handleResetUserState = (state: UserState, payload: any) => {
  return {
    ...defaultState,
    checkForLoginCookieComplete: true
  };
};

const checkForLoginCookieCompleteHandler = (state: UserState, tokens: any) => {
  var decodedIdToken: any = decode(tokens.idToken);
  var email = decodedIdToken.email;
  _axios.defaults.headers.common['Id-token'] = tokens.idToken;
  _axios.defaults.headers.common['Refresh-token'] = tokens.refreshToken;
  _axios.defaults.headers.common['Access-token'] = tokens.accessToken;
  _axios.defaults.headers.common['User'] = email;
  return {
    ...state,
    ...tokens,
    email,
    checkForLoginCookieComplete: true,
    isLoggedIn: true
  };
};

const checkForLoginCookieFailHandler = (state: UserState, payload: any) => {
  return {
    ...state,
    checkForLoginCookieComplete: true,
    isLoggedIn: false
  };
};

const authenticateCompleteHandler = (state: UserState, loginResponse: any) => {
  if (loginResponse.status === 200) {
    var tokens = loginResponse.data.tokens;
    var tokenObject = {
      idToken: tokens.id_token,
      refreshToken: tokens.refresh_token,
      accessToken: tokens.access_token
    };
    var decodedIdToken: any = decode(tokens.id_token);
    var email = decodedIdToken.email;
    var tokenString = JSON.stringify(tokenObject);
    _axios.defaults.headers.common['Id-token'] = tokens.id_token;
    _axios.defaults.headers.common['Refresh-token'] = tokens.refresh_token;
    _axios.defaults.headers.common['Access-token'] = tokens.access_token;
    _axios.defaults.headers.common['User'] = email;
    setCookie('tokens', tokenString);
    return {
      ...state,
      ...tokenObject,
      email,
      loginResponse: '',
      isLoggedIn: true
    };
  } else if (loginResponse.status === 401) {
    return {
      ...state,
      loginResponse: 'You have entered an invalid username or password',
      isLoggedIn: false
    };
  } else if (loginResponse.status === 404) {
    return {
      ...state,
      loginResponse: 'Error 404: Something went wrong!',
      isLoggedIn: false
    };
  } else {
    return {
      ...state,
      loginResponse: 'Error 404: Something else!',
      isLoggedIn: false
    };
  }
};
