import React, { Component } from 'react';
import { 
    ButtonGroup
} from '@material-ui/core';
import classNames from 'classnames/bind';
import styles from './ButtonGroup.module.scss';

let cx = classNames.bind(styles);

/**
   * Form Element -> Button Group Component based on MaterialUI but customized for Ikigai
   * @param _label Label - string
   * @param _variant Variant - string - e.g contained | outlined
   * @param _color Color - string - e.g primary | secondary
   * @param _type Button type - string - e.g submit | button
   * @param _error Error message - string
   * @param _disabled Is Button disabeld - boolean - true | false
   */
export default class IkiButtonGroup extends Component {
  render() {
    const { 
      _variant, 
      _color,
      _buttons
    } = this.props;
    //const _endIcon = this.props._endIcon ? this.props._endIcon : '';
    const _size = this.props._size ? this.props._size : 'small';
    const _disabled = this.props._disabled ? this.props._disabled : false;
    //console.log("IkiButtonGroup props: ", this.props);
    var buttonGroupClass = cx({
      buttonGroup: true,
      'primary': _color === 'primary',
      'secondary': _color === 'secondary'
    });
    return(
      <>
        <ButtonGroup
            variant={_variant}
            size={_size}
            color={_color}
            disabled={_disabled}
            className={buttonGroupClass}
        >
            {_buttons}
        </ButtonGroup>
      </>
    );
  }
}