import React, { useState, Fragment } from 'react';
import IkiDialog from '../IkiDialog';
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';
import IkiAutocomplete from '../../components/IkiFormElements/IkiAutocomplete';
import {
  Box,
  FormHelperText,
  Fab,
  Typography,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem
} from '@material-ui/core';
import FileInputPY from '../file-input-dash';
import ErrorIcon from '@material-ui/icons/Error';
import AlertMsg from '../AlertMsg';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';

export function AddDashboard(props: any) {
  const {
    dashboardId,
    name,
    handleDialogClose,
    handleSubmit,
    handleSubmitOverwrite,
    handleCancel,
    open,
    isEdit,
    dashUploadStatus,
    newDashboardNameExists,
    dismissAlert,
    datasetIds,
    datasets,
  } = props;
  //console.log("add dashboard props: ", props);
  const [dashboardName, setDashboardName] = useState('');
  const [dashboardNameError, setDashboardNameError] = useState('');
  const [datasetId, setDatasetId] = useState(datasetIds);
  const [file, setFile] = useState();
  const [value, setValue] = useState([]);
  const [isValidated, setIsvalidated] = useState(false);

  const handleAdddashboardSubmit = () => {
    //console.log(dashUploadStatus);
    
    if(dashboardName !== '' && file !== ''){
      if (isEdit) {
        if(datasetId.filter((a:any)=>{return a === ''||a===''}).length>0){
          setIsvalidated(true);
        }else{
          if ((file || dashboardName !== '') && datasetId.length > 0) {
            handleSubmit(dashboardName, file, datasetId,dashboardId);
            setIsvalidated(false);
          } else {
            setIsvalidated(true);
          }
        }
      } else {
    //console.log(dashUploadStatus);
        if(datasetId.filter((a:any)=>{return a === ''|| a===""}).length>0){
          setIsvalidated(true);
        }else{
          if (file && dashboardName !== '' && datasetId.length>0) {
            handleSubmit(dashboardName, file, datasetId,dashboardId);
            //console.log(dashUploadStatus);
  
            setIsvalidated(false);
          } else {
            setIsvalidated(true);
          }
        }
        
      }
    }else{
      if(dashboardName === '') {
        setDashboardNameError('Required')
        setIsvalidated(false);
      } else {
        setIsvalidated(true);
      }
      //console.log("no val: " + dashboardName);
    }
  }

  const handleAdddashboardSubmitOverwrite = () => {
    if(dashboardName !== '' && file !== ''){
      //console.log("has val: " + dashboardName);
      if (isEdit) {
        if ((file || dashboardName !== '') && datasetId.length > 0) {
          handleSubmitOverwrite(dashboardName, file,datasetId);
          setIsvalidated(false);
        } else {
          setIsvalidated(true);
        }
      } else {
        if (file && dashboardName !== '' && datasetId.length > 0) {
          handleSubmitOverwrite(dashboardName, file, datasetId);
          setIsvalidated(false);
        } else {
          setIsvalidated(true);
        }
      }
    }else{
      //console.log("no val: " + dashboardName);
      setIsvalidated(true);
    }
  }
//console.log(datasets);

  const handleOnEnter = () => {
    isEdit ? setDashboardName(name) : setDashboardName('');
    setDashboardNameError('');
    isEdit ? setDatasetId(datasetIds) : setDatasetId(['']);
    isEdit ? setValue(datasetIds) : setValue([]);
    setFile(null);
    //console.log("datasetId: ", datasetId, "value: ", value);
  }

  const handleOnClose = () => {
    setFile(null);
    setIsvalidated(false);
    setDashboardName('');
    setDashboardNameError('');
    handleDialogClose();
  };

  const handleOnAddDatasetChange = (option: any) => {
    //console.log("handleOnAddDatasetChange option: ", option);
    if(datasets) {
      var selectedDatasetID: string = "";
      var selectedDatasetIndex: number = 0;
      var datasetsArr: any = datasetId;
      if(datasets.length > 0) {
        datasets.forEach((dataset: any, index: number) => {
          if(dataset.name === option) {
            selectedDatasetID = dataset.dataset_id;
            selectedDatasetIndex = index;
          }
        });
      }
      //console.log("selectedDatasetID: ", selectedDatasetID);
      if(selectedDatasetID !== "") {
        //datasetsArr.push(selectedDatasetID);
        //var datasetIdLength = datasetId.length;
        var val: any = datasetId;
        val[selectedDatasetIndex] = selectedDatasetID;
        datasetsArr[selectedDatasetIndex] = selectedDatasetID
        var temp = datasetsArr.map(
          (a: any, i: number) => {
            return val[i] ? val[i]:false
          }).filter(
            (a: any) => {
              return a!==false
            }
          );
        setDatasetId(temp);
        setValue(temp);
      }

      /*var updatedAssociatedDatasets: any = datasetId
      updatedAssociatedDatasets[selectedDatasetIndex] = selectedDatasetID;
      console.log("updatedAssociatedDatasets: ", updatedAssociatedDatasets, "datasetId: ", datasetId);
      var val: any = "";
      val[selectedDatasetIndex] = selectedDatasetID;
      var temp = updatedAssociatedDatasets.map(
        (a: any, i: number) => {
          return val[i] ? val[i]:false
        }).filter(
          (a: any) => {
            return a!==false
          }
        );
      setDatasetId(temp);
      setValue(temp);*/

      // init-old parameters -> event: any, index: any, datasetID: string
      /*console.info("handleOnAddDatasetChange: ", event, "index: ", index, "datasetID: ", datasetID, "datasetId arr: ", datasetId);
      var updatedAssociatedDatasets:any = datasetId
      updatedAssociatedDatasets[index] = datasetID
      console.log("updatedAssociatedDatasets: ", updatedAssociatedDatasets, "event.target.value: ", event.target.value, "datasetId: ", datasetId);
      var val:any = value
      val[index] = datasetID
      var temp = updatedAssociatedDatasets.map((a:any,i:number)=>{return val[i] ? val[i]:false}).filter((a:any)=>{return a!==false})
      console.log("val: ", val + "temp: ", temp);
      setDatasetId(temp)
      setValue(temp)*/
    }
  }

  return (
    <IkiDialog
      _open={open}
      _onExit={handleOnClose}
      _onEnter={handleOnEnter}
      _onClose={handleOnClose}
      _type='clouploadne'
      _icon={false}
      _title={isEdit ? 'Edit Dashboard' : 'Upload New Dashboard'}
      _subtitle=''
      _size='xs'
      _align='left'
      _content={
        <>
          {newDashboardNameExists === true ?
            <Fragment>
              <AlertMsg
                alertType="warning"
                alertContent={
                  <Fragment>
                      <div>
                        <span>Dashboard with same name already exists.</span>
                        <br />
                        <strong>{dashboardName}</strong>
                      </div>
                      <div>Please note:<br /> All flows which contain this dashboard will be reflected by this change.</div>
                      <div>
                        <strong>Are you sure you want to continue and overwrite dashboard?</strong>
                      </div>
                  </Fragment>
                }
                alertButtons={
                  <Fragment>
                    <IkiButton
                      _variant='contained'
                      _color='default'
                      _label="No, go back"
                      _type='button'
                      _fullWidth={false}
                      _loading={false}
                      _onClick={() => dismissAlert()}
                    />
                    <IkiButton
                      _variant='contained'
                      _color='primary'
                      _label="Yes, overwrite"
                      _type='button'
                      _isWarning={true}
                      _fullWidth={false}
                      _loading={false}
                      _onClick={() => handleAdddashboardSubmitOverwrite()}
                    />
                  </Fragment>
                }
              />
            </Fragment>
          :
            <div>
              {isEdit &&
                <div className="section">
                  <div className="txt-dialog-faded1">{dashboardId}</div>
                  <div className="txt-dialog-impact1">{name}</div>
                </div>
              }
              <div className="section">
                <IkiInput 
                      _label="Dashboard name"
                      _placeholder="Enter new name for Dashboard"
                      _type='text'
                      _autoComplete="new-password"
                      _value={dashboardName}
                      _onChange={(e: any) => {
                        setDashboardName(e.target.value);
                      }}
                      _characterCounter={40}
                      _autoFocus={true}
                      _fullWidth={true}
                      _error={dashboardNameError}
                />
                <Box mt={2}>
                  <Typography variant="caption" color="textSecondary">Python Script File * </Typography>
                </Box>
                <FileInputPY
                  onFileAdded={(file: File) => setFile(file)}
                  disabled={dashUploadStatus === 'pending'}
                  fileData={file}
                ></FileInputPY>
                {isValidated && !isEdit && !file && (
                    <FormHelperText className="input-error" error>
                      <ErrorIcon />
                      <span>File is required</span>
                    </FormHelperText>
                )}
              </div>
              <div className="section">
                <Box mt={2}>
                  <div className="flow-nested-arg in-add-dash-dialog" key={'ASSOCIATED_DATASET'}>
                    <div className="flow-nested-arg-title">
                      <div className="flow-nested-arg-label">ASSOCIATED DATASETS</div>
                      {/* <Fragment>
                        <Button
                          className="is-right"
                          startIcon={<AddIcon />}
                          onClick={() => {
                            setDatasetId([...datasetId,' '])
                          }}
                          >
                            Add
                        </Button>
                      </Fragment> */}
                    </div>
                    {datasetId.map((data:any, index:number)=>(
                      <div className="flow-addon-associated_datasets" key={index}>
                        {(
                          <Fab
                            color="primary"
                            aria-label="delete"
                            size="small"
                            className="flow-nested-associated-btn"
                            disabled={datasetId.length === 1}
                            onClick={() => {
                              var temp:any = datasetId.filter((a:any,i:number)=>{return i!=index})
                              setDatasetId(temp)
                              setValue(temp)
                            }}
                          >
                            <DeleteIcon />
                          </Fab>
                        )}
                            <IkiInput 
                              key={'ikinputselect-' + index} 
                              _required={true}
                              _error={isValidated && (datasetId[index] === ' ' || datasetId[index] === '') ? 'Cannot be empty' : ''}
                              _label={'Associated Dataset # ' + (index + 1)}
                              _placeholder={"Select Associated Dataset"}
                              _type='text'
                              _autoComplete="new-password"
                              _value={value[index] ? datasetId[index] : ' '}
                              _isSelect={true}
                              _fullWidth={true}
                              _selectOptions={datasets}
                              _selectOptionsKeyForVal='dataset_id'
                              _selectOptionsKeyForTxt='name'
                              _onChange={(event: any) => {
                                var updatedAssociatedDatasets:any = datasetId
                                updatedAssociatedDatasets[index] = event.target.value
                                var val:any = value
                                val[index] = event.target.value
                                var temp = updatedAssociatedDatasets.map((a:any,i:number)=>{return val[i] ? val[i]:false}).filter((a:any)=>{return a!=false})
                                setDatasetId(temp)
                                setValue(temp)
                              }}
                            />
                        </div>
                      ))
                    }
                    <div>
                      <IkiButton
                        _variant='contained'
                        _color='default'
                        _label='Add Associated Dataset'
                        _type='button'
                        _fullWidth={true}
                        _loading={false}
                        _disabled={false}
                        _onClick={() => setDatasetId([...datasetId,''])}
                        _startIcon={<AddIcon />}
                      />
                    </div>
                </div>
                
                  {isValidated && datasetId.length < 1 &&(
                    <FormHelperText className="input-error" error>
                      <ErrorIcon />
                      <span>Minimum one Dataset's required</span>
                    </FormHelperText>
                  )}
                </Box>
              </div>
            </div>
          }
        </>
      }
      _actions={
        newDashboardNameExists !== true &&
          <Fragment>
            <IkiButton
              _variant='text'
              _color='default'
              _label="Cancel"
              _type='button'
              _fullWidth={false}
              _loading={false}
              _onClick={() => handleCancel()}
            />
            <IkiButton
              _variant='contained'
              _color='primary'
              _label={isEdit ? 'Update' : 'Upload'}
              _type='button'
              _fullWidth={false}
              _loading={dashUploadStatus === 'pending'}
              _disabled={dashUploadStatus === 'pending'}
              _onClick={() => handleAdddashboardSubmit()}
            />
          </Fragment>
      }
    />
  );
}
