import React, { Component } from 'react';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';

import { Button } from '@material-ui/core';

import SearchIcon from '@material-ui/icons/Search';

type ScheduleFlowsListProps = {
  title: string;
  list: any[];
};

type ScheduleFlowsListState = {};

class ScheduleFlowsList extends Component<
  ScheduleFlowsListProps,
  ScheduleFlowsListState
> {
  render() {
    return (
      <div className="tab-content">
        {/* <div>{this.props.title}</div> */}
        <div>
          <Table className="table" component="div">
            <TableHead component="div" className="table-head">
              <TableRow component="div">
                <TableCell component="div">Name</TableCell>
                <TableCell component="div">Date</TableCell>
                <TableCell component="div">Execution</TableCell>
                <TableCell component="div" className="project-buttons"></TableCell>
              </TableRow>
            </TableHead>
            <TableBody component="div" className="table-body">
              {this.props.list.map((listItem: any) => {
                return (
                  <TableRow component="div">
                    <TableCell component="div">{listItem.name}</TableCell>
                    <TableCell component="div">{listItem.date}</TableCell>
                    <TableCell component="div">{listItem.execution}</TableCell>
                    <TableCell component="div" className="project-buttons has-txt-right">
                      <Button
                        color="primary"
                        size="small"
                      >
                        <SearchIcon fontSize="small" />
                        <span>View</span>
                      </Button>
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </div>
      </div>
    );
  }
}

export default ScheduleFlowsList;
