import React, { Component, Fragment } from 'react';
import IkiLoader from '../../../components/IkiLoader';

type VizCentralContentProps = {
  url: string;
  loading: boolean;
};

type VizCentralContentState = {
  iFrameContentLoaded: boolean;
};

class VizCentralContent extends Component<VizCentralContentProps, VizCentralContentState> {
  state = {
    iFrameContentLoaded: false
  }
  componentDidMount() {
    //this.setState({ iFrameContentLoaded: false });
    //console.log("VizCentralContent - componentDidMount...");
  }

  getVizCentralContent() {
    return(
      <Fragment>
          <IkiLoader
            loadingTxt = "Preparing Preview..."
            fadeOut = {this.state.iFrameContentLoaded}
            placement = "viz"
          />
          <iframe
            title="iKigai"
            width="100%"
            height="100%"
            onLoad={() => {
              this.props.loading &&
                  this.setState({ iFrameContentLoaded: true })
            }}
            src={this.props.url}
            sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
        ></iframe>
      </Fragment>
    );
  }

  render() {
    return (
      <Fragment>
          {this.getVizCentralContent()}
      </Fragment>
    );
  }
}

export default VizCentralContent;