import React, { Component } from 'react';
import { 
  FormControl,
  TextField,
  FormHelperText,
  Chip,
  ListSubheader
} from '@material-ui/core';
import Autocomplete from '@material-ui/lab/Autocomplete';
import classNames from 'classnames/bind';
import ErrorIcon from '@material-ui/icons/Error';
import styles from './Autocomplete.module.scss';
import { getFriendlyLabel } from '../../../util';

let cx = classNames.bind(styles);

/**
   * Form Element -> Input Component based on MaterialUI but customized for Ikigai
   * @param _label Label - string
   * @param _placeholder Placeholder - string
   * @param _value Value - string
   * @param _type Input type - string
   * @param _error Error message - string
   * @param _required Is Input Required - boolean - true | false
   * @param _fullWidth Is Input Full Width - boolean - true | false
   * @param _autoFocus Auto Focus - boolean - true | false
   * @param _size Size - string - small | medium (default)
   * @param _margin Margin - string - dense | none | normal (default)
   */
export default class IkiAutocomplete extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inptVal: ''
    }
  }

  handleSelection = (event, index) => {
    //console.log("handleSelection: ", "index: ", index);
    var _index = this.props._index ? this.props._index : 0;
    var _keyValue = this.props._keyValue ? this.props._keyValue : '';
    var _nameValue = this.props._nameValue ? this.props._nameValue : '';
    var _selectOptions = this.props._selectOptions ? this.props._selectOptions : null;
    var selectedOptionIndex = 0;
    _selectOptions.forEach((option) => {
        if(option[_nameValue] === index) {
            selectedOptionIndex = option[_keyValue];
        }

    })
    //console.log("handleSelection---", "_keyValue: ", _keyValue, "_nameValue: ", _nameValue, "_selectOptions: ", _selectOptions + "selectedOption: ", selectedOption, "selectedID: ", selectedID);
    this.props._onChange(event, _index, selectedOptionIndex);
  }

  prepareComponent = () => {
    const { 
      _label, 
      _placeholder, 
      _value,
      _index
    } = this.props;
    const _error = this.props._error ? this.props._error : false;
    const _required = this.props._required ? this.props._required : false;
    const _fullWidth = this.props._fullWidth ? this.props._fullWidth : false;
    const _autoFocus = this.props._autoFocus ? this.props._autoFocus : false;
    const _size = this.props._size ? this.props._size : 'medium';
    const _margin = this.props._margin ? this.props._margin : 'normal';
    const _isMultiple = this.props._isMultiple ? this.props._isMultiple : false;
    const _selectOptions = this.props._selectOptions ? this.props._selectOptions : [];
    const _noSelectOptionsTxt = this.props._noSelectOptionsTxt ? this.props._noSelectOptionsTxt : '';
    const _keyValue = this.props._keyValue ? this.props._keyValue : '';
    const _nameValue = this.props._nameValue ? this.props._nameValue : '';
    const _groupByValue = this.props._groupByValue ? this.props._groupByValue : '';

    let errorMsg = "";
    if(_error !== ''){
      errorMsg = _error;
    }else{
      if(_required && _value === "") {
        errorMsg = 'Required';
      }
    }

    var _selectOptionsArr = [];
    var _selectedVal = "";
    if(_selectOptions) {
        _selectOptions.forEach((option, index) => {
            //console.log("option: ", option);
            if(_keyValue === "" && _nameValue === "") {
              _selectOptionsArr.push(option);
            } else if(_groupByValue !== "") {
              //_selectOptionsArr[option[_groupByValue]] = option[_nameValue];
              //_selectOptionsArr[index] = _selectOptions[index];
              _selectOptionsArr[index] = {
                [_groupByValue]: option[_groupByValue],
                [_keyValue]: option[_keyValue],
                [_nameValue]: option[_nameValue]
              };
            } else {
              _selectOptionsArr.push(option[_nameValue]);
              if(option[_keyValue] === _value) {
                  _selectedVal = option[_nameValue];
              }
            }
        })
    }
    //_selectOptionsArr.sort();

    if(_groupByValue !== '') {
      _selectOptions.sort((a, b) => -b[_groupByValue].localeCompare(a[_groupByValue]));
      _selectOptionsArr.sort((a, b) => -b[_groupByValue].localeCompare(a[_groupByValue]));
    } else {
      _selectOptions.sort();
      _selectOptionsArr.sort();
    }

    //console.log("_selectOptions: ", _selectOptions);
    //console.log("_selectOptionsArr: ", _selectOptionsArr);

    var autocompleHolderClass = cx({
      autocompleteHolder: true
    });
    var autocompleteClass = cx({
      autocomplete: true
    });

    if(_isMultiple) {
      return(
        <>
          <FormControl 
            fullWidth={_fullWidth}
            error={_error !== ''}
            className={autocompleHolderClass}
            //margin={_margin}
          >
            <Autocomplete
              multiple
              className={autocompleteClass}
              options={_selectOptions}
              getOptionLabel={(option) => getFriendlyLabel(option)}
              value={_value !== "" ? _value : []}
              inputValue={this.state.inptVal}
              onChange={(event, option) => {
                this.props._onChange(_label, option);
                this.setState({ inptVal: '' });
              }}
              renderInput={
                (params) => 
                  <TextField {
                      ...params
                    } 
                    variant="outlined" 
                    label={_label}
                    placeholder={_placeholder}
                    InputLabelProps={{
                      shrink: true
                    }}
                    inputProps={{
                        ...params.inputProps,
                        autoComplete: 'new-password', // disable autocomplete and autofill
                    }}
                    onChange={(event) => this.setState({ inptVal: event.target.value })}
                  />
              }
              //renderTags={(_value, getTagProps) => this.props._renderTags(_value, getTagProps)}
            />
          </FormControl>
        </>
      );
    } else if(_groupByValue !== '') {
      return(
        <>
          <FormControl 
            fullWidth={_fullWidth}
            error={_error !== ''}
            className={autocompleHolderClass}
            //margin={_margin}
          >
            <Autocomplete
              className={autocompleteClass}
              options={_selectOptionsArr}
              groupBy={
                _groupByValue !== '' ? (
                  (option) => option[_groupByValue]
                ):(
                  () => {}
                )}
              getOptionLabel={(option) => option[_nameValue]}
              renderInput={
                (params) => 
                  <TextField {
                      ...params
                    } 
                    variant="outlined" 
                    label={_label}
                    placeholder={_placeholder}
                    InputLabelProps={{
                      shrink: true
                    }}
                    inputProps={{
                        ...params.inputProps,
                        autoComplete: 'new-password', // disable autocomplete and autofill
                    }}
                  />
              }
              defaultValue={_value}
              onChange={(event, option) => this.props._onChange(event, option)}
            />
          </FormControl>
        </>
      );
    } else {
      return(
        <>
          <FormControl 
            fullWidth={_fullWidth}
            error={_error && errorMsg !== ""}
            className={autocompleHolderClass}
            //margin={_margin}
          >
            <Autocomplete
              className={autocompleteClass}
              options={_selectOptionsArr}
              getOptionLabel={(option) => option}
              defaultValue={_selectedVal !== "" ? _selectedVal : ''}
              onChange={(event, option) => this.props._onChange(_label, option)}
              renderInput={
                (params) => 
                  <TextField {
                      ...params
                    } 
                    error={_error && errorMsg !== ""}
                    variant="outlined" 
                    label={_label}
                    placeholder={_placeholder}
                    InputLabelProps={{
                      shrink: true
                    }}
                    inputProps={{
                        ...params.inputProps,
                        autoComplete: 'new-password', // disable autocomplete and autofill
                    }}
                  />
              }
              //renderTags={(_value, getTagProps) => this.props._renderTags(_value, getTagProps)}
            />
            {_error && errorMsg !== "" && (
              <FormHelperText 
                className="input-error" 
                error
              >
                <ErrorIcon />
                <span>{errorMsg}</span>
              </FormHelperText>
            )}
          </FormControl>
        </>
      );
    }
  }

  render() {
    //console.log("IkiAutocomplete props: ", this.props);
    return this.prepareComponent()
  }
}