import React, { Component } from 'react';
//import { Link } from 'react-router-dom';
import {
  LinearProgress
} from '@material-ui/core';
import IkiPopupMenu from '../IkiPopupMenu';
import classNames from 'classnames/bind';
import styles from './ItemBox.module.scss';

let cx = classNames.bind(styles);

/**
   * Item Box
   * @param _type Type - string
   */
export default class IkiItemBox extends Component {

  state = {
    clickedOnBoxMenu: false
  }

  handleOnBoxMenuClick = () => {
    //console.log("clicked box menu");
    this.setState({ clickedOnBoxMenu: true })
  }

  handleCloseBoxMenuClick = () => {
    this.setState({ clickedOnBoxMenu: false })
  }

  handleBoxClick = (_type, _id, _functions, _data) => {
    //console.log("_functions: ", _functions);
    if(_type === 'Project' || _type === 'Flow') {
      _functions[0].edit(_id);
    } else if(_type === 'Dataset') {
      _functions[3].browse(_id);
    } else if(_type === 'Dashboard') {
      _functions[2].browse(_id);
    }
  }

  render() {
    const { 
      _type,
      _icon,
      //_link,
      _functions
    } = this.props;
    
    let _title= '', _id = '', _owner = '', _data;
    if(this.props._data) {
      _data = this.props._data;

      if(_type === 'Project') {
        _title = _data.name ? _data.name : '';
        _id = _data.project_id ? _data.project_id : '';
        _owner = _data.owner ? _data.owner : '';
      } else if(_type === 'Flow') {
        _title = _data.name ? _data.name : '';
        _id = _data.pipeline_id ? _data.pipeline_id : '';
      } else if(_type === 'Dataset') {
        _title = _data.name ? _data.name : '';
        _id = _data.dataset_id ? _data.dataset_id : '';
      }else if(_type === 'Dashboard') {
        _title = _data.name ? _data.name : '';
        _id = _data.dashboard_id ? _data.dashboard_id : '';
      }
    }

    const _uploadInfo = this.props._uploadInfo ? this.props._uploadInfo : [];
    var uploadStatus = false, percentCompleted = 0, percentCompletedFormated = '0.00', objectUploading = [];
    if(_uploadInfo && _uploadInfo.length > 0){
      //console.log("_uploadInfo: ", _uploadInfo, ", _id: ", _id);
      objectUploading = _uploadInfo.filter((uploadingObject) => {
        return uploadingObject.status !== 'complete' && uploadingObject.datasetId === _id
      })
      if(objectUploading.length > 0){
        var objectUploadingIndex = objectUploading.length - 1;
        //console.log("objectUploadingIndex: ", objectUploadingIndex, ", objectUploading: ", objectUploading);
        uploadStatus = true;
        percentCompleted = _uploadInfo[objectUploadingIndex].percentCompleted;
        percentCompletedFormated = percentCompleted.toFixed(2);
      }
      //console.log("objectUploading: ", objectUploading, ", percentCompleted: ", percentCompleted)
    }

    var boxClass = cx({
      itemBox: true,
      'clicked': this.state.clickedOnBoxMenu,
      'uploading': uploadStatus
    });
    //console.log("IkiItemBox: ", this.props, ", _uploadInfo: ", _uploadInfo);
    return(
      <>
        <div className={boxClass}>
          <div className={styles.header}>
            <span className={styles.icon}>{_icon}</span>
            <span className={styles.type}>{_type}</span>
            {_functions !== '' && (
              <span className={styles.menu}>
                <IkiPopupMenu
                  _type={_type}
                  _data={
                    { 
                      _id: _id,
                      _name: _title,
                      ..._data
                    }
                  }
                  _functions={_functions}
                  _open={() => this.handleOnBoxMenuClick()}
                  _close={() => this.handleCloseBoxMenuClick()}
                />
              </span>
            )}
          </div>
          <div className={styles.content}>
            <div 
              className={
                uploadStatus ? '' : styles.link
              }
              onClick={() => 
                !uploadStatus && (this.handleBoxClick(_type, _id, _functions, _data))
              }
            >
              <div className={styles.title}>
                {_title}
              </div>
              <div className={styles.faded1}>
                {_id}
              </div>
              {_owner !== '' && (
                <div className={styles.newSection}>
                  <div className={styles.faded1}>Owner</div>
                  <div>{_owner}</div>
                </div>
              )}
            </div>
          </div>
          {uploadStatus && (
            <div className={styles.uploadingDatabox}>
              <div>
                <LinearProgress className={styles.linearProgress} variant="determinate" color="primary" value={percentCompleted} />
                <span className="linear-progress-val"> {percentCompletedFormated} %</span>
              </div>
            </div>
          )}
        </div>
      </>
    );
  }

}