import { State } from './../../reducers';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import {
  handleCleanSession,
  handleProjectReturn,
  handleFillSession,
  updateFlowDataActionStart
} from '../../actions';
import SchedulesComponent from './schedules-component';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  cleanSession: () => {
    dispatch(handleCleanSession())
  },
  fireReturnToProject: (sourcePage: string) => {
    dispatch(handleProjectReturn(sourcePage))
  },
  fillSessions: (proSess: string, projectId: string) => {
    dispatch(handleFillSession(proSess, projectId))
  },
  updateFlowData: (flowData: any) => {
    dispatch(updateFlowDataActionStart(flowData));
  },
});
const mapStateToProps = (state: State) => ({
  data: state.flowReducerState,
  email: state.userReducerState.email,
  metabaseLoginId: state.flowReducerState.metabaseLoginId,
  activePageType: state.flowReducerState.activePageType,
  uploadProgress: state.flowReducerState.uploadProgress,
});

export const SchedulesContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(SchedulesComponent);
