import React, { Component } from 'react';
import { Route } from 'react-router';
import { Switch } from 'react-router-dom';
import { ProfileContainer } from '../../../routes/profile/profile-container';
import { ProjectsContainer } from '../../../routes/projects/projects-container';
import { ProjectContainer } from '../../../routes/project/project-container';
import { SchedulesContainer } from '../../../routes/schedules/schedules-container';
import classNames from 'classnames/bind';
import styles from './Content.module.scss';
let cx = classNames.bind(styles);

export default class IkigaiContent extends Component {
  prepareContent = () => {
    const _type = this.props._type ? this.props._type : '';
    const _content = this.props._type ? this.props._content : '';
    let _navMinimzed = this.props._navMinimzed ? this.props._navMinimzed : false;
    var contentAreaClass = cx({
      contentArea: true,
      'minimized': _navMinimzed
    });
    //console.log('IkigaiContent...', this.props);
    if(_type === 'sub-page') {
      return (
        <>
          <div className={contentAreaClass}>
            {_content}
          </div>
        </>
      );
    } else {
      return (
        <>
          <div className={contentAreaClass}>
            <Switch>
              <Route
                path="/dashboard/project/:id"
                component={ProjectContainer}
              ></Route>
              <Route
                path="/dashboard/projects"
                component={ProjectsContainer}
              ></Route>
              <Route
                path="/dashboard/flow-schedules/:id"
                component={SchedulesContainer}
              ></Route>
              <Route
                path="/dashboard/project-schedules/:id"
                component={SchedulesContainer}
              ></Route>
              <Route
                path="/dashboard/schedules"
                component={SchedulesContainer}
              ></Route>
              <Route
                path="/dashboard/profile"
                component={ProfileContainer}
              ></Route>
              <Route 
                  path="/dashboard" 
                  component={ProjectsContainer}
              ></Route>
            </Switch>
          </div>
        </>
      );
    }
  }
  render() {
    return this.prepareContent();
  }
}
