import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  withStyles,
  Dialog,
  DialogContent,
  DialogActions,
  Button,
  Tabs,
  Tab,
  Box,
  Grid
} from '@material-ui/core';
import './schedule-dialog.scss';
import ScheduleSettings from './schedule-settings';
import ScheduleFlowsList from './schedule-flows-list';
import { convertToCronString } from '../../../util/cron';

function TabContainer(props: any) {
  return <div className="tab-container">{props.children}</div>;
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired
};

type ScheduleDialogProps = {
  pastScheduledActions: any;
  upcomingScheduledActions: any;
  settings: any;
  classes: any;
  open: boolean;
  onSubmit: (scheduleObject: any) => void;
  onCancel: () => void;
};

type ScheduleDialogState = {
  selectedTab: number;
  open: boolean;
  settings: any;
  validated: boolean;
  applicableSettings: any;
};

const styles = (theme: any) => ({
  sheduleDialog: {},
  scheduleDialogContent: {
    padding: 0
  }
});

class ScheduleDialog extends Component<
  ScheduleDialogProps,
  ScheduleDialogState
  > {
  state = {
    validated: false,
    selectedTab: 0,
    open: false,
    settings: {},
    applicableSettings: {}
  };
  componentDidMount() {
    this.setState({
      open: this.props.open,
      settings: this.props.settings
    });
  }
  render() {
    return (
      <div>
        <div>Schedule</div>
        <Dialog
          open={true}
          onClose={this.handleRequestClose}
          className="dialog schedule"
          fullWidth={true}
          maxWidth="md"
        >
          <DialogContent className="dialog-content">
            <Box className="dialog-title default-color" mb={2}>
              Schedule Flow execution
            </Box>
            <Grid container className="tab-container">
              <Grid item xs={12}>
                <Tabs
                  value={this.state.selectedTab}
                  onChange={(event, value) => {
                    this.handleChange(event, value);
                  }}
                  classes={{
                    indicator: 'flow-tab-indicator'
                  }}
                >
                  <Tab label="Set Schedule" />
                  <Tab label="Past Schedules" disabled />
                  <Tab label="Upcoming Schedules" disabled />
                </Tabs>
              </Grid>
            </Grid>
            {this.state.selectedTab === 0 && (
              <TabContainer>{this.getScheduleForm()}</TabContainer>
            )}
            {this.state.selectedTab === 1 && (
              <TabContainer>{this.getPastFlows()}</TabContainer>
            )}
            {this.state.selectedTab === 2 && (
              <TabContainer>{this.getUpcomingFlows()}</TabContainer>
            )}
          </DialogContent>
          <DialogActions className="footer">
            <Button
              onClick={() => {
                this.cancelButtonHandler();
              }}
              color="default"
              className="button"
            >
              Cancel
            </Button>
            <Button
              onClick={() => {
                this.applyButtonHandler();
              }}
              variant="contained"
              color="primary"
              className="button"
            >
              Apply
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
  getScheduleForm() {
    return (
      <ScheduleSettings
        onChange={(name: string, settings: any) => {
          this.updateSettings(name, settings);
        }}
        validated={this.state.validated}
        settings={this.state.settings}
      />
    );
  }
  updateSettings(name: string, settings: any) {
    var applicableSettings = Object.keys(settings).filter(
      (key: any) => settings[key].enabled
    )[0];

    this.setState({
      ...this.state,
      applicableSettings: { ...settings[applicableSettings], name: name }
    });
  }
  getPastFlows() {
    return (
      <ScheduleFlowsList
        title="Past Flows"
        list={this.props.pastScheduledActions}
      ></ScheduleFlowsList>
    );
  }
  getUpcomingFlows() {
    return (
      <ScheduleFlowsList
        title="Upcoming Flows"
        list={this.props.upcomingScheduledActions}
      ></ScheduleFlowsList>
    );
  }
  handleChange(event: any, selectedTab: any) {
    this.setState({ selectedTab });
  }
  cancelButtonHandler() {
    this.setState({ open: false });
    this.props.onCancel();
  }
  applyButtonHandler() {
    this.setState({ open: false });
    var s = this.state.applicableSettings as any;
    const name = s.name;
    var cronString;
    this.setState({
      validated: true
    });
    if (name === '') {
      return;
    }
    // eslint-disable-next-line
    var time, hours, minutes;
    if (s.atTime) {
      time = s.atTime;
      // eslint-disable-next-line
      hours = time.hours();
      // eslint-disable-next-line
      minutes = time.minutes();
      var userTime = s.atTime.format('HH:mm');
      var userStartDate = s.startDate.format('YYYY-MM-DD');
      var userFullStartDatetime = userStartDate + " " + userTime + ":00";
      var userFullStartDatetimeUTC = (new Date(userFullStartDatetime).getTime() / 1000).toFixed(0);
      var userEndDate = s.endDate.format('YYYY-MM-DD');
      var userFullEndDatetime = userEndDate + " " + userTime + ":00";
      var userFullEndDatetimeUTC = (new Date(userFullEndDatetime).getTime() / 1000).toFixed(0);
      var utcHours = new Date(time).getUTCHours();
      var utcMinutes = new Date(time).getUTCMinutes();
      if (s.days) {
        var daysOfWeek = s.days
          .filter((day: any) => day.value)
          .map((day: any) => day.shortForm);
        var weeks = s.recurEvery;
        cronString = convertToCronString(
          { value: utcMinutes },
          { value: utcHours },
          { value: weeks * 7, recur: true },
          null,
          daysOfWeek
        );
      } else {
        var days = s.recurEvery;
        cronString = convertToCronString(
          { value: utcMinutes },
          { value: utcHours },
          { value: days, recur: true },
          null,
          null
        );
      }
      this.props.onSubmit({
        cron: cronString,
        start_time: userFullStartDatetimeUTC,
        end_time: userFullEndDatetimeUTC,
        name: name
      });
    } else if (s.startTime) {
      time = s.startTime;
      hours = time.hours();
      minutes = time.minutes();
      var userDate = s.startDate.format('YYYY-MM-DD');
      // eslint-disable-next-line
      var userTime = s.startTime.format('HH:mm');
      var userFullDatetime = userDate + " " + userTime + ":00";
      // eslint-disable-next-line
      var utcHours = new Date(time).getUTCHours();
      // eslint-disable-next-line
      var utcMinutes = new Date(time).getUTCMinutes();
      var userFullDatetimeUTC = (new Date(userFullDatetime).getTime() / 1000).toFixed(0);
      cronString = convertToCronString(
        { value: utcMinutes },
        { value: utcHours },
        null,
        null,
        null
      );
      this.props.onSubmit({
        cron: cronString,
        start_time: userFullDatetimeUTC,
        end_time: '',
        name: name
      });
    }
  }
  handleRequestClose() { }
}

export default withStyles(styles)(ScheduleDialog);
