import React, { Component } from 'react';
import { NavLink, withRouter } from 'react-router-dom';
import CustomScrollbars from '../../../util/CustomScrollbars';
import classNames from 'classnames/bind';
import { ChatNavModule } from '../IkiNavActionButtons';
import styles from './SideMenu.module.scss';

let cx = classNames.bind(styles);

class DrawerMenu extends Component {
  componentDidMount() {
    //console.log('DrawerMenu...', this.props);
    const { history } = this.props;
    const that = this;
    const pathname = `${history.location.pathname + window.location.search}`; // get current path
    const menuLi = document.getElementsByClassName('menu');
    for (let i = 0; i < menuLi.length; i++) {
      menuLi[i].onclick = function(event) {
        const parentLiEle = that.closest(this, 'li');
        if (menuLi[i].classList.contains('menu') && parentLiEle !== null) {
          event.stopPropagation();
          if (menuLi[i].classList.contains('open')) {
            menuLi[i].classList.remove('open', 'active');
          } else {
            menuLi[i].classList.add('open', 'active');
          }
        } else {
          for (let j = 0; j < menuLi.length; j++) {
            const parentLi = that.closest(this, 'li');
            if (
              menuLi[j] !== this &&
              (parentLi === null || !parentLi.classList.contains('open'))
            ) {
              menuLi[j].classList.remove('open');
            } else {
              if (menuLi[j].classList.contains('open')) {
                menuLi[j].classList.remove('open');
              } else {
                menuLi[j].classList.add('open');
              }
            }
          }
        }
      };
    }

    const activeLi = document.querySelector('a[href="' + pathname + '"]'); // select current a element
    try {
      activeLi.classList.add('active');
      const activeNav = this.closest(activeLi, 'ul'); // select closest ul
      if (activeNav.classList.contains('sub-menu')) {
        this.closest(activeNav, 'li').classList.add('open');
      } else {
        this.closest(activeLi, 'li').classList.add('open');
      }
    } catch (error) {}
  }

  componentWillReceiveProps(nextProps) {
    const { history } = nextProps;
    const pathname = `${history.location.pathname + window.location.search}`; // get current path

    const activeLi = document.querySelector('a[href="' + pathname + '"]'); // select current a element
    try {
      const activeNav = this.closest(activeLi, 'ul'); // select closest ul
      if (activeNav.classList.contains('sub-menu')) {
        this.closest(activeNav, 'li').classList.add('open');
      } else {
        this.closest(activeLi, 'li').classList.add('open');
      }
    } catch (error) {}
  }

  closest(el, selector) {
    try {
      let matchesFn;
      // find vendor prefix
      [
        'matches',
        'webkitMatchesSelector',
        'mozMatchesSelector',
        'msMatchesSelector',
        'oMatchesSelector'
      ].some(function(fn) {
        if (typeof document.body[fn] === 'function') {
          matchesFn = fn;
          return true;
        }
        return false;
      });

      let parent;

      // traverse parents
      while (el) {
        parent = el.parentElement;
        if (parent && parent[matchesFn](selector)) {
          return parent;
        }
        el = parent;
      }
    } catch (e) {}

    return null;
  }

  render() {
    //console.log('DrawerMenu...', this.props);
    const { 
      _email,
      _uploadProgress,
      _logoutHandler,
      _navMinimzed,
      _getMenuItemActive,
      _menuItemActive,
      _activePageType,
      _appData,
      _type,
      _page
    } = this.props;
    var navMenuHolderScrollbarClass = cx({
      navMenuHolderScrollbar: true,
      'minimized': _navMinimzed
    });
    var navMenuHolderClass = cx({
      navMenuHolder: true,
      'minimized': _navMinimzed
    });
    let urlParamProjectId = '';
    let schedulePathname = '';
    if(this.props.history.location.pathname) {
      schedulePathname = this.props.history.location.pathname;
      var pathname_array = schedulePathname.split("/");
      var page_id = pathname_array[pathname_array.length - 1];
      var page_type = pathname_array[2];
      if(page_type === 'project') {
        schedulePathname = '/dashboard/project-schedules/' + page_id;
      } else if(pathname_array[1] === 'flow') {
        schedulePathname = '/dashboard/flow-schedules/' + page_id;
      } else {
        schedulePathname = '/dashboard/schedules'
      }
      //console.log('page_id...', page_id, 'page_type...', page_type);
    }
    return (
      <CustomScrollbars className={navMenuHolderScrollbarClass}>
        <div className={navMenuHolderClass}>
          <ul className={styles.navMenu}>
            <li className={_menuItemActive === '' ? 'menu open' : 'menu'}>
              <NavLink to="/dashboard/projects" className="active" onClick={() => { _getMenuItemActive('projects')}}>
                <i className="zmdi zmdi-folder-outline zmdi-hc-fw" />
                <span className="nav-text">Projects</span>
              </NavLink>
            </li>
            <li className="menu">
              {/* <div className="link-alike" onClick={() => { _getMenuItemActive('schedule')}}>
                <i className="zmdi zmdi-time zmdi-hc-fw" />
                <span className="nav-text">Schedules</span>
              </div> */}
              {_page !== 'Dataset' && _page !== 'Dashboard' && (
                <NavLink to={schedulePathname} className="active" onClick={() => { _getMenuItemActive('schedule')}}>
                  <i className="zmdi zmdi-time zmdi-hc-fw" />
                  <span className="nav-text">Schedules</span>
                </NavLink>
              )}
            </li>
          </ul>

          <ChatNavModule 
            uploadProgress={_uploadProgress} 
          />

          <ul className={styles.navMenu}>
            <li className="menu">
              <NavLink to="/dashboard/profile" onClick={() => { _getMenuItemActive('profile')}}>
                <i className="zmdi zmdi-account-circle zmdi-hc-fw" />
                {/* <span className="nav-text">{_email.split('@')[0]}</span> */}
                <span className="nav-text">Profile</span>
              </NavLink>
            </li>
            <li className="menu">
              <div className="link-alike" onClick={_logoutHandler}>
                <i className="zmdi zmdi-power zmdi-hc-fw" />
                <span className="nav-text">Log Out</span>
              </div>
            </li>
          </ul>
        </div>
      </CustomScrollbars>
    );
  }
}

export default withRouter(DrawerMenu);
