import React, { Fragment, ReactNode } from 'react';
//import ReactDOM from 'react-dom';
import Draggable from 'react-draggable';
import {
    Popover,
    IconButton,
    Menu,
    MenuItem,
    TextField
  } from '@material-ui/core';
import IkiButton from '../../IkiFormElements/IkiButton';
//import IkiInput from '../../IkiFormElements/IkiInput';
import CloseIcon from '@material-ui/icons/Close';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import CheckIcon from '@material-ui/icons/Check';
//import PhotoCameraIcon from '@material-ui/icons/PhotoCamera';
import SupportFeedbackDialog from './feedback-dialog';
import domtoimage from 'dom-to-image';
import SnackbarMsg from '../../../components/SnackbarMsg';
import emailjs from 'emailjs-com';
//var getElement = require('get-element')

export function SupportPopover(props: any) {
  const {
    setSupportPopoverAnchorEl,
    anchorSupportPopover,
    openSupportPopover,
    loggedUserEmail
  } = props;
  //const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);
  const [addCommentStatus, setAddCommentStatus] = React.useState(false);

  const [mouseX, setMouseX] = React.useState(0);
  const [mouseY, setMouseY] = React.useState(0);

  const [newComment, setNewComment] = React.useState('');
  const [commentsArray, setCommentsArray] = React.useState([] as any);

  const [mouseoverCommentBox, setCommentBoxMouseOver] = React.useState(false);

  const [anchorElCommentActionBtn, setAnchorElCommentActionBtn] = React.useState<null | HTMLElement>(null);

  const [selectedCommentBox, setSelectedCommentBox] = React.useState(null);
  const [editCommentStatus, setEditCommentStatus] = React.useState(false);

  const [updateEditComment, setUpdateEditComment] = React.useState('');

  const [feedbackImg, setFeedbackImg] = React.useState('');
  const [feedbackDialogState, setFeedbackDialogState] = React.useState(false);

  const [commentTextareaErrorState, setCommentTextareaErrorState] = React.useState(false);
  const [commentEditTextareaErrorState, setCommentEditTextareaErrorState] = React.useState(false);

  const [snackbarIsOpen, setSnackbarOpen] = React.useState(false);
  const [snackbarMessageIs, setSnackbarMessage] = React.useState('');
  const [snackbarIsType, setSnackbarType] = React.useState('');

  const [feedbackDialogSubmitState, setFeedbackDialogSubmitState] = React.useState(false);

  const [hideSupportPopover, setHideSupportPopover] = React.useState(false);

  const handleCloseSupportPopover = (e: React.MouseEvent<HTMLButtonElement>) => {
    //console.log("ELEMENT: " + e.currentTarget.id);
    if(e.currentTarget.id === 'closeSupportPopoverBtn'){
        setSupportPopoverAnchorEl(null);  
        setCommentsArray([] as any);
    }
  };

  const handleCloseSupportPopover2 = () => {
    setSupportPopoverAnchorEl(null);  
    setCommentsArray([] as any);
  }

  const handleCloseAddcommPopover = (e: React.MouseEvent<HTMLButtonElement>) => {
    //console.log("ELEMENT: " + e.currentTarget.id);
    setAddCommentStatus(false);  
  };

  const id = openSupportPopover ? 'support-popover' : undefined;

  const idAddComment = addCommentStatus ? 'addcomment-popover' : undefined;

/*   const getXYcoordinates = (event: React.MouseEvent<HTMLDivElement>) => {
    //console.log("x: " + event.nativeEvent.offsetX + ", y: " + event.nativeEvent.offsetY)
    setMouseX(event.nativeEvent.offsetX);
    setMouseY(event.nativeEvent.offsetY);
  } */

  const popupNewComment = (event: React.MouseEvent<HTMLDivElement>) => {
    //console.log("x: " + mouseX + ", y: " + mouseY)
    //console.log("clicked element: " + event.currentTarget.className);
    if(mouseoverCommentBox === false && editCommentStatus === false){
        setMouseX(event.nativeEvent.offsetX);
        setMouseY(event.nativeEvent.offsetY);
        if(addCommentStatus === true){
            setAddCommentStatus(false);
        }else{
            setAddCommentStatus(true);
            setNewComment('');
            setCommentTextareaErrorState(false);
        }
    }
  }

  const handleCommentChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setNewComment(e.target.value)
  }

  const handleAddCommentBtn = (e: React.MouseEvent<HTMLButtonElement>) => {	
    let date = new Date().getDate();
    let hours = new Date().getHours();
    let minutes = new Date().getMinutes();
    let seconds = new Date().getSeconds();
    let datetime = date + "-" + hours + "-" + minutes + "-" + seconds;
    if(newComment !== ''){
        setCommentsArray([
            ...commentsArray,
            {
            id: datetime,
            comment: newComment,
            pTop: mouseY,
            pLeft: mouseX
            }
        ]);
        setNewComment('')
        setAddCommentStatus(false);
    }else{
        setCommentTextareaErrorState(true)
    }
  }

  const handleCloseCommentBtn = (e: React.MouseEvent<HTMLButtonElement>) => {	
    setAddCommentStatus(false);
    setCommentTextareaErrorState(false)
    setCommentEditTextareaErrorState(false)
  }

  const handleCommentBoxMouseOver = (event: React.MouseEvent<HTMLDivElement>) => {
    setCommentBoxMouseOver(true);
    //console.log("changed state: " + mouseoverCommentBox);
  }

  const handleCommentBoxMouseOut = (event: React.MouseEvent<HTMLDivElement>) => {
    setCommentBoxMouseOver(false);
    //console.log("changed state: " + mouseoverCommentBox);
  }

  const openCommentMenu = (event: React.MouseEvent<HTMLButtonElement>, selectedComment: any, selectedCommentVal: any) => {	
    setSelectedCommentBox(selectedComment);
    setAnchorElCommentActionBtn(event.currentTarget);
    setUpdateEditComment(selectedCommentVal);
  }

  const handleCloseCommentActionBtn = (e: React.MouseEvent<HTMLButtonElement>) => {	
    setUpdateEditComment('');
    setAnchorElCommentActionBtn(null);
  }

  const handleEditComment = () => {
    setEditCommentStatus(true);
    setAnchorElCommentActionBtn(null);
  }

  const handleCancelEditComment = () => {
    setEditCommentStatus(false);
    setSelectedCommentBox(null);
    setCommentEditTextareaErrorState(false)
  }

  const handleUpdateEditTextarea = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setUpdateEditComment(e.target.value);
  }

  const handleConfirmEditComment = (e: React.MouseEvent<HTMLButtonElement>) => {
    if(updateEditComment !==''){
        setEditCommentStatus(false);
        setSelectedCommentBox(null);
        let commentBoxIndex = selectedCommentBox as unknown as number;
        commentsArray[commentBoxIndex] = {...commentsArray[commentBoxIndex], comment: updateEditComment};
    }else{
        setCommentEditTextareaErrorState(true)
    }
  }

  const handleDeleteComment = () => {
    let commentBoxIndex = selectedCommentBox as unknown as number;
    commentsArray.splice(commentBoxIndex, 1);
    setSelectedCommentBox(null);
    setAnchorElCommentActionBtn(null);
  }

  const prepareSendFeedback = (e: React.MouseEvent<HTMLButtonElement>) => {
    setSnackbarMessage('Taking screenshot...');
    setSnackbarType('processing');
    setSnackbarOpen(true);
    setHideSupportPopover(true);
    setTimeout(() => {
        var a:any = document.getElementsByClassName('titlebar')[0];
        a.remove()
        domtoimage.toPng(document.body, { quality: 1.0 })
            .then(function (dataUrl) {
                document.body.appendChild(a)
                var img = new Image();
                img.src = dataUrl;
                //document.body.appendChild(img);
                setFeedbackImg(dataUrl);
                setFeedbackDialogState(true);
                //console.log("activeUserEmail: " + loggedUserEmail);
                setHideSupportPopover(false);
                setSnackbarMessage('Screenshot taken successfully');
                setSnackbarType('success');
                setSnackbarOpen(true);
            })
            .catch(function (error) {
                console.error('Error: cant get image!', error);
                setHideSupportPopover(false);
                setSnackbarMessage('Screenshot failed');
                setSnackbarType('error');
                setSnackbarOpen(true);
            });
    }, 500);
  }

  const downloadScreenshot = (e: React.MouseEvent<HTMLButtonElement>) => {
    var ts = Math.round((new Date()).getTime() / 1000);
    var convertedTimestamp = timeConverter(ts);
    setSnackbarMessage('Taking screenshot...');
    setSnackbarType('info');
    setSnackbarOpen(true);
    setHideSupportPopover(true);
    setTimeout(() => {
        var a:any = document.getElementsByClassName('titlebar')[0];
        a.remove()
        domtoimage.toJpeg(document.body)
            .then(function (dataUrl) {
                document.body.appendChild(a)
                var link = document.createElement('a');
                link.download = 'IkigaiLabs_' + convertedTimestamp + '.jpeg';
                link.href = dataUrl;
                link.click();
                setHideSupportPopover(false);
                setSnackbarMessage('Screenshot taken successfully');
                setSnackbarType('success');
                setSnackbarOpen(true);
        }).catch(function (error) {
            console.error('Error: cant get image!', error);
            setHideSupportPopover(false);
            setSnackbarMessage('Screenshot failed');
            setSnackbarType('error');
            setSnackbarOpen(true);
        });
    }, 500);
  }

  const handleFeedbackDialogCancel = () => {
    setFeedbackDialogState(false);
  }

  const handleFeedbackDialogSubmit = (userEmail: string, feedbackMessage: string, feedbackImageCheckbox: boolean, targetType: string) => {
    handleFeedbackDialogSubmitState(true);
    emailjs.init("user_cWIBjEHSnNuFLsiYV4cpL"); 

    var screenshot = "", template_id = "";
    if(feedbackImageCheckbox === true){
        template_id = 'ikigailabs_screenshot_template';
        screenshot = feedbackImg;
    }else{
        template_id = 'ikigailabs_no_screenshot_template';
        screenshot = "";
    }

    var greeting_msg = "", endEmail = "";
    if(targetType === 'user'){
        greeting_msg = "Hello " + userEmail + ",";
        endEmail = userEmail;
    }else{
        greeting_msg = "";
        endEmail = "support@ikigailabs.io";
    }

    var template_params = {
        "email": endEmail,
        "greeting_msg": greeting_msg,
        "from_name": "Ikigai Labs - Messaging",
        "reply_to": loggedUserEmail,
        "to_name": userEmail,
        "message_html": feedbackMessage,
        "screenshot": screenshot
    }

   emailjs.send('sendgrid', template_id, template_params) //Insert your email service ID and email template ID
    .then(function(response) {
       console.log('SUCCESS!', response.status, response.text);
       if(response.status === 200){
        setSnackbarMessage('You have succesfully shared screenshot');
        setSnackbarType('success');
        setSnackbarOpen(true);
        handleFeedbackDialogSubmitState(false);
        handleFeedbackDialogCancel();
        handleCloseSupportPopover2();
       }else{
        setSnackbarMessage('Screenshot sharing failed');
        setSnackbarType('error');
        setSnackbarOpen(true);
        handleFeedbackDialogSubmitState(false);
        handleFeedbackDialogCancel();
       }
    }, function(error) {
       console.log('FAILED...', error);
       setSnackbarMessage('Screenshot sharing failed');
       setSnackbarType('error');
       setSnackbarOpen(true);
       handleFeedbackDialogSubmitState(false);
       handleFeedbackDialogCancel();
    });
};

const handleFeedbackDialogSubmitState = (fdState: boolean) => {
    setFeedbackDialogSubmitState(fdState);
    return fdState;
}

  const timeConverter = (UNIX_timestamp: any) => {
    var a = new Date(UNIX_timestamp * 1000);
    var months = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec'
    ];
    var year = a.getFullYear();
    var month = months[a.getMonth()];
    var date = a.getDate();
    var hour = a.getHours();
    var min = a.getMinutes();
    var sec = a.getSeconds();
    var time =
      date + '-' + month + '-' + year + '_' + hour + '-' + min + '-' + sec;
    return time;
  }

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  }

  return (
      <div>
            <Popover
                id={id}
                className={hideSupportPopover === true ? "support-popover hide" : "support-popover"}
                open={openSupportPopover || false}
                anchorEl={anchorSupportPopover}
                onClose={handleCloseSupportPopover}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'center',
                }}
                transformOrigin={{
                    vertical: 'bottom',
                    horizontal: 'center',
                }}
            >
                <div className="support-popover-content">
                    <div className="heading">
                        <span className="title">Screenshot</span>
                        <IconButton id="closeSupportPopoverBtn" className="is-right" size="small" onClick={handleCloseSupportPopover}>
                            <CloseIcon fontSize="small" />
                        </IconButton>
                    </div>
                    <div className="step">
                        <div className="step-title">Step 1</div>
                        <div className="step-descr">Click anywhere on screen to create comment</div>
                    </div>
                    <div className="step">
                        <div className="step-title">Step 2</div>
                        <div className="step-descr">Once finished, click "Send" or "Download" button</div>
                    </div>
                    <div className="actions-div">
                        <IkiButton
                            _variant='contained'
                            _color='primary'
                            _label="Send"
                            _type='button'
                            _fullWidth={false}
                            _onClick={(e: any) => prepareSendFeedback(e)}
                        />
                        <IkiButton
                            _variant='contained'
                            _color='default'
                            _label="Download"
                            _type='button'
                            _fullWidth={false}
                            _onClick={(e: any) => downloadScreenshot(e)}
                        />
                    </div>
                </div>
                <div className="support-comment-frame fade-in" 
                    onClick={popupNewComment}>
                        {commentsArray.map((comment: { 
                            id: string | number | undefined; 
                            comment: React.ReactNode; 
                            pTop: number, 
                            pLeft: number 
                        }, index: string | number | undefined) => (
                            <Draggable>
                                <div key={comment.id} className="comment-popover" style={{
                                    top: comment.pTop+'px', 
                                    left: comment.pLeft+'px'}}
                                    onMouseOver={handleCommentBoxMouseOver}
                                    onMouseOut={handleCommentBoxMouseOut}
                                >
                                    <div className="heading">
                                        <span className="title">Comment</span>
                                        {selectedCommentBox === index && editCommentStatus ?
                                            <Fragment>
                                                <IconButton className="is-right" size="small" style={{marginLeft: 'auto'}} onClick={handleCancelEditComment}>
                                                    <CloseIcon fontSize="small" />
                                                </IconButton>
                                                <IconButton className="is-right" size="small" style={{marginLeft: '7px'}} onClick={handleConfirmEditComment}>
                                                    <CheckIcon fontSize="small" />
                                                </IconButton>
                                            </Fragment>
                                        :
                                            <IconButton className="is-right" size="small" onClick={(event) => openCommentMenu(event, index, comment.comment)}>
                                                <MoreHorizIcon fontSize="small" />
                                            </IconButton>
                                        }
                                    </div>
                                    <div className="body">
                                        {selectedCommentBox === index && editCommentStatus ?
                                            <TextField
                                                multiline
                                                rows="4"
                                                variant="outlined"
                                                value={updateEditComment}
                                                autoFocus
                                                onChange={handleUpdateEditTextarea}
                                                error={commentEditTextareaErrorState}
                                            />
                                        :
                                            <div>{comment.comment}</div>
                                        }
                                    </div>
                                </div>
                            </Draggable>
                        ))}
                </div>
            </Popover>
            <Popover
                id={idAddComment}
                className="addcomm-popover"
                open={addCommentStatus}
                onClose={handleCloseAddcommPopover}
                anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
                anchorReference="anchorPosition"
                anchorPosition={{ top: mouseY, left: mouseX }}
            >
                <div className="addcomm-popover-content">
                    <div className="heading">
                        <span className="title">Add comment</span>
                    </div>
                    <div className="textarea-box">
                        <TextField
                            multiline
                            rows="4"
                            variant="outlined"
                            autoFocus
                            placeholder="Comment"
                            onChange={handleCommentChange}
                            error={commentTextareaErrorState}
                        />
                    </div>
                    <div className="actions-div">
                        <IkiButton
                            _variant='text'
                            _color='default'
                            _label="Cancel"
                            _type='button'
                            _fullWidth={false}
                            _onClick={(e: any) => handleCloseCommentBtn(e)}
                        />
                        <IkiButton
                            _variant='contained'
                            _color='primary'
                            _label="Add"
                            _type='button'
                            _fullWidth={false}
                            _onClick={(e: any) => handleAddCommentBtn(e)}
                        />
                    </div>
                </div>
            </Popover>
            <Menu
                classes={{
                    paper: 'action-btn-menu'
                }}
                className="comment-menu-overlay"
                anchorEl={anchorElCommentActionBtn}
                keepMounted
                open={Boolean(anchorElCommentActionBtn)}
                onClose={handleCloseCommentActionBtn}
            >
                <MenuItem onClick={handleEditComment}>
                    <EditIcon />
                    <span>Edit</span>
                </MenuItem>
                <MenuItem onClick={handleDeleteComment}>
                    <DeleteIcon />
                    <span>Delete</span>
                </MenuItem>
            </Menu>
            <SupportFeedbackDialog 
                handleFeedbackDialogCancel = {handleFeedbackDialogCancel}
                handleFeedbackDialogClose = {handleFeedbackDialogCancel}
                openFeedbackDialog = {feedbackDialogState}
                feedbackImgData = {feedbackImg}
                onFeedbackDialogSubmit = {handleFeedbackDialogSubmit}
                feedbackSubmitState = {feedbackDialogSubmitState}
                handleDialogSubmitState = {handleFeedbackDialogSubmitState}
            />
            <SnackbarMsg
                snackbarOpen={snackbarIsOpen}
                snackbarType={snackbarIsType}
                snackbarMsg={snackbarMessageIs}
                onSnackbarClose={handleSnackbarClose}
            />
    </div>
  );
}
