import { connect } from 'react-redux';
import { Dispatch } from 'redux';

import { State } from '../../reducers';
import {
  deleteNodeAction,
  sendAppDataAction,
  updateFlowDataActionStart,
  getFacetDocActionStart,
  renameProjectActionStart,
  createDatasetActionStart,
  editDatasetActionStart,
  getDatasetActionStart,
  getSuggestionsActionStart,
  getDashboardActionStart,
  handleProjectReturn,
  handleCleanSession,
  logoutAction
} from '../../actions';

import FlowDiagramComponent from './flow-diagram-component';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  logout: () => {
    dispatch(logoutAction());
  },
  deleteNode: (nodeId: string) => {
    dispatch(deleteNodeAction(nodeId));
  },
  sendAppData: (pipelineId: any) => {
    dispatch(sendAppDataAction(pipelineId));
  },
  getFacetDoc: () => {
    dispatch(getFacetDocActionStart());
  },
  fireReturnToProject: (sourcePage: string) => {
    dispatch(handleProjectReturn(sourcePage))
  },
  cleanSession: () => {
    dispatch(handleCleanSession())
  },
  updateFlowData: (flowData: any) => {
    dispatch(updateFlowDataActionStart(flowData));
  },
  renameProject: (projectId: string, newName: string) =>
    dispatch(renameProjectActionStart(projectId, newName)),
  createDataset: (projectId: string, datasetName: string, file: File) =>
    dispatch(createDatasetActionStart(projectId, datasetName, file)),
  getDatasets: (projectId: string) =>
    dispatch(getDatasetActionStart(projectId)),
  getDashboards: (projectId: string) =>
    dispatch(getDashboardActionStart(projectId)),
  editDataset: (
    projectId: string,
    datasetId: string,
    datasetName: string,
    file: File
  ) => dispatch(editDatasetActionStart(projectId, datasetId, datasetName, file)),
  getSuggestions: (data:any) => dispatch(getSuggestionsActionStart(data))
});

const mapStateToProps = (state: State) => ({
  email: state.userReducerState.email,
  data: state.flowReducerState,
  facetDocLoaded: state.facetReducerState.facetDocLoaded,
  componentList: state.facetReducerState.componentList,
  projects: state.flowReducerState.projects,
  uploadProgress: state.flowReducerState.uploadProgress,
});

export const FlowDiagramContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(FlowDiagramComponent);
