import React, { useState, Fragment } from 'react';
import {
  FormHelperText
} from '@material-ui/core';
import IkiDialog from '../IkiDialog';
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';
import FileInput from '../file-input';
import ErrorIcon from '@material-ui/icons/Error';
import AlertMsg from '../AlertMsg';

export function AddDatasetDialog(props: any) {
  const {
    datasetID,
    name,
    handleDialogClose,
    handleSubmit,
    handleSubmitOverwrite,
    handleCancel,
    open,
    isEdit,
    newDatasetNameExists,
    dismissAlert,
  } = props;
  //console.log("isEdit: ", isEdit, "name: ", name);

  const [datasetName, setDatasetName] = useState(name);
  const [datasetNameError, setDatasetNameError] = useState('');
  const [file, setFile] = useState();
  const [fileError, setFileError] = useState('');
  const [isValidated, setIsvalidated] = useState(false);

  const handleAddDatasetSubmit = () => {
    if(datasetName !== '' && file !== ''){
      //console.log("has val: " + datasetName);
      if (isEdit) {
        if (file || datasetName !== '') {
          handleSubmit(datasetName, file);
          setIsvalidated(false);
        } else {
          setIsvalidated(true);
        }
      } else {
        if (file && datasetName !== '') {
          handleSubmit(datasetName, file);
          setIsvalidated(false);
        } else {
          setIsvalidated(true);
        }
      }
    }else{
      if(datasetNameError === '') {
        setDatasetNameError('Required');
      }
      if(file === '') {
        setFileError('Required');
      }
      setIsvalidated(false);
      //console.log("no val: " + datasetName);
    }
  }

  const handleAddDatasetSubmitOverwrite = () => {
    if(datasetName !== '' && file !== ''){
      //console.log("has val: " + datasetName);
      if (isEdit) {
        if (file || datasetName !== '') {
          handleSubmitOverwrite(datasetName, file);
          setIsvalidated(false);
        } else {
          setIsvalidated(true);
        }
      } else {
        if (file && datasetName !== '') {
          handleSubmitOverwrite(datasetName, file);
          setIsvalidated(false);
        } else {
          setIsvalidated(true);
        }
      }
    }else{
      //console.log("no val: " + datasetName);
      setIsvalidated(true);
    }
  }

  const handleOnEnter = () => {
    //console.log("onEnter");
    isEdit ? setDatasetName(name) : setDatasetName('')
    setDatasetNameError('');
    setFileError('');
    setFile(null);
    setDatasetName('');
  }

  const handleOnClose = () => {
    setFile(null);
    setIsvalidated(false);
    setDatasetName('');
    setDatasetNameError('');
    setFileError('');
    handleDialogClose();
  };

  return (
    <IkiDialog
      key={'add-dataset-dialog'}
      _open={open}
      _onExit={handleOnClose}
      _onEnter={handleOnEnter}
      _onClose={handleOnClose}
      _type='clouploadne'
      _icon={false}
      _title={isEdit ? 'Edit Dataset' : 'Upload New Dataset'}
      _subtitle=''
      _size='xs'
      _align='left'
      _content={
        <>
          {newDatasetNameExists === true ?
            <Fragment>
              <AlertMsg
                alertType="warning"
                alertContent={
                  <Fragment>
                      <div>
                        <span>Dataset with same name already exists.</span>
                        <br />
                        <strong>{datasetName}</strong>
                      </div>
                      <div>Please note:<br /> All flows which contain this dataset will be reflected by this change.</div>
                      <div>
                        <strong>Are you sure you want to continue and overwrite dataset?</strong>
                      </div>
                  </Fragment>
                }
                alertButtons={
                  <Fragment>
                    <IkiButton
                      _variant='contained'
                      _color='default'
                      _label="No, go back"
                      _type='button'
                      _fullWidth={false}
                      _loading={false}
                      _onClick={() => dismissAlert()}
                    />
                    <IkiButton
                      _variant='contained'
                      _color='primary'
                      _label="Yes, overwrite"
                      _type='button'
                      _isWarning={true}
                      _fullWidth={false}
                      _loading={false}
                      _onClick={() => handleAddDatasetSubmitOverwrite()}
                    />
                  </Fragment>
                }
              />
            </Fragment>
          :
            <div>
              {isEdit &&
                <div className="section">
                  <div className="txt-dialog-faded1">{datasetID}</div>
                  <div className="txt-dialog-impact1">{name}</div>
                </div>
              }
              <div className="section">
                <IkiInput 
                      _label="Dataset name"
                      _placeholder="Enter new name for Dataset"
                      _type='text'
                      _autoComplete="new-password"
                      _value={datasetName}
                      _onChange={(e: any) => {
                        setDatasetName(e.target.value);
                      }}
                      _characterCounter={40}
                      _autoFocus={true}
                      _fullWidth={true}
                      _error={datasetNameError}
                />
                <FileInput
                  onFileAdded={(file: File) => setFile(file)}
                  // disabled={uploadStatus === 'pending'}
                  fileData={file}
                ></FileInput>
                {fileError !== '' && (
                  <FormHelperText className="input-error" error>
                    <ErrorIcon />
                    <span>{fileError}</span>
                  </FormHelperText>
                )}
              </div>
            </div>
          }
        </>
      }
      _actions={
        newDatasetNameExists !== true &&
          <Fragment>
            <IkiButton
              _variant='text'
              _color='default'
              _label="Cancel"
              _type='button'
              _fullWidth={false}
              _loading={false}
              _onClick={() => handleCancel()}
            />
            <IkiButton
              _variant='contained'
              _color='primary'
              _label={isEdit ? 'Update' : 'Upload'}
              _type='button'
              _fullWidth={false}
              _loading={false}
              _disabled={false}
              _onClick={() => handleAddDatasetSubmit()}
            />
          </Fragment>
      }
    />
  );
}
