import * as React from 'react';
import IkigaiLayout from '../components/IkiLayout';

type AppdState = {
  open: boolean;
};

type AppProps = {
  data: any;
  history: any;
  match: any;
  classes: any;
  flowsLoadedError: string;
  flowsLoaded: boolean;
  email: string;
  uploadProgress: any;
  logout: () => void;
  deleteDataset: (datasetId:string) => void;
};

class IkiApp extends React.Component<
  AppProps,
  AppdState
> {
  state = {
    open: false
  };
  componentDidMount() {
    //console.log('IkiApp comp props...', this.props);
    /*console.log('IkiApp props...', this.props);
    if(this.props.data.activeProjectId !== '') {
      console.log('this.props.data.activeProjectId... ', this.props.data.activeProjectId);
    }*/
  }
  render() {
    return (
        <>
          <IkigaiLayout
            _appData={this.props.data}
            _historyData={this.props.history}
            _matchData={this.props.match}
            _email={this.props.email}
            _uploadProgress={this.props.uploadProgress}
            _logoutHandler={this.props.logout}
          />
        </>
    );
  }
}

export default IkiApp;
