const handleDeleteDatasetRequestComplete = (
  state: FlowState,
  datasetId: any
) => {
  return {
    ...state,
    datasets: state.datasets.filter(
      (datasetData: any) => datasetData.dataset_id !== datasetId
    )
  };
};

const handleDeleteDatasetRequestFail = (state: FlowState, reason: string) => {
  return state;
};

const handleDeleteDashboardRequestComplete = (
  state: FlowState,
  dashboardId: any
) => {
  return {
    ...state,
    dashboards: state.dashboards.filter(
      (dashboardData: any) => dashboardData.dashboard_id !== dashboardId
    )
  };
};

const handleDeleteDashboardRequestFail = (state: FlowState, reason: string) => {
  return state;
};

const handleGetDatasetsRequestFail = (state: FlowState, payload: any) => {
  return {
    ...state,
    datasetsLoaded: true,
    datasetsLoadedError: 'Unable to load datasets'
  };
};

const handleGetDashboardsRequestFail = (state: FlowState, payload: any) => {
  return {
    ...state,
    dashboardsLoaded: true,
    dashboardsLoadedError: 'Unable to load dashboards'
  };
};

const handleGetDatasetsRequestComplete = (state: FlowState, payload: any) => {
  //console.log("PAYLOAD handleGetDatasetsRequestComplete: ");
  //console.log(state.datasets);
  const { datasets, projectId } = payload;

  if (datasets.length > 0) {
    var filteredDatasets = state.datasets.filter((datasetData: any) => {
      //console.log(datasetData.project_id, projectId);
      return datasetData.project_id !== projectId;
    });

    //console.log("filteredDatasets: ");
    //console.log(filteredDatasets);

    return {
      ...state,
      datasets: [...filteredDatasets, ...datasets],
      datasetsLoaded: true,
      datasetsLoadedError: ''
    };
  } else {
    return {
      ...state,
      datasetsLoaded: true,
      datasetsLoadedError: ''
    };
  }
};

const handleGetDashboardsRequestComplete = (state: FlowState, payload: any) => {
  //console.log("PAYLOAD handleGetDatasetsRequestComplete: ");
  //console.log(state.datasets);
  const { dashboards, projectId } = payload;
  //console.log(payload);

  if (dashboards.length > 0) {
    var filteredDashboards = state.dashboards.filter((dashboardsData: any) => {
      //console.log(datasetData.project_id, projectId);
      return dashboardsData.project_id !== projectId;
    });

    //console.log("filteredDatasets: ");
    //console.log(filteredDatasets);

    return {
      ...state,
      dashboards: [...filteredDashboards, ...dashboards],
      dashboardsLoaded: true,
      dashboardsLoadedError: ''
    };
  } else {
    return {
      ...state,
      dashboardsLoaded: true,
      dashboardsLoadedError: ''
    };
  }
};

const handleGetDatasetsRequestStart = (state: FlowState, payload: any) => {
  //console.log(payload);
  return {
    ...state,
    datasetsLoaded: false
  };
};

const handlegetDashboardsRequestStart = (state: FlowState, payload: any) => {
  //console.log(payload);
  return {
    ...state,
    dashboardsLoaded: false
  };
};

const handleResetFlowState = (state: FlowState, payload: any) => {
  return {
    ...defaultState
  };
};

const handleRenameProjectRequestFail = (state: FlowState, payload: any) => {
  return state;
};

const handleRenameProjectRequestComplete = (state: FlowState, payload: any) => {
  const { projectId, newName } = payload;
  var project = state.projects.filter(
    (p: any) => p.project_id === projectId
  );
  if (project.length > 0) {
    project = project[0];
    project.name = newName;
    var otherProjects = state.projects.filter(
      (p: any) => p.project_id !== projectId
    );
    return {
      ...state,
      projects: [...otherProjects, project]
    };
  } else {
    return state;
  }
};

const handleFlowsRequestStart = (state: FlowState, payload: string) => {
  //console.log('handleFlowsRequestStart');
  return {
    ...state,
    flowsLoaded: false
  };
};

const handleDeleteProjectRequestComplete = (
  state: FlowState,
  project_id: string
) => {
  var projects = state.projects.filter(
    (project: any) => project.project_id !== project_id
  );
  return {
    ...state,
    projects: projects
    //projects: [...state.projects, { ...projects.project, project: projects.project} ]
  };
};

const handleDeleteProjectRequestFail = (state: FlowState, reason: string) => {
  return state;
};

const handleCreateProjectRequestComplete = (state: FlowState, project: any) => {
  //console.log(project);
  return {
    ...state,
    //projects: [...state.projects, { projects: project }]
    projects: [...state.projects, { ...project, project: project }]
  };
};

const handleDuplicateProjectRequestFail = (state: FlowState, reason: any) => {
  return state;
  //TODO assign reason to state variable
};

const handleDuplicateProjectRequestComplete = (state: FlowState, project: any) => {
  //console.log("reducer - handleDuplicateProjectRequestComplete: ", state, project);
  if (project.owner !== project.user) {
    return state
  } else {
    return {
      ...state,
      //projects: [...state.projects, { projects: project }]
      projects: [...state.projects, { ...project, project: project }]
    };
  }
};

const handleCreateProjectRequestFail = (state: FlowState, reason: any) => {
  return state;
  //TODO assign reason to state variable
};

const handleGetProjectRequestFail = (state: FlowState, reason: any) => {
  return {
    ...state,
    projectsLoaded: false,
    projectsLoadedError: reason
  };
};

const handleGetProjectRequestComplete = (state: FlowState, projects: any) => {
  //console.log(projects);
  return {
    ...state,
    projects: projects,
    projectsLoaded: true,
    projectsLoadedError: ''
  };
};

const handleRestoreFlowComplete = (state: FlowState, id: any) => {
  var flow = state.flows.filter((flow: any) => flow.id === id)[0];
  flow.isDeleted = false;
  var flows = state.flows.filter((flow: any) => flow.id !== id);
  flows.push(flow);
  return {
    ...state,
    flows: flows
  };
};

const handleDeleteFlowComplete = (state: FlowState, flowData: any) => {
  if (state.flows[flowData.project_id]) {
    var flows = state.flows[flowData.project_id].filter(
      (flow: any) => flow.pipeline_id !== flowData.pipeline_id
    );
    return {
      ...state,
      flows: { ...flows, [flowData.project_id]: flows }
    };
  } else {
    return state;
  }
};

const handleUpdateFlowDataComplete = (state: FlowState, flowData: any) => {
  var flows = state.flows.filter((flow: any) => flow.id !== flowData.id);
  flows.push(flowData);
  return {
    ...state,
    flows: flows
  };
};

const handleSaveFlowsRequestComplete = (state: FlowState, response: any) => {
  return state;
};

const handleFlowsRequestComplete = (state: FlowState, payload: any) => {
  return {
    ...state,
    flows: {
      ...state.flows,
      [payload.projectId]: payload.flowsData
    },
    flowsLoaded: true,
    flowsLoadedError: ''
  };
};

const handleFlowsRequestFail = (state: FlowState, response: any) => {
  //console.log("response: ");
  //console.log(response);
  let flowsLoadedError = '';
  if (response.status === 401) {
    flowsLoadedError = "Unauthorized. You don't have access to visit this page";
  } else if (response.status === 404) {
    flowsLoadedError = 'Error 404: Something went wrong';
  } else {
    flowsLoadedError = 'Error: Something went wrong';
  }
  return {
    ...state,
    flowsLoaded: false,
    flowsLoadedError: flowsLoadedError
  };
};

const handleAddFlow = (state: FlowState, pipelineObj: any) => {
  var projectFlows = [
    ...state.flows[pipelineObj.pipeline.project_id],
    pipelineObj
  ];
  return {
    ...state,
    creatingFlow: false,
    flows: { ...state.flows, [pipelineObj.pipeline.project_id]: projectFlows }
  };
};

const handleSendAppData = (state: FlowState, response: object) => {
  return {
    ...state,
    response: response
  };
};

