import React, { Component } from 'react';
import IkiItemBox from '../IkiItemBox';
//import classNames from 'classnames/bind';
import styles from './Grid.module.scss';

//let cx = classNames.bind(styles);

/**
   * Grid
   * @param _type Type - string
   */
export default class IkiGrid extends Component {
  render() {
    const { 
      _type,
      _icon,
      _link,
      _data
    } = this.props;
    const _functions = this.props._functions ? this.props._functions : '';
    const _uploadInfo = this.props._uploadInfo ? this.props._uploadInfo : [];
    //console.log("IkiGrid: ", this.props);
    return(
      <>
        <div className={styles.grid}>
          {_data.map((item, num) => (
            <IkiItemBox
              key={num}
              _type={_type}
              _icon={_icon}
              _link={_link}
              _data={item}
              _functions={_functions}
              _uploadInfo={_uploadInfo}
            /> 
          ))}
        </div>
      </>
    );
  }
}