import React, { useCallback, useState, useEffect } from 'react';
import { useDropzone } from 'react-dropzone';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import DeleteIcon from '@material-ui/icons/Delete';

import './index.scss';
import { IconButton } from '@material-ui/core';

const FileInput = (props: any) => {
  //console.log("fileData: " + props.fileData)
  // const [file, setFile] = useState();
  const [deleteAction, setDeleteAction] = useState(false);
  const onDrop = useCallback(
    acceptedFiles => {
      // Do something with the files
      // setFile(acceptedFiles[0]);
      props.onFileAdded(acceptedFiles[0]);
      setDeleteAction(false);
    },
    [props]
  );
  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    accept: props.accept ? props.accept : '.csv'
  });

  const handleDeleteFile = () => {
    // setFile(null);
    setDeleteAction(true);
  };

  useEffect(() => {
    return function cleanup() {
      // setFile(null);
      setDeleteAction(false);
    };
  }, []);

  return (
    <section className="dropzone-container">
      {!props.fileData || deleteAction ? (
        <div
          {...getRootProps({
            className: props.disabled ? 'dropzone disabled' : 'dropzone'
          })}
        >
          <input {...getInputProps()} />
          <p>Drop the file or click to select file.</p>
        </div>
      ) : (
        <div className="upload-file">
          <div className="upload-file-title">
            <AttachFileIcon />
            <span className="upload-name">{props.fileData.name}</span>
            <span className="upload-size">
              ({(props.fileData.size / 1000).toFixed(2)} KB)
            </span>
          </div>
          <IconButton disabled={props.disabled} onClick={handleDeleteFile}>
            <DeleteIcon color="primary" />
          </IconButton>
        </div>
      )}
    </section>
  );
};

export default FileInput;
