import { combineReducers } from 'redux';
import { flowReducer, FlowState } from './flow-reducer';
import { userReducer, UserState } from './user-reducer';
import { facetReducer, FacetState } from './facet-reducer';
import { connectRouter } from 'connected-react-router';

export interface State {
  flowReducerState: FlowState;
  userReducerState: UserState;
  facetReducerState: FacetState;
  router: any;
}

export const createRootReducer = (history: any) =>
  combineReducers<State>({
    flowReducerState: flowReducer,
    userReducerState: userReducer,
    facetReducerState: facetReducer,
    router: connectRouter(history)
  });
