import React, { Component, Fragment } from 'react';
import {
  Button,
  Grid,
  Box,
  TablePagination,
  Card,
  CardContent
} from '@material-ui/core';

import FileSaver from 'file-saver';

import SearchIcon from '@material-ui/icons/Search';
import ViewListIcon from '@material-ui/icons/ViewList';
import ViewComfyIcon from '@material-ui/icons/ViewComfy';
import AccountTreeOutlinedIcon from '@material-ui/icons/AccountTreeOutlined';
import FindInPageIcon from '@material-ui/icons/FindInPage';
import {
  getPipeline,
  createPipeline
} from '../../../services/app-data.service';
import {
  ImportFlowDialog,
  CloneFlowDialog
} from '../../../components/dialogs';
import SnackbarMsg from '../../../components/SnackbarMsg';
import TablePaginationActions from '@material-ui/core/TablePagination/TablePaginationActions';
import IkiButton from '../../../components/IkiFormElements/IkiButton';
import IkiButtonGroup from '../../../components/IkiFormElements/IkiButtonGroup';
import IkiInput from '../../../components/IkiFormElements/IkiInput';
import IkiGrid from '../../../components/IkiLayout/IkiGrid';
import IkiDialog from '../../../components/IkiDialog';
import IkiTable from '../../../components/IkiLayout/IkiTable';

type FlowsProps = {
  flows: any;
  creatingFlow: boolean;
  projects: any;
  projectId: string;
  subpageClick: (subpageLink: string) => void;
  addFlow: (flowObj: any) => void;
  deleteFlow: (flowObj: any) => void;
};

type FlowsState = {
  sort: string;
  deleteDialogState: boolean;
  deletePipeline: any;
  deletePipelineName: string;
  deletePipelineID: string;
  selectedDatasetData?: any;
  selectedDashData?: any;
  newFlowDialogState: boolean;
  newFlowName: string;
  newFlowNameError: boolean;
  newFlowNameErrorMsg: string;
  projectId: string;
  projectNameEditMode: Boolean;
  title: string;
  projectDeleteDialogState: boolean;
  projectShareDialogState: boolean;
  projectShareDialogErrorState: boolean;
  projectShareDialogErrorMsg: string;
  snackbarShareProSuccessState: boolean;
  editProjectDialogState: boolean;
  addDatasetDialogState: boolean;
  addDashboardtDialogState: boolean;
  addDatasetMinimize: boolean;
  editDatasetDialogState: boolean;
  editDashboardDialogState: boolean;
  deleteDatasetDialogState: boolean;
  uploadDatasetDialogState: boolean;
  duplicateDialogProjectState: boolean;
  importFlowDialogState: boolean;
  importFlowNameExists: boolean;
  tabValue: number;
  datasetEditMode: boolean;
  dashEditMode: boolean;
  snackbarOpen: boolean;
  snackbarMessage: any;
  snackbarType: string;
  sharingLoading: boolean;
  renamingLoading: boolean;
  deletingLoading: boolean;
  newlyAddedDatasetNameExists: boolean;
  newlyAddedDashNameExists: boolean;
  addFlowLoading: boolean;
  rowsPerPage: number;
  page: number;
  searchTerm: string;
  flowsFilter: string;
  renderAsList: boolean;
  tableSortAsc: boolean;
  tableSortProperty: string;
  toggleFilterDisplay: boolean;
  clickedBox: string;
  cloneFlowDialogState: boolean;
  cloneFlowId: string;
  cloneFlowName: string;
  cloneFlowNameError: boolean;
  cloneFlowNameErrorMsg: string;
  cloneProcess: boolean;
  sessionCreated: boolean;
  sessionID: string;
  showContent: boolean;
  loadingTxt: string;
};

class FlowsComponent extends Component<FlowsProps, FlowsState> {
  state: FlowsState = {
    sort: 'all',
    deletePipeline: null,
    deleteDialogState: false,
    deletePipelineName: '',
    deletePipelineID: '',
    newFlowDialogState: false,
    newFlowName: '',
    newFlowNameError: false,
    newFlowNameErrorMsg: '',
    projectId: '',
    projectNameEditMode: false,
    title: '',
    projectDeleteDialogState: false,
    projectShareDialogState: false,
    projectShareDialogErrorState: false,
    projectShareDialogErrorMsg: '',
    snackbarShareProSuccessState: false,
    editProjectDialogState: false,
    addDatasetDialogState: false,
    addDashboardtDialogState: false,
    deleteDatasetDialogState: false,
    uploadDatasetDialogState: false,
    editDatasetDialogState: false,
    editDashboardDialogState: false,
    duplicateDialogProjectState: false,
    tabValue: 0,
    datasetEditMode: false,
    addDatasetMinimize: false,
    dashEditMode: false,
    snackbarOpen: false,
    snackbarMessage: null,
    snackbarType: '',
    sharingLoading: false,
    renamingLoading: false,
    deletingLoading: false,
    newlyAddedDatasetNameExists: false,
    newlyAddedDashNameExists: false,
    addFlowLoading: false,
    rowsPerPage: 16,
    page: 0,
    searchTerm: '',
    flowsFilter: 'all',
    renderAsList: false,
    tableSortAsc: true,
    tableSortProperty: '',
    toggleFilterDisplay: false,
    clickedBox: '',
    importFlowDialogState: false,
    importFlowNameExists: false,
    cloneFlowDialogState: false,
    cloneFlowId: '',
    cloneFlowName: '',
    cloneFlowNameError: false,
    cloneFlowNameErrorMsg: '',
    cloneProcess: false,
    sessionCreated: false,
    sessionID: '',
    showContent: false,
    loadingTxt: ''
  };
  componentDidMount() {
    //console.log("this.props: ", this.props);
  }
  componentWillUnmount() {

  }

  onRequestSort = (event: any, property: string) => {
    this.setState({
      tableSortAsc: !this.state.tableSortAsc,
      tableSortProperty: property
    });
  };

  handleDialogkOpen = (flow: any) => {
    //console.log("handleDialogkOpen: ", flow);
    this.setState({
      deleteDialogState: true,
      deletePipeline: flow,
      deletePipelineName: flow._name,
      deletePipelineID: flow._id
    });
    //console.log("deleteDialogState: " + this.state.deleteDialogState)
  };

  handleOnDownloadClick(flowId: string, flowName: string) {
    //console.log("flowId: ", flowId + ", flowName: " + flowName);
    getPipeline(flowId).then(
      (pipelineData: any) => {
        var definition = pipelineData.pipeline.definition;
        if (definition.facets) {
          definition.facets.map((facet: any) => {
            if (
              facet.facet_type === 'IMPORTED' &&
              facet.facet_group === 'DATASET'
            ) {
              delete facet.arguments.bucket;
              Object.keys(facet.arguments).map((key: string) => {
                if (key === 'path' && facet.arguments[key]) {
                  facet.arguments[key] = '';
                }
                return ''
              });
            } else if (
              facet.facet_type === 'EXPORTED' &&
              facet.facet_group === 'DATASET'
            ) {
              delete facet.arguments.bucket;
              delete facet.arguments.project_id;
              delete facet.arguments.user_email;
              delete facet.arguments.dataset_id;
              delete facet.arguments.overwrite;
              // eslint-disable-next-line
              Object.keys(facet.arguments).map((key: string) => {
                if (
                  (key === 'dataset_name') &&
                  facet.arguments[key]
                ) {
                  facet.arguments[key] = '';
                }
              });
            } else if (facet.facet_type === 'S3') {
              // eslint-disable-next-line
              Object.keys(facet.arguments).map((key: string) => {
                if (
                  (key === 'bucket' ||
                    key === 'path' ||
                    key === 'aws_access_key_id' ||
                    key === 'aws_secret_access_key') &&
                  facet.arguments[key]
                ) {
                  facet.arguments[key] = '';
                }
              });
            }
            return facet;
          });
          var blb = new Blob([JSON.stringify(definition)], {
            type: 'text/json'
          });
          FileSaver.saveAs(blb, pipelineData.pipeline.name + '.iki');
          //console.log(pipelineData);
        } else {
          this.setState({
            snackbarMessage: (
              <Fragment>
                <div className="msg">This pipeline is empty</div>
              </Fragment>
            ),
            snackbarType: 'error',
            snackbarOpen: true
          });
        }
      }
    );
  }

  handleOnClone = (pipelineId: string, pipelineName: string) => {
    this.setState({
      cloneFlowDialogState: true,
      cloneFlowId: pipelineId,
      cloneFlowName: pipelineName
    });
    //console.log("deleteDialogState: " + this.state.deleteDialogState)
  };

  handleChangeSearchTerm = (e: any) => {
    this.setState({
      searchTerm: e.target.value
    });
  };

  handleRenderAsList = () => {
    this.setState({ renderAsList: true });
  };

  handleRenderAsGrid = () => {
    this.setState({ renderAsList: false });
  };

  handleChangePage = (event: unknown, newPage: number) => {
    this.setPage(newPage);
  };

  handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.setRowsPerPage(parseInt(event.target.value, 10));
    this.setPage(0);
  };

  setPage = (page: number) => {
    this.setState({ page: page });
  };

  setRowsPerPage = (rows: number) => {
    this.setState({ rowsPerPage: rows });
  };

  handleToggleFilter = () => {
    if (this.state.toggleFilterDisplay === true) {
      this.setState({ toggleFilterDisplay: false });
    } else {
      this.setState({ toggleFilterDisplay: true });
    }
  }

  onImportFlowClick() {
    this.setState({
      importFlowDialogState: true
    });
  }

  onNewFlowItemClick() {
    this.setState({
      newFlowDialogState: true
    });
    //this.props.addFlow();
  }

  handleImportFlowDialogCancel() {
    this.setState({
      importFlowDialogState: false,
      importFlowNameExists: false
    });
  }

  handleCloneFlowDialogCancel() {
    this.setState({
      cloneFlowDialogState: false,
      cloneFlowId: '',
      cloneFlowName: '',
      cloneFlowNameError: false,
      cloneFlowNameErrorMsg: ''
    });
  }

  handleImportFlowDialogSubmit(flowName: string, definition: any) {
    //var flowsArray = this.props.flows[this.props.projectId];
    //console.log("flowsArray: ", flowsArray);
    var checkFlowNameOverlap = false;
    // eslint-disable-next-line  
    this.props.flows.map((flowData: any) => {
      //console.log("FLOW - NAME: " + flowName + ", name: " + flowData.pipeline.name);
      if (flowData.name === flowName) {
        //console.log("HITHITHIT!!! - NAME: " + flowName + ", name: " + flowData.pipeline.name);
        checkFlowNameOverlap = true;
      }
      return ''
    });
    if (checkFlowNameOverlap !== false) {
      this.setState({
        importFlowNameExists: true
      });
    } else {
      this.setState({
        importFlowNameExists: false
      });
      /*this.props.addFlow({
        name: flowName,
        projectId: this.props.projectId,
        definition: definition
      });*/
      var name = flowName;
      var projectId = this.props.projectId;
      const flowObj = { name, projectId, definition }
      createPipeline(flowObj).then((pipelineIdByResponse: any) => {
        //this.props.history.push(`/flow/${pipelineIdByResponse}`);
        this.props.subpageClick(`/flow/${pipelineIdByResponse}`)
      });
    }
  }

  handleCloneFlowDialogSubmit(flowName: string, originalFlowId: string, targetProjectId: string, cloneType: string) {
    //var flowsArray = this.props.flows[this.props.projectId];
    var checkFlowNameOverlap = false;
    // eslint-disable-next-line
    this.props.flows.forEach((flowData: any) => {
        //console.log("FLOW - NAME: " + flowName + ", name: " + flowData.pipeline.name);
        if (flowData.name === flowName) {
          //console.log("HITHITHIT!!! - NAME: " + flowName + ", name: " + flowData.pipeline.name);
          checkFlowNameOverlap = true;
        }
    });
    if (flowName === '') {
      this.setState({
        cloneFlowNameError: true,
        cloneFlowNameErrorMsg: 'Flow name required',
        cloneProcess: false
      });
    } else if (checkFlowNameOverlap !== false) {
      this.setState({
        cloneFlowNameError: true,
        cloneFlowNameErrorMsg:
          'Flow with same name already exists. Please choose different name',
        cloneProcess: false
      });
    } else {
      this.setState({
        cloneFlowNameError: false,
        cloneFlowNameErrorMsg: '',
        addFlowLoading: true
      });
      getPipeline(originalFlowId).then(
        (pipelineData: any) => {
          //console.log("pipelineData: ");
          //console.log(pipelineData);
          var definition = pipelineData.pipeline.definition;
          //console.log(definition);
          // var schedule = pipelineData.pipeline.schedule;
          if (definition.facets) {
            definition.facets.map((facet: any) => {
              if (cloneType !== 'with-data') {
                if (
                  facet.facet_type === 'IMPORTED' &&
                  facet.facet_group === 'DATASET'
                ) {
                  delete facet.arguments.bucket;
                  Object.keys(facet.arguments).map((key: string) => {
                    if (key === 'path' && facet.arguments[key]) {
                      facet.arguments[key] = '';
                    }
                    return ''
                  });
                } else if (
                  facet.facet_type === 'EXPORTED' &&
                  facet.facet_group === 'DATASET'
                ) {
                  delete facet.arguments.bucket;
                  delete facet.arguments.project_id;
                  delete facet.arguments.user_email;
                  delete facet.arguments.dataset_id;
                  delete facet.arguments.overwrite;
                  Object.keys(facet.arguments).map((key: string) => {
                    if (
                      (key === 'dataset_name') &&
                      facet.arguments[key]
                    ) {
                      facet.arguments[key] = '';
                    }
                    return ''
                  });
                } else if (facet.facet_type === 'S3') {
                  Object.keys(facet.arguments).map((key: string) => {
                    if (
                      (key === 'bucket' ||
                        key === 'path' ||
                        key === 'aws_access_key_id' ||
                        key === 'aws_secret_access_key') &&
                      facet.arguments[key]
                    ) {
                      facet.arguments[key] = '';
                    }
                    return ''
                  });
                }
              }
              return facet;
            });
          }
          /*this.props.getFlows(targetProjectId);
          this.props.addFlow({
            name: flowName,
            projectId: targetProjectId,
            definition: definition
          });*/
          var name = flowName;
          var projectId = targetProjectId;
          const flowObj = { name, projectId, definition }
          createPipeline(flowObj).then((pipelineIdByResponse: any) => {
            //this.props.history.push(`/flow/${pipelineIdByResponse}`);
            this.props.subpageClick(`/flow/${pipelineIdByResponse}`)
          });
        }
      );
    }
  }

  getFlows(flows: any) {
    //console.log("flows: ", flows, ", flows.length: ", flows.length)
    //var allFlows = flows.length;
    flows = this.sortFlows(this.filterFlows(flows));
    var pageFlows = flows.slice(
        this.state.page * this.state.rowsPerPage,
        this.state.page * this.state.rowsPerPage + this.state.rowsPerPage
    );
    if (flows && flows.length === 0) {
      return (
        <Fragment>
          {this.getFlowsFilters(flows)}
          <Grid item xs={12}>
            <Box display="flex" alignItems="center" className="no-results">
              <FindInPageIcon className="no-results-icon" />
              <span className="no-results-txt">No flows found</span>
            </Box>
          </Grid>
        </Fragment>
      );
    }
    return(
      <Fragment>
        {this.getFlowsFilters(flows)}
        {this.state.renderAsList ? (
          <Grid item xs={12} className="i-pb-0">
            <Card className="cardbox has-table tableHolder">
              <CardContent className="content table-inside">
                <IkiTable
                      _type="Flow"
                      _link='/flow/'
                      _header={
                        [
                          {
                            key: 'name',
                            name: 'Flow'
                          },
                          {
                            key: 'pipeline_id',
                            name: 'ID'
                          }
                        ]
                      }
                      _data={pageFlows}
                      _functions={
                        [
                          {
                            edit: (itemId: string) => this.props.subpageClick(`/flow/${itemId}`)
                          },
                          {
                            delete: (item: any) => this.handleDialogkOpen(item)
                          },
                          {
                            download: (itemId: string, itemName: string) => this.handleOnDownloadClick(itemId, itemName)
                          },
                          {
                            clone: (itemId: string, itemName: string) => this.handleOnClone(itemId, itemName)
                          }
                        ]
                      }
                  />
              </CardContent>
            </Card>
          </Grid>
        ):(
          <IkiGrid
            _type='Flow'
            _link='/flow/'
            _icon={<AccountTreeOutlinedIcon />}
            _data={pageFlows}
            _functions={
              [
                {
                  edit: (itemId: string) => this.props.subpageClick(`/flow/${itemId}`)
                },
                {
                  delete: (item: any) => this.handleDialogkOpen(item)
                },
                {
                  download: (itemId: string, itemName: string) => this.handleOnDownloadClick(itemId, itemName)
                },
                {
                  clone: (itemId: string, itemName: string) => this.handleOnClone(itemId, itemName)
                }
              ]
            }
          />
        )}
        {flows.length > this.state.rowsPerPage && (
          <TablePagination
                  component="div"
                  className="pagination-sm"
                  labelRowsPerPage="Flows per page"
                  rowsPerPageOptions={[8, 16, 32]}
                  colSpan={3}
                  count={flows.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  SelectProps={{
                    inputProps: {
                      'aria-label': 'Flows per page'
                    }
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
          />
        )}
      </Fragment>
    );
  }

  sortFlows = (flows: any) => {
    //console.log("SORT PROJECTS: ");
    //console.log(flows);
    if (this.state.tableSortProperty !== '') {
      return this.sortOn(flows, this.state.tableSortProperty);
    } else {
      return flows;
    }
  };

  sortOn = (arr: any, property: string) => {
    //console.log("ARR: ");
    //console.log(arr);
    var sortedArray = arr.sort((a: any, b: any) => {
      if (b[property] < a[property]) {
        return this.state.tableSortAsc ? 1 : -1;
      }
      if (b[property] > a[property]) {
        return this.state.tableSortAsc ? -1 : 1;
      }
      return 0;
    });
    return sortedArray;
  };

  filterFlows = (flows: any) => {
    //console.log("import p: ");
    //console.log(flows);
    if (this.state.searchTerm !== '') {
      flows = flows.filter(
        (p: any) =>
          p.name
            .toLowerCase()
            .indexOf(this.state.searchTerm.toLowerCase()) > -1
      );
    }
    return flows;
  };

  viewTypeSwitchButtons = () => {
    const buttons = [
      <Button
        key={1}
        color={this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsList()}
      >
        <ViewListIcon />
      </Button>,
      <Button
        key={2}
        color={!this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsGrid()}
      >
        <ViewComfyIcon />
      </Button>
    ]
    return buttons
  }

  getFlowsFilters = (flows: any) => {
    if(flows) {
      return (
        <div className="filters-and-pagination">
          <div className="table-filters">
                <IkiInput 
                  _label=""
                  _placeholder="Search..."
                  _type='text'
                  _autoComplete="new-password"
                  _value={this.state.searchTerm}
                  _size="small"
                  _margin="none"
                  _filterType={true}
                  _onChange={(e: any) => this.handleChangeSearchTerm(e)}
                  _startIcon={
                    {
                      _icon: (
                        <SearchIcon fontSize="small" />
                      )
                    }
                  }
                />
                <IkiButtonGroup
                  _variant="contained"
                  _size="small"
                  _color="secondary"
                  _buttons={
                    this.viewTypeSwitchButtons()
                  }
                />
              </div>
              <div className="table-pagination">
                <TablePagination
                  component="div"
                  className="flows-pagination"
                  labelRowsPerPage="Flows per page"
                  rowsPerPageOptions={[8, 16, 32]}
                  colSpan={3}
                  count={flows.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  SelectProps={{
                    inputProps: {
                      'aria-label': 'Flows per page'
                    }
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
                />
              </div>
          </div>
      );
    }
  };

  render() {
    var flows: any = [];
    if(this.props.flows){
      flows = this.props.flows;
    }
      //var flows = this.props.flows;
      //var allFlows = flows.length;
      //flows = this.sortFlows(this.filterFlows(flows));
      //var pageFlows = flows.slice(
      //  this.state.page * this.state.rowsPerPage,
      //  this.state.page * this.state.rowsPerPage + this.state.rowsPerPage
      //);
      return (
        <Fragment>
              <div className="under-tab-buttons">
                      <IkiButton
                        _variant='contained'
                        _color='primary'
                        _label="Add New Flow"
                        _type='button'
                        _fullWidth={false}
                        _loading={false}
                        _startIcon={
                          <i className="zmdi zmdi-plus zmdi-hc-fw"></i>
                        }
                        _onClick={() => this.onNewFlowItemClick()}
                      />
                      <IkiButton
                        _variant='contained'
                        _color='primary'
                        _label="Import a Flow"
                        _type='button'
                        _fullWidth={false}
                        _loading={false}
                        _startIcon={
                          <i className="zmdi zmdi-upload zmdi-hc-fw"></i>
                        }
                        _onClick={() => this.onImportFlowClick()}
                      />
                    {/*this.props.data.flowsLoaded && 
                      this.getFlowsFilters(flows)
                    */}
              </div>
              <div className="cardbox-holder">
                  {this.getFlows(flows)}                  
              </div>
              {this.newFlowDialog()}
              {this.deleteFlowDialog()}
              <ImportFlowDialog
                isImportingProgress={this.props.creatingFlow}
                open={this.state.importFlowDialogState}
                handleCancel={() => this.handleImportFlowDialogCancel()}
                importFlowNameExists={this.state.importFlowNameExists}
                handleSubmit={(flowName: string, definition: any) => this.handleImportFlowDialogSubmit(flowName, definition)}
              ></ImportFlowDialog>
              <CloneFlowDialog
                isCloningProgress={this.state.cloneProcess}
                cloneFlowId={this.state.cloneFlowId}
                cloneFlowName={this.state.cloneFlowName}
                cloneFlowError={this.state.cloneFlowNameErrorMsg}
                projectsList={this.props.projects}
                projectId={this.props.projectId}
                open={this.state.cloneFlowDialogState}
                handleCancel={() => this.handleCloneFlowDialogCancel()}
                handleSubmit={(flowName: string, originalFlowId: string, targetProjectId: string, cloneType: string) => {
                  this.setState({ cloneProcess: true });
                  this.handleCloneFlowDialogSubmit(flowName, originalFlowId, targetProjectId, cloneType)
                }}
              ></CloneFlowDialog>
              <SnackbarMsg
                snackbarOpen={this.state.snackbarOpen}
                snackbarType={this.state.snackbarType}
                snackbarMsg={this.state.snackbarMessage}
                onSnackbarClose={this.handleSnackbarClose}
              />
        </Fragment>
      );
  }

  newFlowDialog() {
    return (
      <IkiDialog
            _open={this.state.newFlowDialogState}
            _onClose={this.handleFlowDialogClose}
            _type='regular'
            _icon={false}
            _title='New flow'
            _subtitle=''
            _size='xs'
            _content={
                <IkiInput 
                  _label="Flow name"
                  _placeholder="Enter flow name"
                  _type='text'
                  _value={this.state.newFlowName}
                  _onChange={(e: any) => {
                    this.setState({
                      newFlowName: e.target.value
                    });
                  }}
                  _characterCounter={40}
                  _autoFocus={true}
                  _fullWidth={true}
                  _error={this.state.newFlowNameErrorMsg}
                />
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={this.handleFlowDialogClose}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Create"
                  _type='button'
                  _fullWidth={false}
                  _loading={this.state.addFlowLoading}
                  _disabled={this.state.addFlowLoading}
                  _isAlternate={true}
                  _onClick={this.handleNewFlowDialogSave}
                />
              </Fragment>
            }
      />
    );
  }

  deleteFlowDialog() {
    return (
      <IkiDialog
            _open={this.state.deleteDialogState}
            _onClose={this.handleDeleteDialogClose}
            _type='delete'
            _icon={false}
            _title='Delete pipeline'
            _subtitle=''
            _size='xs'
            _align='center'
            _content={
              <>
                <div className="section">
                  <div className="txt-dialog-faded1">{this.state.deletePipelineID}</div>
                  <div className="txt-dialog-impact1">{this.state.deletePipelineName}</div>
                </div>
                <div className="section">
                  <div className="txt-dialog-impact2 error">Are you sure ?</div>
                </div>
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={this.handleDeleteDialogClose}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Delete"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _disabled={false}
                  _isDanger={true}
                  _onClick={() => this.onDelete(this.state.deletePipeline)}
                />
              </Fragment>
            }
          />
    );
  }

  onDelete(flowObj: any) {
    this.props.deleteFlow(flowObj);
    this.setState({
      deleteDialogState: false,
      deletePipeline: '',
      snackbarMessage: (
        <Fragment>
          <div className="msg">You have succesfully deleted pipeline</div>
          <div className="val">{this.state.deletePipelineName}</div>
        </Fragment>
      ),
      snackbarType: 'success',
      snackbarOpen: true
    });
  }

  handleFlowDialogClose = () => {
    this.setState({
      newFlowDialogState: false,
      newFlowName: '',
      newFlowNameError: false,
      addFlowLoading: false
    });
  };

  handleDeleteDialogClose = () => {
    this.setState({
      deleteDialogState: false,
      deletePipeline: null,
      deletePipelineName: '',
      deletePipelineID: ''
    });
    //console.log("deleteDialogState: " + this.state.deleteDialogState)
  };

  handleNewFlowDialogSave = () => {
    if (this.props.creatingFlow) return;
    //this.subpageClicked = true;
    var flowName = this.state.newFlowName;
    //var flowsArray = this.props.data.flows[this.props.projectId];
    var checkFlowNameOverlap = false;
    // eslint-disable-next-line
    this.props.flows.map((flowData: any) => {
        //console.log("FLOW - NAME: " + flowName + ", name: " + flowData.pipeline.name);
        if (flowData.name === flowName) {
          //console.log("HITHITHIT!!! - NAME: " + flowName + ", name: " + flowData.pipeline.name);
          checkFlowNameOverlap = true;
        }
        return ''
    });
    if (flowName === '') {
      this.setState({
        newFlowNameError: true,
        newFlowNameErrorMsg: 'Flow name required'
      });
    } else if (checkFlowNameOverlap !== false) {
      this.setState({
        newFlowNameError: true,
        newFlowNameErrorMsg:
          'Flow with same name already exists. Please choose different name'
      });
    } else {
      this.setState({
        newFlowNameError: false,
        newFlowNameErrorMsg: '',
        addFlowLoading: true
      });
      setTimeout(() => {
        var name = flowName;
        var projectId = this.props.projectId;
        var definition = {};
        const flowObj = { name, projectId, definition }
        createPipeline(flowObj).then((pipelineIdByResponse: any) => {
          //this.props.history.push(`/flow/${pipelineIdByResponse}`);
          this.props.subpageClick(`/flow/${pipelineIdByResponse}`)
        });
        /*this.props.addFlow({
          name: flowName,
          projectId: this.props.projectId
        })*/
      }, 2000);
    }
  };

  handleSnackbarClose = () => {
    this.setState({ snackbarOpen: false });
  };
  
}

export default FlowsComponent;
