import React, { useState } from 'react';
import {
  Dialog,
  DialogContent,
  Box,
  DialogActions,
  Button,
  TextField,
  FormHelperText
} from '@material-ui/core';

import ErrorIcon from '@material-ui/icons/Error';

export function EditDashboardDialog(props: any) {
  const {
    dashboardId,
    handleDialogClose,
    handleSubmit,
    handleCancel,
    open
  } = props;

  const [dashboardName, setDashboardName] = useState('');
  const [isValidated, setIsvalidated] = useState(false);
  return (
    <Dialog
      onClose={handleDialogClose}
      aria-labelledby="customized-dialog-title"
      open={open}
      className="dialog"
      fullWidth={true}
      maxWidth="xs"
      disableBackdropClick={true}
    >
      <DialogContent className="dialog-content">
        <div>
          <Box className="dialog-title default-color" mb={2}>
            Edit dashboard
          </Box>
          <div>
            <div className="project-id">{dashboardId}</div>
            <div className="project-name">{props.dashboardName}</div>
          </div>
          <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            required
            label="Dashboard name"
            placeholder="Enter new name for Dashboard"
            type="search"
            autoComplete="new-username"
            value={dashboardName}
            onChange={(e: any) => {
              setDashboardName(e.target.value);
            }}
            error={isValidated && dashboardName === ''}
            autoFocus
            InputLabelProps={{ shrink: true }}
          />
          {isValidated && dashboardName === '' && (
            <FormHelperText className="input-error" error>
              <ErrorIcon />
              <span>Dashboard name required</span>
            </FormHelperText>
          )}
        </div>
      </DialogContent>
      <DialogActions className="footer">
        <Button
          autoFocus
          onClick={() => handleCancel()}
          color="default"
          className="button"
        >
          Cancel
        </Button>
        <Button
          variant="contained"
          color="primary"
          className="button"
          onClick={() => {
            if (dashboardName !== '') {
              handleSubmit(dashboardName);
              setIsvalidated(false);
            } else {
              setIsvalidated(true);
            }
          }}
        >
          Update
        </Button>
      </DialogActions>
    </Dialog>
  );
}
