import React, { Fragment } from 'react';
import {
  IconButton, Popover, LinearProgress, Button
} from '@material-ui/core';
import { SupportPopover } from './support-popover';
import PhotoCameraIcon from '@material-ui/icons/PhotoCamera';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import PublishIcon from "@material-ui/icons/Publish";
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import Badge from '@material-ui/core/Badge';
import DeleteIcon from '@material-ui/icons/Delete';
import ReplayIcon from '@material-ui/icons/Replay';
import CloseIcon from '@material-ui/icons/Close';
// new styling concept needs to be applied to this component (modular styling)
import './nav-module.scss';

export function ChatNavModule(props: any) {

  const {
    activeUserEmail,
    uploadProgress
  } = props;

  //console.log("uploadProgress: ", uploadProgress);

  const [anchorSupportPopover, setSupportPopoverAnchorEl] = React.useState<HTMLButtonElement | null>(null);
  const [uploadDetailsPopover, setUploadDetailsPopover] = React.useState<HTMLButtonElement | null>(null);
  const openSupportPopover = Boolean(anchorSupportPopover);
  const openUploadDetailsPopover = Boolean(uploadDetailsPopover);

  const handleSupportPopoverOpen = (event: React.MouseEvent<HTMLButtonElement>) => {
    setSupportPopoverAnchorEl(event.currentTarget);
  };

  const handleUploadDetailsPopoverOpen = (event: React.MouseEvent<HTMLButtonElement>) => {
    setUploadDetailsPopover(event.currentTarget);
    /*if(uploadDetailsPopover!=null){
      setUploadDetailsPopover(null);
    }else{
      setUploadDetailsPopover(event.currentTarget);
    }*/
  };

  const handleUploadDetailsPopoverClose = (e: React.MouseEvent<HTMLButtonElement>) => {
    setUploadDetailsPopover(null);
  };

  return (
    <Fragment>
        <ul className="bottom-menu">
            <li className="menu">
              <div className="link-alike upload-menu-item" onClick={(event: any) => handleUploadDetailsPopoverOpen(event)}>
                {uploadProgress ? (
                  (uploadProgress.filter((data:any)=>{
                    return data.status !== 'complete' && data.status !== 'deleted'}
                  ).length > 0) ?
                  uploadDetailsPopover ? (
                        <Badge badgeContent={uploadProgress.filter((data:any) => {
                          return data.status === 'failed'}
                          ).length > 0 ? '!' : uploadProgress.filter((data:any)=>{ 
                            return data.status !== 'complete' && data.status !== 'deleted'}
                            ).length} color="secondary"
                        >
                          <i className="zmdi zmdi-cloud-upload zmdi-hc-fw" />
                          <span className="nav-text">Uploading</span>
                        </Badge>
                  ) : (
                      <Badge badgeContent={uploadProgress.filter((data:any)=>{
                        return data.status === 'failed'}
                        ).length > 0 ? '!' : uploadProgress.filter((data:any)=>{ 
                          return data.status !== 'complete' && data.status !== 'deleted'
                        }).length} color="secondary"
                      >
                        <i className="zmdi zmdi-cloud-upload zmdi-hc-fw" />
                        <span className="nav-text">Uploading</span>
                      </Badge>
                  ) : (
                    ''
                  )
                ) : (
                  ''
                )}
              </div>
            </li>
            <li className="menu">
              <div className="link-alike" onClick={(event: any) => handleSupportPopoverOpen(event)}>
                <i className="zmdi zmdi-camera zmdi-hc-fw" />
                <span className="nav-text">Screenshot</span>
              </div>
            </li>
            {/* <li className="menu">
              <div className="link-alike">
                <i className="zmdi zmdi-comments zmdi-hc-fw" />
                <span className="nav-text">Chat</span>
              </div>
            </li> */}
          </ul>
        {/* {anchorSupportPopover ?
          <IconButton className="btn-support is-active" onClick={handleSupportPopoverOpen}>
            <PhotoCameraIcon />
          </IconButton>
        :
          <IconButton className="btn-support" onClick={handleSupportPopoverOpen}>
            <PhotoCameraIcon />
          </IconButton>
        }
        <IconButton className="btn-chat">
          <QuestionAnswerIcon />
        </IconButton>
        <IconButton className="btn-upload" onClick={handleUploadDetailsPopoverOpen}>
          <CloudUploadIcon />
        </IconButton> */}
        {/* {uploadProgress ? (
          (uploadProgress.filter((data:any)=>{
            return data.status !== 'complete' && data.status !== 'deleted'}
          ).length > 0) ?
          uploadDetailsPopover ? (
              <IconButton className="btn-upload is-active" onClick={handleUploadDetailsPopoverOpen}>
                <Badge badgeContent={uploadProgress.filter((data:any) => {
                  return data.status === 'failed'}
                  ).length > 0 ? '!' : uploadProgress.filter((data:any)=>{ 
                    return data.status !== 'complete' && data.status !== 'deleted'}
                    ).length} color="secondary"
                >
                  <PublishIcon />
                </Badge>
              </IconButton>
          ):(
            <IconButton className="btn-upload" onClick={handleUploadDetailsPopoverOpen}>
              <Badge badgeContent={uploadProgress.filter((data:any)=>{
                return data.status === 'failed'}
                ).length > 0 ? '!' : uploadProgress.filter((data:any)=>{ 
                  return data.status !== 'complete' && data.status !== 'deleted'
                }).length} color="secondary"
              >
                <PublishIcon />
              </Badge>
            </IconButton>
            ): ''
          ):(
            <IconButton className="btn-upload" onClick={handleUploadDetailsPopoverOpen}>
              <PublishIcon />
            </IconButton>
          )
        } */}
      <SupportPopover 
        handleSupportPopoverOpen={handleSupportPopoverOpen} 
        setSupportPopoverAnchorEl={setSupportPopoverAnchorEl} 
        anchorSupportPopover={anchorSupportPopover}
        openSupportPopover={openSupportPopover}
        loggedUserEmail={activeUserEmail}
      />
      {uploadProgress &&
        uploadProgress.filter((data:any)=>{return data.status !== 'complete' && data.status !== 'deleted'}).length > 0 &&
          <Popover
            className="support-popover"
            open={openUploadDetailsPopover}
            anchorEl={uploadDetailsPopover}
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
            }}
          >
              <div className="support-popover-content">
                  <div className="heading">
                    <span className="title">Uploading</span>
                    <IconButton id="closeSupportPopoverBtn" className="is-right" size="small" onClick={handleUploadDetailsPopoverClose}>
                      <CloseIcon fontSize="small" />
                    </IconButton>
                  </div>
                    {uploadProgress.filter((data:any)=>{return data.status !== 'complete' && data.status !=='deleted'}).sort().map((data:any)=>(
                        <div className="step">
                            <div>
                              <div className="step-title">
                                {data.datasetName}
                              </div>
                              <div className="clip-txt step-descr" style={{width:"100%"}}>
                                {data.status === 'uploading' ? (
                                  <LinearProgress variant="determinate" color="primary" value={data.percentCompleted} />
                                  ) : data.status === 'Processing' ? (
                                  <LinearProgress variant="determinate" color="primary" value={data.percentCompleted} />
                                  ) : (
                                  <Button onClick={()=>{data.onRetry()}}><ReplayIcon/>{"Retry"}</Button>
                                  )} 
                                {data.status==='preparing' ? (
                                  <LinearProgress variant="determinate" color="primary" value={data.percentCompleted} />
                                  ) : data.status === 'uploading' ? (
                                  <LinearProgress variant="determinate" color="primary" value={data.percentCompleted} />
                                  ) : data.status === 'Processing' ? (
                                  <LinearProgress variant="determinate" color="primary" value={data.percentCompleted} />
                                  ) : ('')} 
                                {data.status === 'failed' ? (
                                  <Button onClick={() => { data.onCancel() }}>
                                    <DeleteIcon/>{"Cancel"}
                                  </Button>
                                  ) : ("")} 
                              </div>
                              {data.status==='preparing' ? (
                                <span>Preparing file...</span>
                                ) : data.status==='uploading'? (
                                  <span> {data.percentCompleted.toFixed(2)} %</span>
                                ) : data.status==='Processing'? (
                                  <span>Processing file...</span>
                                ) : (
                                <span> {data.percentCompleted.toFixed(2)} %</span>
                                )}
                          </div>
                        </div>
                      ))}
              </div>
              <div className="progress-back-frame" onClick={()=>setUploadDetailsPopover(null)}>

              </div>
          </Popover>
      }
     <SupportPopover       
        loggedUserEmail={activeUserEmail}
      />
    </Fragment>
  );
}
