import React, { Component } from 'react';
import { 
  Button
} from '@material-ui/core';
import classNames from 'classnames/bind';
import styles from './Button.module.scss';

let cx = classNames.bind(styles);

/**
   * Form Element -> Button Component based on MaterialUI but customized for Ikigai
   * @param _label Label - string
   * @param _variant Variant - string - e.g contained | outlined
   * @param _color Color - string - e.g primary | secondary
   * @param _type Button type - string - e.g submit | button
   * @param _error Error message - string
   * @param _disabled Is Button disabeld - boolean - true | false
   * @param _fullWidth Is Button Full Width - boolean - true | false
   * @param _loading Is Loading - boolean - true | false
   * @param _startIcon MaterialUI Icon - component e.g. <Visibility />
   * @param _endIcon MaterialUI Icon - component e.g. <Visibility />
   */
export default class IkiButton extends Component {
  render() {
    const { 
      _label, 
      _variant, 
      _type, 
      _color,
      _disabled, 
      _fullWidth,
      _loading
    } = this.props;
    //const _endIcon = this.props._endIcon ? this.props._endIcon : '';
    const _startIcon = this.props._startIcon ? this.props._startIcon : '';
    const _onClick = this.props._onClick ? this.props._onClick : null;
    const _isDanger = this.props._isDanger ? this.props._isDanger : false;
    const _isWarning = this.props._isWarning ? this.props._isWarning : false;
    //const _isAlternate = this.props._isAlternate ? this.props._isAlternate : false;
    const _align = this.props._align ? this.props._align : 'center';
    //console.log("IkiButton props: ", this.props, '_inputProps: ', _inputProp);
    let color = '';
    if(_color === 'warning') {
      color = 'inherit';
    }else{
      color = _color;
    }
    var btnClass = cx({
      button: true,
      'left': _align === 'left',
      'warning': _isWarning
    });
    var btnSpinnerClass = cx({
      btnSpinner: true,
      'on-error': _isDanger,
      'alternate': _disabled
    });
    return(
      <>
        <Button
          type={_type}
          variant={_variant}
          color={color}
          fullWidth={_fullWidth}
          disabled={_disabled}
          className={btnClass}
          //startIcon={_startIcon}
          //endIcon={_endIcon}
          onClick={
            _onClick !== null ? (
              this.props._onClick
            ):(
              () => {}
            )
          }
        >
          {_loading ? ( 
            <span className={btnSpinnerClass}></span>
          ):(
            _startIcon !== '' && (
              _startIcon
            )
          )}
          <span>{_label}</span>
        </Button>
      </>
    );
  }
}