import React from 'react';
import {
  Dialog,
  DialogContent,
  Box,
  DialogActions,
  Button
} from '@material-ui/core';

export function CreateSessionDialog(props: any) {
  const { handleCreate, open } = props;

  return (
    <Dialog
      aria-labelledby="customized-dialog-title"
      open={open}
      className="dialog"
      fullWidth={true}
      maxWidth="xs"
      disableBackdropClick={true}
    >
      <DialogContent className="dialog-content">
        <div>
          <Box className="dialog-title default-color" mb={2}>
            Session Expired. Create A New Session.
          </Box>
        </div>
      </DialogContent>
      <DialogActions className="footer">
        <Button
          autoFocus
          onClick={() => handleCreate()}
          color="primary"
          className="button"
        >
          Create
        </Button>
      </DialogActions>
    </Dialog>
  );
}
