import React, { Component, Fragment } from 'react';
import {
  Button,
  Grid,
  Box,
  TablePagination,
  Card,
  CardContent
} from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import ViewListIcon from '@material-ui/icons/ViewList';
import ViewComfyIcon from '@material-ui/icons/ViewComfy';
import FindInPageIcon from '@material-ui/icons/FindInPage';
import BarChartIcon from '@material-ui/icons/BarChart';
import SnackbarMsg from '../../../components/SnackbarMsg';
import TablePaginationActions from '@material-ui/core/TablePagination/TablePaginationActions';
import { AddDashboard } from '../../../components/dialogs/add-dashboard';
import { EditDashboardDialog } from '../../../components/dialogs/edit-dashboard';
import IkiButton from '../../../components/IkiFormElements/IkiButton';
import IkiButtonGroup from '../../../components/IkiFormElements/IkiButtonGroup';
import IkiInput from '../../../components/IkiFormElements/IkiInput';
import IkiGrid from '../../../components/IkiLayout/IkiGrid';
import IkiTable from '../../../components/IkiLayout/IkiTable';

type FlowsProps = {
  dashboards: any;
  datasets: any;
  projects: any;
  projectId: string;
  subpageClick: (subpageLink: string) => void;
  createDashboard: (projectId: string, datasetId: any, name: string, file: File) => void;
  editDashboard: (projectId: string, datasetId: any, name: string, dashboardId: string, file: File) => void;
  deleteDashboard: (dashboardId: string) => void;
  uploadProgress: any;
  dashUploadStatus: any;
};

type FlowsState = {
  sort: string;
  d_sort: string,
  deleteDialogState: boolean;
  deletePipeline: any;
  deletePipelineName: string;
  deletePipelineID: string;
  selectedDatasetData?: any;
  selectedDashData?: any;
  newFlowDialogState: boolean;
  newFlowName: string;
  newFlowNameError: boolean;
  newFlowNameErrorMsg: string;
  projectId: string;
  projectNameEditMode: Boolean;
  title: string;
  projectDeleteDialogState: boolean;
  projectShareDialogState: boolean;
  projectShareDialogErrorState: boolean;
  projectShareDialogErrorMsg: string;
  snackbarShareProSuccessState: boolean;
  editProjectDialogState: boolean;
  addDatasetDialogState: boolean;
  addDashboardtDialogState: boolean;
  addDatasetMinimize: boolean;
  editDatasetDialogState: boolean;
  editDashboardDialogState: boolean;
  deleteDatasetDialogState: boolean;
  uploadDatasetDialogState: boolean;
  duplicateDialogProjectState: boolean;
  importFlowDialogState: boolean;
  importFlowNameExists: boolean;
  tabValue: number;
  datasetEditMode: boolean;
  dashEditMode: boolean;
  snackbarOpen: boolean;
  snackbarMessage: any;
  snackbarType: string;
  sharingLoading: boolean;
  renamingLoading: boolean;
  deletingLoading: boolean;
  newlyAddedDatasetNameExists: boolean;
  newlyAddedDashNameExists: boolean;
  addFlowLoading: boolean;
  rowsPerPage: number;
  page: number;
  searchTerm: string;
  flowsFilter: string;
  renderAsList: boolean;
  tableSortAsc: boolean;
  tableSortProperty: string;
  toggleFilterDisplay: boolean;
  clickedBox: string;
  dash_rowsPerPage: number;
  dash_page: number;
  dash_searchTerm: string;
  dash_flowsFilter: string;
  dash_renderAsList: boolean;
  dash_tableSortAsc: boolean;
  dash_tableSortProperty: string;
  dash_toggleFilterDisplay: boolean;
  dash_clickedBox: string;
  cloneFlowDialogState: boolean;
  cloneFlowId: string;
  cloneFlowName: string;
  cloneFlowNameError: boolean;
  cloneFlowNameErrorMsg: string;
  cloneProcess: boolean;
  sessionCreated: boolean;
  sessionID: string;
  showContent: boolean;
  loadingTxt: string;
};

class DashboardsComponent extends Component<FlowsProps, FlowsState> {
  state: FlowsState = {
    sort: 'all',
    d_sort: 'all',
    deletePipeline: null,
    deleteDialogState: false,
    deletePipelineName: '',
    deletePipelineID: '',
    newFlowDialogState: false,
    newFlowName: '',
    newFlowNameError: false,
    newFlowNameErrorMsg: '',
    projectId: '',
    projectNameEditMode: false,
    title: '',
    projectDeleteDialogState: false,
    projectShareDialogState: false,
    projectShareDialogErrorState: false,
    projectShareDialogErrorMsg: '',
    snackbarShareProSuccessState: false,
    editProjectDialogState: false,
    addDatasetDialogState: false,
    addDashboardtDialogState: false,
    deleteDatasetDialogState: false,
    uploadDatasetDialogState: false,
    editDatasetDialogState: false,
    editDashboardDialogState: false,
    duplicateDialogProjectState: false,
    tabValue: 0,
    datasetEditMode: false,
    addDatasetMinimize: false,
    dashEditMode: false,
    snackbarOpen: false,
    snackbarMessage: null,
    snackbarType: '',
    sharingLoading: false,
    renamingLoading: false,
    deletingLoading: false,
    newlyAddedDatasetNameExists: false,
    newlyAddedDashNameExists: false,
    addFlowLoading: false,
    rowsPerPage: 16,
    page: 0,
    searchTerm: '',
    flowsFilter: 'all',
    renderAsList: false,
    tableSortAsc: true,
    tableSortProperty: '',
    toggleFilterDisplay: false,
    clickedBox: '',
    dash_rowsPerPage: 16,
    dash_page: 0,
    dash_searchTerm: '',
    dash_flowsFilter: 'all',
    dash_renderAsList: false,
    dash_tableSortAsc: true,
    dash_tableSortProperty: '',
    dash_toggleFilterDisplay: false,
    dash_clickedBox: '',
    importFlowDialogState: false,
    importFlowNameExists: false,
    cloneFlowDialogState: false,
    cloneFlowId: '',
    cloneFlowName: '',
    cloneFlowNameError: false,
    cloneFlowNameErrorMsg: '',
    cloneProcess: false,
    sessionCreated: false,
    sessionID: '',
    showContent: false,
    loadingTxt: ''
  };
  componentDidMount() {
    //console.log("this.props: ", this.props);
  }
  componentWillUnmount() {

  }

  onRequestSort = (event: any, property: string) => {
    this.setState({
      tableSortAsc: !this.state.tableSortAsc,
      tableSortProperty: property
    });
  };

  handleChangeSearchTerm = (e: any) => {
    this.setState({
      searchTerm: e.target.value
    });
  };

  handleRenderAsList = () => {
    this.setState({ renderAsList: true });
  };

  handleRenderAsGrid = () => {
    this.setState({ renderAsList: false });
  };

  handleChangePage = (event: unknown, newPage: number) => {
    this.setPage(newPage);
  };

  handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.setRowsPerPage(parseInt(event.target.value, 10));
    this.setPage(0);
  };

  setPage = (page: number) => {
    this.setState({ page: page });
  };

  setRowsPerPage = (rows: number) => {
    this.setState({ rowsPerPage: rows });
  };

  handleToggleFilter = () => {
    if (this.state.toggleFilterDisplay === true) {
      this.setState({ toggleFilterDisplay: false });
    } else {
      this.setState({ toggleFilterDisplay: true });
    }
  }

  sortDashboards = (dashboards: any) => {
    //console.log("SORT PROJECTS: ");
    //console.log(dashboards);
    if (this.state.tableSortProperty !== '') {
      return this.sortOn(dashboards, this.state.tableSortProperty);
    } else {
      return dashboards;
    }
  };

  sortOn = (arr: any, property: string) => {
    //console.log("ARR: ");
    //console.log(arr);
    var sortedArray = arr.sort((a: any, b: any) => {
      if (b[property] < a[property]) {
        return this.state.tableSortAsc ? 1 : -1;
      }
      if (b[property] > a[property]) {
        return this.state.tableSortAsc ? -1 : 1;
      }
      return 0;
    });
    return sortedArray;
  };

  filterDashboards = (dashboards: any) => {
    //console.log("import p: ");
    //console.log(dashboards);
    if (this.state.searchTerm !== '') {
      dashboards = dashboards.filter(
        (p: any) =>
          p.name
            .toLowerCase()
            .indexOf(this.state.searchTerm.toLowerCase()) > -1
      );
    }
    return dashboards;
  };

  onNewDatasetItemClick() {
    this.setState({
      addDatasetDialogState: true,
      datasetEditMode: false
    });
  }

  viewTypeSwitchButtons = () => {
    const buttons = [
      <Button
        key={1}
        color={this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsList()}
      >
        <ViewListIcon />
      </Button>,
      <Button
        key={2}
        color={!this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsGrid()}
      >
        <ViewComfyIcon />
      </Button>
    ]
    return buttons
  }

  getDashboardsFilters = (dashboards: any) => {
    if(dashboards) {
      return (
        <div className="filters-and-pagination">
          <div className="table-filters">
                <IkiInput 
                  _label=""
                  _placeholder="Search..."
                  _type='text'
                  _autoComplete="new-password"
                  _value={this.state.searchTerm}
                  _size="small"
                  _margin="none"
                  _filterType={true}
                  _onChange={(e: any) => this.handleChangeSearchTerm(e)}
                  _startIcon={
                    {
                      _icon: (
                        <SearchIcon fontSize="small" />
                      )
                    }
                  }
                />
                <IkiButtonGroup
                  _variant="contained"
                  _size="small"
                  _color="secondary"
                  _buttons={
                    this.viewTypeSwitchButtons()
                  }
                />
              </div>
              <div className="table-pagination">
                <TablePagination
                  component="div"
                  className="flows-pagination"
                  labelRowsPerPage="Dashboards per page"
                  rowsPerPageOptions={[8, 16, 32]}
                  colSpan={3}
                  count={dashboards.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  SelectProps={{
                    inputProps: {
                      'aria-label': 'Dashboards per page'
                    }
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
                />
              </div>
          </div>
      );
    }
  };

  getDashboards(dashboards: any) {
    var dashboardsArr: any = [], pageDashboards, dashboardsWithUploading: any = [];
    let dashboardUploading: any;
    if(this.props.dashboards) {
      dashboardsArr = this.props.dashboards;
      dashboardsArr = this.sortDashboards(this.filterDashboards(dashboardsArr));
      pageDashboards = dashboardsArr.slice(
        this.state.page * this.state.rowsPerPage,
        this.state.page * this.state.rowsPerPage + this.state.rowsPerPage
      );
      dashboardsArr.forEach((datasetData: any, index: number) => {
        dashboardUploading = this.props.uploadProgress.filter((uploadProgressData: any)=>{
          return uploadProgressData.status !== 'complete' && datasetData.dataset_id === uploadProgressData.datasetId}
        )
        if(Object.keys(dashboardUploading).length > 0){
          //dashboardsWithUploading.push(datasetData.dataset_id);
          dashboardsWithUploading[datasetData.dataset_id] = datasetData.dataset_id;
        }
      });
    }
    //console.log("dashboardsWithUploading: ", dashboardsWithUploading, ", dashboardUploading: ", dashboardUploading);
    if (dashboardsArr && dashboardsArr.length === 0) {
      return (
        <Fragment>
          {this.getDashboardsFilters(dashboardsArr)}
          <Grid item xs={12}>
            <Box display="flex" alignItems="center" className="no-results">
              <FindInPageIcon className="no-results-icon" />
              <span className="no-results-txt">No dashboards found</span>
            </Box>
          </Grid>
        </Fragment>
      );
    }
    return(
      <Fragment>
        {this.getDashboardsFilters(dashboardsArr)}
        {this.state.renderAsList ? (
          <Grid item xs={12} className="i-pb-0">
            <Card className="cardbox has-table tableHolder">
              <CardContent className="content table-inside">
                <IkiTable
                      _type="Dashboard"
                      _link='/dash/'
                      _header={
                        [
                          {
                            key: 'name',
                            name: 'Dashboard'
                          },
                          {
                            key: 'dashboard_id',
                            name: 'ID'
                          }
                        ]
                      }
                      _data={pageDashboards}
                      _functions={
                        [
                          {
                            edit: (item: any) => this.handleDashEdit(item)
                          },
                          {
                            delete: (item: any) => this.handleDashboardDelete(item)
                          },
                          {
                            browse: (itemId: string) => this.props.subpageClick(`/dash/${itemId}`)
                          }
                        ]
                      }
                  />
              </CardContent>
            </Card>
          </Grid>
        ):(
          <IkiGrid
            _type='Dashboard'
            _link='/dash/'
            _icon={<BarChartIcon />}
            _data={pageDashboards}
            _uploadInfo={dashboardUploading}
            _functions={
              [
                {
                  edit: (item: any) => this.handleDashEdit(item)
                },
                {
                  delete: (item: any) => this.handleDashboardDelete(item)
                },
                {
                  browse: (itemId: string) => this.props.subpageClick(`/dash/${itemId}`)
                }
              ]
            }
          />
        )}
        {dashboardsArr.length > this.state.rowsPerPage && (
          <TablePagination
                  component="div"
                  className="pagination-sm"
                  labelRowsPerPage="Datasets per page"
                  rowsPerPageOptions={[8, 16, 32]}
                  colSpan={3}
                  count={dashboardsArr.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  SelectProps={{
                    inputProps: {
                      'aria-label': 'Datasets per page'
                    }
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
          />
        )}
      </Fragment>
    );
  }

  render() {
    var dashboards: any = [];
    if(this.props.dashboards){
      dashboards = this.props.dashboards;
    }
    var projectData = this.props.projects.filter(
      (p: any) => p.project_id === this.props.projectId
    )[0];
    var datasetsExisting = false;
    if(this.props.datasets && this.props.datasets.length > 0) {
      datasetsExisting = true;
    }
    return (
      <Fragment>
          <Fragment>
              <div className="under-tab-buttons">
                        <IkiButton
                          _disabled={!datasetsExisting}
                          _variant='contained'
                          _color='primary'
                          _label="Upload New Dashboard"
                          _type='button'
                          _fullWidth={false}
                          _loading={false}
                          _startIcon={
                            <i className="zmdi zmdi-cloud-upload zmdi-hc-fw"></i>
                          }
                          _onClick={() => this.onDashboardItemClick()}
                        />
            </div>
            <div className="cardbox-holder">
              {this.getDashboards(dashboards)}
            </div>

            <AddDashboard
                projectId={this.props.projectId}
                projectName={projectData ? projectData.name : ''}
                isEdit={this.state.dashEditMode}
                open={
                    this.state.addDashboardtDialogState ||
                    this.props.dashUploadStatus === 'pending'
                }
                name={
                    this.state.selectedDashData
                    ? this.state.selectedDashData.name
                    : ''
                }
                datasetIds={
                    this.state.selectedDashData
                    ? this.state.selectedDashData.dataset_id
                    : []
                }
                dashboardId={
                    this.state.selectedDashData
                    ? this.state.selectedDashData.dashboard_id
                    : ''
                }
                datasets={this.handleDatasetsForDash()}
                dashUploadStatus={this.props.dashUploadStatus}
                handleDialogClose={() => this.handleAddDashboardDialogClose()}
                handleSubmit={(name: string, file: File, datasetId: any, dashboardId: string) =>
                    this.handleAddDashDailogSubmit(datasetId, name, file, dashboardId)
                }
                handleSubmitOverwrite={(name: string, file: File, datasetId: any) =>
                    this.handleAddDashDialogSubmitOverwrite(name, file, datasetId)
                }
                handleCancel={() => this.handleAddDashboardDialogClose()}
                newDashboardNameExists={this.state.newlyAddedDashNameExists}
                dismissAlert={() => this.handleDismissDashNameExists()}
            ></AddDashboard>
            <EditDashboardDialog
                dashboardId={
                    this.state.selectedDashData
                    ? this.state.selectedDashData.dashboard_id
                    : ''
                }
                dashboardName={
                    this.state.selectedDashData
                    ? this.state.selectedDashData.name
                    : ''
                }
                open={this.state.editDashboardDialogState}
                handleDialogClose={() => this.handleEditDashboardDialogClose()}
                handleSubmit={() => this.handleEditDashboardDialogSubmit()}
                handleCancel={() => this.handleEditDashboardDialogClose()}
            ></EditDashboardDialog>

            <SnackbarMsg
              snackbarOpen={this.state.snackbarOpen}
              snackbarType={this.state.snackbarType}
              snackbarMsg={this.state.snackbarMessage}
              onSnackbarClose={this.handleSnackbarClose}
            />
          </Fragment>
      </Fragment>
    );
  }

  handleDismissDashNameExists() {
    this.setState({
      newlyAddedDashNameExists: false
    });
  }

  handleAddDashDailogSubmit(datasetId: any, name: string, file: File, dashboardId: string) {
    var checkDashNameOverlap = false;
    const dashboards = this.props.dashboards.filter(
      (dashboardData: any) =>
        dashboardData.project_id === this.props.projectId
    );
    // eslint-disable-next-line
    dashboards.map((dashboardData: any) => {
        //console.log("NAME: " + name + ", dashboardData.name: " + dashboardData.name + ", id: " + dashboardData.dataset_id);
        if (
          dashboardData.name === name &&
          dashboardData.project_id === this.props.projectId
        ) {
          checkDashNameOverlap = true;
        }
        return ''
    });
    //console.log("checkDashNameOverlap: " + checkDashNameOverlap);
    if (checkDashNameOverlap !== false) {
      this.setState({ newlyAddedDashNameExists: true });
    } else {
      if (!this.state.dashEditMode) {
        this.props.createDashboard(this.props.projectId, datasetId, name, file);
        //this.props.createDataset(this.props.projectId, name, file, "", false);
        this.setState({
          snackbarMessage: (
            <Fragment>
              <div className="msg">
                You have succesfully uploaded new dashboard.
              </div>
              <div className="val">{name}</div>
            </Fragment>
          ),
          snackbarType: 'success',
          addDashboardtDialogState: false,

          snackbarOpen: true
        });
      } else {
        this.props.editDashboard(
          this.props.projectId,
          datasetId,
          name,
          dashboardId,
          file
        );
        this.setState({
          snackbarMessage: (
            <Fragment>
              <div className="msg">You have succesfully edited dashboard: </div>
              <div className="val">{name}</div>
            </Fragment>
          ),
          snackbarType: 'success',
          snackbarOpen: true
        });
      }
      this.handleAddDashboardDialogClose();
    }
  }
  handleAddDashDialogSubmitOverwrite(name: string, file: File, datasetId: any) {
    // CALL TO API TO OVERWRITE DATASET
    //console.log('OVERWRITE dashboard: ' + name);
    //this.props.createDataset(this.props.projectId, name, file);
    var existingDashboardId = '';
    const dashboards = this.props.dashboards.filter(
      (dashData: any) =>
        dashData.project_id === this.props.projectId
    );
    // eslint-disable-next-line
    dashboards.map((dashData: any) => {
        //console.log("NAME: " + name + ", datasetData.name: " + datasetData.name + ", id: " + datasetData.dataset_id);
        if (
          dashData.name === name &&
          dashData.project_id === this.props.projectId
        ) {
          existingDashboardId = dashData.dashboard_id;
        }
        return ''
    });
    if (existingDashboardId !== '') {
      this.props.editDashboard(this.props.projectId, datasetId, name, existingDashboardId, file)
      // this.props.editDataset(
      //   this.props.projectId,
      //   existingDatasetId,
      //   name,
      //   file
      // );
      // this.props.getDashboards(this.props.projectId)

      this.setState({
        newlyAddedDashNameExists: false,
        snackbarMessage: (
          <Fragment>
            <div className="msg">You have succesfully updated dashboard: </div>
            <div className="val">{name}</div>
          </Fragment>
        ),
        snackbarType: 'success',
        snackbarOpen: true
      });
      this.handleAddDashboardDialogClose();
    }
  }

  handleAddDashboardDialogClose() {
    this.setState({ addDashboardtDialogState: false });
  }

  handleDatasetsForDash() {
    var dForDash: any = [];
    if(this.props.datasets && this.props.datasets.length > 0) {
      dForDash = this.props.datasets.filter(
        (datasetData: any) => datasetData.project_id === this.props.projectId
      )
      return dForDash;
    } else {
      return null;
    }
    //this.props.data.dashboards.filter((d:any)=>{return d.project_id = this.props.projectId})
    //console.log("dForDash: ", dForDash);
    //return dForDash;
  }

  onDashboardItemClick() {
    this.setState({
      addDashboardtDialogState: true,
      dashEditMode: false
    });
  }

  handleDashEdit(data: any) {
    this.setState({
      addDashboardtDialogState: true,
      selectedDashData: data,
      dashEditMode: true
    })
  }
  handleEditDashboardDialogClose() {
    this.setState({ addDashboardtDialogState: false });
  }
  handleEditDashboardDialogSubmit() {
    this.handleEditDashboardDialogClose();
  }
  handleDashboardBrowse(data: any) {
    var subpageLink = `/dash/${data.dashboard_id}`;
    this.props.subpageClick(subpageLink);
  }
  handleDashboardDelete(data: any) {
    this.props.deleteDashboard(data.dashboard_id)
    this.setState({
      snackbarMessage: (
        <Fragment>
          <div className="msg">You have succesfully deleted dashboard: </div>
          <div className="val">{data.dashboard_id}</div>
        </Fragment>
      ),
      snackbarType: 'success',
      snackbarOpen: true
    });
  }

  handleSnackbarClose = () => {
    this.setState({ snackbarOpen: false });
  };
  
}

export default DashboardsComponent;
