import Command from './command';

class CommandManager {
  stack: Array<Command> = [];
  index: number = 0;

  addCommand(command: Command) {
    this.stack.length = this.index;
    this.stack.push(command);
    command.execute();
    this.index++;
  }

  canPerformUndo() {
    return this.index > 0;
  }

  canPerformRedo() {
    return this.index < this.stack.length;
  }

  undo() {
    if (this.canPerformUndo()) {
      this.index--;
      const command: Command = this.stack[this.index];
      command.undo();
    }
  }

  redo() {
    if (this.canPerformRedo()) {
      const command: Command = this.stack[this.index];
      command.execute();
      this.index++;
    }
  }

  empty() {
    this.stack = [];
  }
}

export default CommandManager;
