import React, { Fragment } from 'react';
import IkiDialog from '../IkiDialog';
import IkiButton from '../IkiFormElements/IkiButton';

export function DeleteProjectDialog(props: any) {
  const {
    projectId,
    projectName,
    handleDialogClose,
    handleDeleteDialogSubmit,
    handleDeleteDialogCancel,
    open,
    deleteSubmitLoading
  } = props;
  return (
    <IkiDialog
            _open={open}
            _onClose={handleDialogClose}
            _type='delete'
            _icon={false}
            _title='Delete project'
            _subtitle=''
            _size='xs'
            _align='center'
            _content={
              <>
                <div className="section">
                  <div className="txt-dialog-faded1">{projectId}</div>
                  <div className="txt-dialog-impact1">{projectName}</div>
                </div>
                <div className="section">
                  <div className="txt-dialog-impact2 error">Are you sure ?</div>
                  <div className="error">
                    All your pipelines inside will be deleted as well.
                  </div>
                </div>
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={handleDeleteDialogCancel}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Delete"
                  _type='button'
                  _fullWidth={false}
                  _loading={deleteSubmitLoading}
                  _disabled={deleteSubmitLoading}
                  _isDanger={true}
                  _onClick={handleDeleteDialogSubmit}
                />
              </Fragment>
            }
    />
  );
}
