import React, { Fragment } from 'react';
import IkiDialog from '../IkiDialog';
import IkiButton from '../IkiFormElements/IkiButton';

export function DeleteDatasetDialog(props: any) {
  const {
    datasetId,
    datasetName,
    handleDialogClose,
    handleSubmit,
    handleCancel,
    open
  } = props;
  return (
    <IkiDialog
            _open={open}
            _onClose={handleDialogClose}
            _type='delete'
            _icon={false}
            _title='Delete dataset'
            _subtitle=''
            _size='xs'
            _align='center'
            _content={
              <>
                <div className="section">
                  <div className="txt-dialog-faded1">{datasetId}</div>
                  <div className="txt-dialog-impact1">{datasetName}</div>
                </div>
                <div className="section">
                  <div className="txt-dialog-impact2 error">Are you sure ?</div>
                </div>
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={handleCancel}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Delete"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _isDanger={true}
                  _onClick={handleSubmit}
                />
              </Fragment>
            }
    />
  );
}
