import React, { Component } from 'react';
import {
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
  } from '@material-ui/core';
import classNames from 'classnames/bind';
import styles from './Dialog.module.scss';

let cx = classNames.bind(styles);

/**
   * Dialog
   * @param _type Type - string
   */
export default class IkiDialog extends Component {
  render() {
    const { 
      _open,
      _onClose,
      _type,
      _title,
      _subtitle,
      _size,
      _content,
      _actions
    } = this.props;
    //const _icon = this.props._icon ? this.props._icon : '';
    const _align = this.props._align ? this.props._align : 'left';
    const _onEnter = this.props._onEnter ? this.props._onEnter : null;
    //console.log("IkiGrid: ", this.props);
    var dialogClass = cx({
        dialog: true,
        'center': _align === 'center',
        'delete': _type === 'delete'
      });
    return(
      <>
        <Dialog
            open={_open}
            onEnter={_onEnter}
            onClose={_onClose}
            className={dialogClass}
            fullWidth={true}
            maxWidth={_size}
            disableBackdropClick={true}
        >
            {_title !== '' && (
              <DialogTitle className={styles.header}>
                {_subtitle !== '' && (
                  <div className={styles.subtitle}>{_subtitle}</div>
                )}
                {_title !== '' && (
                  <div className={styles.title}>{_title}</div>
                )}
              </DialogTitle>
            )}
            {_content !== '' && (
              <DialogContent className={styles.content}>{_content}</DialogContent>
            )}
            <DialogActions className={styles.footer}>
              {_actions}
            </DialogActions>
        </Dialog>
      </>
    );
  }
}