  /**
   * Manually adds value for bucket key of the facet group dataset.
   * The default value is stored in bucketDefaultValueForDataset
   * @param attributeTypes Attribute types of the facet.
   */

  checkForDefaultValues(attributeTypes: any) {
    let attrs = {};
    let keys = Object.keys(attributeTypes);
    for (var i = 0; i < keys.length; i++) {
      if (
        keys[i] === 'bucket' &&
        this.props.facetGroupTitle.toLowerCase() === 'dataset'
      ) {
        attrs[keys[i]] = this.bucketDefaultValueForDataset;
      }
      // if (attributeTypes[keys[i]].default_value) {
      //   attrs[keys[i]] = attributeTypes[keys[i]].default_value;
      // }
    }
    return attrs;
  }
  /**
   * Checks if there there are any hashmaps and checks if there are any existing entries to that key
   * in the facet arguments. If there are arguments, this function adds that many number of attribute types for the children
   * @param attributeTypes Attribute types of the facet
   * @param attributes Existing attribute arguments of the facets.
   */
  checkForHashMaps(attributeTypes: any, attributes: any) {
    Object.keys(attributeTypes).map((key: any) => {
      var attr: any = attributeTypes[key];
      var children = attr.children;
      if (attr.argument_type === 'MAP' && attr.is_list) {
        attr.children = [];
        if (
          attributes[key] &&
          Array.isArray(attributes[key]) &&
          attributes[key].length > 0
        ) {
          for (var i = 0; i < attributes[key].length; i++) {
            attr.children.push({ ...children });
          }
        } else {
          attr.children.push({ ...children });
        }
      }
      return ''
    });
    return attributeTypes;
  }

