import React, { Fragment } from 'react';

import IkiDialog from '../IkiDialog';
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';

export function EditProjectDialog(props: any) {
  const {
    handleDialogClose,
    open,
    projectID,
    projectName,
    onSubmit,
    renameSubmitLoading
  } = props;

  const [newName, setNewName] = React.useState(projectName);
  //const [nameErrorMsg, setNameErrorMsg] = React.useState('');
  const [nameErrorState, setNameErrorState] = React.useState(false);

  const handleNameChange = (e: any) => {
    setNewName(e.target.value);
  };

  const handleEditDialogSubmit = (e: any) => {
    if (newName !== '') {
      onSubmit(newName);
      setNameErrorState(false);
    } else {
      //setNameErrorMsg('Project name required');
      setNameErrorState(true);
    }
  };

  const handleOnEnter= () => {
    setNewName(projectName);
  }

  const handleOnClose= () => {
    handleDialogClose();
    setNameErrorState(false);
  }

  return (
    <IkiDialog
            _open={open}
            _onEnter={handleOnEnter}
            _onClose={handleOnClose}
            _type='share'
            _icon={false}
            _title='Rename Project'
            _subtitle=''
            _size='xs'
            _align='left'
            _content={
              <>
                <div className="section">
                  <div className="txt-dialog-faded1">{projectID}</div>
                  <div className="txt-dialog-impact1">{projectName}</div>
                </div>
                <div className="section">
                  <IkiInput 
                    _label="Project name"
                    _placeholder="Enter project name"
                    _type='text'
                    _autoComplete="new-password"
                    _value={newName}
                    _onChange={handleNameChange}
                    _characterCounter={40}
                    _autoFocus={true}
                    _fullWidth={true}
                    _error={nameErrorState ? 'Required' : ''}
                  />
                </div>
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={handleOnClose}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Rename"
                  _type='button'
                  _fullWidth={false}
                  _loading={renameSubmitLoading}
                  _disabled={renameSubmitLoading}
                  _onClick={handleEditDialogSubmit}
                />
              </Fragment>
            }
        />
  );
}
