import React, { Component, Fragment } from 'react';
import { Link } from "react-router-dom";
import {
  Button,
  CssBaseline,
  TextField,
  FormHelperText,
  Link as MaterialUiLink,
  Grid,
  Box,
  Typography,
  Container,
  Divider,
  InputAdornment,
  IconButton
} from '@material-ui/core';
import ErrorIcon from '@material-ui/icons/Error';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import { confirmForgotPassword } from '../../../services';
//import '../login/login.css';
var passwordValidator = require('password-validator');

interface Props {
  data: any;
  classes: any;
  history: any;
}

interface State {
  codeValidation: boolean;
  codeValue: any;
  codeErrorMsg: any;
  userEmail: any;
  verificationCodeSubmitErrorMsg: string;
  verificationCodeSubmitErrorState: boolean;
  passwordValidation: boolean;
  passwordValidation2: boolean;
  passwordValue: any;
  passwordValue2: any;
  passErrorMsg: any;
  passErrorMsg2: any;
  showPassword: boolean;
  showPassword2: boolean;
  resetpassSubmitErrorMsg: string;
  resetpassSubmitErrorState: boolean;
  resetpassSubmitSuccessState: boolean;
  resetpassSubmitSuccessMsg: string;
  rpassSubmitState: boolean;
  timeRemainingInSeconds: number;
}

// Define rules and minimum requirements for password
var passwordRules = new passwordValidator();
passwordRules
.is().min(8)            // Minimum length 8
.is().max(100)          // Maximum length 100
.has().uppercase()      // Must have uppercase letters
.has().lowercase()      // Must have lowercase letters
.has().digits()         // Must have digits
.has().not().spaces();  // Should not have spaces

class ResetPassword extends Component<Props, State> {

  private timer: any;

  state = {
    codeValidation: true,
    codeValue: '',
    codeErrorMsg: '',
    userEmail: '',
    verificationCodeSubmitErrorMsg: '',
    verificationCodeSubmitErrorState: false,
    passwordValidation: true,
    passwordValidation2: true,
    passwordValue: '',
    passwordValue2: '',
    passErrorMsg: '',
    passErrorMsg2: '',
    showPassword: true,
    showPassword2: true,
    resetpassSubmitErrorMsg: '',
    resetpassSubmitErrorState: false,
    resetpassSubmitSuccessState: false,
    resetpassSubmitSuccessMsg: '',
    rpassSubmitState: false,
    timeRemainingInSeconds: 10
  }

  componentDidMount(){
    this.setState({ userEmail: '' })
    let getURL: any = window.location.search;
    //console.log("getURL: " + getURL);
    if(getURL !== ''){
      let urlParam: any = new URLSearchParams(getURL);
      //console.log("urlParam: " + urlParam);
      if(urlParam !== ''){
        let userEmail: any = urlParam.get('e');
        //console.log("userEmail: " + userEmail);
        if(userEmail !== ''){
          let userEmailConverted: any = encodeURIComponent(userEmail).replace('%20','+');
          let userEmailDecoded: any = decodeURIComponent(userEmailConverted);
          //console.log("email: " + userEmail);
          this.getUserEmail(userEmailDecoded)
        }
      }
    }
  }

  getUserEmail = (eParam: string) => {
    this.setState({ userEmail: eParam })
  }

  handlePassChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    //console.log("get pass val: " + e.target.value)
    this.setState({ passwordValue: e.target.value })
  }

  handlePassChange2 = (e: React.ChangeEvent<HTMLInputElement>) => {
    //console.log("get pass val: " + e.target.value)
    this.setState({ passwordValue2: e.target.value })
  }

  handleClickShowPassword = () => {
    if(this.state.showPassword === true){
      this.setState({ showPassword: false })
    }else{
      this.setState({ showPassword: true })
    }
  }

  handleClickShowPassword2 = () => {
    if(this.state.showPassword2 === true){
      this.setState({ showPassword2: false })
    }else{
      this.setState({ showPassword2: true })
    }
  }

  decrementTimeRemaining = () => {
    if (this.state.timeRemainingInSeconds > 0) {
      this.setState({
        timeRemainingInSeconds: this.state.timeRemainingInSeconds - 1
      });
    } else {
      clearInterval(this.timer!);
      this.setState({
        timeRemainingInSeconds: 10
      });
      this.props.history.push("/login");
    }
  };

  preparePageContent = () => {
    const {
      codeValue,
      codeValidation,
      codeErrorMsg,
      userEmail,
      verificationCodeSubmitErrorMsg,
      verificationCodeSubmitErrorState,
      passwordValidation,
      passwordValidation2,
      passwordValue,
      passwordValue2,
      passErrorMsg,
      passErrorMsg2,
      showPassword,
      showPassword2,
      resetpassSubmitErrorMsg,
      resetpassSubmitErrorState,
      resetpassSubmitSuccessState,
      resetpassSubmitSuccessMsg,
      rpassSubmitState,
      timeRemainingInSeconds
    } = this.state;
    if(this.state.userEmail !== ''){
      return(
        <Fragment>
          {rpassSubmitState === true &&
            <div className="mask"></div>
          }
          <Container className="main-container" component="main" maxWidth="xs">
            <CssBaseline />
            <div className="paper-box">
              <Typography className="login-logo" component="h2" variant="caption">
                <span>Ikigai</span>
                <span>Labs</span>
              </Typography>
              <Box mt={4} mb={1}>
                <Typography component="h1" variant="h5">
                Reset password
                </Typography>
                <Divider variant="middle" className="subtitle-divider" />
              </Box>
              <Typography className="subtitle" component="div" variant="caption">
                Please enter verification code you received on your email
              </Typography>
              <Box mt={1} mb={1}>
                <strong>{userEmail}</strong>
              </Box>
              {resetpassSubmitSuccessState === false ?
                <form 
                  className="login-form" 
                  autoComplete="off" 
                  noValidate
                  onSubmit={this.handleSConfirmFormSubmit}
                >
                  {codeValidation ?
                    <TextField
                      variant="outlined"
                      margin="normal"
                      required
                      fullWidth
                      label="Verification code"
                      InputLabelProps={
                        {
                          shrink: true
                        }
                      }
                      placeholder="Enter verification code"
                      type="search"
                      autoComplete="new-username"
                      autoFocus
                      value={codeValue}
                      onChange={this.handleCodeChange}
                    />
                  :
                    <div>
                      <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        label="Verification code"
                        InputLabelProps={
                          {
                            shrink: true
                          }
                        }
                        placeholder="Enter verification code"
                        type="search"
                        autoComplete="new-username"
                        autoFocus
                        value={codeValue}
                        onChange={this.handleCodeChange}
                        error
                      />
                      <FormHelperText className="input-error" error>
                        <ErrorIcon />
                        <span>{codeErrorMsg}</span>
                      </FormHelperText>
                    </div>
                  }
                  {passwordValidation ?
                      <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        label="New password"
                        InputLabelProps={
                          {
                            shrink: true
                          }
                        }
                        placeholder="Enter new password"
                        type={showPassword ? "password" : "text"}
                        autoComplete="new-password"
                        value={passwordValue}
                        onChange={this.handlePassChange}
                        InputProps={{
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton
                                edge="end"
                                aria-label="toggle password visibility"
                                onClick={this.handleClickShowPassword}
                                onMouseDown={event => { event.preventDefault(); }}
                                size="small"
                              >
                                {showPassword ? <VisibilityOff fontSize="small" /> : <Visibility fontSize="small" />}
                              </IconButton>
                            </InputAdornment>
                          ),
                        }}
                      />
                    :
                      <div>
                        <TextField
                          variant="outlined"
                          margin="normal"
                          required
                          fullWidth
                          label="New password"
                          InputLabelProps={
                            {
                              shrink: true
                            }
                          }
                          placeholder="Enter new password"
                          type={showPassword ? "password" : "text"}
                          autoComplete="new-password"
                          value={passwordValue}
                          onChange={this.handlePassChange}
                          error
                          InputProps={{
                            endAdornment: (
                              <InputAdornment position="end">
                                <IconButton
                                  edge="end"
                                  aria-label="toggle password visibility"
                                  onClick={this.handleClickShowPassword}
                                  onMouseDown={event => { event.preventDefault(); }}
                                  size="small"
                                >
                                  {showPassword ? <VisibilityOff fontSize="small" /> : <Visibility fontSize="small" />}
                                </IconButton>
                              </InputAdornment>
                            ),
                          }}
                        />
                        <FormHelperText className="input-error" error>
                          <ErrorIcon />
                          <span>{passErrorMsg}</span>
                        </FormHelperText>
                      </div>
                    }
                    {passwordValidation2 ?
                        <TextField
                          variant="outlined"
                          margin="normal"
                          required
                          fullWidth
                          label="Confirm password"
                          InputLabelProps={
                            {
                              shrink: true
                            }
                          }
                          placeholder="Enter new password again"
                          type={showPassword2 ? "password" : "text"}
                          autoComplete="new-password"
                          value={passwordValue2}
                          onChange={this.handlePassChange2}
                          InputProps={{
                            endAdornment: (
                              <InputAdornment position="end">
                                <IconButton
                                  edge="end"
                                  aria-label="toggle password visibility"
                                  onClick={this.handleClickShowPassword2}
                                  onMouseDown={event => { event.preventDefault(); }}
                                  size="small"
                                >
                                  {showPassword2 ? <VisibilityOff fontSize="small" /> : <Visibility fontSize="small" />}
                                </IconButton>
                              </InputAdornment>
                            ),
                          }}
                        />
                      :
                        <div>
                          <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            label="Confirm password"
                            InputLabelProps={
                              {
                                shrink: true
                              }
                            }
                            placeholder="Enter new password again"
                            type={showPassword2 ? "password" : "text"}
                            autoComplete="new-password"
                            value={passwordValue2}
                            onChange={this.handlePassChange2}
                            error
                            InputProps={{
                              endAdornment: (
                                <InputAdornment position="end">
                                  <IconButton
                                    edge="end"
                                    aria-label="toggle password visibility"
                                    onClick={this.handleClickShowPassword2}
                                    onMouseDown={event => { event.preventDefault(); }}
                                    size="small"
                                  >
                                    {showPassword2 ? <VisibilityOff fontSize="small" /> : <Visibility fontSize="small" />}
                                  </IconButton>
                                </InputAdornment>
                              ),
                            }}
                          />
                          <FormHelperText className="input-error" error>
                            <ErrorIcon />
                            <span>{passErrorMsg2}</span>
                          </FormHelperText>
                        </div>
                      }
                  {resetpassSubmitErrorState === true &&
                    <Box mt={4} mb={4}>
                      <div className="submit-error has-txt-centered">{resetpassSubmitErrorMsg}</div>
                    </Box>
                  }
                  {verificationCodeSubmitErrorState === true &&
                    <Box mt={4} mb={4}>
                      <div className="submit-error has-txt-centered">{verificationCodeSubmitErrorMsg}</div>
                    </Box>
                  }
                  <Box mt={4}>
                    <Grid container spacing={2} className="has-txt-centered">
                          <Grid item xs={12}>
                            <Button
                              type="submit"
                              variant="contained"
                              color="primary"
                              className="button"
                              fullWidth
                            >
                              {rpassSubmitState === true &&
                                <span className="btn-spinner"></span>
                              }
                              <span>Reset</span>
                            </Button>
                          </Grid>
                          <Grid item xs={12}>
                            <Link to="/login">
                              or go back to Sign in page
                            </Link>
                          </Grid>
                    </Grid>
                  </Box>
                </form>
              :
                <Fragment>
                  <Box mt={4} mb={4}>
                    <div className="submit-success has-txt-centered">{resetpassSubmitSuccessMsg}</div>
                  </Box>
                  <Box mt={4} mb={4}>
                    <span>Redirecting to Sign in page in </span>
                    <strong>{timeRemainingInSeconds}</strong>
                  </Box>
                </Fragment>
              }
            </div>
            <Box className="login-copyright">
              <Typography variant="body2" color="textSecondary" align="center">
                {'Copyright  '}
                <MaterialUiLink color="inherit" href="http://ikigailabs.io/">
                  IkigaiLabs.io
                </MaterialUiLink>{' '}
                {new Date().getFullYear()}
                {'.'}
              </Typography>
            </Box>
          </Container>
        </Fragment>
      )
    }else{
      return(
        <Container className="main-container" component="main" maxWidth="xs">
          <CssBaseline />
          <div className="paper-box">
            <Typography className="login-logo" component="h2" variant="caption">
              <span>Ikigai</span>
              <span>Labs</span>
            </Typography>
            <Box mt={4} mb={1}>
              <Typography component="h1" variant="h5">
                Reset password
              </Typography>
              <Divider variant="middle" className="subtitle-divider" />
            </Box>
            <Typography className="subtitle" component="div" variant="caption">
              Please enter verification code you received on your email
            </Typography>
            <Box mt={4}>
              <div className="submit-error has-txt-left">
                <Box mb={1}>Something went wrong, there are two possibilities:</Box>
                <ol className="error-list">
                  <li>You haven't submited "Forgot password" form</li>
                  <li>Verification code has expired</li>
                </ol>
              </div>
            </Box>
            <Box mt={4} mb={2}>
              <Link to="/forgot-password">
                Go to "Forgot password" page
              </Link>
            </Box>
          </div>
          <Box className="login-copyright">
            <Typography variant="body2" color="textSecondary" align="center">
              {'Copyright  '}
              <MaterialUiLink color="inherit" href="http://ikigailabs.io/">
                IkigaiLabs.io
              </MaterialUiLink>{' '}
              {new Date().getFullYear()}
              {'.'}
            </Typography>
          </Box>
        </Container>
      )
    }
  }

  handleCodeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    //console.log("get email val: " + e.target.value)
    this.setState({ codeValue: e.target.value })
  }

  handleSConfirmFormSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
    this.setState({ rpassSubmitState: true });
    e.preventDefault()

    let codeIsValid = false
    let passIsValid = false
    let passIsValid2 = false

    setTimeout(() => {
      if(this.state.codeValue !== ''){
        codeIsValid = true
        this.setState({ 
          codeValidation: true, 
          codeErrorMsg: '' 
        })
      }else{
        codeIsValid = false
        this.setState({ 
          codeValidation: false, 
          codeErrorMsg: 'Verification code is required' 
        })
      }

      if(this.state.passwordValue !== ''){
        //console.log("email with val: " + this.state.passwordValue)
        if(passwordRules.validate(this.state.passwordValue) === true){
          this.setState({ 
            passwordValidation: true, 
            passErrorMsg: ''
          })
          passIsValid = true
        }else{
          this.setState({ 
            passwordValidation: false, 
            passErrorMsg: 'Password must contain uppercase and lowercase letter, number and be 8 - 16 characters long'
          })
          passIsValid = false
        }
      }else{
        //console.log("email without val: " + this.state.passwordValue)
        this.setState({ 
          passwordValidation: false,
          passErrorMsg: 'Password is required'
        })
      }

      if(this.state.passwordValue2 !== ''){
        //console.log("email with val: " + this.state.passwordValue2)
        if(this.state.passwordValue2 === this.state.passwordValue){
          this.setState({ 
            passwordValidation2: true, 
            passErrorMsg2: ''
          })
          passIsValid2 = true
        }else{
          this.setState({ 
            passwordValidation2: false, 
            passErrorMsg2: 'Passwords are not equal'
          })
          passIsValid2 = false
        }
      }else{
        //console.log("email without val: " + this.state.passwordValue2)
        this.setState({ 
          passwordValidation2: false,
          passErrorMsg2: 'Confirm password is required'
        })
      }

      if(codeIsValid === true && passIsValid === true && passIsValid2 === true){
        // COMPLETE REGISTRATION / CONFIRM-SIGN-UP API CALL GOES HERE AND IF EVERYTHING OK, GOES TO DASHBOARD
        confirmForgotPassword(this.state.userEmail, this.state.codeValue, this.state.passwordValue).then(res => {
          if(res.status === 200){
            this.setState({ verificationCodeSubmitErrorMsg: '' })
            this.setState({ verificationCodeSubmitErrorState: false })
            this.setState({ resetpassSubmitSuccessMsg: 'Congratulations. You have successfully created new password.' })
            this.setState({ resetpassSubmitSuccessState: true })
            this.timer = setInterval(() => {
              this.decrementTimeRemaining();
            }, 1000);
          }else if(res.status === 401){
            //this.setState({ verificationCodeSubmitErrorMsg: 'Error 401: Something went wrong!' })
            this.setState({ verificationCodeSubmitErrorMsg: 'Unable to reset password.' })
            this.setState({ verificationCodeSubmitErrorState: true })
          }else if(res.status === 404){
            this.setState({ verificationCodeSubmitErrorMsg: 'Error: Something went wrong.' })
            this.setState({ verificationCodeSubmitErrorState: true })
          }else{
            this.setState({ verificationCodeSubmitErrorMsg: 'Unable to reset password.' })
            this.setState({ verificationCodeSubmitErrorState: true })
          }
        }).catch(err => {
          if(err.response.status === 404) {
            this.setState({ verificationCodeSubmitErrorMsg: 'Unable to reset password.' })
            this.setState({ verificationCodeSubmitErrorState: true })
          } else if(err.response.status === 401) {
            //this.setState({ verificationCodeSubmitErrorMsg: 'Error 401: Something went wrong!' })
            this.setState({ verificationCodeSubmitErrorMsg: 'Error: Something went wrong.' })
            this.setState({ verificationCodeSubmitErrorState: true })
          }else{
            this.setState({ verificationCodeSubmitErrorMsg: 'Unable to reset password.' })
            this.setState({ verificationCodeSubmitErrorState: true })
          }
        })
      }
      this.setState({ rpassSubmitState: false });
    }, 1000);
  }

  render() {
    return (
      this.preparePageContent()
    )
  }
}

export default ResetPassword;