import React, { Component, Fragment } from 'react';
import { 
  getDashboard,
  enterSession as enterSessionManually,
  browseDashboard, 
  finishBrowseDashboard, 
  getProject,
  exitSession as exitSessionManually
} from '../../services';
import IkiLoader from '../../components/IkiLoader';
import SnackbarMsg from '../../components/SnackbarMsg';
import IkigaiLayout from '../../components/IkiLayout';
import styles from './DashComponent.module.scss';

// const captalize=(str:string)=>{
//   return str.charAt(0).toUpperCase() + str.slice(1);
// }

interface Props {
  data: any;
  match: any;
  classes: any;
  history: any;
  email: string;
  uploadProgress: any;
  enterSession: (projectId: string) => void;
  getDatasets: (projectId:string) => void;
  fireReturnToProject: (sourcePage: string) => void;
  cleanSession: () => void;
  logout: () => void;
}

interface State {
  dashId: string;
  projectId: string;
  projectName:string;
  snackbarOpen:boolean;
  snackbarMessage:any;
  snackbarType: string;
  dashLoaded:boolean;
  dashData:any;
  dashurl:string;
  loadingTxt:string;
  iFrameContentLoaded:boolean;
  statusDatetime:string;
  statusTxt:string;
  hideAutoLayoutLoading: boolean;
}

class DashViewComponent extends Component<Props, State> {
  loadingRetryCounter: number = 0;
  state = {
    dashId: '',
    projectId:'',
    projectName:'',
    browseStatus: '',
    sessionCreated: false,
    createSessionDialogOpened: false,
    snackbarOpen: false,
    snackbarType: '',
    snackbarMessage: null,
    visualizationOpened: false,
    dashLoaded:false,
    dashurl:'',
    loadingTxt:'Loading...',
    iFrameContentLoaded:false,
    statusDatetime:'',
    statusTxt:'',
    dashData:{},
    hideAutoLayoutLoading: false
  };

  async componentDidMount() {
    var urlParamDashId = this.props.match.params['id'];
    var findProjectId: string = "";
    var findProjectName: string = "";
    this.setState({ 
      loadingTxt: "Loading Dashboard...",
      dashId: urlParamDashId
    });
    this.props.fireReturnToProject("dash");
    
    getDashboard(urlParamDashId).then((response: any) => {
        findProjectId = response.dashboard.project_id;
        
        this.setState({
          dashData: response.dashboard
        })
        getProject(findProjectId).then((response:any)=>{
          findProjectName = response.project.name
          this.setState({
            projectName: findProjectName
          })
        })
        this.props.getDatasets(findProjectId)
        
        this.initialLoading(urlParamDashId, findProjectId);
    }).catch(()=>{
      this.setState({
        snackbarMessage: (
          <Fragment>
              <div className="msg">Something went wrong. Please try again</div>
          </Fragment>
          ),
          snackbarType: 'error',
          snackbarOpen: true
      })
    })
}

checkSession = async (dashboardId: string, projectId: string) => {
  //await exitSession(projectId);
  this.setState({ loadingTxt: "Session expired. Re-connecting..." })
  await enterSessionManually(projectId).then((enterSessionNewID: any) =>{
      //console.log("enterSessionNewID: " + enterSessionNewID);
      this.initialLoading(dashboardId, projectId);
  });
  //this.initialLoading(datasetId, projectId);
}

async initialLoading(dashboardId: string, projectId: string){
  var urlParamDashId = dashboardId;
  var findProjectId = projectId;
  this.setState({
    projectId:findProjectId
  })
  browseDashboard(findProjectId,dashboardId).then((res:any)=>{
      this.setState({ 
        loadingTxt: "Working on details..."
      })
      setTimeout(()=>{
        this.setState({ 
          loadingTxt: "Finalizing view...",
          dashLoaded: true,
          dashurl: res.url
        })
      },3000)
  }).catch((err: any) => {
    if(this.loadingRetryCounter < 3 && err===501){
      this.loadingRetryCounter++;
      this.checkSession(urlParamDashId, findProjectId);
    }else{
      this.setState({
        snackbarMessage: (
          <Fragment>
              <div className="msg">Something went wrong. Please try again</div>
          </Fragment>
          ),
          snackbarType: 'error',
          snackbarOpen: true,
          dashLoaded: true,
          iFrameContentLoaded: true
      });
      setTimeout(() => {
        this.props.history.push(`/dashboard/project/${projectId}`);
      }, 3000)
    }
  });
}
  componentWillUnmount() {
    finishBrowseDashboard(this.state.dashId,this.state.projectId).then((data)=>{
      let pathName:string = '';
      if(this.props.history.location.pathname) {
        pathName = this.props.history.location.pathname;
        var projectId = '';
        if(this.state.projectId) {
          projectId = this.state.projectId;
        }
        if(pathName === '/dashboard/projects' || pathName === '/dashboard/profile') {
          exitSessionManually(projectId).then((exitSessRes: any) => {
            this.props.cleanSession();
          }).catch((exitSessErr: any) => {
            this.props.cleanSession();
          });
        }
      }
    }).catch((err)=>{

    });
  }

  handleSnackbarClose = () => {
    this.setState({ snackbarOpen: false });
  };

  handleGoBack = (event: React.MouseEvent<HTMLElement, MouseEvent>) => {
    //this.props.history.goBack();
    //this.props.history.push(`/dashboard/project/${this.state.projectId}`);
    this.props.history.push(`/dashboard/project/${this.state.projectId}`);
  };

  render() {
    const{
      dashurl,
      loadingTxt,
      dashLoaded,
      dashData,
      iFrameContentLoaded,
      projectName,
    } = this.state
    const{
      datasets,
      datasetsLoaded
    } = this.props.data

    if (!this.state) return;
    var loadingFinished = false;
    if(iFrameContentLoaded && dashLoaded){
      loadingFinished = true;
    }

    return (
      <Fragment>
        <IkigaiLayout
          _type='sub-page'
          _page='Dashboard'
          _uploadProgress={this.props.uploadProgress}
          _handleGoBack={(event: any) => this.handleGoBack(event)}
          _content={
            <>
              {!this.state.hideAutoLayoutLoading &&
                <IkiLoader
                  loadingTxt={loadingTxt}
                  fadeOut={loadingFinished}
                  history={this.props.history}
                  backLink={`/dashboard/project/${this.state.projectId}`}
                />
              }
              <div className={styles.dashCentralPanel}>
                <iframe
                    title={"iKigai"}
                    key={0}
                    width="100%"
                    height="100%"
                    className={styles.metabaseIframe}
                    onLoad={() => {
                      dashLoaded &&
                          this.setState({ iFrameContentLoaded: true })
                    }}
                    src={dashurl}
                    sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
                ></iframe>
            </div>
            </>
          }
          _rightSidebar={
            <div className="info-drawer-root">
              <div className="info-drawer-section">
                  <div className="info-drawer-title">
                    <span className="title">Info</span>
                  </div>
                  <div className="info-drawer-title-group">
                    <div className="info-drawer-title-title">Project name:</div>
                    <div className="info-drawer-title-subtitle clip-txt">{projectName}</div>
                  </div>
                  <div className="info-drawer-title-group">
                    <div className="info-drawer-title-title">Dashboard Id:</div>
                    <div className="info-drawer-title-subtitle clip-txt">{dashData['dashboard_id']}</div>
                  </div>
                  <div className="info-drawer-title-group">
                    <div className="info-drawer-title-title">Dashboard name:</div>
                    <div className="info-drawer-title-subtitle clip-txt">{dashData['name']}</div>
                  </div>
                  <div className="info-drawer-title-group">
                    <div className="info-drawer-title-title">Datasets Linked:</div>
                      {dashData['dataset_id'] && datasetsLoaded && dashData['dataset_id'].map((data:any, datasetIndex: number) => (
                        <div className="info-drawer-title-subtitle clip-txt" key={'dash-assoc-data-' + datasetIndex}>
                          {datasets.filter((k:any) => {
                            return k.dataset_id === data
                          })[0].name}
                        </div>
                      ))}
                  </div>
              </div>
            </div>
          }
          _logoutHandler={this.props.logout}
        />
        <SnackbarMsg
          snackbarOpen={this.state.snackbarOpen}
          snackbarType={this.state.snackbarType}
          snackbarMsg={this.state.snackbarMessage}
          onSnackbarClose={this.handleSnackbarClose}
        />
      </Fragment>
    );
  }
}
export default DashViewComponent;