export const createPipeline = (flowObj: any): Promise<object> => {
  // Need to pass an empty flow object
  //var date = getCurrentDate();
  const { name, projectId, definition } = flowObj;
  var data = {
    pipeline: {
      name: name,
      project_id: projectId,
      schedule: {
        name: '',
        cron: '',
        start_time: '',
        end_time: ''
      },
      definition: definition ? definition : {} //,
      // title: 'Flow Title',
      // createdAt: date,
      // modifiedAt: date
    }
  };
  const promise: Promise<object> = new Promise<object>(resolve => {
    _axios.post(`${API_BASE}/create-pipeline`, data).then(res => {
      //console.log("res.data: " + res.data.pipeline_id);
      //console.log(res.data)
      resolve(res.data.pipeline_id);
    });
  });
  return promise;
};

export const getPipeline = (id: string): Promise<object> => {
  const promise: Promise<object> = new Promise<object>((resolve, reject) => {
    _axios
      .get(`${API_BASE}/get-pipeline`, { params: { pipeline_id: id } })
      .then((res: any) => {
        if (res.status === 200) {
          resolve(res.data);
        } else {
          reject('Could not get pipeline with id: ' + id);
        }
      })
      .catch((error: any) => {
        reject('Could not get pipeline with id: ' + id);
      });
  });
  return promise;
};

