export const duplicateProject = (projectId: string, name: string, targetEmailInserted: string): Promise<object> => {
  var state = store.getState();
  var targetEmail: string = targetEmailInserted !== '' ? targetEmailInserted : state.userReducerState.email;
  //console.log("SERVICES - projectId: " + projectId + ", name: " + name + ", email: " + targetEmail);
  var data = {
    project: {
      project_id: projectId,
      name: name
    },
    user: {
      email: targetEmail
    }
  };
  const promise = new Promise<object>((resolve, reject) => {
    _axios.post('/duplicate-project', data).then((res: any) => {
      if (res.status === 200) {
        resolve(res.data.project_id);
      } else {
        reject('Error duplicating project.');
      }
    }).catch((error: any) => {
      reject(error.response.status);
    });
  });
  return promise;
};

