export const uploadDataset = (
  datasetId: string,
  dataset_name: string,
  project_id: string,
  file: File,
  datasetOverwrite: boolean
) => {
  //console.log("dataset_id: " + datasetId + ", dataset_name: " + dataset_name + ", project_id: " + project_id + ", datasetOverwrite: " + datasetOverwrite + ", file: " + file.name);
  var retryFun = () => {
    store.dispatch(editDatasetActionStart(project_id, datasetId, dataset_name, file))
  }

  var cancelFun = () => {
    store.dispatch(deleteDatasetActionStart(datasetId))
  }

  store.dispatch(uploadDatasetProgress(0, datasetId, dataset_name, 'uploading', retryFun, cancelFun))
  getDatasetUploadUrl(datasetId, file.name).then((dUploadUrlRes: any) => {
    store.dispatch(uploadDatasetProgress(0, datasetId, dataset_name, 'uploading', retryFun, cancelFun))
    var updateFun = (percent: number) => {
      store.dispatch(uploadDatasetProgress(percent, datasetId, dataset_name, 'uploading', retryFun, cancelFun))
    }
    uploadDatasetWithURL(dUploadUrlRes.url, dUploadUrlRes.content_type, file, updateFun).then((dUploadWithUrlRes: any) => {
      store.dispatch(uploadDatasetProgress(100, datasetId, dataset_name, 'Processing', retryFun, cancelFun))
      confirmDatasetUpload(datasetId, file.name).then((confirmDUploadRes: any) => {
        store.dispatch(uploadDatasetProgress(100, datasetId, dataset_name, 'complete', retryFun, cancelFun))
      }).catch((err) => {
        store.dispatch(uploadDatasetProgress(0, datasetId, dataset_name, 'failed', retryFun, cancelFun))
      });
    }).catch((err) => {
      store.dispatch(uploadDatasetProgress(0, datasetId, dataset_name, 'failed', retryFun, cancelFun))
    });
  }).catch((err) => {
    store.dispatch(uploadDatasetProgress(0, datasetId, dataset_name, 'failed', retryFun, cancelFun))
  });
}
export const getDatasetUploadUrl = (
  datasetId: string,
  datasetFilename: string,
) => {
  var data = {
    dataset_id: datasetId,
    filename: datasetFilename
  };
  const promise = new Promise<object>((resolve, reject) => {
    _axios
      .get('/get-dataset-upload-url', { params: data })
      .then((res: any) => {
        if (res.status === 200) {
          resolve(res.data);
        } else {
          reject('Erorr: Unable to get getDatasetUploadUrl!');
        }
      });
  });

  return promise;
}
export const uploadDatasetWithURL = (
  datasetUploadUrl: string,
  datasetContentType: string,
  datasetFile: File,
  updatePercent: any
) => {
  var options = {
    headers: {
      "Content-Type": datasetContentType,
    },
    onUploadProgress: (progressEvent: any) => {
      var percentCompleted = (progressEvent.loaded * 100) / progressEvent.total
      updatePercent(percentCompleted)
    }
  };
  const promise = new Promise<object>((resolve, reject) => {
    axios.put(datasetUploadUrl, datasetFile, options).then((res: any) => {
      if (res.status === 200) {
        resolve(res.data);
      } else {
        reject('Erorr: Unable to upload Dataset file!');
      }
    }).catch((err) => {
      reject('Erorr: Unable to upload Dataset file!');
    });
  });
  return promise;
}
