export const downloadDatasetWithURL = (
  datasetDownloadUrl: string,
  datasetFilename: string,
  datasetContentType: string
) => {
  // eslint-disable-next-line
  var data = {
    filename: datasetFilename
  };
  /*
  Content-Disposition: "attachment;filename=report.xls"
  Content-Type: "application/octet-stream" // or Content-type: "application/vnd.ms-excel"
  */
  var options = {
    headers: {
      'Accept': datasetContentType,
      //'Content-Type': datasetContentType,
      'Content-Type': 'application/json',
      //'Content-Type': "application/octet-stream", 
      //'Content-Disposition': "attachment;filename="+datasetFilename,
    },
    transformRequest: (data: any, headers: any) => {
      headers['Accept'] = datasetContentType; 
      //headers['Content-Type'] = datasetContentType; 
      headers['Content-Type'] = 'application/json'; 
      //headers['Content-Type'] = "application/octet-stream"; 
      //headers['Content-Disposition'] = "attachment;filename="+datasetFilename;   
      return data;
    },
    transformResponse: (data: any, headers: any) => {
      //console.log(data) // this should now be JSON
      headers.responseType = 'blob'
      //headers.responseType = 'arraybuffer'
      return data;
    }
  };
  //var contentType = null;
  const promise = new Promise<object>((resolve, reject) => {
    try {
      fetch(datasetDownloadUrl, options)
      .then((response: any) => {
        //console.log("response: ", response);
        if (response.ok) {
          //contentType = response.headers.get('content-type');
          //console.log("contentType: ", contentType);
          return response.blob();
        }else {
          reject('Erorr: Unable to download Dataset!');
        }
      }).then((blob: any) => {
        var res = {
          blobFile: blob,
          fileName: datasetFilename
        }
        resolve(res);
        //console.log("blob: ", blob);
        // download(blob, 'file.pdf', contentType);
        /*var link = document.createElement('a');
        link.download = 'IkigaiLabs_' + datasetFilename;
        link.setAttribute("download", 'IkigaiLabs_' + datasetFilename);
        //link.href = res.fileUrl;
        link.href = URL.createObjectURL(blob);
        link.click();*/
      });
    } catch (err) {
      reject('Erorr: Unable to download Dataset!');
    }

    /*axios.get(datasetDownloadUrl, options).then((res: any) => {
        if (res.status === 200) {
          resolve(res.data);
        } else {
          reject('Erorr: Unable to download Dataset!');
        }
    });*/
  });
  return promise;
};

