export const cleanDataset = (datasetId: string, projectId: string, cleanOption: string, columnName: string, uType: string, uFormat: string): Promise<object> => {
  //console.log("datasetId: " + datasetId + ", projectId: " + projectId + ", cleanOption: " + cleanOption + ", columnName: " + columnName + ", uType: " + uType + ", uFormat: " + uFormat);
  var data: any = {};
  if (columnName === "" && uType === "" && uFormat === "") {
    data = {
      dataset_id: datasetId,
      option: cleanOption,
      arguments: {}
    };
  } else if (columnName !== "" && uType === "" && uFormat === "") {
    data = {
      dataset_id: datasetId,
      option: cleanOption,
      arguments: {
        column_name: columnName
      }
    };
  } else {
    data = {
      dataset_id: datasetId,
      option: cleanOption,
      arguments: {
        column_name: columnName,
        uniformization_type: uType,
        uniformization_format: uFormat
      }
    };
  }
  const promise = new Promise<object>((resolve, reject) => {
    _axios
      .post('/clean-dataset', data)
      .then((res: any) => {
        if (res.status === 200) {
          resolve(res.data);
        } else {
          reject(res.status);
        }
      })
      .catch((error: any) => {
        //reject(error.response.status);
        //reject(error);
        if (error && error !== "") {
          if (error.response && error.response !== "") {
            reject(error.response.status);
          } else {
            reject(504);
          }
        } else {
          reject(504);
        }
      });
  });
  return promise;
};

