export const isStreamUploadRunning = (datasetId: string): Promise<object> => {
  var data = {
    dataset_id: datasetId
  };

  const promise = new Promise<object>((resolve, reject) => {
    _axios
      .get(`${API_BASE}/is-stream-upload-running`, { params: data })
      .then((res: any) => {
        if (res.status === 200) {
          if (res.data.status === true) {
            setTimeout(() => { resolve(isStreamUploadRunning(datasetId)) }, 3000)
          } else {
            resolve(res)
          }
        } else {
          reject('Error: Unable to check if upload is running!');
        }
      }).catch((err: any) => {
        reject('Error: Unable to check if upload is running!')
      });
  });

  return promise;
};

