import React, { useState } from 'react';
import {
  Dialog,
  DialogContent,
  Box,
  DialogActions,
  Button,
  TextField,
  FormHelperText
} from '@material-ui/core';

import ErrorIcon from '@material-ui/icons/Error';

export function EditDatasetDialog(props: any) {
  const {
    datasetId,
    handleDialogClose,
    handleSubmit,
    handleCancel,
    open
  } = props;

  const [datasetName, setDatasetName] = useState('');
  const [isValidated, setIsvalidated] = useState(false);
  return (
    <Dialog
      onClose={handleDialogClose}
      aria-labelledby="customized-dialog-title"
      open={open}
      className="dialog"
      fullWidth={true}
      maxWidth="xs"
      disableBackdropClick={true}
    >
      <DialogContent className="dialog-content">
        <div>
          <Box className="dialog-title default-color" mb={2}>
            Edit dataset
          </Box>
          <div>
            <div className="project-id">{datasetId}</div>
            <div className="project-name">{props.datsetName}</div>
          </div>
          <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            required
            label="Dataset name"
            placeholder="Enter new name for Dataset"
            type="search"
            autoComplete="new-username"
            value={datasetName}
            onChange={(e: any) => {
              setDatasetName(e.target.value);
            }}
            error={isValidated && datasetName === ''}
            autoFocus
            InputLabelProps={{ shrink: true }}
          />
          {isValidated && datasetName === '' && (
            <FormHelperText className="input-error" error>
              <ErrorIcon />
              <span>Dataset name required</span>
            </FormHelperText>
          )}
        </div>
      </DialogContent>
      <DialogActions className="footer">
        <Button
          autoFocus
          onClick={() => handleCancel()}
          color="default"
          className="button"
        >
          Cancel
        </Button>
        <Button
          variant="contained"
          color="primary"
          className="button"
          onClick={() => {
            if (datasetName !== '') {
              handleSubmit(datasetName);
              setIsvalidated(false);
            } else {
              setIsvalidated(true);
            }
          }}
        >
          Update
        </Button>
      </DialogActions>
    </Dialog>
  );
}
