import React, { Component } from 'react';
import { Link } from "react-router-dom";
import {
  Button,
  CssBaseline,
  TextField,
  FormHelperText,
  Link as MaterialUiLink,
  Grid,
  Box,
  Typography,
  Container,
  Divider
} from '@material-ui/core';
import ErrorIcon from '@material-ui/icons/Error';
import { completeUserRegistration } from '../../../services';
//import '../login/login.css';

interface Props {
  data: any;
  classes: any;
  history: any;
}

interface State {
  codeValidation: boolean;
  codeValue: any;
  codeErrorMsg: any;
  userEmail: any;
  confirmCodeSubmitErrorMsg: string;
  confirmCodeSubmitErrorState: boolean;

}

class SignupConfirm extends Component<Props, State> {

  state = {
    codeValidation: true,
    codeValue: '',
    codeErrorMsg: '',
    userEmail: '',
    confirmCodeSubmitErrorMsg: '',
    confirmCodeSubmitErrorState: false
  }

  componentDidMount(){
    this.setState({ userEmail: '' })
    let getURL: any = window.location.search;
    //console.log("getURL: " + getURL);
    if(getURL !== ''){
      let urlParam: any = new URLSearchParams(getURL);
      //console.log("urlParam: " + urlParam);
      if(urlParam !== ''){
        let userEmail: any = urlParam.get('e');
        //console.log("userEmail: " + userEmail);
        if(userEmail !== ''){
          let userEmailConverted: any = encodeURIComponent(userEmail).replace('%20','+');
          let userEmailDecoded: any = decodeURIComponent(userEmailConverted);
          //console.log("email: " + userEmail);
          this.getUserEmail(userEmailDecoded)
        }
      }
    }
  }

  getUserEmail = (eParam: string) => {
    this.setState({ userEmail: eParam })
  }

  preparePageContent = () => {
    const {
      codeValue,
      codeValidation,
      codeErrorMsg,
      userEmail,
      confirmCodeSubmitErrorMsg,
      confirmCodeSubmitErrorState
    } = this.state;
    if(this.state.userEmail !== ''){
      return(
        <Container className="main-container" component="main" maxWidth="xs">
          <CssBaseline />
          <div className="paper-box">
            <Typography className="login-logo" component="h2" variant="caption">
              <span>Ikigai</span>
              <span>Labs</span>
            </Typography>
            <Box mt={4} mb={1}>
              <Typography component="h1" variant="h5">
                Complete registration
              </Typography>
              <Divider variant="middle" className="subtitle-divider" />
            </Box>
            <Typography className="subtitle" component="div" variant="caption">
              Please enter confirmation code you received on your email
            </Typography>
            <Box mt={1} mb={1}>
              <strong>{userEmail}</strong>
            </Box>
            <form 
              className="login-form" 
              autoComplete="off" 
              noValidate
              onSubmit={this.handleSConfirmFormSubmit}
            >
              {codeValidation ?
                <TextField
                  variant="outlined"
                  margin="normal"
                  required
                  fullWidth
                  label="Confirmation code"
                  InputLabelProps={
                    {
                      shrink: true
                    }
                  }
                  placeholder="Enter Confirmation code"
                  type="search"
                  autoComplete="new-username"
                  autoFocus
                  value={codeValue}
                  onChange={this.handleCodeChange}
                />
              :
                <div>
                  <TextField
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    label="Confirmation code"
                    InputLabelProps={
                      {
                        shrink: true
                      }
                    }
                    placeholder="Enter Confirmation code"
                    type="search"
                    autoComplete="new-username"
                    autoFocus
                    value={codeValue}
                    onChange={this.handleCodeChange}
                    error
                  />
                  <FormHelperText className="input-error" error>
                    <ErrorIcon />
                    <span>{codeErrorMsg}</span>
                  </FormHelperText>
                </div>
              }
              {confirmCodeSubmitErrorState === true &&
                <Box mt={4} mb={4}>
                  <div className="submit-error has-txt-centered">{confirmCodeSubmitErrorMsg}</div>
                </Box>
              }
              <Box mt={4}>
                <Grid container spacing={2} className="has-txt-centered">
                  <Grid item xs={12}>
                    <Button
                      type="submit"
                      variant="contained"
                      color="primary"
                      className="button"
                      fullWidth
                    >
                      Confirm
                    </Button>
                  </Grid>
                  <Grid item xs={12}>
                    <Link to="/login">
                      or go back to Sign in page
                    </Link>
                  </Grid>
                </Grid>
              </Box>
            </form>
          </div>
          <Box className="login-copyright">
            <Typography variant="body2" color="textSecondary" align="center">
              {'Copyright  '}
              <MaterialUiLink color="inherit" href="http://ikigailabs.io/">
                IkigaiLabs.io
              </MaterialUiLink>{' '}
              {new Date().getFullYear()}
              {'.'}
            </Typography>
          </Box>
        </Container>
      )
    }else{
      return(
        <Container className="main-container" component="main" maxWidth="xs">
          <CssBaseline />
          <div className="paper-box">
            <Typography className="login-logo" component="h2" variant="caption">
              IkigaiLabs
            </Typography>
            <Box mt={4} mb={1}>
              <Typography component="h1" variant="h5">
                Complete registration
              </Typography>
              <Divider variant="middle" className="subtitle-divider" />
            </Box>
            <Typography className="subtitle" component="div" variant="caption">
              Please enter confirmation code you received on your email
            </Typography>
            <Box mt={4}>
              <div className="submit-error has-txt-left">
                <Box mb={1}>Something went wrong, there are two possibilities:</Box>
                <ol className="error-list">
                  <li>Confirmation code has expired</li>
                  <li>You haven't used link from email to reach this page</li>
                </ol>
              </div>
            </Box>
            <Box mt={4} mb={2}>
              <Link to="/SignUp">
                Don't have an account? Sign Up
              </Link>
            </Box>
          </div>
          <Box className="login-copyright">
            <Typography variant="body2" color="textSecondary" align="center">
              {'Copyright  '}
              <MaterialUiLink color="inherit" href="http://ikigailabs.io/">
                IkigaiLabs.io
              </MaterialUiLink>{' '}
              {new Date().getFullYear()}
              {'.'}
            </Typography>
          </Box>
        </Container>
      )
    }
  }

  handleCodeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    //console.log("get email val: " + e.target.value)
    this.setState({ codeValue: e.target.value })
  }

  handleSConfirmFormSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
    e.preventDefault()

    let codeIsValid = false

    if(this.state.codeValue !== ''){
      codeIsValid = true
      this.setState({ 
        codeValidation: true, 
        codeErrorMsg: '' 
      })
    }else{
      codeIsValid = false
      this.setState({ 
        codeValidation: false, 
        codeErrorMsg: 'Confirmation code is required' 
      })
    }

    if(codeIsValid === true){
      // COMPLETE REGISTRATION / CONFIRM-SIGN-UP API CALL GOES HERE AND IF EVERYTHING OK, GOES TO DASHBOARD
      completeUserRegistration(this.state.userEmail, this.state.codeValue).then(res => {
        if(res.status === 200){
          this.setState({ confirmCodeSubmitErrorMsg: '' })
          this.setState({ confirmCodeSubmitErrorState: false })
          this.props.history.push('/dashboard')
        }else if(res.status === 401){
          //this.setState({ confirmCodeSubmitErrorMsg: 'Error 401: Something went wrong!' })
          this.setState({ confirmCodeSubmitErrorMsg: 'Unable to complete registration.' })
          this.setState({ confirmCodeSubmitErrorState: true })
        }else if(res.status === 404){
          this.setState({ confirmCodeSubmitErrorMsg: 'Error: Something went wrong.' })
          this.setState({ confirmCodeSubmitErrorState: true })
        }else{
          this.setState({ confirmCodeSubmitErrorMsg: 'Unable to complete registration.' })
          this.setState({ confirmCodeSubmitErrorState: true })
        }
      }).catch(err => {
        if(err.response.status === 404) {
          this.setState({ confirmCodeSubmitErrorMsg: 'Unable to complete registration.' })
          this.setState({ confirmCodeSubmitErrorState: true })
        } else if(err.response.status === 401) {
          //this.setState({ confirmCodeSubmitErrorMsg: 'Error 401: Something went wrong!' })
          this.setState({ confirmCodeSubmitErrorMsg: 'Error: Something went wrong.' })
          this.setState({ confirmCodeSubmitErrorState: true })
        }else{
          this.setState({ confirmCodeSubmitErrorMsg: 'Unable to complete registration.' })
          this.setState({ confirmCodeSubmitErrorState: true })
        }
      })
    }
  }

  render() {
    return (
      this.preparePageContent()
    )
  }
}

export default SignupConfirm;