class Command {
  _execute: Function;
  _undo: Function;
  _executedValue: any;
  _undoValue: any;
  constructor(execute: Function, undo: Function) {
    this._execute = execute;
    this._undo = undo;
  }

  execute() {
    this._executedValue = this._execute(this._undoValue);
    console.log(this._undoValue);
  }

  undo() {
    console.log(this._executedValue);
    this._undoValue = this._undo(this._executedValue);
  }
}

export default Command;
