import * as React from 'react';
import {
  DefaultLinkModel,
  DefaultLinkFactory
} from '@projectstorm/react-diagrams';
import { Fab, Tooltip } from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import Visibility from '@material-ui/icons/Visibility';
import DetailsIcon from '@material-ui/icons/Details';
//import { func } from 'prop-types';

import './link.scss';
// eslint-disable-next-line
import classes from '*.module.css';

export class CustomLinkModel extends DefaultLinkModel {
  listenerRef: any;
  added: boolean = false;
  isConnectedToANode:boolean = false;
  buttonsState:boolean = false;
  handleButtonsStateEnabled: () => void;
  handleButtonsStateDisabled: () => void;
  constructor() {
    super({
      type: 'custom-link',
      width: 4
    });
    this.handleButtonsStateEnabled = () => {
      this.buttonsState = true;
      //console.log("handleButtonsStateEnabled!");
    }
    this.handleButtonsStateDisabled = () => {
      this.buttonsState = false;
      //console.log("handleButtonsStateDisabled!");
    }
  }
}

type CustomLinkState = {
  hover: boolean;
};

export class CustomLinkSegment extends React.Component<
  {
    model: CustomLinkModel;
    path: string;
  },
  CustomLinkState
> {
  state: CustomLinkState = {
    hover: false
  };
  path: SVGPathElement | null = null;
  pathContainer: any;
  percent: number = 50;
  arrow: SVGPolygonElement | null = null;
  buttonGroup: any = null;
  mounted: boolean = false;
  eventsAdded: boolean = false;
  radtodeg: number = 180 / Math.PI;
  callback: () => any = () => {};
  /* constructor(props: any) {
    super(props);
  } */
  componentDidMount() {
    this.mounted = true;
    this.callback = () => {
      if (!this.path || !this.arrow) {
        return;
      }

      //this.percent += 2;
      // if (this.percent > 100) {
      //   this.percent = 2;
      // }

      let point = this.path.getPointAtLength(
        this.path.getTotalLength() * (this.percent / 100.0)
      );

      let point2 = this.path.getPointAtLength(
        this.path.getTotalLength() * ((this.percent - 2) / 100.0)
      );
      if (!this.eventsAdded) {
        this.eventsAdded = true;
        this.pathContainer.addEventListener('mouseover', () => {
          //if (!this.props.model.isLocked()) {
          this.setState({ hover: true });
          // }
        });

        this.pathContainer.addEventListener('mouseout', () => {
          //if (!this.props.model.isLocked()) {
          this.setState({ hover: false });
          //}
        });
      }

      var dx = point.x - point2.x;
      var dy = point.y - point2.y;
      var angle = 90 + Math.atan2(dy, dx) * this.radtodeg;

      this.arrow.setAttribute(
        'transform',
        'translate(' + point.x + ' ' + point.y + ') rotate(' + angle + ')'
      );
      var px = point.x - 50;
      var py = point.y + 10;
      this.buttonGroup.style.transform =
        'translate(' + px + 'px, ' + py + 'px)';

      this.buttonGroup.style.pointerEvents = 'all';

      if (this.mounted) {
        requestAnimationFrame(this.callback);
      }
    };
    requestAnimationFrame(this.callback);
  }
  componentWillUnmount() {
    this.mounted = false;
  }
  handlePeekClick(e: any) {
    this.props.model.handleButtonsStateEnabled();
    this.props.model.fireEvent({ model: this.props.model }, 'peek');
  }
  handlePreviewClick(e: any) {
    this.props.model.fireEvent({ model: this.props.model }, 'browseLink');
  }
  handleEditClick() {
    this.props.model.fireEvent({ model: this.props.model }, 'editLink');
  }
  handleDeleteClick() {
    this.props.model.fireEvent({ model: this.props.model }, 'deleteLink');
  }
  render() {
    //console.log("arrow props: ");
    //console.log(this.props);
    var hitArea = this.props.model.getOptions().width || 0;
    if (this.props.model.getOptions().width !== undefined) {
      hitArea += 60;
    }
    return (
      <React.Fragment>
        <g
          className="custom-link"
          ref={ref => {
            this.pathContainer = ref;
          }}
        >
          <path
            fill="none"
            ref={ref => {
              this.path = ref;
            }}
            strokeWidth={this.props.model.getOptions().width}
            d={this.props.path}
            className={'link-path'}
          />
          <path
            fill="none"
            ref={ref => {
              this.path = ref;
            }}
            strokeWidth={this.props.model.getOptions().width}
            d={this.props.path}
            className={
              this.state.hover
                ? 'link-path pulse-path hovered'
                : 'link-path pulse-path'
            }
          />
          <path
            fill="none"
            ref={ref => {
              this.path = ref;
            }}
            strokeWidth={hitArea}
            stroke="rgba(0,0,0,0)"
            className="link-path-hitarea"
            d={this.props.path}
          />
          <polygon
            ref={ref => {
              this.arrow = ref;
            }}
            points="0,-10 -10,10 10,10"
            className="link-arrow"
          />
          <foreignObject
            className="link-button-group-container"
            width="100%"
            height="100%"
          >
            <div
              ref={ref => {
                this.buttonGroup = ref;
              }}
              className={
                !this.props.model.isLocked() && this.state.hover
                  ? 'link-button-group show'
                  : 'link-button-group hide'
              }
            >
              <Tooltip 
                arrow={true}
                title="Peek" 
                placement="right" 
                className="default-tooltip"
                classes={{
                  tooltip: "default-tooltip",
                  popper: "default-popper"
                }}
              >
                <Fab
                  color="primary"
                  aria-label="peek"
                  size="small"
                  onClick={this.handlePeekClick.bind(this)}
                  className="peek-arrow"
                  disabled={this.props.model.buttonsState}
                >
                  <Visibility />
                </Fab>
              </Tooltip>
              <Tooltip 
                arrow={true}
                title="Preview" 
                placement="right" 
                className="default-tooltip"
                classes={{
                  tooltip: "default-tooltip",
                  popper: "default-popper"
                }}
              >
                <Fab
                  color="primary"
                  aria-label="visualize"
                  size="small"
                  onClick={this.handlePreviewClick.bind(this)}
                  disabled={this.props.model.buttonsState}
                >
                  <DetailsIcon />
                </Fab>
              </Tooltip>
              <Tooltip 
                arrow={true}
                title="Edit" 
                placement="right" 
                className="default-tooltip"
                classes={{
                  tooltip: "default-tooltip",
                  popper: "default-popper"
                }}
              >
                <Fab
                  color="primary"
                  aria-label="edit"
                  size="small"
                  onClick={this.handleEditClick.bind(this)}
                  disabled={this.props.model.buttonsState}
                >
                  <EditIcon />
                </Fab>
              </Tooltip>
              <Tooltip 
                arrow={true}
                title="Delete" 
                placement="right" 
                className="default-tooltip"
                classes={{
                  tooltip: "default-tooltip",
                  popper: "default-popper"
                }}
              >
                <Fab
                  color="primary"
                  aria-label="delete"
                  size="small"
                  onClick={this.handleDeleteClick.bind(this)}
                  disabled={this.props.model.buttonsState}
                >
                  <DeleteIcon />
                </Fab>
              </Tooltip>
            </div>
          </foreignObject>
        </g>
      </React.Fragment>
    );
  }
}

export class CustomLinkFactory extends DefaultLinkFactory {
  constructor() {
    super('custom-link');
  }
  generateModel(): CustomLinkModel {
    return new CustomLinkModel();
  }
  generateLinkSegment(model: CustomLinkModel, selected: boolean, path: string) {
    return (
      <g>
        <CustomLinkSegment model={model} path={path} />
      </g>
    );
  }
}
