import React, { Component, Fragment } from 'react';
import { Link } from "react-router-dom";
import {
  Button,
  CssBaseline,
  TextField,
  FormHelperText,
  Link as MaterialUiLink,
  Grid,
  Box,
  Typography,
  Container,
  Divider
} from '@material-ui/core';
import ErrorIcon from '@material-ui/icons/Error';
import * as EmailValidator from 'email-validator';
import { initiateForgotPassword } from '../../../services';
//import '../login/login.css';

interface Props {
  data: any;
  classes: any;
  history: any;
}

interface State {
  emailValidation: boolean;
  emailValue: any;
  emailErrorMsg: any;
  fpassSubmitErrorState: boolean;
  fpassSubmitErrorMsg: string;
  fpassSubmitSuccessState: boolean;
  fpassSubmitSuccessMsg: string;
  fpassSubmitState: boolean;
  timeRemainingInSeconds: number;
}

class FPass extends Component<Props, State> {

  private timer: any;
  
  state = {
    emailValidation: true,
    emailValue: '',
    emailErrorMsg: '',
    fpassSubmitErrorState: false,
    fpassSubmitErrorMsg: '',
    fpassSubmitSuccessState: false,
    fpassSubmitSuccessMsg: '',
    fpassSubmitState: false,
    timeRemainingInSeconds: 10
  }

  handleEmailChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    //console.log("get email val: " + e.target.value)
    this.setState({ emailValue: e.target.value })
  }

  handleFPassFormSubmit = (e: React.ChangeEvent<HTMLFormElement>) => {
    this.setState({ fpassSubmitState: true });
    e.preventDefault()

    let emailIsValid = false

    setTimeout(() => {
      if(this.state.emailValue !== ''){
        //console.log("email with val: " + this.state.emailValue)
        if(EmailValidator.validate(this.state.emailValue) === true){
          this.setState({ 
            emailValidation: true,
            emailErrorMsg: ''
          })
          emailIsValid = true
        }else{
          this.setState({ 
            emailValidation: false, 
            emailErrorMsg: 'Invalid email' 
          })
        }
      }else{
        //console.log("email without val: " + this.state.emailValue)
        this.setState({ 
          emailValidation: false, 
          emailErrorMsg: 'Email is required' 
        })
      }

      if(emailIsValid === true){
        // FORGOT PASSWORD / INITIATE-FORGOT-PASSWORD API CALL GOES HERE AND IF EVERYTHING OK, GOES TO LOGIN
        initiateForgotPassword(this.state.emailValue).then(res => {
          if(res.status === 200){
            this.setState({ fpassSubmitErrorMsg: '' })
            this.setState({ fpassSubmitErrorState: false })
            //this.setState({ fpassSubmitSuccessMsg: 'Email message with instructions has been sent. Please, click on button bellow to Reset password' })
            this.setState({ fpassSubmitSuccessMsg: 'Email message with instructions has been sent' })
            this.setState({ fpassSubmitSuccessState: true })
            this.timer = setInterval(() => {
              this.decrementTimeRemaining();
            }, 1000);
          }else if(res.status === 401){
            //this.setState({ fpassSubmitErrorMsg: 'Error 401: Something went wrong!' })
            this.setState({ fpassSubmitErrorMsg: 'You have entered an invalid email/username' })
            this.setState({ fpassSubmitErrorState: true })
          }else if(res.status === 404){
            this.setState({ fpassSubmitErrorMsg: 'Error 404: Something went wrong!' })
            this.setState({ fpassSubmitErrorState: true })
          }else{
            this.setState({ fpassSubmitErrorMsg: 'Email not found' })
            this.setState({ fpassSubmitErrorState: true })
          }
        }).catch(err => {
          if(err.response.status === 404) {
            this.setState({ fpassSubmitErrorMsg: 'Error 404: Something went wrong!' })
            this.setState({ fpassSubmitErrorState: true })
          } else if(err.response.status === 401) {
            //this.setState({ fpassSubmitErrorMsg: 'Error 401: Something went wrong!' })
            this.setState({ fpassSubmitErrorMsg: 'You have entered an invalid email/username' })
            this.setState({ fpassSubmitErrorState: true })
          }else{
            this.setState({ fpassSubmitErrorMsg: 'Email not found' })
            this.setState({ fpassSubmitErrorState: true })
          }
        })
        //this.props.history.push('/login')
      }
      this.setState({ fpassSubmitState: false });
    }, 1000);
  }

  decrementTimeRemaining = () => {
    if (this.state.timeRemainingInSeconds > 0) {
      this.setState({
        timeRemainingInSeconds: this.state.timeRemainingInSeconds - 1
      });
    } else {
      clearInterval(this.timer!);
      this.setState({
        timeRemainingInSeconds: 10
      });
      this.props.history.push("/reset-password?e=" + this.state.emailValue);
    }
  };

  render() {
    const {
      emailValue,
      emailValidation,
      emailErrorMsg,
      fpassSubmitErrorState,
      fpassSubmitErrorMsg,
      fpassSubmitSuccessState,
      fpassSubmitSuccessMsg,
      fpassSubmitState,
      timeRemainingInSeconds
    } = this.state;
    return (
      <Fragment>
        {fpassSubmitState === true &&
          <div className="mask"></div>
        }
        <Container className="main-container" component="main" maxWidth="xs">
          <CssBaseline />
          <div className="paper-box">
            <Typography className="login-logo" component="h2" variant="caption">
              <span>Ikigai</span>
              <span>Labs</span>
            </Typography>
            <Box mt={4} mb={1}>
              <Typography component="h1" variant="h5">
                Forgot Password
              </Typography>
              <Divider variant="middle" className="subtitle-divider" />
            </Box>
            <Typography className="subtitle" component="div" variant="caption">
              Lost your password? <br />
              Please enter your username or email address. You will receive a link to create a new password via email.
            </Typography>
            {fpassSubmitSuccessState === false ?
              <form 
                className="login-form" 
                autoComplete="off" 
                noValidate
                onSubmit={this.handleFPassFormSubmit}
              >
                {emailValidation ?
                  <TextField
                    id="fpass-email"
                    variant="outlined"
                    margin="normal"
                    required
                    fullWidth
                    label="Email"
                    InputLabelProps={
                      {
                        shrink: true
                      }
                    }
                    placeholder="Enter Your Email address"
                    type="search"
                    autoComplete="new-username"
                    autoFocus
                    value={emailValue}
                    onChange={this.handleEmailChange}
                  />
                :
                  <div>
                    <TextField
                      id="fpass-email"
                      variant="outlined"
                      margin="normal"
                      required
                      fullWidth
                      label="Email"
                      InputLabelProps={
                        {
                          shrink: true
                        }
                      }
                      placeholder="Enter Your Email address"
                      type="search"
                      autoComplete="new-username"
                      autoFocus
                      value={emailValue}
                      onChange={this.handleEmailChange}
                      error
                    />
                    <FormHelperText className="input-error" error>
                      <ErrorIcon />
                      <span>{emailErrorMsg}</span>
                    </FormHelperText>
                  </div>
                }
                {fpassSubmitErrorState === true &&
                  <Box mt={4} mb={4}>
                    <div className="submit-error has-txt-centered">{fpassSubmitErrorMsg}</div>
                  </Box>
                }
                <Box mt={4}>
                  <Grid container spacing={2} className="has-txt-centered">
                    <Grid item xs={12}>
                        <Button
                          type="submit"
                          variant="contained"
                          color="primary"
                          className="button"
                          fullWidth
                        >
                          {fpassSubmitState === true &&
                            <span className="btn-spinner"></span>
                          }
                          <span>Send reset email</span>
                        </Button>
                        <div><br/></div>
                        <Link to="/login">
                          or go back to Sign in page
                        </Link>
                    </Grid>
                  </Grid>
                </Box>
              </form>
            :
              <React.Fragment>
                <Box mt={4} mb={4}>
                  <div className="submit-success has-txt-centered">{fpassSubmitSuccessMsg}</div>
                </Box>
                <Box mt={4} mb={4}>
                  <span>Redirecting to Reset password page in </span>
                  <strong>{timeRemainingInSeconds}</strong>
                </Box>
              </React.Fragment>
            }
          </div>
          <Box className="login-copyright">
            <Typography variant="body2" color="textSecondary" align="center">
              {'Copyright  '}
              <MaterialUiLink color="inherit" href="http://ikigailabs.io/">
                IkigaiLabs.io
              </MaterialUiLink>{' '}
              {new Date().getFullYear()}
              {'.'}
            </Typography>
          </Box>
        </Container>
      </Fragment>
    );
    }
}

export default FPass;