import React, { Component } from 'react';
//import SnackbarMsg from './components/SnackbarMsg';
//import './App.scss';
//import './themes/index.scss';
import {
  FlowDiagramContainer,
  DatasetContainer,
  IkiAppContainer,
  LoginContainer,
  //SignupContainer,
  //FPassContainer,
  //ResetPasswordContainer,
  DashViewContainer
} from './routes';

import ProtectedRoute from './components/protected-route';

import { Switch, Route } from 'react-router-dom';
import { Dispatch } from 'redux';
import { checkForLoginCookieActionStart, softLogoutAction } from './actions';
import { connect } from 'react-redux';
import { State } from './reducers';
import { _axios } from './common';

//import Fab from "@material-ui/core/Fab";

//import PublishIcon from "@material-ui/icons/Publish";
//import { Button, Popover } from '@material-ui/core';
//import LinearProgress from '@material-ui/core/LinearProgress';
//import ReplayIcon from '@material-ui/icons/Replay';
//import ErrorOutlineIcon from '@material-ui/icons/ErrorOutline';
//import red from '@material-ui/core/colors/red';
//import DeleteIcon from '@material-ui/icons/Delete';

// Check if production or development mode
if(process.env.NODE_ENV){
  console.log("process.env.NODE_ENV: " + process.env.NODE_ENV);
}

type AppProps = {
  checkForLoginCookie: () => void;
  checkForLoginComplete: boolean;
  isLoggedIn: boolean;
  softLogout: () => void;
};

const mapDispatchToProps = (dispatch: Dispatch) => ({
  checkForLoginCookie: () => dispatch(checkForLoginCookieActionStart()),
  softLogout: () => dispatch(softLogoutAction())
});

const mapStateToProps = (state: State) => ({
  checkForLoginComplete: state.userReducerState.checkForLoginCookieComplete,
  isLoggedIn: state.userReducerState.isLoggedIn,
});

class AppComponent extends Component<AppProps> {
  state = { 
    winWidth: 0, 
    winHeight: 0,
    internetSnackbarAppearance: '',
    internetSnackbarOpen: false,
    internetSnackbarDuration: '',
    internetSnackbarType: '',
    internetSnackbarMsg: '',
    showProgress:false,
  };
  componentDidMount() {
    //Intercept responses and if the API throws error and the status code is 401 logout user.
    _axios.interceptors.response.use(
      response => {
        return response;
      },
      error => {
        if (error.response) {
          const { status } = error.response;
          if (status === 401) {
            //Soft logout just logs out the user without calling the logout API but just clearing the cookies.
            this.props.softLogout();
          }
        }
        return Promise.reject(error);
      }
    );
    //Check if login cookie exists and navigate to dashboard if it exists.
    this.props.checkForLoginCookie();
    console.log(_axios.interceptors.response);
    window.addEventListener('resize', this.windowDimensions);
    this.windowDimensions();
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.windowDimensions);
  }
  windowDimensions = () => {
    this.setState({
      winWidth: window.innerWidth,
      winHeight: window.innerHeight
    });
  };
  getMobileResizeMessage() {
    if (this.state.winWidth < 1024) {
      return (
        <div className="mobile-wrap-container">
          <div className="mobile-wrap">
            <div className="logo">
              <span className="ikigai">Ikigai</span>
              <span className="labs">Labs</span>
            </div>
            <div className="icon-holder-wrap">
              <div className="icon-holder">
                <i className="zmdi zmdi-smartphone-android"></i>
              </div>
              <div className="icon-holder">
                <i className="zmdi small zmdi-long-arrow-right"></i>
              </div>
              <div className="icon-holder">
                <i className="zmdi zmdi-desktop-windows"></i>
              </div>
            </div>
            <div className="txt-holder">
              <div>Please use a device with a larger screen </div>
              <div> or maximize your browser window</div>
              <div>Thank you</div>
            </div>
            <div className="copyright">
              <span>{'Copyright  IkigaiLabs, '}</span>
              <span>{new Date().getFullYear()}</span>
            </div>
          </div>
        </div>
      );
    }
  }
  render() {
    // this.checkInternetConnection();
    if (!this.props.checkForLoginComplete) {
      return (
        <div className="loader-wrapper">
          <div className="loader"></div>
          <div className="loader-txt">Loading...</div>
        </div>
      );
    } else {
      return (
        <div>
          {this.getMobileResizeMessage()}
          <Switch>
            <Route path="/login" component={LoginContainer}></Route>
            {/* <Route path="/signup" component={SignupContainer}></Route>
            <Route path="/forgot-password" component={FPassContainer}></Route>
            <Route
              path="/reset-password"
              component={ResetPasswordContainer}
            ></Route> */}
            {/* <Route path="/complete-registration" component={SignupConfirmContainer}></Route> */}
            {/* <Route path="/dashboard" component={IkiAppContainer}></Route> */}
            <ProtectedRoute
              path="/dashboard"
              loggedin={this.props.isLoggedIn}
              redirectTo="/login"
              component={IkiAppContainer}
            ></ProtectedRoute>
            <ProtectedRoute
              path="/flow/:id"
              loggedin={this.props.isLoggedIn}
              redirectTo="/login"
              component={FlowDiagramContainer}
            ></ProtectedRoute>
            <ProtectedRoute
              path="/dash/:id"
              loggedin={this.props.isLoggedIn}
              redirectTo="/login"
              component={DashViewContainer}
            ></ProtectedRoute>
            <ProtectedRoute
             path="/dataset/:id"
             loggedin={this.props.isLoggedIn}
             redirectTo="/login"
             component={DatasetContainer}
             ></ProtectedRoute>
            <Route path="/" component={LoginContainer}></Route>
          </Switch>
        </div>
      );
    }
  }
}

export const App = connect(mapStateToProps, mapDispatchToProps)(AppComponent);

//export  App;
