import React, { Component } from 'react';
//import classNames from 'classnames/bind';
import styles from './Divider.module.scss';
//let cx = classNames.bind(styles);

export default class IkiDivider extends Component {
  render() {
    //const _size = this.props._size ? this.props._size : 'normal';
    //const _color = this.props._color ? this.props._color : 'default';
    const _text = this.props._text ? this.props._text : '';
    return (
      <div className={styles.textDivider}>
        {_text !== '' && (
          <span>{_text}</span>
        )}
      </div>
    );
  }
}
