import React, { Component } from 'react';
/*import {
    Button
} from '@material-ui/core';*/
//import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import './iki-loader.scss';
/*import {
    cancelAPIrequestFunction
  } from '../../services';*/

export default class IkiLoader extends Component {
    /*handleGoBack(backLink){
        //window.history.back();
        //cancelAPIrequestFunction();
        //this.props.history.push(backLink);
    }*/
    
    render() {
        const { 
            loadingTxt, 
            fadeOut, 
            //backLink 
        } = this.props;
        var placementClass = "fixed";
        if(this.props.placement){
            placementClass = this.props.placement;
        }
        var componentClassName = "";
        if(fadeOut){
            componentClassName = "iki-loader-wrap hide " + placementClass;
        }else{
            componentClassName = "iki-loader-wrap " + placementClass;
        }
        return (
            <div className={componentClassName}>
                <div className="iki-loader-inner">
                    <span className="iki-spinner"></span>
                    <div className="iki-loader-txt">{loadingTxt}</div>
                </div>
                {/*backLink &&
                    <div className="ikiloader-back-btn-holder">
                        <Button
                            color="default"
                            variant="contained"
                            className="info-drawer-action-button"
                            startIcon={<ArrowRightAltIcon className="back-icon" />}
                            onClick={() => this.handleGoBack(backLink)}
                        >
                            Go Back
                        </Button>
                    </div>
                */}
            </div>
        );
    }
}