import React, { Component, Fragment } from 'react';
import {
  Box,
  Button,
  Grid,
  Container,
  Card,
  CardContent,
  TablePagination
} from '@material-ui/core';
import FindInPageIcon from '@material-ui/icons/FindInPage';
import SearchIcon from '@material-ui/icons/Search';
import ViewListIcon from '@material-ui/icons/ViewList';
import ViewComfyIcon from '@material-ui/icons/ViewComfy';
import FolderOutlinedIcon from '@material-ui/icons/FolderOutlined';
import IkiLoader from './../../components/IkiLoader';
import SnackbarMsg from './../../components/SnackbarMsg';
import IkiPageHeader from '../../components/IkiLayout/IkiPageHeader'
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiButtonGroup from '../../components/IkiFormElements/IkiButtonGroup';
import IkiInput from '../../components/IkiFormElements/IkiInput';
import IkiGrid from '../../components/IkiLayout/IkiGrid';
import IkiDialog from '../../components/IkiDialog';
import IkiTable from '../../components/IkiLayout/IkiTable';
import { getCookie, removeCookie } from '../../util/cookie';
import {
  ShareProjectDialog,
  DuplicateProjectDialog
} from '../../components/dialogs';
import {
  shareProject,
  exitSession as exitSessionManually
} from './../../services/app-data.service';

//import './projects-style.scss';
import TablePaginationActions from '@material-ui/core/TablePagination/TablePaginationActions';
// import { kMaxLength } from 'buffer';

type ProjectsProps = {
  projectsLoaded: boolean;
  projectsLoadedError: string;
  projects: any;
  data: any;
  classes: any;
  history: any;
  createNewProject: (name: string) => void;
  deleteProject: (id: string) => void;
  duplicateProject: (projectId: string, name: string, targetUser: string) => void;
  email: string;
  metabaseLoginId: string;
  activeProjectId: string;
  cleanSession: () => void;
  fireReturnToProject: (sourcePage: string) => void;
};
type ProjectsState = {
  sort: string;
  newProjectDialogState: boolean;
  newProjectName: string;
  deleteDialogState: boolean;
  deleteProjectID: string;
  tblRowActionBtnState: boolean;
  newProjectNameError: boolean;
  newProjectNameErrorMsg: string;
  snackbarCreateProSuccessState: boolean;
  projectDeleteDialogState: boolean;
  deleteDialogProjectID: string;
  deleteDialogProjectName: string;
  projectShareDialogState: boolean;
  shareDialogProjectID: string;
  shareDialogProjectName: string;
  duplicateDialogProjectID: string;
  duplicateDialogProjectName: string;
  duplicateDialogProjectState: boolean;
  emailValidation: boolean;
  emailErrorMsg: string;
  projectShareDialogErrorState: boolean;
  projectShareDialogErrorMsg: string;
  rowsPerPage: number;
  page: number;
  searchTerm: string;
  projectsFilter: string;
  renderAsList: boolean;
  tableSortAsc: boolean;
  tableSortProperty: string;
  clickedBox: string;
  snackbarOpen: boolean;
  snackbarMessage: any;
  snackbarType: string;
  sharingLoading: boolean;
  newProjectLoading: boolean;
  deletingLoading: boolean;
  duplicateLoading: boolean;
};

class ProjectsComponent extends Component<ProjectsProps, ProjectsState> {
  filterOptions: any = [];
  state = {
    sort: 'all',
    newProjectDialogState: false,
    newProjectName: '',
    newProjectNameError: false,
    newProjectNameErrorMsg: '',
    deleteDialogState: false,
    deleteProjectID: '',
    tblRowActionBtnState: false,
    snackbarCreateProSuccessState: false,
    projectDeleteDialogState: false,
    deleteDialogProjectID: '',
    deleteDialogProjectName: '',
    projectShareDialogState: false,
    shareDialogProjectID: '',
    shareDialogProjectName: '',
    emailValidation: true,
    emailErrorMsg: '',
    projectShareDialogErrorState: false,
    projectShareDialogErrorMsg: '',
    rowsPerPage: 16,
    page: 0,
    searchTerm: '',
    projectsFilter: 'all',
    renderAsList: true,
    tableSortAsc: true,
    tableSortProperty: '',
    clickedBox: '',
    snackbarOpen: false,
    snackbarMessage: null,
    snackbarType: '',
    sharingLoading: false,
    newProjectLoading: false,
    deletingLoading: false,
    duplicateDialogProjectID: '',
    duplicateDialogProjectName: '',
    duplicateDialogProjectState: false,
    duplicateLoading: false
  };

  componentDidMount() {
    this.props.cleanSession();
    //console.log('Projects props...', this.props);
    //console.log('ProjectsComponent props...', this.props);
    //var activeProjectId = '';
    //var ikiProject_cookie = '';
    //ikiProject_cookie = getCookie('ikiProject');
    //console.log('ikiProject_cookie: ', ikiProject_cookie);
    /*this.props.fireReturnToProject("projects");
    if(this.props.activeProjectId !== '') {
      activeProjectId = this.props.activeProjectId;
    } 

    if(activeProjectId !== '') {
      exitSessionManually(activeProjectId).then((exitSessRes: any) => {
        this.props.cleanSession();
      }).catch((exitSessErr: any) => {
        //this.props.cleanSession();
      });
    }*/

    this.filterOptions = [
      {
        value: 'all',
        text: 'All projects'
      },
      {
        value: 'shared',
        text: 'Shared with me'
      }
    ]
  }

  handleEditProject = (projectId: string) => {
    this.props.history.push(`/dashboard/project/${projectId}`);
  };

  handleClickOverBox = (boxID: string) => {
    //let className = e.target.parentElement.className.split(' ')[0];
    //console.log("className: " + className);
    this.setState({ clickedBox: boxID });
  };

  handleCloseClickOverBox = (boxID: string) => {
    this.setState({ clickedBox: '' });
  };

  handleDeleteProjectDialog = (projectId: string, projectName: string) => {
    this.setState({
      deleteDialogProjectID: projectId,
      deleteDialogProjectName: projectName,
      projectDeleteDialogState: true
    });
  };

  handleShareProjectDialog = (projectId: string, projectName: string) => {
    this.setState({
      shareDialogProjectID: projectId,
      shareDialogProjectName: projectName,
      projectShareDialogState: true
    });
  };

  handleDuplicateProjectDialog = (projectId: string, projectName: string) => {
    this.setState({
      duplicateDialogProjectID: projectId,
      duplicateDialogProjectName: projectName,
      duplicateDialogProjectState: true
    });
  };

  /* handleDeleteProject = (projectId: string) => {
    this.props.deleteProject(projectId);
    this.setState({ projectDeleteDialogState: false });
  }; */

  handleDialogOpen = () => {
    this.setState({
      newProjectDialogState: true
    });
  };

  handleDialogClose = () => {
    this.setState({
      newProjectDialogState: false,
      newProjectName: '',
      projectDeleteDialogState: false,
      newProjectNameError: false,
      newProjectNameErrorMsg: '',
      projectShareDialogState: false,
      projectShareDialogErrorState: false,
      projectShareDialogErrorMsg: '',
      duplicateDialogProjectState: false
    });
  };

  handleDialogSave = () => {
    var projectsArray = this.props.projects;
    var checkProjectNameOverlap = false;
    // eslint-disable-next-line
    {
      projectsArray.map((projectData: any) => {
        if (projectData.name === this.state.newProjectName) {
          //console.log("HITHITHIT!!! - NAME: " + this.state.newProjectName + ", name: " + projectData.name);
          checkProjectNameOverlap = true;
        }
        return ''
      })
    }
    this.setState({ newProjectLoading: true })
    var newProjectName = this.state.newProjectName;
    if (newProjectName === '') {
      this.setState({
        newProjectNameError: true,
        newProjectLoading: false,
        newProjectNameErrorMsg: 'Project name required'
      });
    } else if (checkProjectNameOverlap !== false) {
      this.setState({
        newProjectNameError: true,
        newProjectLoading: false,
        newProjectNameErrorMsg:
          'Project with same name already exists. Please choose different name'
      });
    } else {
      this.props.createNewProject(newProjectName);
      setTimeout(() => {
        this.setState({
          newProjectNameError: false,
          newProjectNameErrorMsg: '',
          newProjectDialogState: false,
          snackbarMessage: (
            <Fragment>
              <div className="msg">
                You have succesfully created new project
              </div>
              <div className="val">{newProjectName}</div>
            </Fragment>
          ),
          snackbarType: 'success',
          snackbarOpen: true,
          newProjectName: '',
          newProjectLoading: false
        });
      }, 1500);
    }
  };

  handleDeleteDialogOpen = (id: string) => {
    this.setState({
      deleteDialogState: true,
      deleteProjectID: id
    });
  };

  handleDeleteDialogClose = () => {
    this.setState({
      deleteDialogState: false,
      deleteProjectID: ''
    });
  };

  handleSnackbarClose = () => {
    this.setState({ snackbarOpen: false });
  };

  /* handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setAge(event.target.value as string);
  };

  handleTableActionBtn = () => {
    if(this.state.tblRowActionBtnState === false){
      this.setState({ tblRowActionBtnState: true });
    }else{
      this.setState({ tblRowActionBtnState: false });
    }
  } */

  handleTableActionBtn = () => {
    if (this.state.tblRowActionBtnState === false) {
      this.setState({ tblRowActionBtnState: true });
    } else {
      this.setState({ tblRowActionBtnState: false });
    }
  };

  handleTableActionBtnOpen = () => {
    this.setState({ tblRowActionBtnState: true });
  };

  handleOpenSuccesSnackbar = () => {
    this.setState({ snackbarCreateProSuccessState: true });
  };

  handleCloseSuccesSnackbar = () => {
    this.setState({ snackbarCreateProSuccessState: false });
  };

  handleOpenShareSuccesSnackbar = () => {
    this.setState({ snackbarOpen: true });
  };

  handleCloseShareSuccesSnackbar = () => {
    this.setState({ snackbarOpen: false });
  };

  handleDeleteDialogCancel = () => {
    this.setState({ projectDeleteDialogState: false });
  };

  handleDeleteDialogSubmit = () => {
    this.setState({ deletingLoading: true });
    this.props.deleteProject(this.state.deleteDialogProjectID);
    setTimeout(() => {
      this.setState({
        projectDeleteDialogState: false,
        snackbarMessage: (
          <Fragment>
            <div className="msg">You have succesfully deleted project</div>
            <div className="val">{this.state.deleteDialogProjectName}</div>
          </Fragment>
        ),
        snackbarType: 'success',
        snackbarOpen: true,
        deletingLoading: false
      });
    }, 1500);
  };

  handleShareDialogCancel = () => {
    this.setState({
      projectShareDialogState: false,
      projectShareDialogErrorState: false,
      projectShareDialogErrorMsg: ''
    });
  };

  handleChangePage = (event: unknown, newPage: number) => {
    this.setPage(newPage);
  };

  handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.setRowsPerPage(parseInt(event.target.value, 10));
    this.setPage(0);
  };

  setPage = (page: number) => {
    this.setState({ page: page });
  };

  setRowsPerPage = (rows: number) => {
    this.setState({ rowsPerPage: rows });
  };

  handleChangeSearchTerm = (e: any) => {
    this.setState({
      searchTerm: e.target.value
    });
  };

  handleChangeProjectFilter = (e: any) => {
    this.setState({
      projectsFilter: e.target.value
    });
  };

  filterProjects = (projects: any) => {
    if (this.state.projectsFilter === 'shared') {
      projects = projects.filter(
        (p: any) => p.owner !== this.props.email
      );
    }
    if (this.state.searchTerm !== '') {
      projects = projects.filter(
        (p: any) =>
          p.name
            .toLowerCase()
            .indexOf(this.state.searchTerm.toLowerCase()) > -1
      );
    }
    return projects;
  };

  handleRenderAsList = () => {
    this.setState({ renderAsList: true });
  };

  handleRenderAsGrid = () => {
    this.setState({ renderAsList: false });
  };

  viewTypeSwitchButtons = () => {
    const buttons = [
      <Button
        key={1}
        color={this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsList()}
      >
        <ViewListIcon />
      </Button>,
      <Button
        key={2}
        color={!this.state.renderAsList ? 'primary' : 'default'}
        onClick={() => this.handleRenderAsGrid()}
      >
        <ViewComfyIcon />
      </Button>
    ]
    return buttons
  }

  getProjectFilters = (projects: any) => {
    return (
      <Fragment>
        <div className="filters-and-pagination">
          <div className="table-filters">
            <IkiInput 
              _label=""
              _placeholder="Search..."
              _type='text'
              _autoComplete="new-password"
              _value={this.state.searchTerm}
              _size="small"
              _margin="none"
              _filterType={true}
              _onChange={(e: any) => this.handleChangeSearchTerm(e)}
              _startIcon={
                {
                  _icon: (
                    <SearchIcon fontSize="small" />
                  )
                }
              }
            />
            <IkiInput 
              _label=""
              _placeholder=""
              _type='text'
              _autoComplete="new-password"
              _value={this.state.projectsFilter}
              _size="small"
              _margin="none"
              _filterType={true}
              _isSelect={true}
              _selectOptions={this.filterOptions}
              _selectOptionsKeyForVal='value'
              _selectOptionsKeyForTxt='text'
              _onChange={(e: any) => this.handleChangeProjectFilter(e)}
            />
            <IkiButtonGroup
              _variant="contained"
              _size="small"
              _color="secondary"
              _buttons={
                this.viewTypeSwitchButtons()  
              }
            />
          </div>
          <div className="table-pagination">
            <TablePagination
              component="div"
              labelRowsPerPage="Projects per page"
              rowsPerPageOptions={[8, 16, 32]}
              colSpan={3}
              count={projects.length}
              rowsPerPage={this.state.rowsPerPage}
              page={this.state.page}
              SelectProps={{
                inputProps: {
                  'aria-label': 'Projects per page'
                }
              }}
              onChangePage={this.handleChangePage}
              onChangeRowsPerPage={this.handleChangeRowsPerPage}
              ActionsComponent={TablePaginationActions}
            />
          </div>
        </div>
      </Fragment>
    );
  };

  onRequestSort = (event: any, property: string) => {
    this.setState({
      tableSortAsc: !this.state.tableSortAsc,
      tableSortProperty: property
    });
  };

  sortProjects = (projects: any) => {
    if (this.state.tableSortProperty !== '') {
      return this.sortOn(projects, this.state.tableSortProperty);
    } else {
      return projects;
    }
  };

  sortOn = (arr: any, property: string) => {
    //console.log(this.state.tableSortAsc);
    var sortedArray = arr.sort((a: any, b: any) => {
      if (b[property] < a[property]) {
        return this.state.tableSortAsc ? 1 : -1;
      }
      if (b[property] > a[property]) {
        return this.state.tableSortAsc ? -1 : 1;
      }
      return 0;
    });
    return sortedArray;
  };

  getProjectsList = () => {
    var { projectsLoaded, projects, metabaseLoginId } = this.props;
    const { page, rowsPerPage } = this.state;
    if (projectsLoaded && (metabaseLoginId === "" || metabaseLoginId === undefined)) {
      //console.log("projects: ");
      //console.log(projects);
      projects = this.sortProjects(this.filterProjects(projects));
      var pageProjects = projects.slice(
        page * rowsPerPage,
        page * rowsPerPage + rowsPerPage
      );
      if (pageProjects.length === 0) {
        return (
          <div className="cardbox-holder">
            {this.getProjectFilters(projects)}
            <Grid item xs={12}>
              <Box display="flex" alignItems="center" className="no-results">
                <FindInPageIcon className="no-results-icon" />
                <span className="no-results-txt">No projects found</span>
              </Box>
            </Grid>
          </div>
        );
      } else {
        /*{pageProjects.map((projectData: any) => {
          console.log("projectData: ");
          console.log(projectData);
        });
        }*/
        return (
          <div className="cardbox-holder">
            {this.getProjectFilters(projects)}
            {this.state.renderAsList && (
              <Grid item xs={12} className="i-pb-0">
                <Card className="cardbox has-table tableHolder">
                  <CardContent className="content table-inside">
                    <IkiTable
                          _type="Project"
                          _link='/dashboard/project/'
                          _header={
                            [
                              {
                                key: 'name',
                                name: 'Project'
                              },
                              {
                                key: 'project_id',
                                name: 'ID'
                              },
                              {
                                key: 'owner',
                                name: 'Owner'
                              }
                            ]
                          }
                          _data={pageProjects}
                          _functions={
                            [
                              {
                                edit: (itemId: string) => this.handleEditProject(itemId)
                              },
                              {
                                delete: (itemId: string, itemName: string) => this.handleDeleteProjectDialog(itemId, itemName)
                              },
                              {
                                share: (itemId: string, itemName: string) => this.handleShareProjectDialog(itemId, itemName)
                              },
                              {
                                clone: (itemId: string, itemName: string) => this.handleDuplicateProjectDialog(itemId, itemName)
                              }
                            ]
                          }
                      />
                  </CardContent>
                </Card>
              </Grid>
            )}
            {!this.state.renderAsList &&
              <IkiGrid
                _type='Project'
                _link='/dashboard/project/'
                _icon={<FolderOutlinedIcon />}
                _data={pageProjects}
                _functions={
                  [
                    {
                      edit: (itemId: string) => this.handleEditProject(itemId)
                    },
                    {
                      delete: (itemId: string, itemName: string) => this.handleDeleteProjectDialog(itemId, itemName)
                    },
                    {
                      share: (itemId: string, itemName: string) => this.handleShareProjectDialog(itemId, itemName)
                    },
                    {
                      clone: (itemId: string, itemName: string) => this.handleDuplicateProjectDialog(itemId, itemName)
                    }
                  ]
                }
              />
            }
            {projects.length > this.state.rowsPerPage && (
                <TablePagination
                  component="div"
                  className="pagination-sm"
                  labelRowsPerPage="Projects per page"
                  rowsPerPageOptions={[8, 16, 32]}
                  colSpan={3}
                  count={projects.length}
                  rowsPerPage={this.state.rowsPerPage}
                  page={this.state.page}
                  SelectProps={{
                    inputProps: {
                      'aria-label': 'Projects per page'
                    }
                  }}
                  onChangePage={this.handleChangePage}
                  onChangeRowsPerPage={this.handleChangeRowsPerPage}
                  ActionsComponent={TablePaginationActions}
                />
            )}
          </div>
        );
      }
    } else {
      //return <Loader />;
      return null;
    }
  };

  handleDuplicateProjectDialogSubmit = (projectId: string, newProjectName: string, targetUser: string) => {
    this.setState({ duplicateLoading: true });
    //console.log("projectId: " + projectId + ", newProjectName: " + newProjectName);
    //console.log("handleDuplicateProjectDialogSubmit - this.props.projects: ");
    //console.log(this.props.projects);
    var matchProjects_Arr = this.props.projects.filter(
      (p: any) => p.name === newProjectName
    );
    //console.log("dp newProject: ");
    //console.log(matchProjects_Arr);
    if (Object.keys(matchProjects_Arr).length > 0) {
      //console.log("COPY name");
      setTimeout(() => {
        this.setState({
          duplicateDialogProjectState: false,
          snackbarMessage: (
            <Fragment>
              <div className="msg">Project with same name already exists. Please choose different name</div>
            </Fragment>
          ),
          snackbarType: 'error',
          snackbarOpen: true,
          duplicateDialogProjectID: '',
          duplicateDialogProjectName: '',
          duplicateLoading: false
        });
      }, 1500);
    } else {
      //console.log("valid name");
      var duplicateAPIError: boolean = false;
      try {
        this.props.duplicateProject(projectId, newProjectName, targetUser);
      } catch (error) {
        //console.log("error: ", error);
        duplicateAPIError = true;
      }
      if (duplicateAPIError) {
        setTimeout(() => {
          this.setState({
            duplicateDialogProjectState: false,
            snackbarMessage: (
              <Fragment>
                <div className="msg">
                  Something went wrong. Please try again
                </div>
              </Fragment>
            ),
            snackbarType: 'error',
            snackbarOpen: true,
            duplicateDialogProjectID: '',
            duplicateDialogProjectName: '',
            duplicateLoading: false
          });
        }, 1500);
      } else {
        setTimeout(() => {
          this.setState({
            duplicateDialogProjectState: false,
            snackbarMessage: (
              <Fragment>
                <div className="msg">
                  You have succesfully cloned project
                </div>
              </Fragment>
            ),
            snackbarType: 'success',
            snackbarOpen: true,
            duplicateDialogProjectID: '',
            duplicateDialogProjectName: '',
            duplicateLoading: false
          });
        }, 1500);
      }
    }
  };

  handleShareDialogSubmit = (email: string) => {
    this.setState({ sharingLoading: true });
    try {
      shareProject(this.state.shareDialogProjectID, email)
        .then((res: any) => {
          //console.log('try res.status: ' + res.status);
          if (res.status === 200) {
            //console.log('sharing success');
            setTimeout(() => {
              this.setState({
                projectShareDialogState: false,
                projectShareDialogErrorState: false,
                projectShareDialogErrorMsg: '',
                snackbarMessage: (
                  <Fragment>
                    <div className="msg">
                      You have succesfully shared project{' '}
                      <span className="val">
                        {this.state.shareDialogProjectName}
                      </span>
                    </div>
                    <div>
                      with user <span className="val">{email}</span>
                    </div>
                  </Fragment>
                ),
                snackbarType: 'success',
                snackbarOpen: true,
                sharingLoading: false
              });
            }, 1500);
          } else {
            this.setState({
              projectShareDialogErrorState: true,
              projectShareDialogErrorMsg:
                'Sharing failed! Check user email and try again.'
            });
          }
        })
        .catch(err => {
          //console.log('try 2 res.status: ' + err);
          this.setState({
            projectShareDialogErrorState: true,
            projectShareDialogErrorMsg:
              'Sharing failed! Check user email and try again.',
            sharingLoading: false
          });
        });
    } catch (e) {
      this.setState({
        projectShareDialogState: false,
        projectShareDialogErrorState: false,
        projectShareDialogErrorMsg: '',
        snackbarMessage: (
          <Fragment>
            <div className="msg">
              Sharing failed! Check user email and try again.
            </div>
          </Fragment>
        ),
        snackbarType: 'error',
        snackbarOpen: true,
        sharingLoading: false
      });
    }
  };

  render() {
    //console.log("props metabaseLoginId: " + this.props.metabaseLoginId);
    //console.log("RENDER: ");
    //console.log(this.props);
    var { projectsLoaded, metabaseLoginId } = this.props;
    if (projectsLoaded && (metabaseLoginId === "" || metabaseLoginId === undefined)) {
      return (
        <Fragment>
          <Container className="main-container" component="main" maxWidth={false}>
            <Grid container>
              <Grid item xs={12}>
                <IkiPageHeader
                  _breadcrumbLink='/dashboard'
                  _breadcrumbText='Dashboard'
                  _breadcrumbTitle='Projects'
                  _actionButtons={
                    <IkiButton
                      _variant='contained'
                      _color='primary'
                      _label="New Project"
                      _type='button'
                      _fullWidth={false}
                      _loading={false}
                      _startIcon={
                        <i className="zmdi zmdi-plus zmdi-hc-fw"></i>
                        //<AddIcon />
                      }
                      _onClick={this.handleDialogOpen}
                    />
                  }
                />
              </Grid>
            </Grid>

            {this.getProjectsList()}
          </Container>
          <IkiDialog
            _open={this.state.newProjectDialogState}
            _onClose={this.handleDialogClose}
            _type='regular'
            _icon={false}
            _title='New project'
            _subtitle=''
            _size='xs'
            _content={
                <IkiInput 
                  _label="Project name"
                  _placeholder="Enter project name"
                  _type='text'
                  _value={this.state.newProjectName}
                  _onChange={(e: any) => {
                    this.setState({
                      newProjectName: e.target.value
                    });
                  }}
                  _characterCounter={40}
                  _autoFocus={true}
                  _fullWidth={true}
                  _error={this.state.newProjectNameErrorMsg}
                />
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={this.handleDialogClose}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Create"
                  _type='button'
                  _fullWidth={false}
                  _loading={this.state.newProjectLoading}
                  _disabled={this.state.newProjectLoading}
                  _isAlternate={true}
                  _onClick={this.handleDialogSave}
                />
              </Fragment>
            }
          />

          <IkiDialog
            _open={this.state.projectDeleteDialogState}
            _onClose={this.handleDialogClose}
            _type='delete'
            _icon={false}
            _title='Delete project'
            _subtitle=''
            _size='xs'
            _align='center'
            _content={
              <>
                <div className="section">
                  <div className="txt-dialog-faded1">{this.state.deleteDialogProjectID}</div>
                  <div className="txt-dialog-impact1">{this.state.deleteDialogProjectName}</div>
                </div>
                <div className="section">
                  <div className="txt-dialog-impact2 error">Are you sure ?</div>
                  <div className="error">
                    All your pipelines inside will be deleted as well.
                  </div>
                </div>
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={this.handleDeleteDialogCancel}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Delete"
                  _type='button'
                  _fullWidth={false}
                  _loading={this.state.deletingLoading}
                  _disabled={this.state.deletingLoading}
                  _isDanger={true}
                  _onClick={this.handleDeleteDialogSubmit}
                />
              </Fragment>
            }
          />
          {/* <DeleteProjectDialog
            projectId={this.state.deleteDialogProjectID}
            projectName={this.state.deleteDialogProjectName}
            handleDialogClose={this.handleDialogClose}
            handleDeleteDialogSubmit={this.handleDeleteDialogSubmit}
            handleDeleteDialogCancel={this.handleDeleteDialogCancel}
            open={this.state.projectDeleteDialogState}
            deleteSubmitLoading={this.state.deletingLoading}
          /> */}
          <ShareProjectDialog
            handleDialogClose={this.handleDialogClose}
            handleShareDialogCancel={this.handleShareDialogCancel}
            open={this.state.projectShareDialogState}
            projectId={this.state.shareDialogProjectID}
            projectName={this.state.shareDialogProjectName}
            projectShareDialogErrorState={this.state.projectShareDialogErrorState}
            projectShareDialogErrorMsg={this.state.projectShareDialogErrorMsg}
            onSubmit={this.handleShareDialogSubmit}
            shareSubmitLoading={this.state.sharingLoading}
          />
          <DuplicateProjectDialog
            projectID={this.state.duplicateDialogProjectID}
            projectName={this.state.duplicateDialogProjectName}
            open={this.state.duplicateDialogProjectState}
            onSubmit={(projectId: string, newProjectName: string, targetUser: string) => this.handleDuplicateProjectDialogSubmit(projectId, newProjectName, targetUser)}
            handleDialogClose={this.handleDialogClose}
            duplicateSubmitLoading={this.state.duplicateLoading}
          />
          <SnackbarMsg
            snackbarOpen={this.state.snackbarOpen}
            snackbarType={this.state.snackbarType}
            snackbarMsg={this.state.snackbarMessage}
            onSnackbarClose={this.handleSnackbarClose}
          />
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Container className="main-container" component="main" maxWidth={false}>
            <Grid container>
              <Grid item xs={12}>
                <IkiLoader
                  loadingTxt="Loading Projects..."
                  fadeOut={false}
                  placement="next-to-nav"
                />
              </Grid>
            </Grid>
          </Container>
        </Fragment>
      );
    }
  }
}

export default ProjectsComponent;
