import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//import classNames from 'classnames/bind';
import styles from './Breadcrumb.module.scss';

//let cx = classNames.bind(styles);

/**
   * Page Breadcrumb
   * @param _parentLink Parent Link - string
   * @param _parentText Parent Text content - string
   * @param _backIcon Back Icon - boolean (not required)
   * @param _pageTitle Page Title/Header - string
   */
export default class IkiBreadcrumb extends Component {
  render() {
    const { 
      _parentLink, 
      _parentText, 
      _pageTitle, 
    } = this.props;
    const _backIcon = this.props._backIcon ? this.props._backIcon : '';
    //console.log("IkiBreadcrumb props: ", this.props);
    return(
      <>
        <div className={styles.breadcrumb}>
            <div className={styles.path}>
                <Link to={_parentLink}>
                    {_backIcon !== '' && (
                        <i className="zmdi zmdi-long-arrow-left" />
                    )}
                    <span>{_parentText}</span>
                </Link>
            </div>
            <div className={styles.title}>
                <span>{_pageTitle}</span>
            </div>
        </div>
      </>
    );
  }
}