import { actionIds } from '../common';
import { push } from 'connected-react-router';
import { FlowState } from '../reducers/flow-reducer';

const getUserState = (state: any) => state.userReducerState;

export const rootSaga = function* root() {
  yield all([
    fork(watchSendAppDataRequestStart),
    fork(watchAddFlowRequest),
    fork(watchGetFlowsRequestStart),
    fork(watchSaveFlowsRequestStart),
    fork(watchUpdateFlowDataStart),
    fork(watchDeleteFlow),
    fork(watchRestoreFlow),
    fork(watchAuthenticationStart),
    fork(watchCheckForLoginCookieStart),
    fork(watchLogoutAction),
    fork(watchGetFlowDocActionStart),
    fork(watchGetProjectsActionStart),
    fork(watchCreateProjectActionStart),
    fork(watchDeleteProjectActionStart),
    fork(watchDuplicateProjectActionStart),
    fork(watchRenameProjectActionStart),
    fork(watchSoftLogoutAction),
    fork(watchGetDatasetsAction),
    fork(watchGetDashboardsAction),
    fork(watchCreateDatasetAction),
    fork(watchCreateDashboardAction),
    fork(watchEditDashboardAction),
    fork(watchEditDatasetAction),
    fork(watchUploadDatasetAction),
    fork(watchDeleteDatasetAction),
    fork(watchCreateDashboardForUploadAction),
    fork(watchLoginMetabaseAction),
    fork(watchGetSuggestions),
    fork(watchDeleteDashboardAction),
  ]);
};

function* watchEditDatasetAction() {
  yield takeEvery(actionIds.EDIT_DATASET_REQUEST_START, _editDataset);
}

function* watchEditDashboardAction() {
  yield takeEvery(actionIds.EDIT_DASHBOARD_REQUEST_START, _editDashboard);
}
function* watchLoginMetabaseAction() {
  yield takeEvery(actionIds.LOGIN_METABASE_SESSION_START, _loginMetabase);
}

function* watchDeleteDatasetAction() {
  yield takeEvery(actionIds.DELETE_DATASET_REQUEST_START, _deleteDataset);
}

function* watchDeleteDashboardAction() {
  yield takeEvery(actionIds.DELETE_DASHBOARD_REQUEST_START, _deleteDashboard);
}

function* watchUploadDatasetAction() {
  yield takeEvery(actionIds.UPLOAD_DATASET_REQUEST_START, _uploadDataset);
}

function* watchCreateDatasetAction() {
  yield takeEvery(actionIds.CREATE_DATASET_REQUEST_START, _createDataset);
}

function* watchCreateDashboardAction() {
  yield takeEvery(actionIds.CREATE_DASHBOARD_REQUEST_START, _createDashboard);
}

function* watchCreateDashboardForUploadAction() {
  yield takeEvery(actionIds.CREATE_DASHBOARD_UPLOAD_REQUEST_START, _createDashboardForUpload);
}

