function* _updateFlowDataStart(action: any) {
  //yield put(updateFlowDataActionComplete(nodeData));
  yield put(saveFlowsActionStart(action.payload));
}

function* _saveFlows(action: any) {
  const response = yield call(saveFlow, action.payload);
  yield put(saveFlowsActionComplete(response));
}

function* _getFlows(action: any) {
  try {
    const projectId = action.payload;
    const response = yield call(getFlows, action.payload);
    if (response.status === 200) {
      var flowsData = response.data.pipelines;
      yield put(getFlowsActionComplete({ projectId, flowsData }));
    } else {
      yield put(getFlowsActionFail(response));
    }
  } catch (e) {
    yield put(getFlowsActionFail(e.response));
  }
}

function* _restoreFlow(action: any) {
  yield put(restoreFlowActionComplete(action.payload));
  //yield put(saveFlowsActionStart());
}

function* _deleteFlow(action: any) {
  yield call(deletePipeline, action.payload.pipeline_id);
  yield put(deleteFlowActionComplete(action.payload));
  //yield put(saveFlowsActionStart());
}

function* _addFlow(action: any) {
  const id: string = yield call(createPipeline, action.payload);
  const pipelineObj: any = yield call(getPipeline, id);
  //const id: string = getRandomString();
  //pipelineObj.pipeline.title = 'Flow Title';
  yield put(addFlowActionComplete(pipelineObj));
  //yield put(saveFlowsActionStart(pipelineObj));
  //yield put(push(`/flow/${id}`));
}

function* _sendAppData(action: any) {
  const response = yield call(sendAppData, action.payload);
  yield put(sendAppDataCompleteAction(response));
}

function* _authenticate(action: any) {
  try {
    const response = yield call(authenticateUser, action.payload);
    yield put(authenticateRequestCompleteAction(response));
    yield put(getProjectsActionStart());
    yield put(push('/dashboard'));
  } catch (e) {
    yield put(authenticateRequestCompleteAction(e.response));
  }
}

function* _checkForLoginCookie() {
  var tokens = getCookie('tokens');
  if (tokens) {
    yield put(checkForLoginCookieActionComplete(tokens));
    yield put(getProjectsActionStart());
    //yield put(push('/dashboard'));
  } else {
    yield put(checkForLoginCookieActionFail());
    //yield put(push('/login'));
  }
}

function* _logout() {
  removeCookie('tokens');
  //removeCookie('ikiProject');
  try {
    yield put(resetFlowStateAction());
    yield put(resetUserStateAction());
    yield call(logout);
  } catch (e) {}
  yield put(push('/'));
}

function* _getFacetDoc() {
  try {
    var facetsData = yield call(getFacetDoc);
    yield put(getFacetDocActionComplete(facetsData));
  } catch (reason) {
    yield put(getFacetDocActionFail(reason));
  }
}

function* _getProjects() {
  try {
    var projects = yield call(getProjects);
    yield put(getProjectsActionComplete(projects));
  } catch (reason) {
    yield put(getProjectsActionFail(reason));
  }
}

function* _createProject(action: any) {
  try {
    var name = action.payload;
    var project_id = yield call(createProject, name);
    var owner = yield select(getUserState);
    yield put(
      createProjectActionComplete({ name, project_id, owner: owner.email })
    );
  } catch (reason) {
    yield put(createProjectActionFail(reason));
  }
}

function* _deleteProject(action: any) {
  try {
    const project_id = yield call(deleteProject, action.payload);
    yield put(push('/dashboard'));
    yield put(deleteProjectActionComplete(project_id));
  } catch (reason) {
    yield put(deleteProjectActionFail(reason));
  }
}

function* _duplicateProject(action: any) {
  try {
    const { projectId, name, targetUser } = action.payload;
    //var owner = yield select(getUserState);
    var currentOwner = yield select(getUserState);
    var currentOwnerEmail = currentOwner.email;
    var ownerEmail = targetUser !== '' ? targetUser : currentOwnerEmail;
    var project_id = yield call(duplicateProject, projectId, name, targetUser);
    //console.log("_duplicateProject - project_id: " + project_id + ", name: " + name);
    yield put(
      //duplicateProjectActionComplete({ name, project_id, owner: owner.email })
      duplicateProjectActionComplete({ name, project_id, owner: ownerEmail, user: currentOwnerEmail })
    );
  } catch (reason) {
    yield put(duplicateProjectActionFail(reason));
  }
}

function* _renameProject(action: any) {
  const { projectId, newName } = action.payload;
  try {
    const project_id = yield call(renameProject, projectId, newName);
    yield put(renameProjectActionComplete(project_id, newName));
  } catch (reason) {
    yield put(renameProjectActionFail(reason));
  }
}

function* _softLogout() {
  removeCookie('tokens');
  //removeCookie('ikiProject');
  yield put(resetFlowStateAction());
  yield put(resetUserStateAction());
  yield put(push('/'));
}

function* _getDatasets(action: any) {
  const projectId = action.payload;
  try {
    const datasetsResponse = yield call(getDatasetsForProject, projectId);
    //console.log("datasetsResponse: ");
    //console.log(datasetsResponse);
    const datasets = datasetsResponse;
    yield put(getDatasetActionComplete({ projectId, datasets }));
  } catch (reason) {
    yield put(getDatasetActionFail(reason));
  }
}

function* _getDashboards(action: any) {
  const projectId = action.payload;
  try {
    const dashboardsresponse = yield call(getDashboardsForProject, projectId);
    //console.log("datasetsResponse: ");
    //console.log(datasetsResponse);
    const dashboards = dashboardsresponse;
    yield put(getDashboardActionComplete({ projectId,dashboards }));
  } catch (reason) {
    yield put(getDatasetActionFail(reason));
  }
}

function* _createDataset(action: any) {
  const { datasetName, projectId, file } = action.payload;
  try {
    const datasetId: any = yield call(createDataset, datasetName, projectId);
    //console.log("_createDataset: ");
    //console.log(datasetId.dataset_id);
    const dataset_id = datasetId.dataset_id;
    //const datasetUploadUrl: any = yield call(getDatasetUploadUrl, dataset_id, "");
    //console.log("datasetUploadUrl: ");
    //console.log(datasetUploadUrl);
    if (file) {
      const datasetOverwrite: boolean = false;
      yield call(
        uploadDataset,
        dataset_id,
        datasetName,
        projectId,
        file,
        datasetOverwrite
      );
    }
    const dataset = yield call(getDataset, dataset_id);
    //console.log("createDatasetActionComplete: ")
    //console.log(dataset);
    yield put(createDatasetActionComplete(dataset));
  } catch (reason) {
    yield put(creatDatasetActionFail(reason));
  }
}
function* _createDashboardForUpload(action: any) {
  const { name,project_id,dataset_id,file } = action.payload;
  try {
    const dashboardData: any = yield call(createDashboard, name, project_id, dataset_id);
    //console.log("_createDataset: ");
    //console.log(datasetId.dataset_id);
    if (file) {
      const dashboardOverwrite: boolean = false;
      yield call(
        uploadDashboard,
        dashboardData.dashboard_id,
        file,
        dashboardOverwrite
      );
    }
    const dashboard = yield call(getDashboard, dashboardData.dashboard_id);
    //console.log("createDatasetActionComplete: ")
    //console.log(dataset);
    yield put(createDashboardActionComplete(dashboard));
  } catch (reason) {
    yield put(creatDashboardActionFail(reason));
  }
}

