function* _createDashboard(action: any) {
  const { name,project_id,dataset_id } = action.payload;
  try {
    const dashboard_id: any = yield call(createDashboard, name, project_id, dataset_id);
    //console.log("_createDataset: ");
    //console.log(datasetId.dataset_id);
    const dashboard = yield call(getDashboard, dashboard_id);
    //console.log("createDatasetActionComplete: ")
    //console.log(dataset);
    yield put(createDashboardActionComplete(dashboard));
  } catch (reason) {
    yield put(creatDashboardActionFail(reason));
  }
}

function* _uploadDataset(action: any) {
  const {
    datasetId,
    projectId,
    file,
    datasetName,
    datasetOverwrite
  } = action.payload;
  try {
    yield call(
      uploadDataset,
      datasetId,
      datasetName,
      projectId,
      file,
      datasetOverwrite
    );
    const dataset = yield call(getDataset, datasetId);
    yield put(editDatasetActionComplete(dataset));
  } catch (reason) {
    yield put(editDatasetActionFail(reason));
  }
}

function* _deleteDataset(action: any) {
  const datasetId = action.payload;
  try {
    yield call(deleteDataset, datasetId);
    yield put(deleteDatasetActionComplete(datasetId));
  } catch (reason) {
    yield put(deleteDatasetActionFail(datasetId));
  }
}

function* _deleteDashboard(action: any) {
  const dashboardId = action.payload;
  try {
    yield call(deleteDashboard, dashboardId);
    yield put(deleteDashboardActionComplete(dashboardId));
  } catch (reason) {
    yield put(deleteDashboardActionFail(dashboardId));
  }
}

function* _editDataset(action: any) {
  const { datasetName, datasetId, projectId, file } = action.payload;
  //console.log(" SAGAS _editDataset - datasetId: " + datasetId + ", datasetName: " + datasetName);
  try {
    yield call(editDataset, datasetId, datasetName, projectId);
    if (file) {
      const datasetOverwrite: boolean = true;
      yield call(
        uploadDataset,
        datasetId,
        datasetName,
        projectId,
        file,
        datasetOverwrite
      );  
    }
    const dataset = yield call(getDataset, datasetId);
    //console.log(" SAGAS _editDataset - dataset: ");
    //console.log(dataset);
    yield put(editDatasetActionComplete(dataset));
  } catch (reason) {
    yield put(editDatasetActionComplete(reason));
  }
}

function* _editDashboard(action: any) {
  const { name, dataset_id, project_id,dashboard_id,file } = action.payload;
  try {
    const fileName = ""
    yield call(editDashboard, dataset_id,name,project_id,dashboard_id,fileName);
    if(file){
      const dashboardOverwrite:boolean = true
      yield call(uploadDashboard, dashboard_id,file,dashboardOverwrite)
    }
    const dashboard = yield call(getDashboard, dashboard_id);
    yield put(editDashboardActionComplete(dashboard));
  } catch (reason) {
    yield put(editDashboardActionComplete(reason));
  }
}
function* _loginMetabase(action: any) {
  try {
    var projectId = action.payload;
    var session_id = yield call(enterSession, projectId);
    //console.log("_loginMetabase");
    //console.log(session_id);
    yield put(
      loginMetabaseActionComplete({ session_id })
    );
  } catch (reason) {
    yield put(loginMetabaseActionFail(reason));
  }
}

function* _getSuggestions(data:any){
  try {
    yield put(getSuggestionsActionStart(data));
  }catch(reason){
    yield put(getSuggestionsActionComplete(reason))
  }
}

export const getState = (state: FlowState) => state;
