import React, { Component, Fragment } from 'react';
import {
  DialogTitle,
  FormControl,
  Box,
  Grid,
  RadioGroup,
  FormControlLabel,
  Radio,
  FormLabel
} from '@material-ui/core';
//import Autocomplete from '@material-ui/lab/Autocomplete';
//import ErrorIcon from '@material-ui/icons/Error';
import IkiDialog from '../../components/IkiDialog';
import IkiButton from '../../components/IkiFormElements/IkiButton';
//import IkiInput from '../../components/IkiFormElements/IkiInput';
import IkiAutocomplete from '../../components/IkiFormElements/IkiAutocomplete';
import { getFriendlyLabel } from '../../util';

//import { createDataset, editDataset } from '../../services/app-data.service';

  type DataprepDialogState = {
    hasError: boolean;
    title: string;
    columnNames: any[];
    columnNameSelected: any;
    uTypeSelected: string;
    uFormatSelected: string;
    isLoading: boolean;
    colNameError: string;
    friendlyLabel: string;
  };
  
  type DataprepDialogProps = {
    open: boolean;
    title: string;
    columnNames: any[];
    uniformizationPreferences: {};
    onSubmit: (columnNameSelected: string, uTypeSelected: string, uFormatSelected: string) => void;
    onCancel: () => void;
  };

  /*const customColumns = [
    { title: "CC_ACTUAL_AMOUNT" },
    { title: "CREDIT_CARD_ID" },
    { title: "DOCUMENT_DATE" },
    { title: "COLUMN THAT DOESN'T EXIST" }
  ]*/

  class DataprepDialog extends Component<DataprepDialogProps, DataprepDialogState> {
    state = {
        hasError: false,
        title: "",
        columnNames: [],
        columnNameSelected: "",
        uTypeSelected: "",
        uFormatSelected: "",
        isLoading: false,
        colNameError: "",
        friendlyLabel: ""
    }

    componentDidMount(){
        //console.log("props columnNames: ");
        //console.log(this.props.columnNames);
        //const columnNames_Arr = Object.keys(this.props.columnNames).map((key) => this.props.columnNames[key]);
        var columnNames_Arr: any = [];
        Object.keys(this.props.columnNames).sort().map((key: any) => {
            var colVal = this.props.columnNames[key];
            var colValBeautified = getFriendlyLabel(colVal);
            columnNames_Arr.push({ "colKey": colVal, "colVal": colValBeautified });
            return { "colKey": colVal, "colVal": colValBeautified }
        })
        //console.log("columnNames_Arr: ");
        //console.log(columnNames_Arr);
        var friendlyLabel = getFriendlyLabel(this.props.title);
        this.setState({
            title: this.props.title,
            friendlyLabel: friendlyLabel,
            //columnNames: this.props.columnNames.sort()
            columnNames: columnNames_Arr
        })
    }

    handleClose(){
        this.setState({
            title: "",
            uTypeSelected: "",
            isLoading: false
        })
        this.props.onCancel();
    }

    handleSubmit(){
        if(this.state.title !== "DEDUPLICATION" && this.state.columnNameSelected === ""){
            this.setState({ colNameError: "Column Name is required" })
        }else{
            this.setState({ 
                isLoading: true ,
                colNameError: ""
            });
            // var dataPrepTitle = this.props.title;
            var columnNameSelected = this.state.columnNameSelected;
            var uTypeSelected = this.state.uTypeSelected;
            var uFormatSelected = this.state.uFormatSelected;
            var selectedValCleaned: string = "";
                Object.keys(this.state.columnNames).map((key: any) => {
                    //console.log("key: " + key);
                    //console.log(this.state.columnNames[key]);
                    if(columnNameSelected === this.state.columnNames[key]["colVal"]){
                        selectedValCleaned = this.state.columnNames[key]["colKey"]
                        return selectedValCleaned
                    }
                    return ''
                });
            //console.log("dataPrepTitle: " + dataPrepTitle + ", columnNameSelected: " + columnNameSelected + ", uTypeSelected: " + uTypeSelected + ", uFormatSelected: " + uFormatSelected)
            this.props.onSubmit(selectedValCleaned, uTypeSelected, uFormatSelected);
        }
    }

    handleColumnNameSelect = (selectedVal: any) =>{
        //console.log("handleColumnNameSelect - selectedVal: ", selectedVal);
        if(selectedVal !== null && selectedVal !== undefined && selectedVal !== ""){
            //var selectedValCleaned = getFriendlyLabel(selectedVal);
            var selectedValCleaned = selectedVal;
            //console.log("handleColumnNameSelect: ");
            //console.log(selectedValCleaned);
            this.setState({ 
                columnNameSelected: selectedValCleaned,
                colNameError: ""
            })
        }else{
            this.setState({ columnNameSelected: "" })
        }
    }

    prepareUniformizationOption(){
        return(
            <Box mb={2}>
                        <Grid container spacing={3}>
                            <Grid item xs={12}>
                                <IkiAutocomplete 
                                    _required={true}
                                    _error={this.state.colNameError}
                                    _label='Select Column Name'
                                    _placeholder='Type to search columns...'
                                    _value={this.state.columnNameSelected}
                                    _isMultiple={false}
                                    //_selectOptions={this.props.columnNames.sort()}
                                    _selectOptions={this.state.columnNames.sort()}
                                    _fullWidth={true}
                                    _keyValue='colKey'
                                    _nameValue='colVal'
                                    _onChange={(event: any, option: any) => this.handleColumnNameSelect(option)}
                                />
                                <FormControl 
                                    className="radio-btn-holder with-label" 
                                    component="fieldset" 
                                    margin="normal"
                                >
                                    <RadioGroup 
                                        aria-label="clone-type" 
                                        name="clone-type" 
                                    >
                                        <FormLabel component="div">Type:</FormLabel>
                                        {
                                            Object.keys(this.props.uniformizationPreferences).length > 0 &&
                                                Object.keys(this.props.uniformizationPreferences["uniformization_types"]).map((uniformizationType: any) => {
                                                    var uType = this.props.uniformizationPreferences["uniformization_types"][uniformizationType];
                                                    //console.log("uniformizationType: ");
                                                    //console.log(this.props.uniformizationPreferences["uniformization_types"][uniformizationType]);
                                                    return (
                                                        <FormControlLabel 
                                                            value={uType}
                                                            key={uniformizationType}
                                                            control={
                                                            <Radio 
                                                                color="primary"
                                                                size="small"
                                                                onClick={(e: any) => this.setState({ uTypeSelected: e.target.value })}
                                                            />
                                                            } 
                                                            label={getFriendlyLabel(uType)} 
                                                        />
                                                    )
                                                })
                                        }
                                    </RadioGroup>
                                </FormControl>
                            </Grid>
                            <Grid item xs={12}>
                                <FormControl 
                                    className="radio-btn-holder with-label" 
                                    component="fieldset" 
                                >
                                    <RadioGroup 
                                        aria-label="clone-type" 
                                        name="clone-type" 
                                    >
                                        <FormLabel component="div">Format:</FormLabel>
                                        { this.state.uTypeSelected !== "" ? (
                                            Object.keys(this.props.uniformizationPreferences).length > 0 &&
                                                Object.keys(this.props.uniformizationPreferences["uniformization_formats"][this.state.uTypeSelected.toLowerCase()]).map((textFormat: any) => {
                                                    var textFormatVal = this.props.uniformizationPreferences["uniformization_formats"][this.state.uTypeSelected.toLowerCase()][textFormat];
                                                    //console.log("textFormat: " + this.props.uniformizationPreferences["uniformization_formats"]["text"][textFormat]);
                                                    return (
                                                        <FormControlLabel 
                                                            value={textFormatVal}
                                                            key={textFormat}
                                                            control={
                                                                <Radio 
                                                                    color="primary"
                                                                    size="small"
                                                                    onClick={(e: any) => this.setState({ uFormatSelected: e.target.value })}
                                                                />
                                                            } 
                                                            label={getFriendlyLabel(textFormatVal)} 
                                                        />
                                                    )
                                                })
                                        ):(
                                            <div className="default-color">Choose Uniformization Type first.</div>
                                        )
                                        }
                                    </RadioGroup>
                                </FormControl>
                            </Grid>
                        </Grid>
                    </Box>
        )
    }

    prepareOptionContent(optionName: string){
        if(optionName === 'DEDUPLICATION'){
            return(
                <Box mb={2}>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <span className="default-color">Deduplication doesn't require any parameter. You are free to proceed, submit.</span>
                        </Grid>
                    </Grid>
                </Box>
            )
        }else{
            return(
                <Box mb={2}>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <IkiAutocomplete 
                                _required={true}
                                _error={this.state.colNameError}
                                _label='Select Column Name'
                                _placeholder='Type to search columns...'
                                _value={this.state.columnNameSelected}
                                _isMultiple={false}
                                _selectOptions={this.state.columnNames.sort()}
                                _fullWidth={true}
                                _keyValue='colKey'
                                _nameValue='colVal'
                                _onChange={(event: any, option: any) => this.handleColumnNameSelect(option)}
                            />
                        </Grid>
                    </Grid>
                </Box>
            )
        }
    }

    /*prepareDialogHeader(title: string){
        return (
                <DialogTitle className="dialog-title default-color" disableTypography={true}>
                    <Box mt={1}>
                        <div className="chain-and-group">
                            <span className="chain-group-title">Data Prep</span>
                        </div>
                        <div className="facet-title">{getFriendlyLabel(title)}</div>
                    </Box>
                </DialogTitle>
        )
    }*/

    render(){
        //console.log("this.props.uniformizationPreferences: ");
        //console.log(this.props.uniformizationPreferences);
        //console.log("uTypeSelected: " + this.state.uTypeSelected);
        const {
            title,
            isLoading
        } = this.state;
        return(
            <IkiDialog
                _open={this.props.open}
                _onClose={() => this.handleClose()}
                _type='share'
                _icon={false}
                _title={this.state.friendlyLabel}
                _subtitle='Data Prep'
                _size='xs'
                _align='left'
                _content={
                    <>
                        {
                            this.props.title === 'UNIFORMIZATION' ? (
                                this.prepareUniformizationOption()
                            ):(
                                this.prepareOptionContent(title)
                            )
                        }
                    </>
                }
                _actions={
                    <Fragment>
                        <IkiButton
                        _variant='text'
                        _color='default'
                        _label="Cancel"
                        _type='button'
                        _fullWidth={false}
                        _loading={false}
                        _onClick={() => this.handleClose()}
                        />
                        <IkiButton
                        _variant='contained'
                        _color='primary'
                        _label="Submit"
                        _type='button'
                        _fullWidth={false}
                        _loading={isLoading}
                        _disabled={isLoading}
                        _onClick={() => this.handleSubmit()}
                        />
                    </Fragment>
                }
            />
        );
    }
}

export default DataprepDialog;