import React, { Component } from 'react';
import { Dialog, DialogTitle, IconButton } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';

import './visualize-dialog.scss';
import Loader from '../../../components/loader';

type VisualizeDialogProps = {
  open: boolean;
  url: string;
  isMetabase: boolean;
  status: string;
  loading: boolean;
  onClose: () => void;
};

type VisualizeDialogState = {
  url: string;
  iFrameContentLoaded: boolean;
};

class VisualizeDialog extends Component<
  VisualizeDialogProps,
  VisualizeDialogState
  > {
  componentDidMount() {
    this.setState({
      iFrameContentLoaded: false
    });
  }
  onClose() {
    this.props.onClose();
  }
  getContent() {
    if (this.props.loading) {
      console.log('here***');

      return (
        <Loader>
          <div className="loader-txt browse-status">{this.props.status}</div>
        </Loader>
      );
    } else {
      return (
        <div className="iframe-container">
          {!this.state.iFrameContentLoaded && (
            <div>
              <Loader>
                <div className="loader-txt browse-status">
                  {this.props.status}
                </div>
              </Loader>
            </div>
          )}
          {this.props.isMetabase && this.state.iFrameContentLoaded && (
            <div className="iframe-cover"></div>
          )}
          <iframe
            title={"iKigai"}
            width="100%"
            height="100%"
            onLoad={() => {
              this.setState({
                iFrameContentLoaded: true
              });
            }}
            src={this.props.url}
            sandbox="allow-same-origin allow-scripts allow-popups allow-forms"
          ></iframe>
        </div>
      );
    }
  }
  render() {
    return (
      <Dialog open={this.props.open} maxWidth="xl" className="visualize-dialog">
        <DialogTitle className="title">
          <IconButton onClick={() => this.onClose()} className="close-btn">
            <CloseIcon />
          </IconButton>
        </DialogTitle>
        {this.getContent()}
      </Dialog>
    );
  }
}

export default VisualizeDialog;
