import React, { Component } from 'react';
import { withStyles } from '@material-ui/styles';
import { Container, Typography, Paper, Grid } from '@material-ui/core';
import { Link } from 'react-router-dom';

type HomeProps = {
  classes: any;
};
type HomeState = {};

const styles = (theme: any) => {
  console.log(theme);
  return {
    link: {
      width: '100%'
    },
    gridRoot: {
      flexGrow: 1,
      marginTop: theme.spacing(10)
    },
    title: {
      marginTop: 20,
      marginBottom: 20,
      textAlign: 'center' as 'center'
    },
    root: {
      padding: theme.spacing(5, 2)
    }
  };
};

class HomeComponent extends Component<HomeProps, HomeState> {
  render() {
    return (
      <Container>
        <h1 className={this.props.classes.title}>Hello there!</h1>
        <div className={this.props.classes.gridRoot}>
          <Grid item xs={6}>
            <Grid container justify="flex-start">
              <Link to="/dashboard" className={this.props.classes.link}>
                <Paper className={this.props.classes.root}>
                  <Typography variant="h5" component="h3">
                    Dashboard
                  </Typography>
                </Paper>
              </Link>
            </Grid>
          </Grid>
        </div>
      </Container>
    );
  }
}

export default withStyles(styles)(HomeComponent);
