import { State } from '../../reducers';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import ProjectComponent from './project-component';
import {
  addFlowAction,
  deleteFlowAction,
  getFlowsActionStart,
  logoutAction,
  renameProjectActionStart,
  deleteProjectActionStart,
  getDatasetActionStart,
  //shareProjectActionStart,
  createDatasetActionStart,
  deleteDatasetActionStart,
  editDatasetActionStart,
  getDashboardActionStart,
  createDashboardForUploadActionStart,
  loginMetabaseActionStart,
  editDashbarodActionStart,
  uploadDashboardActionStart,
  handleProjectReturn,
  handleCleanSession,
  deleteDashboardActionStart,
  handleFillSession,
  duplicateProjectActionStart
} from '../../actions';

const mapDispatchToProps = (dispatch: Dispatch) => ({
  addFlow: (flowObj: any) => dispatch(addFlowAction(flowObj)),
  deleteFlow: (flowObj: any) => dispatch(deleteFlowAction(flowObj)),
  getFlows: (projectId: string) => dispatch(getFlowsActionStart(projectId)),
  logout: () => dispatch(logoutAction()),
  renameProject: (projectId: string, newName: string) =>
    dispatch(renameProjectActionStart(projectId, newName)),
  deleteProject: (id: string) => dispatch(deleteProjectActionStart(id)),
  getDatasets: (projectId: string) =>
    dispatch(getDatasetActionStart(projectId)),
  getDashboards: (projectId:string) => 
    dispatch(getDashboardActionStart(projectId)),
  createDataset: (projectId: string, datasetName: string, file: File) =>
    dispatch(createDatasetActionStart(projectId, datasetName, file)),
  createDashboard: (projectId: string, datasetId: any, name:string, file: File) =>
    dispatch(createDashboardForUploadActionStart(projectId, datasetId, name, file)),
  editDataset: (
    projectId: string,
    datasetId: string,
    datasetName: string,
    file: File
  ) =>
    dispatch(editDatasetActionStart(projectId, datasetId, datasetName, file)),
  editDashboard:(
    projectId:string,
    datasetId:any,
    name:string,
    dashboardId:string,
    file:File
  ) => dispatch(editDashbarodActionStart(projectId,datasetId,name,dashboardId,file)),
  uploadDashboard:(
    dashboardId:string,
    file:File
  ) => dispatch(uploadDashboardActionStart(file,dashboardId)),
  deleteDataset: (datasetId: string) =>
    dispatch(deleteDatasetActionStart(datasetId)),
  deleteDashboard:(dashboardId:string) =>
    dispatch(deleteDashboardActionStart(dashboardId)),
  enterSession: (projectId: string) => {
      dispatch(loginMetabaseActionStart(projectId))
  },
  fireReturnToProject: (sourcePage: string) => {
    dispatch(handleProjectReturn(sourcePage))
  },
  cleanSession: () => {
    dispatch(handleCleanSession())
  },
  fillSessions: (proSess: string, projectId: string) => {
    dispatch(handleFillSession(proSess, projectId))
  },
  duplicateProject: (projectId: string, name: string, targetUser: string) => {
    dispatch(duplicateProjectActionStart(projectId, name, targetUser));
  },
});
const mapStateToProps = (state: State) => ({
  data: state.flowReducerState,
  email: state.userReducerState.email,
  metabaseLoginId: state.flowReducerState.metabaseLoginId,
  activePageType: state.flowReducerState.activePageType,
  uploadProgress: state.flowReducerState.uploadProgress,
});

export const ProjectContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(ProjectComponent);
