import React, { Component, Fragment } from 'react';
import { 
  Snackbar as MuiSnackbar,
  IconButton,
  CircularProgress 
} from '@material-ui/core';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import ErrorIcon from '@material-ui/icons/Error';
import CloseIcon from '@material-ui/icons/Close';
import HelpIcon from '@material-ui/icons/Help';
import WarningIcon from '@material-ui/icons/Warning';
import './snackbar.scss';

export default class SnackbarMsg extends Component {
  constructor(props) {
    super(props);
    this.state = {
      snackbarOpenState: false
    }
  }

  componentDidMount() {
    /*const snackbarOpen = this.props.snackbarOpen;
    this.setState({ snackbarOpenState: snackbarOpen });*/
  }

  setSnackbarIcon = () => {
    if(this.props.snackbarType === 'success'){
        return <CheckCircleIcon className="state-icon" />
    }else if(this.props.snackbarType === 'error'){
        return <ErrorIcon className="state-icon" />
    }else if(this.props.snackbarType === 'warning'){
        return <WarningIcon className="state-icon" />
    }else if(this.props.snackbarType === 'processing'){
        /* return (
          <Fragment>
            <span className="btn-spinner alternate"></span>
          </Fragment>
        ); */
        return <CircularProgress size={24} className="state-icon" />
    }else{
        return <HelpIcon className="state-icon" />
    }
  }

  handleOnBlur(){
    this.setState({ snackbarOpenState: true });
  }

  render() {
    //const snackbarOpen = this.state.snackbarOpenState;
    const snackbarOpen = this.props.snackbarOpen;
    const snackbarType = this.props.snackbarType;
    const snackbarMsg = this.props.snackbarMsg;
    let snackbarDuration = 6000;
    //console.log("snack: " + snackbarOpen);
    if(this.props.snackbarDuration === 'infinite'){
      snackbarDuration = 99999;
      return (
        <MuiSnackbar
            anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
            open={snackbarOpen}
            autoHideDuration={snackbarDuration}
            className={"snackbar " + snackbarType}
            message={
              <Fragment>
                {this.setSnackbarIcon()}
                <span>{snackbarMsg}</span>
              </Fragment>
            }
            onBlur={this.handleOnBlur}
          />
      );
    }else{
      return (
        <MuiSnackbar
            anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
            open={snackbarOpen}
            onClose={this.props.onSnackbarClose}
            autoHideDuration={6000}
            className={"snackbar " + snackbarType}
            message={
              <Fragment>
                {this.setSnackbarIcon()}
                <span>{snackbarMsg}</span>
              </Fragment>
            }
            action={[
              <IconButton
                key="close"
                aria-label="close"
                onClick={this.props.onSnackbarClose}
                className="close-btn"
                size="small"
              >
                <CloseIcon fontSize="small" />
              </IconButton>
            ]}
          />
      );
    }
      
  }
}
