import React, { useState, Fragment } from 'react';
import {
  Box,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio
} from '@material-ui/core';
//import ErrorIcon from '@material-ui/icons/Error';
import IkiButton from '../IkiFormElements/IkiButton';
import IkiInput from '../IkiFormElements/IkiInput';
import IkiDialog from '../IkiDialog';

export function CloneFlowDialog(props: any) {
  const {
    handleSubmit,
    handleCancel,
    open,
    isCloningProgress,
    cloneFlowId,
    cloneFlowName,
    cloneFlowError,
    projectsList,
    projectId
  } = props;
  const [flowName, setFlowName] = useState(cloneFlowName);
  const [flowNameError, setFlowNameError] = useState('');
  const [cloneType, setCloneType] = React.useState('with-data');
  const [cloneTypeDisabled, setCloneTypeDisabled] = React.useState(false);
  const [project, setProject] = React.useState(projectId);
  const [projectError, setProjectError] = React.useState('');

  const handleCloneType = (event: React.ChangeEvent<HTMLInputElement>) => {
    setCloneType((event.target as HTMLInputElement).value);
  };

  const handleProject = (event: React.ChangeEvent<{ value: unknown }>) => {
    var selectedProjectId = event.target.value;
    setProject(selectedProjectId as string);
    if(selectedProjectId !== projectId){
      setCloneTypeDisabled(true);
      setCloneType('no-data');
    }else{
      setCloneTypeDisabled(false);
      setCloneType('with-data');
    }
  };

  const handleNamingOnEnter = () => {
    var copyName = cloneFlowName;
    var copyIndex = copyName.indexOf('copy');
    if (copyIndex > -1) {
      var copyVersion = parseInt(copyName.substring(copyIndex + 4));
      if (isNaN(copyVersion)) {
        copyVersion = 1;
        copyName += ' ' + copyVersion;
      } else {
        copyVersion++;
        copyName = copyName.substring(0, copyIndex + 4) + ' ' + copyVersion;
      }
    } else {
      copyName = cloneFlowName + ' copy';
    }
    setFlowName(copyName);
  };

  const prepareProjectsList = () => {
    var projectsArray: any = [];
    projectsList.forEach((projectData: any, index: number) => {
      var projectInfo = {
        value: projectData.project_id,
        text: projectData.name
      }
      projectsArray.push(projectInfo);
    })
    //console.log("projectsArray: ", projectsArray);
    return (
      <IkiInput
        //_disabled={cloneType === 'no-data'} 
        _required={true}
        _label="Cloned Flow target Project"
        _placeholder=""
        _type='text'
        _autoComplete="new-password"
        _value={project}
        _isSelect={true}
        _selectOptionsKeyForVal='value'
        _selectOptionsKeyForTxt='text'
        _fullWidth={true}
        _selectOptions={projectsArray}
        _onChange={handleProject}
        _error={projectError}
      />
    );
  };

  const handleDialogSubmit = () => {
    //console.log('cloneFlowError: ' + cloneFlowError + ", flowName: " + flowName);
    var countErrors: number = 0;
    if(flowName === ''){
      countErrors++;
      setFlowNameError('Required');
    }else{
      if(cloneFlowError !== '') {
        countErrors++;
        setFlowNameError(cloneFlowError);
      } else {
        setFlowNameError('');
      }
    }
    if(project === ''){
      countErrors++;
      setProjectError('Required');
    }else{
      setProjectError('');
    }
    if(countErrors === 0) {
      handleSubmit(flowName, cloneFlowId, project, cloneType);
    }
    //console.log('countErrors...', countErrors);
  }

  return (
    <IkiDialog
            _open={open}
            _onEnter={() => {
              //setFlowName('');
              setProject(projectId);
              setFlowNameError('');
              setProjectError('');
              handleNamingOnEnter();
              setCloneType('with-data');
              setCloneTypeDisabled(false);
            }}
            _onClose={
              () => {
                setFlowName('');
                setFlowNameError('');
                setProjectError('');
                setProject('');
                handleCancel();
                setCloneType('with-data');
                setCloneTypeDisabled(false);
              }
            }
            _onExit={
              () => {
                setFlowName('');
                setFlowNameError('');
                setProjectError('');
                setProject('');
                handleCancel();
                setCloneType('with-data');
                setCloneTypeDisabled(false);
              }
            }
            _type='regular'
            _icon={false}
            _title='Clone Flow'
            _subtitle=''
            _size='xs'
            _content={
              <Fragment>
                <div className="section">
                  <div className="txt-dialog-faded1">{cloneFlowId}</div>
                  <div className="txt-dialog-impact1">{cloneFlowName}</div>
                </div>
                <Box mb={2}>
                  <FormControl 
                    className="radio-btn-holder with-label" 
                    component="fieldset" 
                    disabled={cloneTypeDisabled}
                  >
                    <FormLabel component="div">Clone type:</FormLabel>
                    <RadioGroup 
                      aria-label="clone-type" 
                      name="clone-type" 
                      value={cloneType} 
                      onChange={handleCloneType}
                    >
                      <FormControlLabel 
                        value="with-data" 
                        control={
                          <Radio 
                            color="primary"
                            size="small"
                          />
                        } 
                        label="Full copy (With data)" 
                      />
                      <FormControlLabel 
                        value="no-data" 
                        control={
                          <Radio 
                            color="primary"
                            size="small"
                          />
                        } 
                        label="Without data" 
                      />
                    </RadioGroup>
                  </FormControl>
                </Box>
                <IkiInput 
                  _required={true}
                  _label="Name of cloned Flow"
                  _placeholder="Enter name of cloned Flow"
                  _type='text'
                  _value={flowName}
                  _onChange={(e: any) => {
                    setFlowName(e.target.value);
                  }}
                  _characterCounter={40}
                  _fullWidth={true}
                  _error={flowNameError}
                />
                {prepareProjectsList()}
              </Fragment>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Cancel"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={() => handleCancel()}
                />
                <IkiButton
                  _variant='contained'
                  _color='primary'
                  _label="Clone"
                  _type='button'
                  _fullWidth={false}
                  _loading={isCloningProgress}
                  _disabled={isCloningProgress}
                  _isAlternate={true}
                  _onClick={handleDialogSubmit}
                />
              </Fragment>
            }
      />
  );

}