import React, { Fragment } from 'react';
import {
  Box,
  Button,
  FormControl,
  FormLabel,
  RadioGroup,
  FormControlLabel,
  Radio,
} from '@material-ui/core';
//import ErrorIcon from '@material-ui/icons/Error';
import AlertMsg from '../../components/AlertMsg';
import IkiDialog from '../../components/IkiDialog';
import IkiButton from '../../components/IkiFormElements/IkiButton';
import IkiInput from '../../components/IkiFormElements/IkiInput';

export function SaveDatasetDialog(props: any) {
  const {
    handleSubmit,
    handleSubmitOverwrite,
    handleCancel,
    open,
    isSaveInProgress,
    datasetId,
    datasetName,
    newDatasetNameExists,
    dismissOverwriteAlert
  } = props;
  
  const [datasetOverwrite, setDatasetOverwrite] = React.useState("overwrite");
  const [newDatasetName, setNewDatasetName] = React.useState("");
  const [newDatasetNameError, setNewDatasetNameError] = React.useState("");
  const [newDatasetId, setNewDatasetId] = React.useState("");
  const [overwriteSubmitInProgress, setOverwriteSubmitInProgress] = React.useState(false);

  const handleSaveDatasetSubmit = () => {
    if(datasetOverwrite === "create-new" && newDatasetName === ""){
        setNewDatasetNameError("When creating new dataset, Name of new Dataset is required!");
    } else{
        setNewDatasetNameError("");
        var overwrite: boolean = false;
        if(datasetOverwrite === "overwrite"){
            overwrite = true
        }
        handleSubmit(datasetId, overwrite, newDatasetName, newDatasetId);
    }
  }

  const handleSetDatasetOverwrite = (event: React.ChangeEvent<HTMLInputElement>) => {
    setDatasetOverwrite((event.target as HTMLInputElement).value);
    setNewDatasetName("");
    setNewDatasetId("");
    setNewDatasetNameError("");
  };

  const handleOverwriteSubmit = () => {
    setOverwriteSubmitInProgress(true);
    var overwrite: boolean = false;
    if(datasetOverwrite === "overwrite"){
      overwrite = true
    }
    handleSubmitOverwrite(datasetId, overwrite, newDatasetName, newDatasetId);
  }

  const handelExitDialog = () => {
    setDatasetOverwrite("overwrite");
    setNewDatasetName("");
    setNewDatasetId("");
    setNewDatasetNameError("");
    handleCancel();
  }

  var subtitle: string = "";
  if(newDatasetNameExists === true) {
    subtitle = '' + datasetName + ' => ' + newDatasetName;
  } else {
    subtitle = datasetName;
  }

  return (
    <IkiDialog
                _open={open}
                _onClose={() => handelExitDialog()}
                _type='share'
                _icon={false}
                _title='Save Dataset'
                _subtitle={subtitle}
                _size='xs'
                _align='left'
                _content={
                    <>
                        {newDatasetNameExists === true ? (
                          <Fragment>
                            <AlertMsg
                              alertType="warning"
                              alertContent={
                                <Fragment>
                                    <Box>
                                      <span>Dataset with same name already exists.</span>
                                      <br />
                                      <strong>{newDatasetName}</strong>
                                    </Box>
                                    <Box mt={3}>Please note:<br /> All flows which contain this dataset will be reflected by this change.</Box>
                                    <Box mt={3}>
                                      <strong>Are you sure you want to continue and overwrite dataset?</strong>
                                    </Box>
                                </Fragment>
                              }
                              alertButtons={
                                <Fragment>
                                  <Button
                                    onClick={() => dismissOverwriteAlert()}
                                    variant="contained"
                                    color="default"
                                    className="button"
                                    disabled={overwriteSubmitInProgress}
                                  >
                                    No, go back
                                  </Button>
                                  <Button
                                    variant="contained"
                                    color="primary"
                                    className="button"
                                    onClick={() => handleOverwriteSubmit}
                                    disabled={overwriteSubmitInProgress}
                                  >
                                    {overwriteSubmitInProgress && (
                                      <span className="btn-spinner alternate"></span>
                                    )}
                                    {overwriteSubmitInProgress ? 'Overwriting...' : 'Yes, overwrite'}
                                  </Button>
                                </Fragment>
                              }
                            />
                          </Fragment>
                        ):(
                          <Box my={2}>
                            <FormControl 
                                className="radio-btn-holder with-label" 
                                component="fieldset" 
                            >
                                <FormLabel component="div">Choose one of following options:</FormLabel>
                                <RadioGroup 
                                    aria-label="clone-type" 
                                    name="clone-type"
                                    value={datasetOverwrite} 
                                    onChange={handleSetDatasetOverwrite} 
                                >
                                  <FormControlLabel 
                                      value="overwrite"
                                      control={
                                      <Radio 
                                          color="primary"
                                          size="small"
                                      />
                                      } 
                                      label="Overwrite current Dataset" 
                                  />
                                  <FormControlLabel 
                                      value="create-new"
                                      control={
                                      <Radio 
                                          color="primary"
                                          size="small"
                                      />
                                      } 
                                      label="Create new Dataset" 
                                  />
                                </RadioGroup>
                            </FormControl>
                          </Box>
                          )}
                          {newDatasetNameExists === false && datasetOverwrite === "create-new" && (
                              <Box mb={2}>
                                  <IkiInput 
                                        _label="Name of new Dataset"
                                        _placeholder="Enter name for new Dataset"
                                        _type='text'
                                        _autoComplete="new-password"
                                        _value={newDatasetName}
                                        _onChange={(e: any) => {
                                          setNewDatasetName(e.target.value);
                                        }}
                                        _characterCounter={40}
                                        _autoFocus={true}
                                        _fullWidth={true}
                                        _error={newDatasetNameError}
                                  />
                              </Box>
                          )}
                    </>
                }
                _actions={
                    <Fragment>
                        <IkiButton
                        _variant='text'
                        _color='default'
                        _label="Cancel"
                        _type='button'
                        _fullWidth={false}
                        _loading={false}
                        _onClick={() => handleCancel()}
                        />
                        <IkiButton
                        _variant='contained'
                        _color='primary'
                        _label="Save"
                        _type='button'
                        _fullWidth={false}
                        _loading={isSaveInProgress}
                        _disabled={isSaveInProgress}
                        _onClick={() => handleSaveDatasetSubmit()}
                        />
                    </Fragment>
                }
        />
  );

}