import React, { Fragment } from 'react';
import {
  Grid,
  Box,
  ExpansionPanel,
  ExpansionPanelSummary,
  ExpansionPanelDetails
} from '@material-ui/core';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import IkiDialog from '../IkiDialog';
import IkiButton from '../IkiFormElements/IkiButton';
import moment from 'moment';

export function PreviewScheduleDialog(props: any) {
  const {
    handleDialogClose,
    open,
    data
  } = props;

  console.log('PreviewScheduleDialog...', props);

  const [scheduleTime, setScheduleTime] = React.useState('');
  const [eventStatusStyle, setEventStatusStyle] = React.useState('');

  const handleOnCloseScheduleDialog = () => {
    handleDialogClose();
  }

  const handleOnEnterScheduleDialog = () => {
    //var scheduleTime = moment(props.data.start).format("DD-MM-YYYY HH:mm");
    var scheduleTime = moment(props.data.start).format("MM/DD/YYYY h:mm A");
    setScheduleTime(scheduleTime);
    var eventStatusStyle: string = '';
    if(props.data.status === 'SUCCESS') {
        eventStatusStyle = 'success';
    } else if(props.data.status === 'FAILED') {
        eventStatusStyle = 'error';
    } else{
        eventStatusStyle = 'pending';
    }
    setEventStatusStyle(eventStatusStyle);
  }

  return (
      <IkiDialog
            _open={open}
            _onEnter={() => handleOnEnterScheduleDialog()}
            _onClose={() => handleOnCloseScheduleDialog()}
            _type='share'
            _icon={false}
            _title='Event'
            _subtitle={props.data.category !== '' ? props.data.category : ''}
            _size='xs'
            _align='left'
            _content={
              <>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <div className="section">
                            <div className="txt-dialog-faded1">Type: </div>
                            <div className="txt-dialog-impact1">Flow</div>
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                        <div className="section">
                            <div className="txt-dialog-faded1">Status: </div>
                            <div className={"txt-dialog-impact1 " + eventStatusStyle}>{props.data.status !== '' ? (props.data.status).toLowerCase() : '-'}</div>
                        </div>
                    </Grid>
                </Grid>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <div className="section">
                            <div className="txt-dialog-faded1">Item: </div>
                            <div className="txt-dialog-impact1">{props.data.pipeline.name}</div>
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                        <div className="section">
                            <div className="txt-dialog-faded1">Project: </div>
                            <div className="txt-dialog-impact1">{props.data.project.name}</div>
                        </div>
                    </Grid>
                </Grid>
                <Grid container spacing={1}>
                    <Grid item xs={6}>
                        <div className="section">
                            <div className="txt-dialog-faded1">Time: </div>
                            <div className="txt-dialog-impact1">{scheduleTime}</div>
                        </div>
                    </Grid>
                    <Grid item xs={6}>
                        <div className="section">
                            <div className="txt-dialog-faded1">Schedule name: </div>
                            <div className="txt-dialog-impact1">{props.data.schedule.name}</div>
                        </div>
                    </Grid>
                </Grid>
                {props.data.status === 'FAILED' && (
                    <Fragment>
                        <Box mt={2}></Box>
                        <Grid container spacing={1}>
                            <Grid item xs={12}>
                                <ExpansionPanel className="expand-panel">
                                    <ExpansionPanelSummary
                                        expandIcon={<ExpandMoreIcon />}
                                        aria-controls="log-content"
                                        id="log-header"
                                    >
                                        <div>Error Log Message</div>
                                    </ExpansionPanelSummary>
                                    <ExpansionPanelDetails>
                                        <div className="error error-log-string">
                                            {props.data.message}
                                        </div>
                                    </ExpansionPanelDetails>
                                </ExpansionPanel>
                            </Grid>
                        </Grid>
                    </Fragment>
                )}
              </>
            }
            _actions={
              <Fragment>
                <IkiButton
                  _variant='text'
                  _color='default'
                  _label="Close"
                  _type='button'
                  _fullWidth={false}
                  _loading={false}
                  _onClick={() => handleOnCloseScheduleDialog()}
                />
              </Fragment>
            }
        />
  );
}
