import React, { Component } from 'react';

import {
  Button,
  IconButton,
  Input,
  FormControl,
  InputLabel,
  Select,
  MenuItem
} from '@material-ui/core';
//import './info-drawer.scss';
//import ArrowBackRounded from '@material-ui/icons/ArrowBackRounded';
import ScheduleIcon from '@material-ui/icons/Schedule';
import EditIcon from '@material-ui/icons/Edit';
import SaveIcon from '@material-ui/icons/Save';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import CheckIcon from '@material-ui/icons/Check';
import PriorityHighIcon from '@material-ui/icons/PriorityHigh';
import AspectRatioIcon from '@material-ui/icons/AspectRatio';
import MinimizeIcon from '@material-ui/icons/Minimize';
import ActionsMenu from './actions-menu';
//import PipelineLogs from './pipeline-logs';
import { getFriendlyLabel } from '../../util';
import IkiButton from '../../components/IkiFormElements/IkiButton';

type InfoDrawerProps = {
  onRun: () => void;
  onSchedule: () => void;
  title: string;
  response: any;
  projectName: string;
  onSaveTitle: (flowName: string, projectName: string) => void;
  onFlowDiagramModeChanged: (mode: string) => void;
  flowDiagramMode: string;
  modeChangePending: boolean;
  pipelineRunning: boolean;
  pipelineLogs: any[];
  pipelineLoaded: boolean;
  facetDocLoaded: boolean;
  validateDiagram: () => boolean;
  editRunFlowRunning: boolean;
  pipelineSpecs: any[];
  pipelineInfo: any;
  updatePipelineArg: (pipeArgName: string, pipeArgValue: any) => void;
};

type InfoDrawerState = {
  title: string;
  projectName: string;
  editMode: boolean;
  flowDiagramMode: string;
  statusBoxMode: boolean;
  selectedActionsTab: string;
  pipelineInfo: any;
};

class InfoDrawer extends Component<InfoDrawerProps, InfoDrawerState> {
  state = {
    title: '',
    editMode: false,
    projectName: '',
    flowDiagramMode: '',
    statusBoxMode: false,
    selectedActionsTab: 'actions',
    pipelineInfo: {}
  };
  componentDidMount() {
    //console.log("InfoDrawer: props: ");
    //console.log(this.props);
    this.setState({
      flowDiagramMode: this.props.flowDiagramMode,
      pipelineInfo: this.props.pipelineInfo
    });
  }
  toggleEditMode() {
    if (!this.state.editMode) {
      this.setState({
        editMode: true,
        title: this.props.title,
        projectName: this.props.projectName
      });
    } else {
      this.setState({
        editMode: false
      });
      this.props.onSaveTitle(this.state.title, this.state.projectName);
    }
  }
  toggleStatusBoxMode() {
    if (!this.state.statusBoxMode) {
      this.setState({ statusBoxMode: true });
    } else {
      this.setState({ statusBoxMode: false });
    }
  }
  timeConverter(UNIX_timestamp: any) {
    var a = new Date(UNIX_timestamp * 1000);
    var months = [
      'Jan',
      'Feb',
      'Mar',
      'Apr',
      'May',
      'Jun',
      'Jul',
      'Aug',
      'Sep',
      'Oct',
      'Nov',
      'Dec'
    ];
    var year = a.getFullYear();
    var month = months[a.getMonth()];
    var date = a.getDate();
    var hour = a.getHours();
    var min = a.getMinutes();
    var sec = a.getSeconds();
    var time =
      date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec;
    return time;
  }

  getResponse() {
    /*if (this.props.response) {
      return <div className="success">Success</div>;
    }*/
    //console.log(this.props.pipelineLogs);
    //console.log("response: " + this.props.response);
    //console.log("pipelineRunning in info-drawer: " + this.props.pipelineRunning);

    var pipelineLogArray = this.props.pipelineLogs;
    var keys = Object.keys(pipelineLogArray);
    if (keys.length === 0) {
      return;
    }
    return keys.map((key: any) => {
      var pipelinelog: any = pipelineLogArray[key];
      var logTimestamp = this.timeConverter(pipelinelog.timestamp);
      return (
        pipelinelog.status && (
          <div
            key={key}
            className={
              pipelinelog.status === 'SUCCESS'
                ? 'status-row fade-in success'
                : 'status-row fade-in failed'
            }
          >
            <div className="status-icon">
              {pipelinelog.status === 'SUCCESS' ? (
                <CheckIcon fontSize="small" />
              ) : (
                  <PriorityHighIcon fontSize="small" />
                )}
            </div>
            <div className="status-data">
              <div className="status-timestamp clip-txt">{logTimestamp}</div>
              <div className="status-user clip-txt">{pipelinelog.user}</div>
              <div className="status-run_type clip-txt">
                {pipelinelog.run_type}
              </div>
              <div className="status-message clip-txt">
                {pipelinelog.message}
              </div>
            </div>
          </div>
        )
      );
    });
  }

  onChangeProjectName(e: any) {
    this.setState({
      projectName: e.target.value
    });
  }
  onChangeTitle(e: any) {
    this.setState({
      title: e.target.value
    });
  }
  toggleFlowDiagramMode(mode: string) {
    this.setState({
      flowDiagramMode: mode
    });
    this.props.onFlowDiagramModeChanged(mode);
  }
  handleActionsTabClick(actionTabSelected: string) {
    this.setState({ selectedActionsTab: actionTabSelected })
  }
  render() {
    const disableButtons =
      this.state.flowDiagramMode === 'interactive' ||
      this.props.modeChangePending ||
      this.props.pipelineRunning;

    var pipeline: any;
    var data_format = "";
    if (this.state.pipelineInfo !== "") {
      pipeline = this.state.pipelineInfo;
      if (pipeline.definition) {
        if (pipeline.definition.arguments) {
          //console.log("arguments ready");
          //console.log(pipeline["definition"]["arguments"]);
          if (pipeline.definition.arguments.data_type_cast_rule && data_format === "") {
            data_format = pipeline.definition.arguments.data_type_cast_rule;
            //console.log("data_format: " + data_format);
          }
        }
      }
    }

    return (
          <div className="info-drawer-root">
            <div className="info-drawer-section">
              <div className="info-drawer-title">
                <span className="title">Info</span>
                <IconButton
                  onClick={() => this.toggleEditMode()}
                  size="small"
                  disabled={disableButtons}
                >
                  {this.state.editMode ? (
                    <SaveIcon fontSize="small" />
                  ) : (
                      <EditIcon fontSize="small" />
                    )}
                </IconButton>
              </div>
              <div className="info-drawer-title-group">
                <div className="info-drawer-title-title">Flow name:</div>
                <div className="info-drawer-title-subtitle clip-txt">
                  {!this.state.editMode && <span>{this.props.title}</span>}
                  {this.state.editMode && (
                    <Input
                      value={this.state.title}
                      onChange={e => this.onChangeTitle(e)}
                      inputProps={{
                        maxLength: 40
                      }}
                    ></Input>
                  )}
                </div>
              </div>
              <div className="info-drawer-title-group">
                <div className="info-drawer-title-title">Project name:</div>
                <div className="info-drawer-title-subtitle clip-txt">
                  {!this.state.editMode && (
                    <span>{this.props.projectName}</span>
                  )}
                  {this.state.editMode && (
                    <Input
                      value={this.state.projectName}
                      onChange={e => this.onChangeProjectName(e)}
                      inputProps={{
                        maxLength: 40
                      }}
                    ></Input>
                  )}
                </div>
              </div>
            </div>
            <div className="info-drawer-section">
              {/* <div className={"info-drawer-title d-inline-block"}>
                <Typography
                  variant="h4"
                  onClick={() => this.handleActionsTabClick("actions")}
                  className={this.state.selectedActionsTab !== "actions" ? "act-tab in-active" : "act-tab"}
                >Actions</Typography>
              </div>
              <div className="info-drawer-title d-inline-block ml-7">
                <Typography
                  variant="h4"
                  className={this.state.selectedActionsTab !== "arguments" ? "act-tab in-active" : "act-tab"}
                  onClick={() => this.handleActionsTabClick("arguments")}
                >Arguments</Typography>
              </div> */}
              <div className="info-drawer-title">
                <span className="title">
                  {this.state.selectedActionsTab === "actions" && (
                      <span>Actions</span>
                  )}
                  {this.state.selectedActionsTab === "arguments" && (
                      <span>Arguments</span>
                  )}
                </span>
                <ActionsMenu
                  onActions={() => {
                    this.setState({ selectedActionsTab: 'actions' });
                  }}
                  onArguments={() => {
                    this.setState({ selectedActionsTab: 'arguments' });
                  }}
                />
              </div>
              {this.state.selectedActionsTab === "actions" && (
                <div className="info-drawer-button-group">
                  <IkiButton
                    _variant='contained'
                    _color='primary'
                    _label="Run Flow"
                    _type='button'
                    _align='left'
                    _fullWidth={true}
                    _disabled={disableButtons}
                    _loading={this.props.pipelineRunning}
                    _startIcon={
                      <PlayArrowIcon />
                    }
                    _onClick={() => this.props.onRun()}
                  />
                  <IkiButton
                    _variant='contained'
                    _color='primary'
                    _label="Schedule Flow"
                    _type='button'
                    _align='left'
                    _fullWidth={true}
                    _disabled={disableButtons}
                    _loading={false}
                    _startIcon={
                      <ScheduleIcon />
                    }
                    _onClick={() => this.props.onSchedule()}
                  />
                </div>
              )}
              {this.state.selectedActionsTab === "arguments" && (
                Object.keys(this.props.pipelineSpecs).length > 0 ? (
                  this.props.pipelineSpecs[0].name === "data_type_cast_rule" && (
                    <div className="mt-10">
                      <div className="form-input mt-10">
                        <FormControl
                          required={this.props.pipelineSpecs[0].is_required}
                          //error={pipelineSpecs[0].error}
                          className="select-control"
                          margin="normal"
                          variant="outlined"
                          size="small"
                          fullWidth
                        >
                          <InputLabel htmlFor={this.props.pipelineSpecs[0].name} shrink={true}>
                            {getFriendlyLabel(this.props.pipelineSpecs[0].name)}
                          </InputLabel>
                          <Select
                            value={data_format ? data_format : ''}
                            variant="outlined"
                            displayEmpty
                            onChange={(event: any) => {
                              //this.onUpdatedNestedComponent(event, parentKey, key, index);
                              this.props.updatePipelineArg("data_type_cast_rule", event.target.value);
                              var pipeline = this.state.pipelineInfo;
                              // eslint-disable-next-line
                              pipeline["definition"]["arguments"] = { ["data_type_cast_rule"]: event.target.value };
                              //console.log("data_type_cast_rule onChange: ");
                              //console.log(pipeline);
                              this.setState({
                                ...this.state,
                                pipelineInfo: pipeline
                              });
                            }}
                            inputProps={{
                              name: this.props.pipelineSpecs[0].name,
                              id: this.props.pipelineSpecs[0].name
                            }}
                          >
                            <MenuItem value="" disabled>Select</MenuItem>
                            {this.props.pipelineSpecs[0].options.map((attr: any, index: number) => {
                              if (this.props.pipelineSpecs[0].name === 'file_type') {
                                var cleanedAttr: any = attr;
                              } else {
                                // eslint-disable-next-line
                                var cleanedAttr: any = getFriendlyLabel(attr);
                              }
                              return (
                                <MenuItem value={attr} key={index}>
                                  {cleanedAttr}
                                </MenuItem>
                              );
                            })}
                          </Select>
                        </FormControl>
                      </div>
                    </div>
                  )
                ) : (
                    <div className="mt-10 secondary">No arguments</div>
                  )
              )}
            </div>
            <div
              className={
                this.state.statusBoxMode
                  ? 'info-drawer-section status-section is-open'
                  : 'info-drawer-section status-section'
              }
            >
              <div className="info-drawer-title">
                <span className="title">Status</span>
                <IconButton
                  onClick={() => this.toggleStatusBoxMode()}
                  size="small"
                >
                  {this.state.statusBoxMode ? (
                    <MinimizeIcon fontSize="small" />
                  ) : (
                      <AspectRatioIcon fontSize="small" />
                    )}
                </IconButton>
              </div>
              <div className="status-holder">
                {this.getResponse()}
              </div>
            </div>
      </div>
    );
  }
}

export default InfoDrawer;
