import { _axios } from '../common';

export const authenticateUser = (credentials: any): Promise<object> => {
  var data = {
    user: {
      email: credentials.email,
      password: credentials.password
    }
  };
  return _axios.post('/authenticate', data);
};

export function loginUser(user: string, pass: string) {
  var data = {
    user: {
      email: user,
      password: pass
    }
  };

  return _axios.post('/authenticate', data);
}

export function signupUser(user: string, pass: string) {
  var data = {
    user: {
      email: user,
      password: pass
    }
  };

  return _axios.post('/register', data);
}

export function completeUserRegistration(user: string, code: string) {
  var data = {
    user: {
      email: user,
      confirmation_code: code
    }
  };

  return _axios.post('/confirm-sign-up', data);
}

export function initiateForgotPassword(user: string) {
  var data = {
    user: {
      email: user
    }
  };

  return _axios.post('/initiate-forgot-password', data);
}

export function confirmForgotPassword(
  user: string,
  code: string,
  pass: string
) {
  var data = {
    user: {
      email: user,
      confirmation_code: code,
      password: pass
    }
  };

  return _axios.post('/confirm-forgot-password', data);
}

export function confirmChangePassword(
  user: string,
  pass: string,
  pass2: string
) {
  var data = {
    user: {
      email: user,
      previous_password: pass,
      proposed_password: pass2
    }
  };

  return _axios.post('/change-password', data);
}

/*export function logout() {
  return _axios.post('/log-out');
}*/

export const logout = (): Promise<object> => {
  const promise = new Promise<object>((resolve, reject) => {
    return _axios.post('/log-out').then((res: any) => {
      if (res.status === 200) {
        resolve(res.data);
        purgeSessions();
      } else {
        reject(res.status);
        purgeSessions();
      }
    });
  });
  return promise;
}

export const purgeSessions = (): Promise<object> => {
  const promise = new Promise<object>((resolve, reject) => {
    var data = {};
    return _axios.post('/purge-sessions', data).then((res: any) => {
      if (res.status === 200) {
        resolve(res.data);
      } else {
        reject(res.status);
      }
    });
  });
  return promise;
};