import React, { Component } from 'react';
import { 
  FormControl,
  TextField,
  FormHelperText,
  InputAdornment,
  IconButton,
  MenuItem
} from '@material-ui/core';
import classNames from 'classnames/bind';
import ErrorIcon from '@material-ui/icons/Error';
import CharacterCounter from '../../characterCounter';
import styles from './Input.module.scss';
import { getFriendlyLabel } from '../../../util';

let cx = classNames.bind(styles);

/**
   * Form Element -> Input Component based on MaterialUI but customized for Ikigai
   * @param _label Label - string
   * @param _placeholder Placeholder - string
   * @param _value Value - string
   * @param _type Input type - string
   * @param _error Error message - string
   * @param _required Is Input Required - boolean - true | false
   * @param _fullWidth Is Input Full Width - boolean - true | false
   * @param _autoFocus Auto Focus - boolean - true | false
   * @param _startIcon MaterialUI Icon - component e.g. <Visibility />
   * @param _endIcon MaterialUI Icon - component e.g. <Visibility />
   * @param _characterCounter Custom - string
   * @param _size Size - string - small | medium (default)
   * @param _margin Margin - string - dense | none | normal (default)
   * @param _filterType Is Filter Type - boolean - true | false (default)
   */
export default class IkiInput extends Component {
  render() {
    const { 
      _label, 
      _placeholder, 
      _value, 
      _type
    } = this.props;
    let _inputProp = {};
    let _InputProp = {};
    let _selectProp = {};
    const _error = this.props._error ? this.props._error : '';
    const _required = this.props._required ? this.props._required : false;
    const _disabled = this.props._disabled ? this.props._disabled : false;
    const _fullWidth = this.props._fullWidth ? this.props._fullWidth : false;
    const _autoFocus = this.props._autoFocus ? this.props._autoFocus : false;
    const _size = this.props._size ? this.props._size : 'medium';
    const _margin = this.props._margin ? this.props._margin : 'normal';
    const _filterType = this.props._filterType ? this.props._filterType : false;
    const _endIcon = this.props._endIcon ? this.props._endIcon : '';
    const _startIcon = this.props._startIcon ? this.props._startIcon : '';
    const _characterCounter = this.props._characterCounter ? this.props._characterCounter : '';
    const _isSelect = this.props._isSelect ? this.props._isSelect : false;
    const _selectOptions = this.props._selectOptions ? this.props._selectOptions : null;
    const _selectOptionsKeyForVal = this.props._selectOptionsKeyForVal ? this.props._selectOptionsKeyForVal : '';
    const _selectOptionsKeyForTxt = this.props._selectOptionsKeyForTxt ? this.props._selectOptionsKeyForTxt : '';
    const _multiline = this.props._multiline ? this.props._multiline : false;
    const _rows = this.props._rows ? this.props._rows : 1;
    //let _customFCkey = (Math.floor(Math.random() * 1001)).toString();
    let errorMsg = "";
    if(_error !== ''){
      errorMsg = _error;
    }else{
      if(_required && _value === "") {
        errorMsg = 'Required';
      }
    }
    if(_characterCounter !== ''){
      //_inputProp.maxLength = _characterCounter;
      _inputProp = { 
        maxLength: _characterCounter 
      };
    }
    if(_startIcon !== ''){
      _InputProp.startAdornment = this.constructStartIconProps();
    }
    if(_endIcon !== ''){
      _InputProp.endAdornment = this.constructEndIconProps();
    }
    if(_isSelect !== ''){
      _selectProp.value = _value;
    }
    //console.log("IkiInput props: ", this.props);
    var inputHolderClass = cx({
      inputHolder: true,
      'has-counter': _characterCounter !== ''
    });
    var inputClass = cx({
      input: true,
      'has-startIcon': _startIcon !== '',
      'of-filter-type': _filterType
    });
    return(
      <>
        <FormControl
          //key={'ikiinput-fc-' + _customFCkey} 
          fullWidth={_fullWidth}
          error={_error !== ''}
          className={inputHolderClass}
          disabled={_disabled}
          //margin={_margin}
        >
          <TextField
            disabled={_disabled}
            className={inputClass}
            margin={_margin}
            variant="outlined"
            fullWidth={_fullWidth}
            label={_label}
            placeholder={_placeholder}
            type={_type}
            select={_isSelect}
            autoComplete="new-username"
            value={_value}
            onChange={this.props._onChange}
            required={_required}
            autoFocus={_autoFocus}
            error={_error !== ''}
            size={_size}
            multiline={_multiline}
            rows={_rows}
            inputProps={
              _inputProp
            }
            InputProps={
              _InputProp
            }
            InputLabelProps={{
              shrink: true
            }}
            SelectProps={
              _selectProp
            }
          >
            {_isSelect && (
              _selectOptions.map(option => (
                _selectOptionsKeyForVal === '' && _selectOptionsKeyForTxt === '' ? (
                  <MenuItem key={option.toString()} value={option}>
                    {getFriendlyLabel(option)}
                  </MenuItem>
                ):(
                  <MenuItem key={option[_selectOptionsKeyForTxt]} value={option[_selectOptionsKeyForVal]}>
                    {option[_selectOptionsKeyForTxt]}
                  </MenuItem>
                )
              ))
            )}
          </TextField>
          {_characterCounter !== '' && (
            <CharacterCounter
              currentLength={_value ? _value.length : 0}
              maxLength={_characterCounter}
              error={_error !== ''}
            />
          )}
          {_error !== '' && (
            <FormHelperText 
              className="input-error" 
              error
            >
              <ErrorIcon />
              <span>{errorMsg}</span>
            </FormHelperText>
          )}
        </FormControl>
      </>
    );
  }
  constructStartIconProps = () => {
    const _startIcon = this.props._startIcon ? this.props._startIcon : '';
    if(_startIcon._withButton) {
      return(
        <InputAdornment 
          position={_startIcon._position}
          className={styles.startIcon}
        >
          <IconButton
            edge={_startIcon._edge}
            onClick={_startIcon._onClick}
            onMouseDown={event => {
               event.preventDefault();
            }}
            size={_startIcon._size}
          >
            {_startIcon._icon}
          </IconButton>
        </InputAdornment>
      )
    }else {
      return(
        <InputAdornment 
          position={_startIcon._position}
          className={styles.startIcon}
        >
          {_startIcon._icon}
        </InputAdornment>
      )
    }
  }
  constructEndIconProps = () => {
    const _endIcon = this.props._endIcon ? this.props._endIcon : '';
    if(_endIcon._withButton) {
      return(
          <InputAdornment 
            position={_endIcon._position}
          >
            <IconButton
              edge={_endIcon._edge}
              onClick={_endIcon._onClick}
              onMouseDown={event => {
                event.preventDefault();
              }}
              size={_endIcon._size}
            >
              {_endIcon._icon}
            </IconButton>
          </InputAdornment>
      )
    } else {
      return(
        <InputAdornment 
          position={_endIcon._position}
        >
          {_endIcon._icon}
        </InputAdornment>
      )
    }
  }
}