import React, { Component, Fragment } from 'react';
import { 
    IconButton 
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import IkiTable from '../../../components/IkiLayout/IkiTable'
import classNames from 'classnames/bind';
import styles from './Peek.module.scss';
let cx = classNames.bind(styles);

type PeekComponentProps = {
  peekInitiated: boolean;
  peekMinMax: string;
  peekLoading: boolean;
  peekContent: any;
  handleClosePeekArrow: () => void;
  handleMinMaxPeekArrow: (minmax: string) => void;
};

type PeekComponentState = {};

class PeekComponent extends Component<PeekComponentProps, PeekComponentState> {
  componentDidMount() {
      //console.log("PeekComponent ...");
      //console.log(this.props);
  }

  render() {
    var peekClass = cx({
        peekWrap: true,
        'show': this.props.peekInitiated,
        'min': this.props.peekMinMax === 'min',
        'max': this.props.peekMinMax === 'max'
    });

    return (
            <div className={peekClass}>
                {this.props.peekInitiated && (
                    <Fragment>
                        <div className="peek-wrap-header">
                            <div className="peek-wrap-title">Peek arrow</div>
                            <IconButton
                                className="is-right mr-16"
                                onClick={
                                () => {
                                    if (this.props.peekMinMax === 'min') {
                                    this.props.handleMinMaxPeekArrow('max')
                                    } else {
                                    this.props.handleMinMaxPeekArrow('min')
                                    }
                                }
                                }
                                size="small"
                                color="default"
                            >
                                {this.props.peekMinMax === 'min' ? (
                                <KeyboardArrowUpIcon fontSize="small" />
                                ) : (
                                    <KeyboardArrowDownIcon fontSize="small" />
                                )
                                }
                            </IconButton>
                            <IconButton
                                className="is-right"
                                onClick={() => this.props.handleClosePeekArrow()}
                                size="small"
                                color="default"
                            >
                                <CloseIcon fontSize="small" />
                            </IconButton>
                        </div>
                        <div className="peek-wrap-content">
                            {this.props.peekLoading && (
                                <div className="iki-loader-wrap peek-load-wrap">
                                <div className="iki-loader-inner">
                                    <span className="iki-spinner"></span>
                                    <div className="iki-loader-txt">Loading...</div>
                                </div>
                                </div>
                            )}
                            {
                                Object.keys(this.props.peekContent).length > 0 && (
                                Object.keys(this.props.peekContent).map((peekContentKey: any) => (
                                    // <div>{peekContentKey}</div>
                                    peekContentKey === "column_names" && (
                                        <IkiTable
                                            key={'peek-tbl-' + peekContentKey}
                                            _type="Peek"
                                            _header={this.props.peekContent["column_names"]}
                                            _stickyHeader={true}
                                            _headerDescr={this.props.peekContent["column_data_types"]}
                                            _data={this.props.peekContent["rows"]}
                                        />
                                    )
                                ))
                                )
                            }
                        </div>
                    </Fragment>
                )}
            </div>
    );
  }
}

export default PeekComponent;