import { State } from '../reducers';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import IkiApp from './iki-app-component';
import { logoutAction, deleteDatasetActionStart } from '../actions';

const mapDispatchToProps = (disptach: Dispatch) => ({
  logout: () => disptach(logoutAction()),
  deleteDataset: (datasetId:string) => disptach(deleteDatasetActionStart(datasetId)),
});
const mapStateToProps = (state: State) => ({
  data: state.flowReducerState,
  flowsLoadedError: state.flowReducerState.flowsLoaded,
  flowsLoaded: state.flowReducerState.flowsLoadedError,
  uploadProgress: state.flowReducerState.uploadProgress,
  email: state.userReducerState.email
});

export const IkiAppContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(IkiApp);
